import { bidLocalStorageKey, periodLen, period, time } from 'config';
import { StateString, AuctionBid } from 'types';
import { BigNumber as BN } from 'bignumber.js';

export const sum = (a: number, b: number): number => a + b;

export const isExpired = (maturity: Date): boolean => maturity < new Date();

export const dateSortFn = (a: Date, b: Date): number =>
  a.getTime() - b.getTime();

export const BNSortFN = (a: BN, b: BN): number => b.toNumber() - a.toNumber();

export const getWeekID = (): number => {
  const week = 60 * 60 * 24 * 7;
  const ts = new Date().getTime();
  return Math.floor(ts / week);
};

export const getUserBidsFromLocalStorage = () => {
  const raw = localStorage.getItem(bidLocalStorageKey);
  if (!raw) {
    return {};
  }
  const data = JSON.parse(raw);
  return data || {};
};

export const getUserAllBids = (): {
  [auctionID: string]: AuctionBid[];
} => {
  try {
    const data = getUserBidsFromLocalStorage();
    return data;
  } catch (e) {
    return {};
  }
};

export const getUserBids = (auctionID: string): AuctionBid[] => {
  try {
    const data = getUserBidsFromLocalStorage();
    return data[auctionID] || [];
  } catch (e) {
    return [];
  }
};

export const setUserBids = (auctionID: string, bids: AuctionBid[]) => {
  const allBids = getUserAllBids();
  allBids[auctionID] = bids;
  localStorage.setItem(bidLocalStorageKey, JSON.stringify(allBids));
};

export const addUserBid = (auctionID: string, bid: AuctionBid) => {
  const bids = getUserBids(auctionID);
  bids.push(bid);
  setUserBids(auctionID, bids);
};

export const removeUserBid = (
  auctionID: string,
  target: AuctionBid
): AuctionBid[] => {
  const bids = getUserBids(auctionID);
  const newBids = bids.filter((bid: AuctionBid) => {
    const priceEq = bid.price === target.price;
    const amountEq = bid.amount === target.amount;
    const randomEq = bid.random === target.random;
    const allEq = priceEq && amountEq && randomEq;
    return !allEq;
  });
  setUserBids(auctionID, newBids);
  return newBids;
};

export const setUserBidReveal = (
  auctionID: string,
  target: AuctionBid,
  reveal: boolean
): AuctionBid[] => {
  const bids = getUserBids(auctionID);
  for (let i = 0; i < bids.length; i++) {
    const bid = bids[i];
    const priceEq = bid.price === target.price;
    const amountEq = bid.amount === target.amount;
    const randomEq = bid.random === target.random;
    const allEq = priceEq && amountEq && randomEq;
    if (allEq) {
      bids[i].reveal = reveal;
    }
  }
  setUserBids(auctionID, bids);
  return bids;
};

export const getBidStep = (strikePrice: number): number => {
  switch (true) {
    case strikePrice < 5:
      return 0.0001;
    case strikePrice < 50:
      return 0.001;
    case strikePrice < 500:
      return 0.01;
    case strikePrice < 5000:
      return 0.1;
    default:
      return 1;
  }
};

export const parsePeriod = (periodSec: number): string => {
  const day = 86400;
  const hour = 3600;
  const minute = 60;

  let remaining = periodSec;
  const days = Math.floor(remaining / day);
  remaining -= days * day;
  const hours = Math.floor(remaining / hour);
  remaining -= hours * hour;
  const minutes = Math.floor(remaining / minute);
  remaining -= minutes * minute;
  return `${days}d ${hours}h ${minutes}m`;
};

export const parseDate = (date: Date): string => {
  const dateString = date.toString();
  return dateString.replace(/\(.+\)/, '');
};

export const getPeriodProgress = (
  status: string,
  closingTime: Date,
  isEmergency: boolean
): {
  percent: number;
  label: string;
} => {
  const now = new Date().getTime();
  let percent = 0;
  let remaining = 0;
  const revealLength = isEmergency
    ? periodLen.emergency.reveal
    : periodLen.main.reveal;
  const winningLength = isEmergency
    ? periodLen.emergency.winning
    : periodLen.main.winning;
  switch (status) {
    case period.bid: {
      const periodEnd = closingTime.getTime();
      remaining = periodEnd - now;
      switch (true) {
        case remaining > 2 * time.DAY:
          percent = 0;
          break;
        case remaining > time.DAY:
          percent = 25;
          break;
        case remaining > time.HOUR * 18:
          percent = 43;
          break;
        default: {
          const hour = remaining / time.HOUR + 3;
          percent = Math.floor(100 - hour);
          break;
        }
      }
      break;
    }
    case period.reveal: {
      const periodEnd = new Date(closingTime.getTime() + revealLength);
      remaining = periodEnd.getTime() - now;
      percent = 100 - Math.floor((remaining * 100) / revealLength);
      break;
    }
    case period.waiting: {
      const periodEnd = new Date(
        closingTime.getTime() + revealLength + winningLength
      );
      remaining = periodEnd.getTime() - now;
      percent = 100 - Math.floor((remaining * 100) / winningLength);
      break;
    }
    case period.winning: {
      const periodEnd = new Date(
        closingTime.getTime() + revealLength + winningLength
      );
      remaining = periodEnd.getTime() - now;
      percent = 100 - Math.floor((remaining * 100) / winningLength);
      break;
    }
    default: {
      percent = 0;
      break;
    }
  }
  const label = remaining / 1000 > 0 ? parsePeriod(remaining / 1000) : '';
  return {
    percent,
    label,
  };
};

export const getNextPeriod = (status: string): string => {
  switch (status) {
    case period.bid: {
      return period.reveal;
    }
    case period.reveal: {
      return period.winning;
    }
    case period.winning: {
      return period.closed;
    }
    default:
      return period.none;
  }
};

export const getUserPeriod = (
  status: StateString,
  isParticipant: boolean,
  mEICalled: boolean
): StateString => {
  switch (status) {
    case period.winning:
      if (!mEICalled) {
        return period.waiting;
      }
      if (isParticipant) {
        return period.winning;
      } else {
        return period.closed;
      }
    default:
      return status;
  }
};

export const formatter = (num: BN): { value: BN; unit: string } => {
  const status = true;
  switch (status) {
    case num.absoluteValue().isGreaterThanOrEqualTo('1e9'):
      return { value: num.dividedBy('1e9').dp(3), unit: 'B' };
    case num.absoluteValue().isGreaterThanOrEqualTo('1e6'):
      return { value: num.dividedBy('1e6').dp(3), unit: 'M' };
    case num.absoluteValue().isGreaterThanOrEqualTo(1000):
      return { value: num.dividedBy(1000).dp(3), unit: 'K' };
    default:
      return { value: num.dp(3), unit: '' };
  }
};

export async function asyncAll(...asyncFunc: any) {
  const result = await Promise.all(asyncFunc);
  return result as any[];
}
