import React, { useState, useEffect } from 'react';
import MultiTransactionModal from 'view/components/modal/MultiTransactionModal';
import {
  PriceInfo,
  ExchangeData,
  LiquidBond,
  defaultExchangeData,
} from 'types';
import BigNumber from 'bignumber.js';
import Approve from '../presentations/multitx/Approve';
import AddLiquidity from '../presentations/multitx/AddLiquidity';
import useERC20 from 'actions/useERC20';
import { useFairSwap } from 'actions/useFairSwap';
import CONTRACT_ADDR from 'libs/src/helper/Constant';
import { LienBrand, LiquidBrand, IDOLBrand, ETHBrand } from 'resource/brands';
import CompletedAddLiquidity from '../presentations/multitx/CompletedAddLiquidity';
import { GlobalState } from 'reducer/store';
import { ADD_LIQUIDITY_SETTLEMENT_BUFFER } from 'resource/constants';
import { useSelector } from 'react-redux';

interface AddLiquidityModalProps {
  isOpen: boolean;
  setIsOpen: any;
  baseAmount: BigNumber;
  settlementAmount: BigNumber;
  exchangeData: ExchangeData;
  lbt?: LiquidBond | undefined;
}

export default (props: AddLiquidityModalProps) => {
  const exchangeDataMap = useSelector(
    (state: GlobalState) => state.base.exchangeDataMap
  );
  const [isSending, setIsSending] = useState(false);
  const [steps, setSteps] = useState(0);
  const [latestExchangeData, setLatestExchangeData] = useState<ExchangeData>(
    defaultExchangeData()
  );
  const { approve } = useERC20();
  const { addLiquidity } = useFairSwap();
  const settlementBrand =
    props.exchangeData.exchangeAddress === CONTRACT_ADDR.LIEN_EXCHANGE
      ? LienBrand
      : props.exchangeData.exchangeAddress === CONTRACT_ADDR.ETH_EXCHANGE
      ? ETHBrand
      : LiquidBrand;
  const bufferedSettlement = props.settlementAmount.multipliedBy(
    ADD_LIQUIDITY_SETTLEMENT_BUFFER
  );

  const txFuncList = [
    async () => {
      // 1. approve base amount
      setIsSending(true);
      const res = await approve(
        CONTRACT_ADDR.STABLECOIN,
        props.exchangeData.exchangeAddress,
        props.baseAmount
      );
      setIsSending(false);
      if (res) {
        setSteps(1);
      }
    },
    async () => {
      // 2. approve base amount
      setIsSending(true);
      const res = await approve(
        props.exchangeData.settlementAddress,
        props.exchangeData.exchangeAddress,
        bufferedSettlement
      );
      setIsSending(false);
      if (res) {
        setSteps(2);
      }
    },
    async () => {
      // 3. add liquidity
      setIsSending(true);
      const res = await addLiquidity(
        props.baseAmount,
        CONTRACT_ADDR.STABLECOIN,
        props.exchangeData
      );
      setIsSending(false);
      if (res) {
        setSteps(3);
      }
    },
  ];

  useEffect(() => {
    setLatestExchangeData(
      exchangeDataMap.get(props.exchangeData.settlementAddress) ||
        defaultExchangeData()
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [steps, props.exchangeData, exchangeDataMap]);

  const txStatusList: string[] = [
    `Approve iDOL`,
    `Approve ${settlementBrand.name}`,
    'Add Liquidity',
    'Completed',
  ];
  const basePrice = {
    brand: IDOLBrand,
    value: props.baseAmount.toString(),
  } as PriceInfo;
  const bufferedSettlementPrice = {
    brand: settlementBrand,
    value: bufferedSettlement.toString(),
  } as PriceInfo;

  const txComponentList = [
    <Approve key={0} title={'add liquidity'} price={basePrice} />,
    <Approve
      key={1}
      title={'add liquidity'}
      price={bufferedSettlementPrice}
      buffer
    />,
    <AddLiquidity
      key={2}
      exchangeData={latestExchangeData}
      baseAmount={props.baseAmount}
      baseBrand={IDOLBrand}
      settlementAmount={props.settlementAmount}
      settlementBrand={settlementBrand}
      lbt={props.lbt}
    />,
    <CompletedAddLiquidity
      key={3}
      exchangeData={latestExchangeData}
      baseAmount={props.baseAmount}
      baseBrand={IDOLBrand}
      settlementAmount={props.settlementAmount}
      settlementBrand={settlementBrand}
      lbt={props.lbt}
    />,
  ];
  return (
    <MultiTransactionModal
      isOpen={props.isOpen}
      setIsOpen={props.setIsOpen}
      txFuncList={txFuncList}
      txStatusList={txStatusList}
      txComponentList={txComponentList}
      steps={steps}
      isSending={isSending}
    />
  );
};
