import React from 'react';

import { PriceInfo, ExchangeRateInfo } from 'types';
import BigNumber from 'bignumber.js';
import LeverageTag from './LeverageTag';

interface PriceBoxProps {
  price: PriceInfo;
  averageLeverage?: BigNumber;
  exchangeRate?: ExchangeRateInfo;
  effective?: number;
  title?: string;
  message?: string;
}

function PriceBox(props: PriceBoxProps) {
  return (
    <div className="price">
      <div className="item1">
        {props.title && <span>{props.title}</span>}
        <span className={props.price.brand.className}>
          {props.price.brand.view(props.price.value, props.effective)}
          <br />
          {props.averageLeverage && (
            <LeverageTag
              leverage={props.averageLeverage}
              message={props.message}
            />
          )}
        </span>
        {props.exchangeRate && (
          <span>
            {'1' +
              props.exchangeRate.fromBrand.unit +
              '=' +
              props.exchangeRate.toBrand.view(
                props.exchangeRate.rate,
                props.effective
              )}
          </span>
        )}
      </div>
    </div>
  );
}

export default PriceBox;
