import React, { useState } from 'react';
import MultiTransactionModal from 'view/components/modal/MultiTransactionModal';
import { BondGroup, defaultLBT, defaultExchangeData } from 'types';
import BigNumber from 'bignumber.js';
import CompleteIssueLBTAndSBT from '../presentations/multitx/CompleteIssueLBTAndSBT';
import IssueLBTAndSBT from '../presentations/multitx/IssueLBTAndSBT';
import useBondMaker from 'actions/useBondMaker';
import { Web3Utils } from 'libs/utils/web3Utils';
import { GlobalState } from 'reducer/store';
import { useSelector } from 'react-redux';

interface IssueLBTAndSBTModalProps {
  isOpen: boolean;
  setIsOpen: any;
  bondGroup: BondGroup;
  inputETH: BigNumber;
}

export default (props: IssueLBTAndSBTModalProps) => {
  const [isSending, setIsSending] = useState(false);
  const [steps, setSteps] = useState(0);
  const { issueNewBonds } = useBondMaker();
  const state = useSelector((state: GlobalState) => state);
  const web3Utils = new Web3Utils();
  const feeETH = web3Utils.feeETH(props.inputETH).toString();
  const txFuncList = [
    async () => {
      setIsSending(true);
      const res = await issueNewBonds(
        props.bondGroup.id,
        props.inputETH.toString()
      );
      setIsSending(false);
      if (res) {
        setSteps(1);
      }
    },
  ];
  const txStatusList: string[] = ['Issue LBT and SBT', 'Completed'];

  const outETH = props.inputETH.dp(2).toString();
  const getSBT = outETH;
  const getLBT = outETH;
  const sbt = props.bondGroup.solidBond;
  const lbt =
    props.bondGroup.liquidBondList.length > 0
      ? props.bondGroup.liquidBondList[0]
      : defaultLBT();
  const exchangeData =
    state.base.exchangeDataMap.get(lbt.bondAddress) || defaultExchangeData();
  const totalLiquidity = exchangeData.baseTokenPool;
  const txComponentList = [
    <IssueLBTAndSBT
      key={0}
      feeETH={feeETH}
      usingEth={outETH}
      getLBT={getLBT}
      getSBT={getSBT}
      sbt={sbt}
      lbt={lbt}
      totalLiquidity={totalLiquidity}
    />,
    <CompleteIssueLBTAndSBT
      key={2}
      feeETH={feeETH}
      usingEth={outETH}
      getLBT={getLBT}
      getSBT={getSBT}
      sbt={sbt}
      lbt={lbt}
      totalLiquidity={totalLiquidity}
    />,
  ];
  return (
    <MultiTransactionModal
      isOpen={props.isOpen}
      setIsOpen={props.setIsOpen}
      txFuncList={txFuncList}
      txStatusList={txStatusList}
      txComponentList={txComponentList}
      steps={steps}
      isSending={isSending}
    />
  );
};
