import React from 'react';
import { StatefulPopover, PLACEMENT, TRIGGER_TYPE } from 'baseui/popover';
import { Block } from 'baseui/block';
import { Label4 } from 'baseui/typography';
import './IconWrapper.scss';

interface IconWrapperProps {
  icon: JSX.Element;
  component: JSX.Element;
  description: string[];
  isDescription: boolean;
  isPadded: boolean;
}

export default function (props: IconWrapperProps) {
  const renderMessage = () => {
    return props.description.map((message: string, index: number) => {
      return (
        <div key={index}>
          <Label4>{message}</Label4>
        </div>
      );
    });
  };
  return (
    <div className={props.isPadded ? `wrapper_1` : `wrapper_2`}>
      {props.component}
      <div className="icon">
        {props.isDescription && (
          <StatefulPopover
            content={<Block padding={'10px'}>{renderMessage()}</Block>}
            triggerType={TRIGGER_TYPE.hover}
            placement={PLACEMENT.bottom}
            returnFocus
            autoFocus
          >
            {props.icon}
          </StatefulPopover>
        )}
      </div>
    </div>
  );
}
