import React from 'react';

import { ExchangeInfo, rateString } from 'types';

interface RateBoxProps {
  exchangeInfo: ExchangeInfo;
}

function RateBox(props: RateBoxProps) {
  const viewText = props.exchangeInfo.rate.isZero()
    ? 'NO RATE'
    : `${props.exchangeInfo.pair} = ${rateString(props.exchangeInfo)}`;
  return (
    <div className="price">
      <div className="item1">
        <span>{viewText}</span>
      </div>
    </div>
  );
}

export default RateBox;
