import React, { useEffect, useState } from 'react';
import AuctionBidRightContainer from 'view/components/pages/AuctionBidRightContainer';
import AuctionRevealRightContainer from 'view/components/pages/AuctionRevealRightContainer';
import AuctionWinningRightContainer from 'view/components/pages/AuctionWinningRightContainer';
import AuctionWaitingRightContainer from 'view/components/pages/AuctionWaitingRightContainer';
import AuctionClosedRightContainer from 'view/components/pages/AuctionClosedRightContainer';
import { Modal, SIZE } from 'baseui/modal';
import { AspectRatioBox } from 'baseui/aspect-ratio-box';
import { BlockProps } from 'baseui/block';
import { FlexGrid, FlexGridItem } from 'baseui/flex-grid';
import { ProgressSteps, NumberedStep } from 'baseui/progress-steps';
import { StateString } from 'types';
import { AuctionGroupColumn } from './AuctionListContainer';
import { period } from 'config';

interface AuctionDetailContainerProps {
  isOpen: boolean;
  setIsOpen: any;
  sbtMaturityDate?: Date;
  auctionStatus?: StateString;
  auctionSelectDatum: AuctionGroupColumn;
}
const leftItemProps: BlockProps = {
  alignItems: 'center',
  justifyContent: 'center',
  display: 'flex',
  padding: '4%',
};

const rightItemProps: BlockProps = {
  justifyContent: 'center',
  display: 'flex',
  padding: '6%',
};

// AuctionDetail => AuctionModal
function AuctionDetailContainer(props: AuctionDetailContainerProps) {
  const [current, setCurrent] = useState(0);
  const [closable, setClosable] = useState(true);
  const { auctionSelectDatum } = props;
  const statusToCurrent = (
    status: string,
    isParticipant: boolean,
    mEICalled: boolean
  ) => {
    switch (status) {
      case period.notStarted: {
        return 'neutral';
      }
      case period.bid: {
        setCurrent(0);
        break;
      }
      case period.reveal: {
        setCurrent(1);
        break;
      }
      case period.waiting: {
        setCurrent(2);
        break;
      }
      case period.winning: {
        setCurrent(3);
        break;
      }
      case period.closed: {
        isParticipant ? setCurrent(3) : setCurrent(4);
        break;
      }
      default: {
        return 'neutral';
      }
    }
  };

  useEffect(() => {
    statusToCurrent(
      auctionSelectDatum.period,
      auctionSelectDatum.isParticipant,
      auctionSelectDatum.result?.end.mEICalled || false
    );
  }, [auctionSelectDatum.period]); // eslint-disable-line

  return (
    <Modal
      onClose={() => props.setIsOpen(false)}
      closeable={closable}
      isOpen={props.isOpen}
      size={SIZE.auto}
      animate
      autoFocus
    >
      <FlexGrid flexGridColumnCount={2}>
        <FlexGridItem {...leftItemProps}>
          <AspectRatioBox width={`${100}%`}>
            <ProgressSteps current={current}>
              <NumberedStep title="Bid Period">
                <p>Deposit iDOL to make a bid</p>
              </NumberedStep>
              <NumberedStep title="Reveal Period">
                <p>Reveal the bid price</p>
              </NumberedStep>
              <NumberedStep title="Winning Period">
                <p></p>
              </NumberedStep>
              <NumberedStep title="Auction End">
                <p></p>
              </NumberedStep>
            </ProgressSteps>
          </AspectRatioBox>
        </FlexGridItem>
        <FlexGridItem {...rightItemProps}>
          {current === 0 ? (
            <AuctionBidRightContainer
              amount={auctionSelectDatum.amount.toString(10)}
              todoStrikePrice={auctionSelectDatum.strikePrice.toString()}
              todoSBTMaturityDate={auctionSelectDatum.maturity}
              todoAuctionStatus={auctionSelectDatum.period}
              todoAuctionID={auctionSelectDatum.auctionID}
              todoAuctionBondAddr={auctionSelectDatum.bondAddress}
              setCurrent={setCurrent}
              setIsOpen={props.setIsOpen}
              setClosable={setClosable}
            />
          ) : current === 1 ? (
            <AuctionRevealRightContainer
              todoStrikePrice={auctionSelectDatum.strikePrice.toString()}
              todoSBTMaturityDate={auctionSelectDatum.maturity}
              todoAuctionStatus={auctionSelectDatum.period}
              setCurrent={setCurrent}
              auctionID={auctionSelectDatum.auctionID}
              setClosable={setClosable}
            />
          ) : current === 2 ? (
            <AuctionWaitingRightContainer />
          ) : current === 3 ? (
            <AuctionWinningRightContainer
              strikePrice={auctionSelectDatum.strikePrice.toString(10)}
              setCurrent={setCurrent}
              auctionID={auctionSelectDatum.auctionID}
              auctionPrice={auctionSelectDatum.strikePrice.toString(10)}
              auctionAmount={(
                auctionSelectDatum.amount.toNumber() /
                10 ** 8
              ).toString()}
              isParticipant={auctionSelectDatum.isParticipant}
              setClosable={setClosable}
            />
          ) : (
            <AuctionClosedRightContainer
              auctionID={auctionSelectDatum.auctionID}
              strikePrice={auctionSelectDatum.strikePrice.toString(10)}
            />
          )}
        </FlexGridItem>
      </FlexGrid>
    </Modal>
  );
}

export default AuctionDetailContainer;
