import React from 'react';
import SubTitle from '../SubTitle';
import ItemPrice from '../ItemPrice';
import { BrandInfo, LiquidBond, ExchangeData } from 'types';
import LiquidListUnitTable from '../LiquidListUnitTable';
import BigNumber from 'bignumber.js';
import ItemNumber from '../ItemNumber';
import SubItem from '../SubItem';
interface CompletedAddLiquidityProps {
  exchangeData: ExchangeData;
  baseAmount: BigNumber;
  settlementAmount: BigNumber;
  baseBrand: BrandInfo;
  settlementBrand: BrandInfo;
  lbt?: LiquidBond;
}

export default (props: CompletedAddLiquidityProps) => {
  const calcDomination = () => {
    return new BigNumber(props.baseAmount)
      .div(props.exchangeData.baseTokenPool)
      .multipliedBy(100)
      .dp(3);
  };
  const lbtTable = props.lbt ? (
    <LiquidListUnitTable
      data={[
        {
          maturity: props.lbt?.maturity,
          strikePrice: props.lbt?.strikePrice.dp(2).toNumber(),
          lbtPrice: props.lbt?.theoreticalPrice.dp(2).toNumber(),
          leverage: props.lbt?.leverage.dp(2).toNumber(),
          totalLiquidity: props.exchangeData.baseTokenPool,
        },
      ]}
    />
  ) : (
    <div></div>
  );
  return (
    <div className="content_box_modal">
      <SubTitle title="Completed" />
      <div className="box">
        <ItemPrice
          name={'Added'}
          value={props.baseAmount.toString()}
          brand={props.baseBrand}
        />
        <ItemPrice
          name={'Added'}
          value={props.settlementAmount.toString()}
          brand={props.settlementBrand}
        />
        {props.lbt && lbtTable}
        <hr />
        <SubItem name="Result" />
        <ItemNumber
          name={'Added Share of Pool'}
          value={`${
            calcDomination().isLessThan(0.01)
              ? '< 0.01'
              : calcDomination().toString()
          }%`}
        />
      </div>
    </div>
  );
};
