import Web3 from 'web3';
import { Contract } from 'web3-eth-contract';
import { AbiItem } from 'web3-utils';
import {
  AuctionContract as Auction,
  AuctionBoardContract as AuctionBoard,
  BondMakerContract as BondMaker,
  //IERC20Contract as IERC20,
  WrapperContract as Wrapper,
  ExchangeFactoryContract as ExchangeFactory,
  ERC20Contract as ERC20,
  StableCoinContract as StableCoin,
  BondTokenContract as BondToken,
  FairSwapContract as FairSwap,
  LIENFairSwapContract as LIENFairSwap,
  LienTokenContract as LienToken,
  OracleContract as Oracle,
} from '../../abi/ABI';
import CONTRACT_ADDR from './Constant';
import { Web3Utils } from '../../utils/web3Utils';
// import { Auction as AuctionInstance } from 'libs/types/Auction';
// import { BondMaker as BondMakerInstance } from 'libs/types/BondMaker';
// import { StableCoin as StableCoinInstance } from 'libs/types/StableCoin';
// import { Erc20 as Erc20Instance } from 'libs/types/ERC20';
// import { BondToken as BondTokenInstance } from 'libs/types/BondToken';
// import { Wrapper as WrapperInstance } from 'libs/types/Wrapper';

function setupContractInstance(
  provider: Web3,
  abi: AbiItem[] | AbiItem,
  contractAddress: string
): Contract {
  return new provider.eth.Contract(abi, contractAddress);
}

export default class Contracts {
  public auction: any;
  public auctionBoard: any;
  public bondMaker: any;
  public stableCoin: any;
  public bondToken: any;
  public fairSwap: any;
  public lienToken: any;
  public lienFairSwap: any;
  public exchangeFactory: any;
  public wrapper: any;
  public erc20: any; // Erc20Instance | undefined;
  public subscriber: any;
  public oracle: any;
  public auctionAddr: string;
  public auctionBoardAddr: string;
  public bondMakerAddr: string;
  public stableCoinAddr: string;
  public lienTokenAddr: string;
  public lienFairSwapAddr: string;
  public exchangeFactoryAddr: string;
  public oracleAddr: string;
  public wrapperAddr: string;
  public web3: any;
  private web3Utils: Web3Utils;
  public address: string;

  constructor() {
    this.web3Utils = new Web3Utils();
    this.address = '';
    this.auctionAddr = CONTRACT_ADDR.AUCTION;
    this.auctionBoardAddr = CONTRACT_ADDR.AUCTION_BOARD;
    this.stableCoinAddr = CONTRACT_ADDR.STABLECOIN;
    this.bondMakerAddr = CONTRACT_ADDR.BONDMAKER;
    this.lienTokenAddr = CONTRACT_ADDR.LIEN_TOKEN;
    this.lienFairSwapAddr = CONTRACT_ADDR.LIEN_EXCHANGE;
    this.exchangeFactoryAddr = CONTRACT_ADDR.LBT_EXCHANGE_FACTORY;
    this.wrapperAddr = CONTRACT_ADDR.WRAPPER;
    this.web3 = new Web3();
    this.oracleAddr = CONTRACT_ADDR.ORACLE;
  }

  // ======= setUp Function =======

  public async setWeb3(provider: Web3, address: string) {
    this.auction = setupContractInstance(
      provider,
      Auction as AbiItem[],
      this.auctionAddr
    ) as any;
    this.auctionBoard = setupContractInstance(
      provider,
      AuctionBoard as AbiItem[],
      this.auctionBoardAddr
    ) as any;
    this.bondMaker = setupContractInstance(
      provider,
      BondMaker as AbiItem[],
      this.bondMakerAddr
    ) as any;
    this.stableCoin = setupContractInstance(
      provider,
      StableCoin as AbiItem[],
      this.stableCoinAddr
    ) as any;
    this.wrapper = setupContractInstance(
      provider,
      Wrapper as AbiItem[],
      this.wrapperAddr
    ) as any;
    this.exchangeFactory = setupContractInstance(
      provider,
      ExchangeFactory as AbiItem[],
      this.exchangeFactoryAddr
    ) as any;
    this.oracle = setupContractInstance(
      provider,
      Oracle as AbiItem[],
      this.oracleAddr
    ) as any;
    this.erc20 = setupContractInstance(provider, ERC20 as AbiItem[], '') as any;
    this.fairSwap = setupContractInstance(provider, FairSwap as AbiItem[], '');
    this.bondToken = setupContractInstance(
      provider,
      BondToken as AbiItem[],
      ''
    );
    this.lienToken = setupContractInstance(
      provider,
      LienToken as AbiItem[],
      this.lienTokenAddr
    );
    this.lienFairSwap = setupContractInstance(
      provider,
      LIENFairSwap as AbiItem[],
      this.lienFairSwapAddr
    );
    this.web3 = provider;
    this.address = address;
  }

  // ======= SendTx Function =======

  public estimateGas(): number {
    return 500000;
  }

  public async sendTx(funcData: any) {
    const gaslimit = this.estimateGas();
    if (gaslimit) {
      return funcData
        .send(
          {
            from: this.address,
            gas: gaslimit,
          },
          (err: any, hash: any) => {
            console.log(err);
          }
        )
        .on('transactionHash', (hash: string) => {
          console.log(hash);
        })
        .on('error', (err: any, receipt: any) => {
          return { err, receipt };
        });
    }
  }

  public async sendTxValue(funcData: any, value: any) {
    const gaslimit = this.estimateGas();
    if (gaslimit) {
      return funcData
        .send({
          from: this.address,
          gaslimit: gaslimit,
          value: value,
        })
        .on('transactionHash', (hash: string) => {
          return hash;
        })
        .on('error', (err: any) => {
          return err;
        });
    }
  }

  public async sendTxToken(funcDataApprove: any, funcData: any) {
    const gaslimit = this.web3Utils.toHex(this.estimateGas());
    if (gaslimit) {
      return funcDataApprove.sendTx().on('transactionHash', (hash: string) => {
        funcData
          .send({
            from: this.address,
            gaslimit: gaslimit,
          })
          .on('transactionHash', (hash: string) => {
            return hash;
          })
          .on('error', (err: any) => {
            return err;
          });
      });
    }
  }
}
