import BigNumber from 'bignumber.js';
import { BaseMyState } from './store';

export const baseMyReducer = (state: BaseMyState, action: any) => {
  switch (action.type) {
    case 'UPDATE_BALANCE':
      return {
        ...state,
        ethBalance: action.payload.ethBalance,
        iDOLBalance: action.payload.iDOLBalance,
        lienBalance: action.payload.lienBalance,
      };
    case 'ADD_BALANCE_BOND_LIST':
      return {
        ...state,
        solidBalanceList: [
          ...state.solidBalanceList,
          ...action.payload.solidBalanceList,
        ],
        liquidBalanceList: [
          ...state.liquidBalanceList,
          ...action.payload.liquidBalanceList,
        ],
      };
    case 'UPDATE_BALANCE_BOND_LIST':
      return {
        ...state,
        solidBalanceList: action.payload.solidBalanceList,
        liquidBalanceList: action.payload.liquidBalanceList,
      };
    case 'ADD_BALANCE_POOLED_IDOL_LIST':
      return {
        ...state,
        pooledIDOLList: [
          ...state.pooledIDOLList,
          ...action.payload.pooledIDOLList,
        ],
      };
    case 'UPDATE_BALANCE_POOLED_IDOL_LIST':
      return {
        ...state,
        pooledIDOLList: action.payload.pooledIDOLList,
      };
    case 'UPDATE_TOTAL':
      return {
        ...state,
        totalDollar: action.payload.totalDollar,
        totalLiquidDollar: action.payload.totalLiquidDollar,
        totalSolidDollar: action.payload.totalSolidDollar,
        totalPooledDollar: action.payload.totalPooledDollar,
        totalLPDollar: action.payload.totalLPDollar,
        averageLeverage: action.payload.averageLeverage,
      };
    case 'UPDATE_WAITING_ORDERS_LIST':
      return {
        ...state,
        waitingOrdersList: action.payload,
      };
    case 'CLEAR_BASE_MY':
      return {
        ethBalance: new BigNumber(0),
        iDOLBalance: new BigNumber(0),
        lienBalance: new BigNumber(0),
        solidBalanceList: [],
        liquidBalanceList: [],
        pooledIDOLList: [],
        lpBalanceList: [],
        totalDollar: new BigNumber(0),
        totalSolidDollar: new BigNumber(0),
        totalLiquidDollar: new BigNumber(0),
        totalPooledDollar: new BigNumber(0),
        totalLPDollar: new BigNumber(0),
        averageLeverage: new BigNumber(0),
        waitingOrdersList: [],
      };
    default:
      return state;
  }
};
