import React, { useState } from 'react';
import ContentTitle from 'view/components/presentations/ContentTitle';
import TrancheLeftContainer from 'view/components/pages/TrancheLeftContainer';
import TrancheRightContainer from 'view/components/pages/TrancheRightContainer';
import InfoContainer from 'view/components/pages/InfoTrancheContainer';
import { trancheDescription } from 'resource/description/index';

function Tranche() {
  const [selectedIndex, setSelectedIndex] = useState<number>(0);
  const onClick = (index?: number | string) => {
    if (index === undefined) {
      setSelectedIndex(0);
    } else {
      setSelectedIndex(Number(index));
    }
  };

  return (
    <div id="content_box">
      <ContentTitle
        title="Manual Tranche"
        isHelp={true}
        description={trancheDescription}
      />
      <TrancheLeftContainer onClick={onClick} selected={selectedIndex} />
      <div className="content_box_1">
        <InfoContainer />
        <TrancheRightContainer selectedBondGroupIndex={selectedIndex} />
      </div>
    </div>
  );
}

export default Tranche;
