import React, { useState, useEffect } from 'react';
import MultiTransactionModal from 'view/components/modal/MultiTransactionModal';
import {
  PriceInfo,
  ExchangeInfo,
  LiquidBond,
  WaitingOrders,
  defaultWaitingOrders,
} from 'types';
import { IDOLBrand } from 'resource/brands';
import BigNumber from 'bignumber.js';
import Approve from '../presentations/multitx/Approve';
import TradeTx from '../presentations/multitx/TradeTx';
import useERC20 from 'actions/useERC20';
import { useFairSwap } from 'actions/useFairSwap';
import ForceExecuteOrders from '../presentations/multitx/ForceExecuteOrders';
import CompletedExecutionOrders from '../presentations/multitx/CompletedExecutionOrders';
import CONTRACT_ADDR from 'libs/src/helper/Constant';
import { GlobalState } from 'reducer/store';
import { useSelector, useDispatch } from 'react-redux';
import { SECOND } from 'resource/constants';

interface TradeModalProps {
  isOpen: boolean;
  setIsOpen: any;
  tokenIn: BigNumber;
  isLimit: boolean;
  tokenAddress: string;
  exchangeAddress: string;
  latestBoxNumber: BigNumber;
  exchangeInfo: ExchangeInfo;
  totalLiquidity: BigNumber;
  lbt?: LiquidBond;
}

export default (props: TradeModalProps) => {
  const [isSending, setIsSending] = useState(false);
  const [steps, setSteps] = useState(0);
  const [
    acceptOrdersListMap,
    waitingOrdersList,
  ] = useSelector((state: GlobalState) => [
    state.event.acceptOrdersListMap,
    state.baseMy.waitingOrdersList,
  ]);
  const dispatch = useDispatch();
  const { approve } = useERC20();
  const {
    orderBaseToSettlement,
    orderSettlementToBase,
    getLatestMyOrders,
    executeUnexecutedBox,
  } = useFairSwap();
  const isBuy = props.exchangeInfo.fromBrand.name === IDOLBrand.name;
  const [txResponse, setTxResponse] = useState<boolean>(false);

  const [yourOrder, setYourOrder] = useState<WaitingOrders>(
    defaultWaitingOrders()
  );
  useEffect(() => {
    if (txResponse) {
      const asyncSetYourOrder = async () => {
        const result = await getLatestMyOrders(props.exchangeAddress);
        setYourOrder(result);
      };
      setTimeout(() => asyncSetYourOrder(), SECOND);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    props.exchangeAddress,
    acceptOrdersListMap,
    waitingOrdersList,
    txResponse,
  ]);
  // update waiting orders.
  useEffect(() => {
    dispatch({
      type: 'UPDATE_WAITING_ORDERS',
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [steps, props.isOpen]);
  const txFuncList = [
    async () => {
      setIsSending(true);
      let res = false;
      if (isBuy) {
        res = await approve(
          CONTRACT_ADDR.STABLECOIN,
          props.exchangeAddress,
          props.tokenIn
        );
      } else {
        res = await approve(
          props.tokenAddress,
          props.exchangeAddress,
          props.tokenIn
        );
      }
      setIsSending(false);
      if (res) {
        setSteps(1);
      }
    },
    async () => {
      setIsSending(true);
      let res = false;
      if (isBuy) {
        // from(base) -> to(settlement)
        res = await orderBaseToSettlement(
          props.tokenIn,
          props.isLimit,
          props.exchangeAddress,
          props.latestBoxNumber
        );
      } else {
        // from(settlement) -> to(base)
        res = await orderSettlementToBase(
          props.tokenIn,
          props.isLimit,
          props.tokenAddress,
          props.exchangeAddress,
          props.latestBoxNumber
        );
      }
      setIsSending(false);
      if (res) {
        // use fairSwap.
        setSteps(2);
        setTxResponse(res);
      }
    },
    async () => {
      if (!yourOrder.isExecuted) {
        // waiting order and can force execute.
        setIsSending(true);
        const res = await executeUnexecutedBox(
          yourOrder.orderCount,
          props.exchangeAddress
        );
        setIsSending(false);
        if (res) {
          setSteps(3);
        }
      } else if (yourOrder.isExecuted) {
        // executed orders data
        setSteps(3);
      } // nothing to do for other cases
    },
  ];
  const txStatusList: string[] = [
    'Approve',
    'Order',
    'FairSwap Execution',
    'Completed',
  ];

  const price = {
    brand: props.exchangeInfo.fromBrand,
    value: props.tokenIn.toString(),
  } as PriceInfo;

  const txComponentList = [
    <Approve key={0} title={'trading'} price={price} />,
    <TradeTx
      key={1}
      exchangeInfo={props.exchangeInfo}
      tokenIn={props.tokenIn}
      isBond={!!props.lbt}
      lbt={props.lbt}
      totalLiquidity={props.totalLiquidity}
    />,
    <ForceExecuteOrders key={2} waitingOrders={yourOrder} />,
    <CompletedExecutionOrders
      key={3}
      tokenIn={props.tokenIn}
      exchangeAddr={props.exchangeAddress}
      isBuy={isBuy}
    />,
  ];
  return (
    <MultiTransactionModal
      isOpen={props.isOpen}
      setIsOpen={props.setIsOpen}
      waitingFlag={2}
      waitingAble={!yourOrder.isExecuted}
      txFuncList={txFuncList}
      txStatusList={txStatusList}
      txComponentList={txComponentList}
      steps={steps}
      isSending={isSending}
    />
  );
};
