import React from 'react';
import { useStyletron } from 'baseui';
import ItemNumber from 'view/components/presentations/ItemNumber';

export interface SBTGroupRows {
  maturity: Date;
  strikePrice: number;
  theoreticalPrice: number;
  isAcceptable: boolean;
}

interface SolidListUnitTableProps {
  data: SBTGroupRows[];
}

export default (props: SolidListUnitTableProps) => {
  const [css] = useStyletron();
  return (
    <div className={css({ height: '120px' })}>
      <ItemNumber
        name={'Maturity'}
        value={props.data[0].maturity.toLocaleDateString()}
      />

      <ItemNumber
        name={'Strike Price'}
        value={'$' + props.data[0].strikePrice.toString()}
      />

      <ItemNumber
        name={'Price'}
        value={'$' + props.data[0].theoreticalPrice.toString()}
      />

      <ItemNumber
        name={'Satus'}
        value={props.data[0].isAcceptable ? `Acceptable` : 'Danger'}
      />
    </div>
  );
};
