import React, { createContext, useCallback, useEffect } from 'react';
import { GlobalState } from 'reducer/store';
import useRecommend from 'actions/useRecommend';
import { useDispatch, useSelector } from 'react-redux';

export const RecommendContext: any = createContext('');
export const RecommendProvider: React.FC = ({ children }): JSX.Element => {
  const dispatch = useDispatch();
  const bondGroupMap = useSelector(
    (state: GlobalState) => state.base.bondGroupMap
  );
  const exchangeDataMap = useSelector(
    (state: GlobalState) => state.base.exchangeDataMap
  );
  const { getBaseRecommendBondGroupList } = useRecommend();

  const dispatchRecommend = useCallback(async () => {
    const baseBestBondGroup = getBaseRecommendBondGroupList(
      bondGroupMap,
      exchangeDataMap
    );
    dispatch({
      type: 'SET_TRANCHE_RECOMMEND_BOND_GROUP_LIST',
      payload: baseBestBondGroup,
    });
    }, [exchangeDataMap, bondGroupMap]); // eslint-disable-line

  useEffect(() => {
    dispatchRecommend();
  }, [dispatchRecommend]);

  return (
    <RecommendContext.Provider value={{ exchangeDataMap, bondGroupMap }}>
      {children}
    </RecommendContext.Provider>
  );
};
