import React from 'react';
import SubTitle from '../SubTitle';
import ItemPrice from '../ItemPrice';
import { BrandInfo, LiquidBond, ExchangeData } from 'types';
import LiquidListUnitTable from '../LiquidListUnitTable';
import BigNumber from 'bignumber.js';
import ItemNumber from '../ItemNumber';

interface RemoveLiquidityProps {
  shareAmount: BigNumber;
  returnBaseToken: BigNumber;
  returnSettlementToken: BigNumber;
  baseBrand: BrandInfo;
  settlementBrand: BrandInfo;
  exchangeData: ExchangeData;
  lbt?: LiquidBond | undefined;
}

export default (props: RemoveLiquidityProps) => {
  const lbtTable = props.lbt ? (
    <LiquidListUnitTable
      data={[
        {
          maturity: props.lbt?.maturity,
          strikePrice: props.lbt?.strikePrice.dp(2).toNumber(),
          lbtPrice: props.lbt?.theoreticalPrice.dp(2).toNumber(),
          leverage: props.lbt?.leverage.dp(2).toNumber(),
          totalLiquidity: props.exchangeData.baseTokenPool,
        },
      ]}
    />
  ) : (
    <div></div>
  );
  return (
    <div className="content_box_modal">
      <SubTitle title="Completed" />
      <div className="box">
        <ItemNumber name={'Remove'} value={props.shareAmount.toString()} />
        <hr />
        <ItemPrice
          name={'You have returned'}
          value={props.returnBaseToken.toString()}
          brand={props.baseBrand}
        />
        <ItemPrice
          name={'You have returned'}
          value={props.returnSettlementToken.toString()}
          brand={props.settlementBrand}
        />
        {props.lbt && lbtTable}
      </div>
    </div>
  );
};
