import React from 'react';
import './ItemPrice.scss';
import './WithIcon.scss';
import { LiquidBond } from 'types';
import LiquidListUnitTable, { LBTGroupRows } from './LiquidListUnitTable';
import { LiquidBrand } from 'resource/brands';
import BigNumber from 'bignumber.js';

interface ItemLiquidProps {
  name: string;
  value: string;
  lbt: LiquidBond;
  totalLiquidity: BigNumber;
}

export default (props: ItemLiquidProps) => {
  const data: LBTGroupRows[] = [
    {
      maturity: props.lbt.maturity,
      strikePrice: props.lbt.strikePrice.dp(2).toNumber(),
      lbtPrice: props.lbt.theoreticalPrice.dp(2).toNumber(),
      leverage: props.lbt.leverage.dp(2).toNumber(),
      totalLiquidity: props.totalLiquidity,
    },
  ];
  return (
    <div>
      <div className="price">
        <span>{props.name}</span>
        <div className={LiquidBrand.className}>
          <span>{LiquidBrand.view(props.value)}</span>
        </div>
      </div>
      <LiquidListUnitTable data={data} />
    </div>
  );
};
