import { EventState } from './store';

// FairSwap
export const ADD_ACCEPT_ORDERS = 'ADD_ACCEPT_ORDERS';
export const ADD_EXECUTION_MAP = 'ADD_EXECUTION';
export const ADD_MOVE_LIQUIDITY = 'ADD_MOVE_LIQUIDITY';
// FairSwap(Only vs LBT)
export const ADD_REMOVE_AFTER_MATURITY = 'ADD_REMOVE_AFTER_MATURITY';
// FairSwap(Only vs Lien)
export const ADD_RECEIVE_DIVIDEND_ETH = 'ADD_RECEIVE_DIVIDEND_ETH';
export const ADD_RECEIVE_DIVIDEND_TOKEN = 'ADD_RECEIVE_DIVIDEND_TOKEN';
// ERC20
export const ADD_FROM_TRANSFER = 'ADD_FROM_TRANSFER';
export const ADD_TO_TRANSFER = 'ADD_TO_TRANSFER';
// LienToken
export const ADD_RECEIVE_DIVIDEND = 'ADD_RECEIVE_DIVIDEND';
export const ADD_RECEIVE_DIVIDEND_OF_ETH = 'ADD_RECEIVE_DIVIDEND_OF_ETH';
// BondMaker
export const ADD_LOG_ISSUE_NEW_BONDS = 'ADD_LOG_ISSUE_NEW_BONDS';
// Wrapper
export const ADD_LOG_ISSUE_IDOL = 'ADD_LOG_ISSUE_IDOL';
export const ADD_LOG_ISSUE_LBT = 'ADD_LOG_ISSUE_LBT';
export const ADD_LOG_RETURN_LOCKED_POOL_USERS =
  'ADD_LOG_RETURN_LOCKED_POOL_USERS';
// StableCoin
export const ADD_LOG_RETURN_LOCKED_POOL = 'ADD_LOG_RETURN_LOCKED_POOL';
export const ADD_LOG_BURN_IDOL = 'ADD_LOG_BURN_IDOL';
export const ADD_LOG_MINT_IDOL = 'ADD_LOG_MINT_IDOL';
// Auction
export const ADD_LOG_CANCEL_BID = 'ADD_LOG_CANCEL_BID';
export const ADD_LOG_AUCTION_RESULT = 'ADD_LOG_AUCTION_RESULT';
// Auction Board
export const ADD_LOG_BID_MEMO = 'ADD_LOG_BID_MEMO';

export const eventReducer = (
  state: EventState,
  action: { type: string; payload: any; key?: any; height?: number }
) => {
  switch (action.type) {
    case ADD_ACCEPT_ORDERS:
      return {
        ...state,
        acceptOrdersListMap: new Map([
          ...(state.acceptOrdersListMap as any),
          ...(new Map([[action.key, [...action.payload]]]) as any),
        ]),
      };
    case ADD_EXECUTION_MAP:
      return {
        ...state,
        executionListMap: new Map([
          ...(state.executionListMap as any),
          ...(new Map([[action.key, [...action.payload]]]) as any),
        ]),
      };
    case ADD_MOVE_LIQUIDITY:
      return {
        ...state,
        moveLiquidityMap: new Map([
          ...(state.moveLiquidityMap as any),
          ...(new Map([[action.key, [...action.payload]]]) as any),
        ]),
      };
    case ADD_REMOVE_AFTER_MATURITY:
      return {
        ...state,
        removeAfterMaturityMap: new Map([
          ...(state.removeAfterMaturityMap as any),
          ...(new Map([[action.key, [...action.payload]]]) as any),
        ]),
      };
    case ADD_RECEIVE_DIVIDEND_ETH:
      return {
        ...state,
        receiveDividendEth: action.payload,
      };
    case ADD_RECEIVE_DIVIDEND_TOKEN:
      return {
        ...state,
        receiveDividendToken: action.payload,
      };
    case ADD_FROM_TRANSFER:
      return {
        ...state,
        fromTransferListMap: new Map([
          ...(state.fromTransferListMap as any),
          ...(new Map([[action.key, [...action.payload]]]) as any),
        ]),
      };
    case ADD_TO_TRANSFER:
      return {
        ...state,
        toTransferListMap: new Map([
          ...(state.toTransferListMap as any),
          ...(new Map([[action.key, [...action.payload]]]) as any),
        ]),
      };
    case ADD_RECEIVE_DIVIDEND:
      return {
        ...state,
        receiveDividend: action.payload,
      };
    case ADD_RECEIVE_DIVIDEND_OF_ETH:
      return {
        ...state,
        receiveDividendOfEth: action.payload,
      };
    case ADD_LOG_ISSUE_NEW_BONDS:
      return {
        ...state,
        logIssueNewBonds: action.payload,
      };
    case ADD_LOG_ISSUE_IDOL:
      return {
        ...state,
        logIssueIDOL: action.payload,
      };
    case ADD_LOG_ISSUE_LBT:
      return {
        ...state,
        logIssueLBT: action.payload,
      };
    case ADD_LOG_RETURN_LOCKED_POOL_USERS:
      return {
        ...state,
        logReturnLockedPoolUsers: action.payload,
      };
    case ADD_LOG_RETURN_LOCKED_POOL:
      return {
        ...state,
        logReturnLockedPool: action.payload,
      };
    case ADD_LOG_BURN_IDOL:
      return {
        ...state,
        logBurnIDOL: action.payload,
      };
    case ADD_LOG_MINT_IDOL:
      return {
        ...state,
        logMintIDOL: action.payload,
      };
    case ADD_LOG_CANCEL_BID:
      return {
        ...state,
        logCancelBid: action.payload,
      };
    case ADD_LOG_AUCTION_RESULT:
      return {
        ...state,
        logAuctionResult: action.payload,
      };
    case ADD_LOG_BID_MEMO:
      return {
        ...state,
        logBidMemo: action.payload,
      };
    case 'CLEAR_EVENT':
      return {
        acceptOrdersListMap: new Map(),
        executionListMap: new Map(),
        moveLiquidityMap: new Map(),
        // FairSwap(Only vs LBT)
        removeAfterMaturityMap: new Map(),
        // FairSwap(Only vs Lien)
        receiveDividendETH: [],
        receiveDividendToken: [],

        // ERC20
        fromTransferListMap: new Map(),
        toTransferListMap: new Map(),

        // LienToken
        receiveDividend: [],
        receiveDividendOfEth: [],

        // BondMaker
        logIssueNewBonds: [],

        // Wrapper
        logIssueIDOL: [],
        logIssueLBT: [],
        logReturnLockedPoolUsers: [],

        // StableCoin
        logReturnLockedPool: [],
        logBurnIDOL: [],
        logMintIDOL: [],

        // Auction
        logCancelBid: [],
        logAuctionResult: [],

        // Auction Board
        logBidMemo: [],
      };
    default:
      return state;
  }
};
