import React, { createContext, useCallback, useEffect, useState } from 'react';
import { GlobalState } from 'reducer/store';
import { lien } from 'actions/useLien';
import {
  BondGroup,
  Bond,
  LiquidBond,
  defaultBondGroup,
  SolidBond,
  ExchangeData,
  isLiquid,
} from 'types';
import BigNumber, { BigNumber as BN } from 'bignumber.js';
import useDerivPricer from 'actions/useDerivPricer';
import { isExpired } from 'libs/utils/utils';
import { minTrancheRows, pagingInterval } from 'config';
import useOracle from 'actions/useOracle';
import CONTRACT_ADDR from 'libs/src/helper/Constant';
import { useDispatch, useSelector } from 'react-redux';

export const BaseContext: any = createContext('');
const EXCHANGE_BOND_KIND = 'BOND';
const EXCHANGE_LIEN_KIND = 'LIEN';
// const EXCHANGE_ETH_KIND = 'ETH';

export const BaseProvider: React.FC = ({ children }): JSX.Element => {
  const dispatch = useDispatch();
  const base = useSelector((state: GlobalState) => state.base);
  const txTrigger = useSelector((state: GlobalState) => state.txTrigger);
  const sdk = useSelector((state: GlobalState) => state.sdk);

  const minTrigger = useSelector(
    (state: GlobalState) => state.timeTrigger.minTrigger
  );
  const { pricer } = useDerivPricer();
  const { latestPrice, getVolatility } = useOracle();
  const [currentProcessGroup, setProcessGroup] = useState(1);
  const [currentProcessedCount, setProcessedCount] = useState(0);
  const [maxPageNum, setMaxPageNum] = useState(1);
  const currentAddress = lien.address;

  const getBondGroupMapAndBondMap = async (
    bondGroupIdList: number[]
  ): Promise<{
    bondGroupMap: Map<number, BondGroup>;
    bondMap: Map<string, Bond>;
  }> => {
    const bondGroupMap: Map<number, BondGroup> = new Map();
    const bondMap: Map<string, Bond> = new Map();
    const dispatchBondPromise = async (index: number) => {
      const bondGroup: BondGroup = defaultBondGroup();
      bondGroup.id = index;
      if (base.bondGroupMap.has(bondGroup.id)) return;
      const actualBondGroup = await lien.bondMaker.getBondGroup(index);
      bondGroup.maturity = new Date(
        lien.utils.fromSec(actualBondGroup.maturity)
      );
      bondGroup.expired = isExpired(bondGroup.maturity);

      // Gets all bondInfo of the bondGroup.
      for (const bondId of actualBondGroup.bondIDs) {
        const bondRet = await lien.bondMaker.getBond(String(bondId));
        const bondType = lien.utils.fnMapToLineSegment(
          await lien.bondMaker.getFnMap(String(bondRet.fnMapID))
        );
        if (lien.utils.isSolid(bondType)) {
          // Solid
          // memo solid
          const maturity = new Date(lien.utils.fromSec(bondRet.maturity));
          const isAcceptable =
            !bondGroup.expired &&
            (await lien.stableCoin.isAcceptableSBT(String(bondId)));
          const rate = await lien.bondToken.getRate(bondRet.bondTokenAddress);
          const solidBond: SolidBond = {
            bondAddress: bondRet.bondTokenAddress,
            bondType: bondType,
            bondId: bondId,
            strikePrice: lien.utils.fromStrikePrice(bondRet.solidStrikePrice),
            theoreticalPrice: bondGroup.expired
              ? rate.multipliedBy(base.oracleEthPrice)
              : new BN(
                  pricer(
                    base.oracleEthPrice.toNumber(),
                    base.oracleVolatility.toNumber(),
                    maturity,
                    bondType
                  ).ans1
                ),
            redeemETH: rate,
            maturity,
            isAcceptable,
            isBurnable: rate.toString() !== '0',
          };
          bondMap.set(bondId, solidBond);
          bondGroup.solidBond = solidBond;
          bondGroup.strikePrice = solidBond.strikePrice;
        } else {
          // Liquid
          const liquidBond = {
            bondAddress: bondRet.bondTokenAddress,
            bondType: bondType,
            bondId: bondId,
            strikePrice: new BN(0),
            leverage: new BN(0),
            theoreticalPrice: new BN(0),
            redeemETH: new BN(0),
            maturity: new Date(lien.utils.fromSec(bondRet.maturity)),
          } as LiquidBond;
          bondGroup.liquidBondList.push(liquidBond);
        }
      }
      // Pricer (fixed underlying price and volatility)
      if (!bondGroup.expired) {
        bondGroup.liquidBondList = bondGroup.liquidBondList.map(
          (liquid: LiquidBond): LiquidBond => {
            const pricerRet = pricer(
              base.oracleEthPrice.toNumber(),
              base.oracleVolatility.toNumber(),
              bondGroup.maturity,
              liquid.bondType
            );
            liquid.theoreticalPrice = new BN(pricerRet.ans1);
            liquid.leverage = new BN(pricerRet.leverage);
            liquid.strikePrice = bondGroup.strikePrice;
            // memo liquid
            bondMap.set(liquid.bondId, liquid);
            return liquid;
          }
        );
      } else {
        // already expired.
        const liquidBondList: LiquidBond[] = [];
        for (const liquid of bondGroup.liquidBondList) {
          liquid.redeemETH = await lien.bondToken.getRate(liquid.bondAddress);
          liquid.leverage = new BN(1);
          liquid.strikePrice = bondGroup.strikePrice;
          liquid.isBurnable = liquid.redeemETH.toString() !== '0';
          liquid.theoreticalPrice = liquid.redeemETH.multipliedBy(
            base.oracleEthPrice
          );
          // memo liquid
          bondMap.set(liquid.bondId, liquid);
          liquidBondList.push(liquid);
        }
        bondGroup.liquidBondList = liquidBondList;
      }
      bondGroupMap.set(index, bondGroup);
    };
    await Promise.all(
      bondGroupIdList.map((bondGroupId: number) =>
        dispatchBondPromise(bondGroupId)
      )
    );
    return { bondGroupMap, bondMap };
  };

  // triggered dispatchBondTrigger & oracle update.
  const dispatchAddedBondMap = useCallback(async () => {
    if (
      currentAddress &&
      lien &&
      base.oracleEthPrice.toString() !== '0' &&
      base.oracleVolatility.toString() !== '0'
    ) {
      let bondGroupIdList: number[] = base.bondGroupIdList;
      if (bondGroupIdList.length === 0) {
        const nextBondID = await lien.bondMaker.getNextBondGroupID();
        const startID = nextBondID - currentProcessedCount - pagingInterval;
        for (
          let i = startID < 1 ? 1 : startID;
          i < nextBondID - currentProcessedCount;
          i++
        ) {
          bondGroupIdList.push(i);
        }
      } else {
        bondGroupIdList = bondGroupIdList.slice(
          currentProcessedCount,
          currentProcessedCount + pagingInterval
        );
      }
      const { bondGroupMap, bondMap } = await getBondGroupMapAndBondMap(
        bondGroupIdList
      );
      dispatch({
        type: 'ADD_BOND_MAP',
        payload: {
          bondGroupMap,
          bondMap,
        },
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    dispatch,
    currentProcessedCount,
    base.oracleEthPrice,
    base.oracleVolatility,
  ]);

  // triggered dispatch recommended ordered BondGroupListId.
  const dispatchOrderedBondGroupIdList = useCallback(async () => {
    try {
      const res = await fetch(`${process.env.REACT_APP_FETCH_BOND_GROUP_URL}`);
      const resText = await res.text();
      const bondGroupIdList = JSON.parse(resText);
      dispatch({
        type: 'UPDATE_ORDERED_BOND_GROUP_ID_LIST',
        payload: bondGroupIdList,
      });
    } catch (err) {
      console.error('fail to get recommended ordered bond groupId List.');
      if (sdk) {
        const nextBondID = (await lien.bondMaker.getNextBondGroupID()) - 1;
        const bondGroupIdList: number[] = [];
        for (let i = nextBondID - 1; i > 0; i--) {
          bondGroupIdList.push(i);
        }
        dispatch({
          type: 'UPDATE_ORDERED_BOND_GROUP_ID_LIST',
          payload: bondGroupIdList,
        });
      }
    }
  }, [dispatch, sdk]);

  // triggered currentProcessGroup
  const dispatchBondTrigger = useCallback(async () => {
    if (currentAddress && lien) {
      const nextBondID = await lien.bondMaker.getNextBondGroupID(); //First time rendering
      const maxPageNum = new BN(nextBondID)
        .minus(1)
        .div(minTrancheRows)
        .dp(0, BN.ROUND_UP)
        .toNumber();
      setMaxPageNum(maxPageNum);
      if (currentProcessGroup > 0)
        setProcessedCount(pagingInterval * (currentProcessGroup - 1));
    }
  }, [currentAddress, currentProcessGroup]);

  const getExchangeDataMapAndExchangeToLBT = async (
    bondList: Bond[]
  ): Promise<{
    exchangeDataMap: Map<string, ExchangeData>;
    exchangeToLBT: Map<string, string>;
  }> => {
    const exchangeDataMap: Map<string, ExchangeData> = new Map();
    const exchangeToLBT: Map<string, string> = new Map();
    const dispatchExchangeData = async (bond: Bond, kind: string) => {
      // if not found exchange.
      try {
        // default eth.
        let settlementAddress = 'ETH';
        let exAddr: string = CONTRACT_ADDR.ETH_EXCHANGE;
        switch (kind) {
          case EXCHANGE_BOND_KIND:
            settlementAddress = bond.bondAddress;
            exAddr = await lien.fairSwap.tokenToExchangeLookup(
              settlementAddress
            );
            break;
          case EXCHANGE_LIEN_KIND:
            settlementAddress = CONTRACT_ADDR.LIEN_TOKEN;
            exAddr = CONTRACT_ADDR.LIEN_EXCHANGE;
        }
        const exchangeData = await lien.fairSwap.getExchangeData(exAddr);
        const myShare = await lien.fairSwap.getShare(currentAddress, exAddr);
        const sellPrice = await lien.ERC20.fromAmount18(
          exchangeData[7].toString()
        );
        const buyPrice = await lien.ERC20.fromAmount18(
          exchangeData[6].toString()
        );
        exchangeDataMap.set(settlementAddress, {
          latestBoxNumber: new BN(exchangeData[0]),
          baseTokenPool: await lien.ERC20.fromAmount8(
            exchangeData[1].toString()
          ),
          settlementTokenPool: await lien.ERC20.fromAmount(
            settlementAddress,
            exchangeData[2].toString()
          ),
          totalShare: await lien.ERC20.fromAmount8(exchangeData[3].toString()),
          baseTokenPerShare: await lien.ERC20.fromAmount18(
            exchangeData[4].toString()
          ),
          settlementTokenPerShare: await lien.ERC20.fromAmount18(
            exchangeData[5].toString()
          ),
          buyPrice: buyPrice,
          sellPrice: sellPrice,
          myShare: await lien.ERC20.fromAmount8(myShare.toString()),
          settlementAddress: settlementAddress,
          exchangeAddress: exAddr,
          bondId: bond.bondId,
        } as ExchangeData);
        exchangeToLBT.set(exAddr, settlementAddress);
      } catch (e) {
        console.error(e);
        return;
      }
    };

    const promiseArray = bondList
      .filter(isLiquid)
      .map((bond: Bond) =>
        dispatchExchangeData(bond as Bond, EXCHANGE_BOND_KIND)
      );
    promiseArray.push(dispatchExchangeData({} as Bond, EXCHANGE_LIEN_KIND));
    // TODO not yet exchange ETH.
    // promiseArray.push(dispatchExchangeData({} as Bond, EXCHANGE_ETH_KIND));
    await Promise.all(promiseArray);
    return {
      exchangeDataMap,
      exchangeToLBT,
    };
  };

  // triggered
  // 1. base.addedBondMap
  const dispatchAddedExchange = useCallback(async () => {
    if (lien && currentAddress) {
      const {
        exchangeDataMap,
        exchangeToLBT,
      } = await getExchangeDataMapAndExchangeToLBT(
        Array.from(base.addedBondMap.values())
      );
      dispatch({
        type: 'ADD_EXCHANGE_MAP',
        payload: {
          exchangeDataMap,
          exchangeToLBT,
        },
      });
      // exchange address set.
      const addressSet = new Set(
        Array.from(exchangeDataMap.values()).map(
          (exchange: ExchangeData) => exchange.exchangeAddress
        )
      );
      dispatch({
        type: 'ADD_EXCHANGE_ADDRESS_SET',
        payload: addressSet,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [base.addedBondMap]);

  // triggered
  // 1. minTrigger
  // 2. change Liquidity(addLiquidity || removeLiquidity || removeAfterMaturity)
  // 3. newTradeData
  // 4. executeUnexecutedBox
  const dispatchUpdateExchange = useCallback(async () => {
    if (lien && currentAddress && base.bondMap) {
      const {
        exchangeDataMap,
        exchangeToLBT,
      } = await getExchangeDataMapAndExchangeToLBT(
        Array.from(base.bondMap.values())
      );
      dispatch({
        type: 'UPDATE_EXCHANGE_MAP',
        payload: {
          exchangeDataMap,
          exchangeToLBT,
        },
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    minTrigger,
    txTrigger.addLiquidity,
    txTrigger.removeLiquidity,
    txTrigger.removeAfterMaturity,
    txTrigger.newTradeData,
    txTrigger.executeUnexecutedBox,
  ]);

  // TODO: added event triggered
  const dispatchUpdateOracle = useCallback(async () => {
    if (currentAddress && lien) {
      const oracleEthPrice = await latestPrice();
      const oracleVolatility = await getVolatility();
      dispatch({
        type: 'UPDATE_ORACLE',
        payload: {
          oracleEthPrice: oracleEthPrice ? oracleEthPrice : new BigNumber(0),
          oracleVolatility: oracleVolatility
            ? oracleVolatility
            : new BigNumber(0),
        },
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dispatch, minTrigger, sdk]);

  useEffect(() => {
    dispatchAddedBondMap();
    dispatchOrderedBondGroupIdList();
  }, [dispatchAddedBondMap, dispatchOrderedBondGroupIdList]);

  useEffect(() => {
    dispatchBondTrigger();
  }, [dispatchBondTrigger]);

  useEffect(() => {
    dispatchAddedExchange();
    dispatchUpdateExchange();
  }, [dispatchAddedExchange, dispatchUpdateExchange]);

  useEffect(() => {
    dispatchUpdateOracle();
  }, [dispatchUpdateOracle]);

  return (
    <BaseContext.Provider
      value={{ setProcessGroup, currentProcessGroup, maxPageNum, base }}
    >
      {children}
    </BaseContext.Provider>
  );
};
