import React from 'react';
import Header from 'view/pages/Header';
import Convert from 'view/pages/Convert';
import Trade from 'view/pages/Trade';
import Tranche from 'view/pages/Tranche';
import Auction from 'view/pages/Auction';
import Balance from 'view/pages/Balance';
import Navigation from 'view/pages/Navigation';
import Operation from 'view/pages/Operation';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { LienTokenProvider } from 'provider/lienTokenProvider';
import { Client as Styletron } from 'styletron-engine-atomic';
import { Provider as StyletronProvider } from 'styletron-react';
import { BaseProvider as BaseUIProvider } from 'baseui';
import 'styles.scss';
import AuctionDetail from 'view/pages/AuctionDetail';
import LienToken from './pages/LienToken';
import { AuctionProvider } from 'provider/auctionProvider';
import myTheme from 'themeProvider';
import { RecommendProvider } from 'provider/recommendProvider';
import { ConvertRecommendProvider } from 'provider/convertRecommendProvider';
import { BaseProvider } from 'provider/baseProvider';
import { BaseMyProvider } from 'provider/baseMyProvider';
import { SubscribeProvider } from 'provider/subscribeProvider';
import { OnLoadProvider } from 'provider/onLoadProvider';
import WaitingOrdersList from './pages/WaitingOrdersList';
import { TimeTriggerProvider } from 'provider/timeTriggerProvider';
import { Footer } from 'view/pages/Footer';
import { HistoryProvider } from 'provider/historyProvider';
import History from './pages/History';

const engine = new Styletron();

function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <Header />
      <div id="container">
        <Navigation />
        <StyletronProvider value={engine}>
          <BaseUIProvider theme={myTheme}>
            <OnLoadProvider>
              <TimeTriggerProvider>
                <BaseProvider>
                  <BaseMyProvider>
                    <RecommendProvider>
                      <SubscribeProvider>
                        <div id="content">
                          <WaitingOrdersList />
                          <Switch>
                            <Route path="/convert">
                              <ConvertRecommendProvider>
                                <Convert />
                              </ConvertRecommendProvider>
                            </Route>
                            <Route path="/trade">
                              <LienTokenProvider>
                                <Trade />
                              </LienTokenProvider>
                            </Route>
                            <Route path="/tranche">
                              <Tranche />
                            </Route>
                            <Route path="/auction/detail">
                              <AuctionDetail />
                            </Route>
                            <Route path="/auction">
                              <AuctionProvider>
                                <Auction />
                              </AuctionProvider>
                            </Route>
                            <Route path="/balance">
                              <Balance />
                            </Route>
                            <Route path="/operation">
                              <Operation />
                            </Route>
                            <Route path="/lien_token">
                              <LienTokenProvider>
                                <LienToken />
                              </LienTokenProvider>
                            </Route>
                            <Route path="/history">
                              <HistoryProvider>
                                <History />
                              </HistoryProvider>
                            </Route>
                            <Route path="/">
                              <ConvertRecommendProvider>
                                <Convert />
                              </ConvertRecommendProvider>
                            </Route>
                          </Switch>
                        </div>
                      </SubscribeProvider>
                    </RecommendProvider>
                  </BaseMyProvider>
                </BaseProvider>
              </TimeTriggerProvider>
            </OnLoadProvider>
          </BaseUIProvider>
        </StyletronProvider>
      </div>
      <Footer />
    </Router>
  );
}

export default App;
