import React, { createContext, useCallback, useEffect } from 'react';
import { GlobalState } from 'reducer/store';
import { useDispatch, useSelector } from 'react-redux';
import CONTRACT_ADDR from 'libs/src/helper/Constant';
import useSubscriber from 'actions/useSubscriber';
import { lien } from 'actions/useLien';
import {
  ADD_MOVE_LIQUIDITY,
  ADD_REMOVE_AFTER_MATURITY,
  ADD_RECEIVE_DIVIDEND_ETH,
  ADD_RECEIVE_DIVIDEND_TOKEN,
  ADD_FROM_TRANSFER,
  ADD_TO_TRANSFER,
  ADD_RECEIVE_DIVIDEND,
  ADD_RECEIVE_DIVIDEND_OF_ETH,
  ADD_LOG_ISSUE_NEW_BONDS,
  ADD_LOG_ISSUE_IDOL,
  ADD_LOG_ISSUE_LBT,
  ADD_LOG_RETURN_LOCKED_POOL_USERS,
  ADD_LOG_RETURN_LOCKED_POOL,
  ADD_LOG_BURN_IDOL,
  ADD_LOG_MINT_IDOL,
  ADD_LOG_CANCEL_BID,
  ADD_LOG_AUCTION_RESULT,
  ADD_LOG_BID_MEMO,
} from 'reducer/eventReducer';

export const HistoryContext: any = createContext('');
export const HistoryProvider: React.FC = ({ children }): JSX.Element => {
  const dispatch = useDispatch();
  const {
    dispatchGetPastEvents,
    dispatchExchangeGetPastEvents,
  } = useSubscriber();
  const history = useSelector((state: GlobalState) => state.history.history);
  const event = useSelector((state: GlobalState) => state.event);
  const exchangeDataMap = useSelector(
    (state: GlobalState) => state.base.exchangeDataMap
  );
  const exchangeAddressSet = useSelector(
    (state: GlobalState) => state.base.exchangeAddressSet
  );
  const bondMap = useSelector((state: GlobalState) => state.base.bondMap);
  const currentAddress = lien.address;

  const dispatchHistory = useCallback(async () => {
    const isLienableAddress = (address: string): boolean => {
      switch (address) {
        case CONTRACT_ADDR.AUCTION ||
          CONTRACT_ADDR.BONDMAKER ||
          CONTRACT_ADDR.WRAPPER ||
          CONTRACT_ADDR.STABLECOIN ||
          CONTRACT_ADDR.AUCTION ||
          CONTRACT_ADDR.AUCTION_BOARD:
          return true;
        default:
          return exchangeAddressSet.has(address);
      }
    };
    const transferHistory = [
      ...Array.from(event.fromTransferListMap.values()),
      ...Array.from(event.toTransferListMap.values()),
    ]
      .flat()
      .filter(
        (transfer: any) =>
          !isLienableAddress(transfer.returnValues.to) ||
          !isLienableAddress(transfer.returnValues.from)
      );
    const allHistory = [
      ...transferHistory,
      ...Array.from(event.acceptOrdersListMap.values()).flat(),
      ...Array.from(event.executionListMap.values()).flat(),
      ...Array.from(event.moveLiquidityMap.values()).flat(),
      ...Array.from(event.removeAfterMaturityMap.values()).flat(),
      ...Array.from(event.receiveDividendETH.values()).flat(),
      ...Array.from(event.receiveDividendToken.values()).flat(),
      ...event.receiveDividendOfEth,
      ...event.logIssueNewBonds,
      ...event.logIssueIDOL,
      ...event.logIssueLBT,
      ...event.logReturnLockedPoolUsers,
      ...event.logReturnLockedPool,
      ...event.logBurnIDOL,
      ...event.logMintIDOL,
      ...event.logMintIDOL,
      ...event.logCancelBid,
      ...event.logAuctionResult,
      ...event.logBidMemo,
    ].sort(
      (a: { blockNumber: number }, b: { blockNumber: number }) =>
        b.blockNumber - a.blockNumber
    );
    dispatch({
      type: 'SET_HISTORY',
      payload: allHistory,
    });
  }, [event, exchangeAddressSet]); // eslint-disable-line

  useEffect(() => {
    dispatchHistory();
  }, [dispatchHistory]);

  // Needs Events List.(only for history)
  const dispatchMoveLiquidity = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      liquidityProvider: currentAddress,
    };
    await dispatchExchangeGetPastEvents(
      filter,
      ADD_MOVE_LIQUIDITY,
      currentAddress,
      exchangeDataMap
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress, exchangeDataMap]);

  // ===== Only iDOL vs LBT =====
  const dispatchRemoveAfterMaturity = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      liquidityProvider: currentAddress,
    };
    await dispatchExchangeGetPastEvents(
      filter,
      ADD_REMOVE_AFTER_MATURITY,
      currentAddress,
      exchangeDataMap
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress, exchangeDataMap]);

  // ===== Only iDOL vs Lien =====
  const dispatchReceiveDividendETH = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      recipient: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_RECEIVE_DIVIDEND_ETH);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress, exchangeDataMap]);

  const dispatchReceiveDividendToken = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      recipient: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_RECEIVE_DIVIDEND_TOKEN);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress, exchangeDataMap]);

  // ===== ERC20 ======
  const dispatchTransfer = useCallback(async () => {
    if (!currentAddress) return;
    const dispatchPromiseList = [];
    dispatchPromiseList.push(
      dispatchGetPastEvents(
        {
          from: currentAddress,
        },
        ADD_FROM_TRANSFER,
        CONTRACT_ADDR.STABLECOIN
      )
    );
    dispatchPromiseList.push(
      await dispatchGetPastEvents(
        {
          to: currentAddress,
        },
        ADD_TO_TRANSFER,
        CONTRACT_ADDR.LIEN_TOKEN
      )
    );
    for (const bond of bondMap.values()) {
      dispatchPromiseList.push(
        dispatchGetPastEvents(
          { from: currentAddress },
          ADD_FROM_TRANSFER,
          bond.bondAddress
        )
      );
      dispatchPromiseList.push(
        dispatchGetPastEvents(
          { to: currentAddress },
          ADD_TO_TRANSFER,
          bond.bondAddress
        )
      );
    }
    await Promise.all(dispatchPromiseList);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress, bondMap]);

  // ===== LienToken ======
  const dispatchReceiveDividend = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      recipient: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_RECEIVE_DIVIDEND);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress]);

  const dispatchReceiveDividendOfEth = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      recipient: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_RECEIVE_DIVIDEND_OF_ETH);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress]);

  // ===== BondMaker ======
  const dispatchLogIssueNewBonds = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      issuer: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_LOG_ISSUE_NEW_BONDS);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress]);

  // ===== Wrapper =====
  const dispatchLogIssueIDOL = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      sender: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_LOG_ISSUE_IDOL);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress]);

  const dispatchLogIssueLBT = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      sender: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_LOG_ISSUE_LBT);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress]);

  const dispatchLogReturnLockedPoolUsers = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      user: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_LOG_RETURN_LOCKED_POOL_USERS);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress]);

  // ===== StableCoin =====
  const dispatchLogReturnLockedPool = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      owner: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_LOG_RETURN_LOCKED_POOL);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress]);

  const dispatchLogBurnIDOL = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      owner: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_LOG_BURN_IDOL);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress]);

  const dispatchLogMintIDOL = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      owner: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_LOG_MINT_IDOL);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress]);

  // ====== Auction ======
  const dispatchLogCancelBid = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      bidder: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_LOG_CANCEL_BID);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress]);

  const dispatchLogAuctionResult = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      bidder: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_LOG_AUCTION_RESULT);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress]);

  // ====== Auction Board ======
  const dispatchLogBidMemo = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      bidder: currentAddress,
    };
    await dispatchGetPastEvents(filter, ADD_LOG_BID_MEMO);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress]);

  useEffect(() => {
    dispatchMoveLiquidity();
    dispatchRemoveAfterMaturity();
    dispatchReceiveDividendETH();
    dispatchReceiveDividendToken();
    dispatchTransfer();
    dispatchReceiveDividend();
    dispatchReceiveDividendOfEth();
    dispatchLogIssueNewBonds();
    dispatchLogIssueIDOL();
    dispatchLogIssueLBT();
    dispatchLogReturnLockedPoolUsers();
    dispatchLogReturnLockedPool();
    dispatchLogBurnIDOL();
    dispatchLogMintIDOL();
    dispatchLogCancelBid();
    dispatchLogAuctionResult();
    dispatchLogBidMemo();
  }, [
    dispatchMoveLiquidity,
    dispatchRemoveAfterMaturity,
    dispatchReceiveDividendETH,
    dispatchReceiveDividendToken,
    dispatchTransfer,
    dispatchReceiveDividend,
    dispatchReceiveDividendOfEth,
    dispatchLogIssueNewBonds,
    dispatchLogIssueIDOL,
    dispatchLogIssueLBT,
    dispatchLogReturnLockedPoolUsers,
    dispatchLogReturnLockedPool,
    dispatchLogBurnIDOL,
    dispatchLogMintIDOL,
    dispatchLogCancelBid,
    dispatchLogAuctionResult,
    dispatchLogBidMemo,
  ]);

  return (
    <HistoryContext.Provider value={{ history }}>
      {children}
    </HistoryContext.Provider>
  );
};
