import React from 'react';
import { ListItem } from 'baseui/list';
import ItemBalance from 'view/components/presentations/ItemBalance';
import { Label3 } from 'baseui/typography';
import { OracleETHPriceBrand, OracleVolatilityBrand } from 'resource/brands';
import BigNumber from 'bignumber.js';
import './MarketStatus.scss';

interface MarketStatusProps {
  oracleEthPrice: BigNumber;
  oracleVolatility: BigNumber;
}

function MarketStatus(props: MarketStatusProps) {
  const oraclePrice = {
    brand: OracleETHPriceBrand,
    value: props.oracleEthPrice.toString(),
  };
  const oracleVol = {
    brand: OracleVolatilityBrand,
    value: props.oracleVolatility.toString(),
  };
  return (
    <div>
      <ListItem sublist>
        <Label3>
          <span className="market_status">Market</span>
        </Label3>
      </ListItem>
      <ListItem sublist>
        <ItemBalance price={oraclePrice} />
      </ListItem>
      <ListItem sublist>
        <ItemBalance price={oracleVol} />
      </ListItem>
    </div>
  );
}

export default MarketStatus;
