import React from 'react';
import { Plus, Delete } from 'baseui/icon';
import './WithIcon.scss';
import {
  Unstable_StatefulDataTable as Table,
  CategoricalColumn,
  CustomColumn,
  RowActionT,
} from 'baseui/data-table';
import BigNumber from 'bignumber.js';
import { PriceInfo } from 'types';
import { useStyletron } from 'styletron-react';
import GettingData from './GettingData';
import NoData from './NoData';

export interface LPRowData {
  exchangeAddress: string;
  myShare: BigNumber;
  totalShare: BigNumber;
  occupancy: BigNumber;
  baseAmount: PriceInfo;
  settlementAmount: PriceInfo;
  settlementId: string;
  isLBT?: boolean;
}

const columns = [
  CategoricalColumn({
    title: 'Exchange ID',
    maxWidth: 100,
    mapDataToValue: (data: LPRowData) => data.exchangeAddress,
  }),
  CustomColumn({
    title: 'My Share',
    mapDataToValue: (data: LPRowData) => data,
    renderCell: function Cell(props: any) {
      return (
        <div>
          {`
            ${props.value.occupancy
              .multipliedBy(100)
              .dp(3)
              .toString()} % (${props.value.myShare
            .dp(2)
            .toString()} / ${props.value.totalShare.dp(2)})`}
        </div>
      );
    },
  }),
  CustomColumn({
    title: 'Base Amount',
    mapDataToValue: (data: LPRowData) => data.baseAmount,
    renderCell: function Cell(props: any) {
      return (
        <div className={props.value.brand.className}>
          {props.value.brand.view(props.value.value)}
        </div>
      );
    },
  }),
  CustomColumn<{ color: string }, {}>({
    title: 'Settlement Amount',
    mapDataToValue: (data: LPRowData) => data.settlementAmount,
    renderCell: function Cell(props: any) {
      return (
        <div className={props.value.brand.className}>
          {props.value.brand.view(props.value.value)}
        </div>
      );
    },
  }),
];

export interface LPTableProps {
  rows: LPRowData[];
  loading: boolean;
  onAdd: (row: LPRowData) => void;
  onRemove: (row: LPRowData) => void;
}

export default (props: LPTableProps) => {
  const [css] = useStyletron();
  const rowActions: RowActionT[] = [
    {
      label: 'Add',
      onClick: ({ row }) => props.onAdd(row.data as LPRowData),
      renderIcon: Plus,
    },
    {
      label: 'Remove',
      onClick: ({ row }) => props.onRemove(row.data as LPRowData),
      renderIcon: Delete,
    },
  ];
  return (
    <div className={css({ height: `${props.rows.length * 60 + 170}px` })}>
      <Table
        columns={columns}
        rows={props.rows}
        rowActions={rowActions}
        loading={props.loading}
        loadingMessage={() => <GettingData />}
        emptyMessage={() => <NoData />}
      />
    </div>
  );
};
