import React, { useState, useEffect, useContext } from 'react';
import { BondGroup } from 'types';
import SubTitle from 'view/components/presentations/SubTitle';
import { GlobalState } from 'reducer/store';
import { BaseContext } from 'provider/baseProvider';
import { minTrancheRows } from 'config';
import { recommendedBondListDescription } from 'resource/description/index';
import LiquidListTable, {
  TrancheBondGroupRows,
} from 'view/components/presentations/LiquidListTableBox';
import { useSelector } from 'react-redux';
import BigNumber from 'bignumber.js';

interface TrancheLeftContainerProps {
  onClick: (index?: number | string) => void;
  selected: number;
}

function split<T extends any[]>(arr: T, size: number) {
  return arr.reduce((all, one, i) => {
    const ch = Math.floor(i / size);
    all[ch] = [].concat(all[ch] || [], one);
    return all;
  }, []);
}

function TrancheLeftContainer(props: TrancheLeftContainerProps) {
  const exchangeDataMap = useSelector(
    (state: GlobalState) => state.base.exchangeDataMap
  );
  const trancheRecommendBondGroupList = useSelector(
    (state: GlobalState) => state.recommend.trancheRecommendBondGroupList
  );
  const bondGroupMap = useSelector(
    (state: GlobalState) => state.base.bondGroupMap
  );
  const { setProcessGroup, maxPageNum } = useContext(BaseContext);

  const [currentPage, setCurrentPage] = React.useState(1);
  // ====== BondList Table ======
  const [recommendLoading, setRecommendLoading] = useState(true);
  const [loading, setLoading] = useState(true);
  const makeBondGroupRows = (
    bondList: BondGroup[]
  ): { id: any; data: TrancheBondGroupRows }[] => {
    return bondList
      .filter((bondGroup) => !bondGroup.expired)
      .sort((a, b) => b.maturity.getTime() - a.maturity.getTime())
      .map((bondGroup: BondGroup): { id: any; data: TrancheBondGroupRows } => {
        const exchangeData = exchangeDataMap.get(
          bondGroup.liquidBondList[0].bondAddress
        );
        const totalLiquidity = exchangeData
          ? exchangeData.baseTokenPool
          : new BigNumber(0);
        return {
          id: bondGroup.id,
          data: {
            id: bondGroup.id,
            maturity: bondGroup.maturity,
            strikePrice: bondGroup.strikePrice,
            sbtPrice: bondGroup.solidBond.theoreticalPrice,
            lbtPrice:
              bondGroup.liquidBondList.length > 0
                ? bondGroup.liquidBondList[0].theoreticalPrice
                : new BigNumber(0),
            leverage: bondGroup.liquidBondList[0].leverage,
            totalLiquidity: totalLiquidity,
            onClickRow: props.onClick,
            selected: props.selected,
          } as TrancheBondGroupRows,
        };
      });
  };

  const [datum, setDatum] = useState<any[]>([]);
  const [recommendDatum, setRecommendDatum] = useState<any[]>([]);
  useEffect(() => {
    setRecommendDatum(makeBondGroupRows(trancheRecommendBondGroupList));
    setDatum(makeBondGroupRows(Array.from(bondGroupMap.values())));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bondGroupMap, exchangeDataMap, trancheRecommendBondGroupList]);

  useEffect(() => {
    recommendDatum.length > 0 && setRecommendLoading(false);
    datum.length > 0 && setLoading(false);
  }, [datum, recommendDatum]); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div className="content_box_3">
      <SubTitle
        title="recommended Bond List"
        isHelp={true}
        description={recommendedBondListDescription}
      />
      <div className="box">
        <LiquidListTable
          loading={loading}
          recommendLoading={recommendLoading}
          recommendRows={recommendDatum.length > 0 ? recommendDatum : []}
          rows={
            datum.length > minTrancheRows
              ? split(datum, minTrancheRows)[currentPage - 1]
              : datum
              ? datum
              : []
          }
          currentPage={currentPage}
          maxPageNum={maxPageNum}
          setCurrentPage={setCurrentPage}
          onClickRow={props.onClick}
          processNextBondGroup={setProcessGroup}
          setLoading={setLoading}
          setRecommendLoading={setRecommendLoading}
        />
      </div>
    </div>
  );
}

export default TrancheLeftContainer;
