import { FlattenLineSegment } from 'types';

const useDerivPricer = () => {
  // ====== DerivPricer Hooks ======
  // TODO: Add BN
  const pdf = (x: number) => {
    // density function of standard normal distribution
    return Math.exp(-0.5 * x * x) / Math.sqrt(2 * Math.PI);
  };

  const generateFunction = (funcParam: number[][]) => {
    const gridNumber: number = funcParam.length;
    const fromGrid: number[] = [];
    const toGrid: number[] = [];
    const a: number[] = [];
    const b: number[] = [];
    for (let i = 0; i < gridNumber; i++) {
      fromGrid.push(Number(funcParam[i][0]));
      toGrid.push(Number(funcParam[i][2]));
      a.push(
        (Number(funcParam[i][3]) - Number(funcParam[i][1])) /
          (Number(funcParam[i][2]) - Number(funcParam[i][0]))
      );
      b.push(
        (Number(funcParam[i][2]) * Number(funcParam[i][1]) -
          Number(funcParam[i][3]) * Number(funcParam[i][0])) /
          (Number(funcParam[i][2]) - Number(funcParam[i][0]))
      );
    }
    return function payoffFunction(x: number) {
      let targetGrid = 0;
      for (let i = 0; i < gridNumber; i++) {
        if (toGrid[i] > x) {
          targetGrid = i;
          break;
        } else if (i === gridNumber - 1) {
          targetGrid = i;
        }
      }
      return a[targetGrid] * x + b[targetGrid];
    };
  };

  const pricer = (
    spot: number,
    vol: number,
    expiryDate: Date,
    functionParams: FlattenLineSegment[]
  ) => {
    const shift = 0.01;
    const payoffFunction = generateFunction(functionParams);
    const now = new Date();
    const nowUnix = Math.floor(now.getTime() / 1000);
    const expiryDateUnix = Math.floor(expiryDate.getTime() / 1000);
    const expiry: number = (expiryDateUnix - nowUnix) / (365 * 60 * 60 * 24);
    const r = 0;
    const mesh = 1000;
    const rndDomainMin = -5;
    const rndDomainMax = 5;
    const stepDelta: number = (rndDomainMax - rndDomainMin) / mesh;
    let runningSum = 0;
    for (let i = 0; i < mesh + 1; i++) {
      const x: number = rndDomainMin + i * stepDelta;
      const movedSpot: number =
        spot *
        Math.exp(
          r * expiry - 0.5 * vol * vol * expiry + vol * Math.sqrt(expiry) * x
        );
      //var payoff:number = Math.max(0, movedSpot - 200);
      const payoff: number = payoffFunction(movedSpot);
      runningSum += payoff * pdf(x) * stepDelta;
    }
    const ans1: number = runningSum * Math.exp(-r * expiry);

    if (ans1 === 0) {
      const ans1 = 0;
      const leverage = 0;
      return { ans1, leverage };
    } else {
      runningSum = 0;
      for (let i = 0; i < mesh + 1; i++) {
        const x: number = rndDomainMin + i * stepDelta;
        const movedSpot: number =
          spot *
          (1 + shift) *
          Math.exp(
            r * expiry - 0.5 * vol * vol * expiry + vol * Math.sqrt(expiry) * x
          );
        //var payoff:number = Math.max(0, movedSpot - 200);
        const payoff: number = payoffFunction(movedSpot);
        runningSum += payoff * pdf(x) * stepDelta;
      }
      const ans2: number = runningSum * Math.exp(-r * expiry);
      const leverage: number = (((ans2 - ans1) / (spot * shift)) * spot) / ans1;
      return { ans1, leverage };
    }
  };
  return { pricer };
};

export default useDerivPricer;
