import { useDispatch, useSelector } from 'react-redux';
import { lien } from 'actions/useLien';
import { BigNumber as BN } from 'bignumber.js';
import { GlobalState } from 'reducer/store';

const useERC20 = () => {
  const dispatch = useDispatch();
  const sdk = useSelector((state: GlobalState) => state.sdk);
  const convertAmount = async (tokenAddr: string, amount: string | BN) => {
    if (lien.address && sdk) {
      const res = await lien.ERC20.convertAmount(tokenAddr, new BN(amount));
      return Promise.resolve(res ? res : '');
    }
  };

  const approve = async (tokenAddr: string, spender: string, amount: BN) => {
    if (lien.address && sdk) {
      try {
        const res = await lien.ERC20.approve(tokenAddr, spender, amount);
        dispatch({
          type: 'APPROVE_ERC20',
          payload: res,
        });
        return true;
      } catch (error) {
        dispatch({
          type: 'SEND_TX_ERROR',
          payload: error,
        });
      }
    }
    return false;
  };
  return { convertAmount, approve };
};

export default useERC20;
