import Contracts from 'libs/src/helper/Contract';
import ERC20 from 'libs/src/balance/ERC20Helper';
import { Web3Utils } from 'libs/utils/web3Utils';
import { BigNumber as BN } from 'bignumber.js';
import BondToken from 'libs/src/balance/BondTokenHelper';

export default class Balance {
  public erc20Helper: ERC20;
  private contracts: Contracts;
  private web3Utils: Web3Utils;
  private bondTokenHelper: BondToken;

  constructor(contracts: Contracts) {
    this.contracts = contracts;
    this.erc20Helper = new ERC20(contracts);
    this.bondTokenHelper = new BondToken(contracts);
    this.web3Utils = new Web3Utils();
  }

  // ===== Methods.call =====

  public async getIDOLBalance(address: string): Promise<BN> {
    return await this.erc20Helper.getBalance(
      this.contracts.stableCoinAddr,
      address
    );
  }

  public async getBondTokenBalance(
    address: string,
    bondTokenAddr: string
  ): Promise<BN> {
    return await this.erc20Helper.getBalance(bondTokenAddr, address);
  }

  public async getETHBalance(address: string): Promise<BN> {
    const wei = await this.contracts.web3.eth.getBalance(address);
    return new BN(this.web3Utils.fromWei(wei));
  }

  public async getLienTokenBalance(address: string): Promise<BN> {
    return await this.erc20Helper.getBalance(
      this.contracts.lienTokenAddr,
      address
    );
  }

  public async burnAll(tokenAddr: string) {
    return this.bondTokenHelper.burnAll(tokenAddr);
  }
}
