import React, { useState, useEffect } from 'react';
import SubTitle from 'view/components/presentations/SubTitle';
import TransactionButton from 'view/components/presentations/TransactionButton';
import { PoolSymbol } from 'types';
import SubItem from 'view/components/presentations/SubItem';
import FormNumber from 'view/components/presentations/FormNumber';
import ItemNumber from 'view/components/presentations/ItemNumber';
import { BigNumber as BN } from 'bignumber.js';
import { isValidBalance } from 'libs/utils/validate';
import RemoveLiquidityModal from 'view/components/modal/RemoveLiquidityModal';

interface LiquidityRemoveProps {
  poolSymbol: PoolSymbol | undefined;
}

function LiquidityRemoveContainer(props: LiquidityRemoveProps) {
  const [shareTokenAmount, setShareTokenAmount] = useState<BN>(new BN(0));
  const [openedRemoveLiquidity, setOpenedRemoveLiquidity] = useState<boolean>(
    false
  );
  const txFunc: () => void = async () => {
    if (props.poolSymbol) {
      setOpenedRemoveLiquidity(true);
    }
  };
  const sharePercentage =
    props.poolSymbol && props.poolSymbol.myShare && props.poolSymbol.totalShare
      ? `${props.poolSymbol.myShare
          .div(props.poolSymbol.totalShare)
          .times(100)
          .dp(3)
          .toString()} %`
      : '';

  // check txDisabled
  const [txDisabled, setTxDisabled] = useState<boolean>(true);
  useEffect(() => {
    if (
      isValidBalance(shareTokenAmount, props.poolSymbol?.myShare || new BN('0'))
    )
      setTxDisabled(false);
    else setTxDisabled(true);
  }, [shareTokenAmount, props.poolSymbol]);

  return (
    <div className="content_box_3">
      <SubTitle title="Remove Liquidity" />
      {props.poolSymbol && (
        <div className="box">
          <ItemNumber
            name={'Total Share'}
            value={
              props.poolSymbol.totalShare
                ? props.poolSymbol.totalShare.dp(3).toString()
                : '0'
            }
          />
          <ItemNumber
            name={'My Share'}
            value={`${
              props.poolSymbol.myShare
                ? props.poolSymbol.myShare.dp(3).toString()
                : '0'
            } (${sharePercentage})`}
          />
          <br />
          <SubItem name="Amount to Remove Liquidity" />
          <FormNumber
            maxValue={props.poolSymbol?.myShare}
            errorMessage="Insufficient Balance"
            unit="Share"
            value={
              props.poolSymbol?.afterMaturity
                ? new BN(props.poolSymbol.myShare)
                : shareTokenAmount
            }
            setValue={setShareTokenAmount}
          />
          <br />
        </div>
      )}
      {props.poolSymbol && (
        <TransactionButton
          name={
            props.poolSymbol?.afterMaturity
              ? 'Remove After Maturity'
              : 'Remove Liquidity'
          }
          txFunc={txFunc}
          disabled={txDisabled}
        />
      )}
      {props.poolSymbol && (
        <RemoveLiquidityModal
          isOpen={openedRemoveLiquidity}
          setIsOpen={setOpenedRemoveLiquidity}
          shareAmount={shareTokenAmount}
          exchangeData={props.poolSymbol}
          afterMaturity={props.poolSymbol.afterMaturity}
        />
      )}
    </div>
  );
}

export default LiquidityRemoveContainer;
