import React, { useState, useEffect } from 'react';
import { SolidBalance, PriceInfo } from 'types';
import SubTitle from 'view/components/presentations/SubTitle';
import PriceBox from 'view/components/presentations/PriceBox';
import { USDBrand } from 'resource/brands';
import useBalance from 'actions/useBalance';
import MySBTListTable, {
  MySBTRowData,
} from 'view/components/presentations/MySBTListTable';
import MintMultiTxModal from 'view/components/modal/MintMultiTxModal';
import BigNumber from 'bignumber.js';
import { useSelector } from 'react-redux';
import { GlobalState } from 'reducer/store';

// BondList Table columns
function BalanceSolidContainer() {
  const state = useSelector((state: GlobalState) => state);
  const { burnAll } = useBalance();
  const [total, setTotal] = useState<PriceInfo>({
    brand: USDBrand,
    value: '0',
  } as PriceInfo);

  const [mintModal, setMintModal] = useState<any>();
  const [isOpen, setIsOpen] = useState(false);
  const makeMySBTRowData = (
    solidList: { bondId: string; balance: BigNumber }[]
  ): { id: any; data: MySBTRowData }[] => {
    return solidList
      .filter(
        (solid: { bondId: string; balance: BigNumber }) =>
          solid.balance.toString() !== '0'
      )
      .filter((solid: { bondId: string; balance: BigNumber }) =>
        state.base.bondMap.has(solid.bondId)
      )
      .map((solid: { bondId: string; balance: BigNumber }) => {
        return {
          ...state.base.bondMap.get(solid.bondId),
          balance: solid.balance,
        } as SolidBalance;
      })
      .map(
        (solid: SolidBalance): { id: any; data: MySBTRowData } =>
          ({
            id: solid.bondType,
            data: {
              bondId: solid.bondId,
              maturity: solid.maturity,
              strikePrice: solid.strikePrice,
              theoreticalPrice: solid.theoreticalPrice,
              redeemETH: solid.redeemETH,
              balance: solid.balance,
              isBurnable: !!solid.isBurnable,
              isAcceptable: !!solid.isAcceptable,
              onBurn: () => burnAll(solid.bondAddress),
              onMint: () => {
                setIsOpen(true);
                setMintModal({
                  bondID: solid.bondId,
                  lockAmount: solid.balance,
                  bondAddr: solid.bondAddress,
                  strikePrice: solid.strikePrice,
                });
              },
            } as MySBTRowData,
          } as { id: any; data: MySBTRowData })
      );
  };

  const [datum, setDatum] = useState<{ id: any; data: MySBTRowData }[]>(
    makeMySBTRowData(Array.from(state.baseMy.solidBalanceList.values()))
  );

  useEffect(() => {
    setDatum(makeMySBTRowData(Array.from(state.baseMy.solidBalanceList)));
    setTotal({
      brand: USDBrand,
      value: state.baseMy.totalSolidDollar.toString(),
    } as PriceInfo);
  }, [state.baseMy.solidBalanceList, state.baseMy.totalSolidDollar]); // eslint-disable-line react-hooks/exhaustive-deps

  const loading = state.baseMy.liquidBalanceList.length === 0;
  return (
    <div className="content_box_4">
      <SubTitle title="My SBT List" />
      <div className="box">
        <PriceBox price={total} title={'Total SBT Value'} />
        <MySBTListTable rows={datum} loading={loading} />
        <br></br>
      </div>
      {mintModal && (
        <MintMultiTxModal
          isOpen={isOpen}
          setIsOpen={setIsOpen}
          bondID={mintModal.bondID as string}
          lockAmount={mintModal.lockAmount as BigNumber}
          bondAddr={mintModal.bondAddr as string}
          strikePrice={mintModal.strikePrice as BigNumber}
        />
      )}
      <div className="button"></div>
    </div>
  );
}

export default BalanceSolidContainer;
