import React, { useState, useEffect, useCallback } from 'react';
import { useSelector } from 'react-redux';
import SubItem from 'view/components/presentations/SubItem';
import ItemNumber from 'view/components/presentations/ItemNumber';
import TransactionButton from 'view/components/presentations/TransactionButton';
import TransactionButtonLoading from 'view/components/presentations/TransactionButtonLoading';
import AuctionBidBoardContainer from 'view/components/pages/auction/AuctionBidBoardContainer';
import { AspectRatioBox } from 'baseui/aspect-ratio-box';
import { Spinner } from 'baseui/spinner';
import { StateString, AuctionBid } from 'types';
import useAuction from 'actions/useAuction';
import { BigNumber as BN } from 'bignumber.js';
import { setUserBidReveal } from 'libs/utils/utils';
import { color } from 'config';
import { mocked, mARMock } from 'mock';
import { GlobalState } from 'reducer/store';

interface AuctionRightContainerProps {
  strikePrice: string;
  todoSBTMaturityDate?: Date;
  todoAuctionStatus?: StateString;
  auctionID: string;
  auctionPrice: string;
  auctionAmount: string;
  setCurrent?: any;
  setClosable: any;
  isParticipant: boolean;
}

interface MakeAuctionResult {
  txDone: boolean;
  winPrice: string;
  winAmount: string;
  iDOLChange: string;
}

function AuctionRightContainer(props: AuctionRightContainerProps) {
  const [isSending, setIsSending] = useState(false);
  const [userStatus, setUserStatus] = useState('');
  const [unrevealBids, setUnrevealBids] = useState<AuctionBid[] | null>(null);
  const [txButtonName, setTxButtonName] = useState('');
  const [txDone, setTxDone] = useState<boolean>(false);
  const [
    mAuctionResult,
    setMAuctionResult,
  ] = useState<MakeAuctionResult | null>(null);
  const { auctionID, isParticipant, setClosable } = props;
  const auctionGroupMap = useSelector(
    (state: GlobalState) => state.auction.auctionGroupMap
  );
  const mARDone = auctionGroupMap.get(auctionID)?.mARDone || false;
  const {
    makeAuctionResult,
    calcMyLowestPrice,
    calcWinnerAmount,
    getMakeAuctionResult,
    receiveUnrevealedBidDistribution,
    getUserUnrevealBids,
  } = useAuction();

  //txFunc
  const txFunc: () => void = async () => {
    const auction = auctionGroupMap.get(auctionID);
    if (!auction) {
      return null;
    }
    const auctionResult = auction.result;
    if (!auctionResult) {
      return null;
    }
    setIsSending(true);
    setClosable(false);

    if (mocked) {
      const { winPrice, winAmount, iDOLChange } = mARMock;
      const newMAuctionResult = {
        txDone: true,
        winPrice,
        winAmount,
        iDOLChange,
      };
      setMAuctionResult(newMAuctionResult);
    } else if (auctionResult) {
      const winnerBids: string[] = [];
      auctionResult.winnerBids.forEach((bid: any) => {
        winnerBids.push(bid.price.times('1e8').toString());
        winnerBids.push(bid.boardIndex.toString());
      });
      const loserBids: string[] = [];
      auctionResult.loserBids.forEach((bid: any) => {
        loserBids.push(bid.price.times('1e8').toString());
        loserBids.push(bid.boardIndex.toString());
      });
      const winnerAmount = await calcWinnerAmount(auctionID, winnerBids);
      const myLowestPrice = await calcMyLowestPrice(
        auctionID,
        winnerAmount[0],
        loserBids
      );
      const success = await makeAuctionResult(
        auctionID,
        myLowestPrice,
        winnerBids,
        loserBids
      );
      if (success) {
        const auctionResults = await getMakeAuctionResult(auctionID);

        let winningSBTTotal = new BN(0);
        let winningIDOLTotal = new BN(0);
        let iDOLChangeTotal = new BN(0);
        for (const { returnValues: auctionResult } of auctionResults) {
          const winningSBT = auctionResult.SBTAmountOfReward;
          const winningIDOL = auctionResult.IDOLAmountOfPayment;
          const IDOLChange = auctionResult.IDOLAmountOfChange;
          winningSBTTotal = winningSBTTotal.plus(new BN(winningSBT));
          winningIDOLTotal = winningIDOLTotal.plus(new BN(winningIDOL));
          iDOLChangeTotal = iDOLChangeTotal.plus(new BN(IDOLChange));
        }

        const winPrice = winningSBTTotal.eq(0)
          ? '0'
          : winningIDOLTotal.div(winningSBTTotal).toString(10);
        const winAmount = winningSBTTotal.div('1e8').toString(10);
        const iDOLChange = iDOLChangeTotal.div('1e8').toString(10);
        const newMAuctionResult = {
          txDone: true,
          winPrice,
          winAmount,
          iDOLChange,
        };
        setMAuctionResult(newMAuctionResult);
        setTxDone(true);
      }
    }
    setClosable(true);
    setIsSending(false);
  };

  //txFunc2
  const txFunc2: () => void = async () => {
    setIsSending(true);

    for (const unrevealBid of unrevealBids || []) {
      const { secret } = unrevealBid;
      await receiveUnrevealedBidDistribution(auctionID, secret);
      setUserBidReveal(auctionID, unrevealBid, false);
    }
    setUnrevealBids([]);
    setIsSending(false);
  };

  const callbackUnrevealBids = useCallback(async () => {
    const newUnrevealedBids = getUserUnrevealBids(auctionID);
    return newUnrevealedBids;
  }, []); // eslint-disable-line

  useEffect(() => {
    (async () => {
      const newUnrevealedBids = (await callbackUnrevealBids()) || [];
      setUnrevealBids(newUnrevealedBids);
    })();
  }, []); // eslint-disable-line

  useEffect(() => {
    const auction = auctionGroupMap.get(auctionID);
    if (mocked) {
      setUserStatus('Win');
      setTxButtonName('Confirm bid price');
      return;
    }

    if (!auction) {
      return;
    }
    const auctionResult = auction.result;
    switch (true) {
      case unrevealBids === null: {
        setUserStatus('');
        break;
      }
      case unrevealBids && unrevealBids.length > 0:
        setUserStatus('Receive');
        setTxButtonName('Dispose of unreveal bids');
        break;
      case !isParticipant:
        setUserStatus('No Bid');
        break;
      case !auctionResult: {
        setUserStatus('');
        break;
      }
      case mARDone || txDone: {
        setUserStatus('Done');
        break;
      }
      case auctionResult &&
        auctionResult.winnerBids.length > 0 &&
        auctionResult.loserBids.length === 0:
        setUserStatus('Win');
        setTxButtonName('Confirm bid price');
        break;
      case auctionResult &&
        auctionResult.winnerBids.length > 0 &&
        auctionResult.loserBids.length > 0:
        setUserStatus('Partially Win');
        setTxButtonName('Confirm bid price');
        break;
      case auctionResult &&
        auctionResult.winnerBids.length === 0 &&
        auctionResult.loserBids.length > 0:
        setUserStatus('Lose');
        setTxButtonName('Withdraw iDOL');
        break;
      default:
        setUserStatus('Lose');
        setTxButtonName('Withdraw iDOL');
        break;
    }
  }, [auctionGroupMap, unrevealBids]); // eslint-disable-line

  return (
    <AspectRatioBox width={`${100}%`}>
      {userStatus === '' ? (
        <>
          <Spinner size="8vh" color={color.lien} />
        </>
      ) : mAuctionResult?.txDone ? (
        <>
          <SubItem name="Tx success!" />
          <ItemNumber
            name="Bid price average"
            value={`$${mAuctionResult.winPrice}`}
          />
          <ItemNumber
            name="winning SBT"
            value={`${mAuctionResult.winAmount} SBT`}
          />
          <ItemNumber
            name="iDOL Change"
            value={`$${mAuctionResult.iDOLChange}`}
          />
        </>
      ) : userStatus === 'Receive' ? (
        <>
          {!isSending ? (
            <>
              <SubItem name="Unrevealed Bid" />
              Unrevealed bids exists. You need to dispose of them.
            </>
          ) : null}
          {isSending ? (
            <TransactionButtonLoading />
          ) : (
            <TransactionButton name={txButtonName} txFunc={txFunc2} />
          )}
        </>
      ) : (
        <>
          {!isSending ? (
            <>
              <SubItem name="Bid SBT" />
              {/*============ REACT-VIS Graph==========*/}
              <ItemNumber name="Result" value={userStatus} />
              <br />
              <AuctionBidBoardContainer
                auctionID={auctionID}
                strikePrice={props.strikePrice}
              />
              <br />
            </>
          ) : null}
          {['Done', 'No Bid'].includes(userStatus) ? null : isSending ? (
            <TransactionButtonLoading />
          ) : (
            <TransactionButton name={txButtonName} txFunc={txFunc} />
          )}
        </>
      )}
    </AspectRatioBox>
  );
}

export default AuctionRightContainer;
