import * as React from 'react';
import { Notification, KIND } from 'baseui/notification';
import WaitingOrdersProgressBar from './WaitingOrdersProgressBar';
import { WaitingOrders } from 'types';
import { useFairSwap } from 'actions/useFairSwap';
import './WaitingOrdersNotification.scss';
import { Button, SIZE } from 'baseui/button';
import IconWrapper from 'view/components/presentations/IconWrapper';
import { HelpCircle } from 'react-feather';
import { quickExecutionDescription } from 'resource/description/index';

interface WaitingOrdersNotificationProps {
  waitingOrders: WaitingOrders;
  onClick: () => void;
}

export default (props: WaitingOrdersNotificationProps) => {
  const { convertExchangeInfoFromOrders } = useFairSwap();
  const exchangeInfo = convertExchangeInfoFromOrders(
    props.waitingOrders.exchangeAddress,
    props.waitingOrders.isBuy
  );

  return (
    <Notification
      closeable
      kind={KIND.warning}
      overrides={{
        Body: { style: { width: 'auto' } },
      }}
    >
      <div className="waiting_orders">
        {'Waiting order : '}
        <span className={exchangeInfo.fromBrand.className}>
          {exchangeInfo.fromBrand.view(props.waitingOrders.tokenIn.toString())}
        </span>
        {' to '}
        <span className={exchangeInfo.toBrand.className}>
          {exchangeInfo.toBrand.view(
            props.waitingOrders.tokenIn
              .multipliedBy(exchangeInfo.rate)
              .toString()
          )}
        </span>
      </div>
      <WaitingOrdersProgressBar
        isExecuted={props.waitingOrders.isExecuted}
        boxCount={props.waitingOrders.boxCount}
        orderCount={props.waitingOrders.orderCount}
      />
      <hr />
      <IconWrapper
        component={
          <Button onClick={() => props.onClick()} size={SIZE.compact}>
            Quick Execution
          </Button>
        }
        icon={<HelpCircle size={16} color="#666" />}
        isDescription={true}
        description={quickExecutionDescription}
        isPadded={false}
      />
    </Notification>
  );
};
