import React from 'react';
import { Modal, ModalBody } from 'baseui/modal';
import PooledIDOLTable, {
  PooledIDOLRowData,
} from 'view/components/presentations/PooledIDOLTable';

interface PooledIDOLModalProps {
  isOpen: boolean;
  setIsOpen: any;
  rows: PooledIDOLRowData[];
  loading: boolean;
}

// AuctuonDetail => AuctionModal
function PooledIDOLModal(props: PooledIDOLModalProps) {
  return (
    <Modal
      onClose={() => props.setIsOpen(false)}
      closeable
      isOpen={props.isOpen}
      animate
      autoFocus
      overrides={{
        Dialog: {
          style: {
            width: '80%',
            height: '90%',
            display: 'flex',
            flexDirection: 'column',
          },
        },
      }}
    >
      <ModalBody>
        <PooledIDOLTable rows={props.rows} loading={props.loading} />
      </ModalBody>
    </Modal>
  );
}

export default PooledIDOLModal;
