import { reducer } from 'reducer/setupReducer';
import { baseReducer } from 'reducer/baseReducer';
import { baseMyReducer } from 'reducer/baseMyReducer';
import { errorReducer } from 'reducer/errorReducer';
import { cacheReducer } from 'reducer/cacheReducer';
import { recommendReducer } from 'reducer/recommendReducer';
import { lienTokenDividendReducer } from 'reducer/lienTokenDividendReducer';
import { lienFairSwapReducer } from './lienFairSwapReducer';
import { auctionReducer } from 'reducer/auctionReducer';
import { txTriggerReducer } from './txTriggerReducer';
import { eventReducer } from './eventReducer';
import { userTriggerReducer } from './userTriggerReducer';
import { timeTriggerReducer } from './timeTriggerReducer';
import { Reducer } from 'redux';
import {
  GlobalState,
  BaseState,
  BaseMyState,
  AuctionState,
  CacheState,
  LienTokenDividendState,
  LienFairSwapState,
  RecommendState,
  EventState,
  TxTriggerState,
  UserTriggerState,
  TimeTriggerState,
  HistoryState,
} from 'reducer/store';
import { historyReducer } from './historyReducer';

const rootReducer: Reducer<GlobalState, any> = (
  state: any,
  action: any
): GlobalState => {
  console.log(action);
  return {
    sdk: reducer(state.sdk, action),
    base: baseReducer(state.base, action) as BaseState,
    baseMy: baseMyReducer(state.baseMy, action) as BaseMyState,
    auction: auctionReducer(state.auction, action) as AuctionState,
    cache: cacheReducer(state.cache, action) as CacheState,
    lienTokenDividend: lienTokenDividendReducer(
      state.lienTokenDividend,
      action
    ) as LienTokenDividendState,
    lienFairSwap: lienFairSwapReducer(
      state.lienFairSwap,
      action
    ) as LienFairSwapState,
    recommend: recommendReducer(state.recommend, action) as RecommendState,
    event: eventReducer(state.event, action) as EventState,
    error: errorReducer(state.error, action) as any,
    txTrigger: txTriggerReducer(state.txTrigger, action) as TxTriggerState,
    userTrigger: userTriggerReducer(
      state.userTrigger,
      action
    ) as UserTriggerState,
    timeTrigger: timeTriggerReducer(
      state.timeTrigger,
      action
    ) as TimeTriggerState,
    history: historyReducer(state.history, action) as HistoryState,
  };
};
export default rootReducer;
