import React, { useState, useEffect } from 'react';
import MultiTransactionModal from 'view/components/modal/MultiTransactionModal';
import { WaitingOrders } from 'types';
import { useFairSwap } from 'actions/useFairSwap';
import ForceExecuteOrders from '../presentations/multitx/ForceExecuteOrders';
import CompletedExecutionOrders from '../presentations/multitx/CompletedExecutionOrders';
import { GlobalState } from 'reducer/store';
import { useSelector } from 'react-redux';

interface ForceExecuteModalProps {
  waitingOrders: WaitingOrders;
  isOpen: boolean;
  setIsOpen: any;
}

export default (props: ForceExecuteModalProps) => {
  const [isSending, setIsSending] = useState(false);
  const [steps, setSteps] = useState(0);
  const { executeUnexecutedBox, getWhenToExecute } = useFairSwap();
  const state = useSelector((state: GlobalState) => state);
  const waitingOrdersList = state.baseMy.waitingOrdersList;
  const [actualWaitingOrders, setActualWaitingOrders] = useState<WaitingOrders>(
    props.waitingOrders
  );
  useEffect(() => {
    const dispatchWaitingOrders = async () => {
      const ret = await getWhenToExecute(
        props.waitingOrders.boxId,
        props.waitingOrders.isBuy,
        props.waitingOrders.isLimit,
        props.waitingOrders.exchangeAddress
      );
      setActualWaitingOrders({
        ...props.waitingOrders,
        ...ret,
      });
    };
    dispatchWaitingOrders();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.waitingOrders, waitingOrdersList, steps]);

  const txFuncList = [
    async () => {
      if (!actualWaitingOrders.isExecuted) {
        // waiting order and can force execute.
        setIsSending(true);
        const res = await executeUnexecutedBox(
          actualWaitingOrders.orderCount,
          actualWaitingOrders.exchangeAddress
        );
        setIsSending(false);
        if (res) {
          setSteps(1);
        }
      } else {
        // executed orders data
        setSteps(1);
      }
    },
  ];
  const txStatusList: string[] = ['Force execute', 'Completed'];
  const txComponentList = [
    <ForceExecuteOrders key={1} waitingOrders={actualWaitingOrders} />,
    <CompletedExecutionOrders
      key={2}
      tokenIn={actualWaitingOrders.tokenIn}
      exchangeAddr={actualWaitingOrders.exchangeAddress}
      isBuy={actualWaitingOrders.isBuy}
    />,
  ];

  return (
    <MultiTransactionModal
      isOpen={props.isOpen}
      setIsOpen={props.setIsOpen}
      waitingFlag={0}
      waitingAble={!actualWaitingOrders.isExecuted}
      txFuncList={txFuncList}
      txStatusList={txStatusList}
      txComponentList={txComponentList}
      steps={steps}
      isSending={isSending}
    />
  );
};
