import React from 'react';
import './ContentTitle.scss';
import IconWrapper from 'view/components/presentations/IconWrapper';
import { HelpCircle } from 'react-feather';

interface ContentTitleProps {
  title: string;
  isHelp?: boolean;
  description?: string[];
}

function ContentTitle(props: ContentTitleProps) {
  return (
    <div id="content_box_title">
      {props.isHelp ? (
        <IconWrapper
          component={<span>{props.title}</span>}
          icon={<HelpCircle size={16} color="#666" />}
          isDescription={props.isHelp}
          description={props.description || []}
          isPadded={false}
        />
      ) : (
        props.title
      )}
    </div>
  );
}

export default ContentTitle;
