import React, { useEffect } from 'react';
import { Modal, ModalBody, SIZE as SIZEMODAL } from 'baseui/modal';
import { AspectRatioBox } from 'baseui/aspect-ratio-box';
import { BlockProps } from 'baseui/block';
import { Button, SIZE, KIND } from 'baseui/button';
import { FlexGrid, FlexGridItem } from 'baseui/flex-grid';
import { ProgressSteps, NumberedStep } from 'baseui/progress-steps';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import IconWrapper from 'view/components/presentations/IconWrapper';
import { HelpCircle } from 'react-feather';
import { quickExecutionDescription } from 'resource/description';

interface MultiTransactionModalProps {
  isOpen: boolean;
  setIsOpen: any;
  txFuncList: any[];
  waitingFlag?: number;
  waitingAble?: boolean;
  txStatusList: string[];
  txComponentList: any[];
  steps: number;
  isSending: boolean;
  override?: any;
}

const leftItemProps: BlockProps = {
  alignItems: 'center',
  justifyContent: 'center',
  display: 'flex',
  flex: '25%',
  padding: '6%',
};

const rightItemProps: BlockProps = {
  alignItems: 'center',
  justifyContent: 'center',
  display: 'flex',
  flex: '70%',
  padding: '2%',
};

const rightChildItemProps: BlockProps = {
  alignItems: 'center',
  justifyContent: 'center',
  display: 'flex',
  padding: '1%',
};

const MultiTransactionModal = (
  props: RouteComponentProps & MultiTransactionModalProps
) => {
  const [current, setCurrent] = React.useState(0);
  useEffect(() => {
    setCurrent(props.steps);
  }, [props.steps]);

  const stepsComponents = props.txStatusList.map((txStatus) => (
    <NumberedStep title={txStatus} key={txStatus} />
  ));
  return (
    <Modal
      onClose={() => props.setIsOpen(false)}
      closeable
      isOpen={props.isOpen}
      animate
      autoFocus
      size={SIZEMODAL.auto}
    >
      <ModalBody>
        <FlexGrid flexGridColumnCount={2}>
          <FlexGridItem {...leftItemProps}>
            <AspectRatioBox width={`${100}%`} height={'auto'}>
              <ProgressSteps current={current}>{stepsComponents}</ProgressSteps>
            </AspectRatioBox>
          </FlexGridItem>
          <FlexGridItem {...rightItemProps}>
            <FlexGrid flexGridColumnCount={1}>
              <FlexGridItem {...rightChildItemProps}>
                {props.txComponentList[current]}
              </FlexGridItem>
              {props.txFuncList[current] && props.waitingFlag !== current && (
                // Simple sign and signature.
                <FlexGridItem {...rightChildItemProps}>
                  <Button
                    kind={KIND.primary}
                    size={SIZE.default}
                    onClick={(e) => props.txFuncList[current]()}
                    isLoading={props.isSending}
                  >
                    Sign and Send Tx
                  </Button>
                </FlexGridItem>
              )}
              {!props.txFuncList[current] && (
                // Close pattern.
                <FlexGridItem {...rightChildItemProps}>
                  <Button
                    kind={KIND.primary}
                    size={SIZE.default}
                    onClick={(e) => {
                      props.setIsOpen(false);
                      props.history.push('/balance');
                    }}
                  >
                    Go to my balance
                  </Button>
                </FlexGridItem>
              )}
              {props.waitingFlag === current && props.waitingAble && (
                // waiting for signature.
                <FlexGridItem {...rightChildItemProps}>
                  <IconWrapper
                    component={
                      <Button
                        kind={KIND.primary}
                        size={SIZE.default}
                        onClick={(e) => props.txFuncList[current]()}
                        isLoading={props.isSending}
                      >
                        Quick Execution
                      </Button>
                    }
                    icon={<HelpCircle size={16} color="#666" />}
                    isDescription={true}
                    description={quickExecutionDescription}
                    isPadded={true}
                  />
                </FlexGridItem>
              )}
              {props.waitingFlag === current && !props.waitingAble && (
                // waiting for signature.
                <FlexGridItem {...rightChildItemProps}>
                  <Button
                    kind={KIND.primary}
                    size={SIZE.default}
                    onClick={(e) => props.txFuncList[current]()}
                  >
                    Done
                  </Button>
                </FlexGridItem>
              )}
            </FlexGrid>
          </FlexGridItem>
        </FlexGrid>
      </ModalBody>
    </Modal>
  );
};

export default withRouter(MultiTransactionModal);
