import React from 'react';
import BigNumber from 'bignumber.js';
import { formatter } from 'libs/utils/utils';
import ItemNumber from 'view/components/presentations/ItemNumber';
import { IDOLBrand } from 'resource/brands';

export interface BondGroupRows {
  maturity: Date;
  strikePrice: number;
  sbtPrice: number;
  lbtPrice: number;
  leverage: number;
  totalLiquidity: BigNumber;
}

interface BondGroupUnitTableProps {
  data: BondGroupRows[];
  sbtDisable?: boolean;
}

export default (props: BondGroupUnitTableProps) => {
  return (
    <div>
      <ItemNumber
        name={'Maturity'}
        value={props.data[0].maturity.toLocaleDateString()}
      />

      <ItemNumber
        name={'Strike Price'}
        value={'$' + props.data[0].strikePrice.toString()}
      />

      {!props.sbtDisable && (
        <ItemNumber
          name={'SBT Price'}
          value={`$ ${props.data[0].sbtPrice.toString()}`}
        />
      )}
      <ItemNumber
        name={'Theoretical Price'}
        value={'$' + props.data[0].lbtPrice.toString()}
      />

      <ItemNumber
        name={'LBT Leverage'}
        value={new BigNumber(props.data[0].leverage).dp(3).toString() + 'x'}
      />

      <ItemNumber
        name={'Total Liquidity'}
        value={
          formatter(props.data[0].totalLiquidity).value.toString() +
          formatter(props.data[0].totalLiquidity).unit
        }
        brand={IDOLBrand}
      />

      <br />
    </div>
  );
};
