import { BigNumber as BN } from 'bignumber.js';
export const lienTokenDividendReducer = (state: any, action: any) => {
  switch (action.type) {
    case 'SET_LIEN_TOKEN_DIVIDEND':
      return action.payload;
    case 'CLEAR_LIEN_TOKEN':
      return {
        termList: [],
        lastReceivedTerm: { eth: new BN(0), idol: new BN(0) },
      };
    case 'RECEIVE_DIVIDENDS':
    default:
      return state;
  }
};
