import React, { useState } from 'react';
import useStableCoin from 'actions/useStableCoin';
import MultiTransactionModal from 'view/components/modal/MultiTransactionModal';
import { PriceInfo, ExchangeRateInfo } from 'types';
import { SolidBrand, IDOLBrand } from 'resource/brands';
import BigNumber from 'bignumber.js';
import {
  LOCK_POOL_PERCENT,
  LOCK_POOL_WITHOUT_PERCENT,
} from 'resource/constants';
import Approve from '../presentations/multitx/Approve';
import Mint from '../presentations/multitx/Mint';
import CompletedMint from '../presentations/multitx/CompletedMint';
import useERC20 from 'actions/useERC20';
import CONTRACT_ADDR from 'libs/src/helper/Constant';

interface MintMultiTxModalProps {
  isOpen: boolean;
  setIsOpen: any;
  bondID: string;
  lockAmount: BigNumber;
  bondAddr: string;
  strikePrice: BigNumber;
}

export default (props: MintMultiTxModalProps) => {
  const [isSending, setIsSending] = useState(false);
  const [steps, setSteps] = useState(0);
  const { mint } = useStableCoin();
  const { approve } = useERC20();
  const txFuncList = [
    async () => {
      setIsSending(true);
      const res = await approve(
        props.bondAddr,
        CONTRACT_ADDR.STABLECOIN,
        props.lockAmount
      );
      setIsSending(false);
      if (res) {
        setSteps(1);
      }
    },
    async () => {
      setIsSending(true);
      const res = await mint(props.bondID, props.lockAmount, props.bondAddr);
      setIsSending(false);
      if (res) {
        setSteps(2);
      }
    },
  ];
  const txStatusList: string[] = ['Approve', 'Mint', 'Completed'];

  const price = {
    brand: SolidBrand,
    value: props.lockAmount.toString(),
  } as PriceInfo;
  const exchangeRate = {
    fromBrand: SolidBrand,
    toBrand: IDOLBrand,
    rate: props.strikePrice.toString(),
  } as ExchangeRateInfo;

  const getIDOL = props.strikePrice
    .multipliedBy(props.lockAmount)
    .multipliedBy(LOCK_POOL_WITHOUT_PERCENT)
    .toString();
  const getPooledIDOL = props.strikePrice
    .multipliedBy(props.lockAmount)
    .multipliedBy(LOCK_POOL_PERCENT)
    .toString();

  const txComponentList = [
    <Approve
      key={0}
      title={'mint stable coin'}
      price={price}
      exchangeRate={exchangeRate}
    />,
    <Mint
      key={1}
      price={price}
      exchangeRate={exchangeRate}
      getIDOL={getIDOL}
      getPooledIDOL={getPooledIDOL}
    />,
    <CompletedMint
      key={2}
      price={price}
      exchangeRate={exchangeRate}
      getIDOL={getIDOL}
      getPooledIDOL={getPooledIDOL}
    />,
  ];
  return (
    <MultiTransactionModal
      isOpen={props.isOpen}
      setIsOpen={props.setIsOpen}
      txFuncList={txFuncList}
      txStatusList={txStatusList}
      txComponentList={txComponentList}
      steps={steps}
      isSending={isSending}
    />
  );
};
