import React from 'react';
import SubTitle from '../SubTitle';
import PriceBox from '../PriceBox';
import SubItem from '../SubItem';
import { PriceInfo, ExchangeRateInfo } from 'types';
interface ApproveProps {
  title: string;
  price: PriceInfo;
  exchangeRate?: ExchangeRateInfo;
  buffer?: boolean;
}

export default (props: ApproveProps) => {
  return (
    <div className="content_box_modal">
      <SubTitle title={`Approve for  ${props.title}`} />
      <div className="box">
        <SubItem
          name={`You approve  ${props.price.brand.name}${
            props.buffer ? '(includes buffer)' : ''
          }`}
        />
        <PriceBox price={props.price} exchangeRate={props.exchangeRate} />
      </div>
    </div>
  );
};
