import { lien } from 'actions/useLien';
import { useDispatch } from 'react-redux';
import {
  ADD_ACCEPT_ORDERS,
  ADD_EXECUTION_MAP,
  ADD_MOVE_LIQUIDITY,
  ADD_REMOVE_AFTER_MATURITY,
  ADD_RECEIVE_DIVIDEND_ETH,
  ADD_RECEIVE_DIVIDEND_TOKEN,
  ADD_FROM_TRANSFER,
  ADD_TO_TRANSFER,
  ADD_RECEIVE_DIVIDEND,
  ADD_RECEIVE_DIVIDEND_OF_ETH,
  ADD_LOG_ISSUE_NEW_BONDS,
  ADD_LOG_ISSUE_IDOL,
  ADD_LOG_ISSUE_LBT,
  ADD_LOG_RETURN_LOCKED_POOL_USERS,
  ADD_LOG_RETURN_LOCKED_POOL,
  ADD_LOG_BURN_IDOL,
  ADD_LOG_MINT_IDOL,
  ADD_LOG_CANCEL_BID,
  ADD_LOG_AUCTION_RESULT,
  ADD_LOG_BID_MEMO,
} from 'reducer/eventReducer';
import { ExchangeData } from 'types';

const useSubscriber = () => {
  const dispatch = useDispatch();

  const dispatchGetPastEvents = async (
    filter: any,
    dispatchType: string,
    address?: string
  ) => {
    const fromFilter = {
      filter,
      fromBlock: 8000000,
    };
    const callFunc = (error: any, events: any[]) => {
      if (error) {
        console.error(error);
      } else if (events.length) {
        dispatch({
          type: dispatchType,
          payload: events,
          key: address,
        });
      }
    };
    switch (dispatchType) {
      case ADD_ACCEPT_ORDERS:
        if (address)
          await lien.subscribe.AcceptOrders(fromFilter, address, callFunc);
        break;
      case ADD_EXECUTION_MAP:
        if (address)
          await lien.subscribe.Execution(fromFilter, address, callFunc);
        break;
      case ADD_MOVE_LIQUIDITY:
        if (address)
          await lien.subscribe.MoveLiquidity(fromFilter, address, callFunc);
        break;
      case ADD_REMOVE_AFTER_MATURITY:
        if (address)
          await lien.subscribe.RemoveAfterMaturity(
            fromFilter,
            address,
            callFunc
          );
        break;
      case ADD_RECEIVE_DIVIDEND_ETH:
        await lien.subscribe.ReceiveDividendETH(fromFilter, callFunc);
        break;
      case ADD_RECEIVE_DIVIDEND_TOKEN:
        await lien.subscribe.ReceiveDividendToken(fromFilter, callFunc);
        break;
      case ADD_FROM_TRANSFER || ADD_TO_TRANSFER:
        if (address)
          await lien.subscribe.Transfer(fromFilter, address, callFunc);
        break;
      case ADD_RECEIVE_DIVIDEND:
        await lien.subscribe.ReceiveDividend(fromFilter, callFunc);
        break;
      case ADD_RECEIVE_DIVIDEND_OF_ETH:
        await lien.subscribe.ReceiveDividendOfEth(fromFilter, callFunc);
        break;
      case ADD_LOG_ISSUE_NEW_BONDS:
        await lien.subscribe.LogIssueNewBonds(fromFilter, callFunc);
        break;
      case ADD_LOG_ISSUE_IDOL:
        await lien.subscribe.LogIssueIDOL(fromFilter, callFunc);
        break;
      case ADD_LOG_ISSUE_LBT:
        await lien.subscribe.LogIssueLBT(fromFilter, callFunc);
        break;
      case ADD_LOG_RETURN_LOCKED_POOL_USERS:
        await lien.subscribe.LogReturnLockedPoolUsers(fromFilter, callFunc);
        break;
      case ADD_LOG_RETURN_LOCKED_POOL:
        await lien.subscribe.LogReturnLockedPool(fromFilter, callFunc);
        break;
      case ADD_LOG_BURN_IDOL:
        await lien.subscribe.LogBurnIDOL(fromFilter, callFunc);
        break;
      case ADD_LOG_MINT_IDOL:
        await lien.subscribe.LogMintIDOL(fromFilter, callFunc);
        break;
      case ADD_LOG_CANCEL_BID:
        await lien.subscribe.LogCancelBid(fromFilter, callFunc);
        break;
      case ADD_LOG_AUCTION_RESULT:
        await lien.subscribe.LogAuctionResult(fromFilter, callFunc);
        break;
      case ADD_LOG_BID_MEMO:
        await lien.subscribe.LogBidMemo(fromFilter, callFunc);
        break;
    }
  };

  const dispatchExchangeGetPastEvents = async (
    filter: any,
    dispatchType: string,
    currentAddress: string,
    exchangeDataMap: Map<string, ExchangeData>
  ) => {
    if (!currentAddress) return;
    await Promise.all(
      Array.from(exchangeDataMap.values()).map((exchangeData: ExchangeData) =>
        dispatchGetPastEvents(
          filter,
          dispatchType,
          exchangeData.exchangeAddress
        )
      )
    );
  };

  return { dispatchGetPastEvents, dispatchExchangeGetPastEvents };
};

export default useSubscriber;
