import React, { createContext, useCallback, useEffect } from 'react';
import { GlobalState } from 'reducer/store';
import { lien } from 'actions/useLien';
import {
  isSolid,
  Bond,
  PooledIDOL,
  ExchangeData,
  WaitingOrders,
  LiquidBond,
  defaultExchangeData,
} from 'types';
import BigNumber, { BigNumber as BN } from 'bignumber.js';
import { useFairSwap } from 'actions/useFairSwap';
import { useDispatch, useSelector } from 'react-redux';
import CONTRACT_ADDR from 'libs/src/helper/Constant';

export const BaseMyContext: any = createContext('');

export const BaseMyProvider: React.FC = ({ children }): JSX.Element => {
  const dispatch = useDispatch();

  const baseMy = useSelector((state: GlobalState) => state.baseMy);
  const bondMap = useSelector((state: GlobalState) => state.base.bondMap);
  const exchangeDataMap = useSelector(
    (state: GlobalState) => state.base.exchangeDataMap
  );
  const oracleEthPrice = useSelector(
    (state: GlobalState) => state.base.oracleEthPrice
  );
  const acceptOrdersListMap = useSelector(
    (state: GlobalState) => state.event.acceptOrdersListMap
  );
  const executionListMap = useSelector(
    (state: GlobalState) => state.event.executionListMap
  );
  const txTrigger = useSelector((state: GlobalState) => state.txTrigger);
  const userTrigger = useSelector((state: GlobalState) => state.userTrigger);
  const auctionModalTrigger = useSelector(
    (state: GlobalState) => state.auction.txModal
  );
  const {
    getWhenToExecute,
    exchangeAddressToSettlementAddress,
  } = useFairSwap();
  const currentAddress = lien.address;

  // triggered:
  // 1. connect metamask
  const dispatchUpdateBalance = useCallback(async () => {
    if (!currentAddress) return;
    const iDOLBalance = await lien.balance.getIDOLBalance(currentAddress);
    const ethBalance = await lien.balance.getETHBalance(currentAddress);
    const lienBalance = await lien.balance.getLienTokenBalance(currentAddress);
    dispatch({
      type: 'UPDATE_BALANCE',
      payload: {
        iDOLBalance,
        ethBalance,
        lienBalance,
      },
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dispatch, currentAddress, txTrigger, auctionModalTrigger]);

  const getSBTAndLBTBalanceList = async (
    bondList: Bond[]
  ): Promise<{
    solidBalanceList: { bondId: string; balance: BigNumber }[];
    liquidBalanceList: { bondId: string; balance: BigNumber }[];
  }> => {
    const solidBalanceList: { bondId: string; balance: BigNumber }[] = [];
    const liquidBalanceList: { bondId: string; balance: BigNumber }[] = [];

    const dispatchBondPromise = async (bond: Bond) => {
      const balance = await lien.balance.getBondTokenBalance(
        currentAddress,
        bond.bondAddress
      );
      if (isSolid(bond)) {
        solidBalanceList.push({ bondId: bond.bondId, balance });
      } else {
        liquidBalanceList.push({ bondId: bond.bondId, balance });
      }
    };
    await Promise.all(bondList.map((bond: Bond) => dispatchBondPromise(bond)));
    return { solidBalanceList, liquidBalanceList };
  };

  // triggered:
  // 1. txTrigger
  const dispatchUpdateBalanceBondList = useCallback(async () => {
    if (!currentAddress) return;
    const {
      liquidBalanceList,
      solidBalanceList,
    } = await getSBTAndLBTBalanceList(Array.from(bondMap.values()) as Bond[]);
    dispatch({
      type: 'UPDATE_BALANCE_BOND_LIST',
      payload: {
        solidBalanceList,
        liquidBalanceList,
      },
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [txTrigger, bondMap, auctionModalTrigger]);

  const getPooledIDOLBalanceList = async (
    bondList: Bond[]
  ): Promise<PooledIDOL[]> => {
    const pooledIDOLList: PooledIDOL[] = [];
    const dispatchPooledIDOLPromise = async (bond: Bond) => {
      if (isSolid(bond)) {
        // solid -> pooled IDOL.
        const currentPoolID = await lien.stableCoin.getCurrentPoolID(
          bond.bondId
        );
        for (let i = 0; ; i++) {
          const poolID = await lien.stableCoin.generatePoolID(bond.bondId, i);
          const auctionID = await lien.auction.generateAuctionID(
            bond.bondId,
            i
          );
          {
            // with wrapper pooled.
            const poolAmount = await lien.wrapper.viewLockedPoolAmountUser(
              poolID,
              currentAddress
            );
            const actualRedeemAmount = await lien.wrapper.getReturnLockedPoolUsers(
              poolID
            );
            if (poolAmount.toString() !== '0') {
              // If poolID is currentPoolID, that's available pool.
              const returnable = poolID !== currentPoolID;
              pooledIDOLList.push({
                amount: poolAmount,
                actualRedeemAmount: actualRedeemAmount,
                bondId: bond.bondId,
                poolId: poolID,
                count: i,
                returnable: returnable,
                wrapper: true,
                auctionID,
              } as PooledIDOL);
            }
          }
          {
            // without wrapper pooled.
            const poolAmountWithoutWrappers = await lien.stableCoin.getLockedPool(
              currentAddress,
              poolID
            );
            const actualPoolAmountWithoutWrappers = await lien.stableCoin.getReturnLockedPool(
              poolID
            );
            const poolAmount = poolAmountWithoutWrappers[0];
            if (poolAmount.toString() !== '0') {
              const returnable = poolID !== currentPoolID;
              pooledIDOLList.push({
                amount: poolAmount,
                actualRedeemAmount: actualPoolAmountWithoutWrappers,
                bondId: bond.bondId,
                poolId: poolID,
                count: i,
                returnable: returnable,
                wrapper: false,
              } as PooledIDOL);
            }
          }
          if (poolID === currentPoolID) break;
        }
      }
    };
    await Promise.all(
      bondList.map((bond: Bond) => dispatchPooledIDOLPromise(bond))
    );
    return pooledIDOLList;
  };

  // triggered:
  // 1. connect metamask
  // 2. added bondMap
  const dispatchUpdatePooledIDOLList = useCallback(async () => {
    if (!currentAddress) return;
    const pooledIDOLList: PooledIDOL[] = await getPooledIDOLBalanceList(
      Array.from(bondMap.values())
    );
    dispatch({
      type: 'UPDATE_BALANCE_POOLED_IDOL_LIST',
      payload: {
        pooledIDOLList,
      },
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    txTrigger.issueNewBonds,
    txTrigger.issueIDOLOnly,
    txTrigger.issueLBTOnly,
    txTrigger.issueLBTAndIDOL,
    txTrigger.returnLockedPool,
    txTrigger.mintNewIDOL,
    bondMap,
  ]);

  // triggered:
  // 1. baseMy.solidBalanceList & baseMy.liquidBalanceList
  const dispatchUpdateTotal = useCallback(async () => {
    if (!currentAddress) return;
    // idol_price = idol -> 1/(SBT2IDOL) -> $
    const SBT2IDOLPrice = await lien.stableCoin.calcSBT2IDOL(new BigNumber(1));
    const IDOLPrice =
      SBT2IDOLPrice.toString() === '0'
        ? new BigNumber(0)
        : new BigNumber(1).dividedBy(SBT2IDOLPrice);

    // lien_price = lien -> idol, idol -> $
    const lienExchangeData =
      exchangeDataMap.get(CONTRACT_ADDR.LIEN_TOKEN) || defaultExchangeData();
    const LIENPrice =
      lienExchangeData.sellPrice.toString() === '0'
        ? new BigNumber(0)
        : IDOLPrice.dividedBy(lienExchangeData.sellPrice);

    const totalWithoutList = baseMy.ethBalance
      .multipliedBy(oracleEthPrice)
      .plus(baseMy.iDOLBalance.multipliedBy(IDOLPrice))
      .plus(baseMy.lienBalance.multipliedBy(LIENPrice));

    const totalSolidDollar = baseMy.solidBalanceList.reduce(
      (
        sum: BigNumber,
        { bondId, balance }: { bondId: string; balance: BigNumber }
      ) => {
        const bond = bondMap.get(bondId) as Bond;
        return sum.plus(bond.theoreticalPrice.multipliedBy(balance));
      },
      new BigNumber(0)
    );
    const totalLiquidDollar = baseMy.liquidBalanceList.reduce(
      (
        sum: BigNumber,
        { bondId, balance }: { bondId: string; balance: BigNumber }
      ) => {
        const bond = bondMap.get(bondId) as Bond;
        return sum.plus(bond.theoreticalPrice.multipliedBy(balance));
      },
      new BigNumber(0)
    );
    // if after maturity, calc by actual return amounts.
    const totalPooledDollar = baseMy.pooledIDOLList.reduce(
      (sum: BigNumber, pool: PooledIDOL) => {
        if (pool.actualRedeemAmount.toString() === '0')
          return sum.plus(pool.amount);
        return sum.plus(pool.actualRedeemAmount);
      },
      new BigNumber(0)
    );
    const totalLPDollar = Array.from(exchangeDataMap.values())
      .filter((ex: any) => {
        const exchangeData = ex as ExchangeData;
        return (
          !exchangeDataMap.has('') &&
          exchangeData.myShare.toString() !== '0' &&
          exchangeData.totalShare.toString() !== '0'
        );
      })
      .map(
        (ex: any): BigNumber => {
          const exchangeData = ex as ExchangeData;
          const myShare = exchangeData.myShare as BigNumber;
          const totalShare = exchangeData.totalShare as BigNumber;
          const occupancy = myShare.dividedBy(totalShare);
          const baseAmount = exchangeData.baseTokenPool.multipliedBy(occupancy);
          const settlementAmount = exchangeData.settlementTokenPool.multipliedBy(
            occupancy
          );
          let sum = new BigNumber(0);

          // TODO: ETH Exchange.
          if (exchangeData.exchangeAddress === CONTRACT_ADDR.LIEN_EXCHANGE) {
            // lien token exchange.
            sum = sum.plus(settlementAmount.multipliedBy(LIENPrice));
          } else if (exchangeData.bondId) {
            const bond = bondMap.get(exchangeData.bondId) as Bond;
            // LBT token exchange.
            sum = sum.plus(
              settlementAmount.multipliedBy(bond.theoreticalPrice)
            );
          }
          sum = sum.plus(baseAmount.multipliedBy(IDOLPrice));
          return sum;
        }
      )
      .reduce((sum, dollar) => sum.plus(dollar), new BN(0));

    const totalDollar = totalWithoutList
      .plus(totalSolidDollar)
      .plus(totalLiquidDollar)
      .plus(totalPooledDollar)
      .plus(totalLPDollar);

    const sumOfLBTBalance = baseMy.liquidBalanceList.reduce(
      (sum: BigNumber, { balance }: { balance: BigNumber }) =>
        sum.plus(balance),
      new BigNumber(0)
    );
    const averageLeverage =
      sumOfLBTBalance.toString() === '0'
        ? new BigNumber(0)
        : baseMy.liquidBalanceList
            .reduce(
              (
                sum: BigNumber,
                { bondId, balance }: { bondId: string; balance: BigNumber }
              ) =>
                sum.plus(
                  (bondMap.get(bondId) as LiquidBond).leverage.multipliedBy(
                    balance
                  )
                ),
              new BigNumber(0)
            )
            .dividedBy(sumOfLBTBalance);

    dispatch({
      type: 'UPDATE_TOTAL',
      payload: {
        totalDollar,
        totalSolidDollar,
        totalLiquidDollar,
        totalPooledDollar,
        totalLPDollar,
        averageLeverage,
      },
    });
  }, [
    dispatch,
    currentAddress,
    baseMy.iDOLBalance,
    baseMy.ethBalance,
    baseMy.lienBalance,
    baseMy.solidBalanceList,
    baseMy.liquidBalanceList,
    baseMy.pooledIDOLList,
    exchangeDataMap,
    oracleEthPrice,
    bondMap,
  ]);

  // TODO: added other triggered updating waiting orders.
  // triggered
  // 1. acceptOrdersListMap,
  // 2. executionListMap
  const dispatchAllWaitingOrders = useCallback(async () => {
    const promiseList = [];
    const waitingOrdersList: WaitingOrders[] = [];
    const dispatchWaitingOrder = async (
      exchangeAddr: string,
      acceptOrders: {
        returnValues: {
          isBuy: boolean;
          isLimit: boolean;
          boxId: number;
          tokenIn: string;
        };
      }
    ) => {
      const result = await getWhenToExecute(
        acceptOrders.returnValues.boxId,
        acceptOrders.returnValues.isBuy,
        acceptOrders.returnValues.isLimit,
        exchangeAddr
      );
      const settlementAddress = exchangeAddressToSettlementAddress(
        exchangeAddr
      );
      if (!result.isExecuted && settlementAddress) {
        waitingOrdersList.push({
          isBuy: acceptOrders.returnValues.isBuy,
          isLimit: acceptOrders.returnValues.isLimit,
          boxId: acceptOrders.returnValues.boxId,
          tokenIn: await lien.ERC20.fromAmountBuyOrSell(
            settlementAddress,
            acceptOrders.returnValues.tokenIn,
            acceptOrders.returnValues.isBuy
          ),
          isExecuted: result.isExecuted,
          boxCount: result.boxCount,
          orderCount: result.orderCount,
          exchangeAddress: exchangeAddr,
        } as WaitingOrders);
      }
    };
    for (const [
      exchangeAddr,
      acceptOrdersList,
    ] of acceptOrdersListMap.entries()) {
      for (const acceptOrders of acceptOrdersList as any[]) {
        promiseList.push(dispatchWaitingOrder(exchangeAddr, acceptOrders));
      }
    }
    await Promise.all(promiseList);
    dispatch({
      type: 'UPDATE_WAITING_ORDERS_LIST',
      payload: waitingOrdersList,
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [acceptOrdersListMap, executionListMap, userTrigger.updateWaitingOrders]);

  useEffect(() => {
    dispatchUpdateBalance();
  }, [dispatchUpdateBalance]);

  useEffect(() => {
    dispatchUpdateBalanceBondList();
  }, [dispatchUpdateBalanceBondList]);

  useEffect(() => {
    dispatchUpdatePooledIDOLList();
  }, [dispatchUpdatePooledIDOLList]);

  useEffect(() => {
    dispatchUpdateTotal();
  }, [dispatchUpdateTotal]);

  useEffect(() => {
    dispatchAllWaitingOrders();
  }, [dispatchAllWaitingOrders]);

  return (
    <BaseMyContext.Provider value={{ baseMy }}>
      {children}
    </BaseMyContext.Provider>
  );
};
