import React, { createContext, useCallback, useEffect } from 'react';
import { AuctionStatus, AuctionGroup } from 'types';
import { GlobalState } from 'reducer/store';
import { lien } from 'actions/useLien';
import { BigNumber as BN } from 'bignumber.js';
import useAuction from 'actions/useAuction';
import { useDispatch, useSelector } from 'react-redux';
import { period, auctionMaxRow } from 'config';
import { getUserPeriod, asyncAll, getUserBids } from 'libs/utils/utils';

export const AuctionContext: any = createContext('');

export const AuctionProvider: React.FC = ({ children }): JSX.Element => {
  const dispatch = useDispatch();
  const auctionGroupMap = useSelector(
    (state: GlobalState) => state.auction.auctionGroupMap
  );
  const auctionPage = useSelector(
    (state: GlobalState) => state.auction.auctionPage
  );
  const minTrigger = useSelector(
    (state: GlobalState) => state.timeTrigger.minTrigger
  );
  const currentAuctionStatus = useSelector(
    (state: GlobalState) => state.auction.currentAuctionStatus
  );
  const currentDisplayAuctionStatus = useSelector(
    (state: GlobalState) => state.auction.currentDisplayAuctionStatus
  );
  const displayPeriod = useSelector(
    (state: GlobalState) => state.auction.displayPeriod
  );
  const txModal = useSelector((state: GlobalState) => state.auction.txModal);
  const sdk = useSelector((state: GlobalState) => state.sdk);
  const {
    getCurrentAuctionStatus,
    listBondIDFromAuctionID,
    getPeriod,
    auctionParticipantInfo,
    getAuctionResult,
    getSortedBidPrice,
    getBoardStatus,
    setAuctionMaxPage,
    setLoading,
    getUserStatus,
  } = useAuction();
  const currentAddress = lien.address;

  const dispatchCurrentAuctionStatus = useCallback(async () => {
    if (!currentAddress) return;
    let newCurrentAuctionStatus = await getCurrentAuctionStatus();
    newCurrentAuctionStatus.sort((a, b) => {
      const aClosing = a.closingTime.getTime();
      const bClosing = b.closingTime.getTime();
      return bClosing - aClosing;
    });

    // calc period
    const getContractInfo = async (
      auctionStatus: AuctionStatus[],
      i: number
    ) => {
      const auction = auctionStatus[i];
      const { auctionID, closingTime, isEmergency } = auction;

      // eslint-disable-next-line
      let period = await getPeriod(closingTime, isEmergency);

      // participantInfo
      const isParticipant = getUserBids(auctionID).length > 0;
      auctionStatus[i].isParticipant = isParticipant;
      period = getUserPeriod(period, isParticipant, true);
      auctionStatus[i].period = period;
    };

    // forEach AuctionID list
    const promiseArray: any = [];
    for (let i = 0; i < newCurrentAuctionStatus.length; i++) {
      promiseArray.push(getContractInfo(newCurrentAuctionStatus, i));
    }
    await Promise.all(promiseArray);

    // BondID list
    const auctionIDList = newCurrentAuctionStatus.map((elm) => {
      return elm.auctionID;
    });
    const newBondIDList = (await listBondIDFromAuctionID(
      auctionIDList
    )) as string[];
    newCurrentAuctionStatus = newCurrentAuctionStatus.map(
      (elm: AuctionStatus, index: number) => {
        const newElm = elm;
        newElm.bondID = newBondIDList[index];
        return newElm;
      }
    );

    const auctionNum = newCurrentAuctionStatus.length;
    const maxPage =
      auctionNum === 0 ? 1 : Math.floor((auctionNum - 1) / auctionMaxRow) + 1; // e.g. 0->1 30->1 31->2
    setAuctionMaxPage(maxPage);
    dispatch({
      type: 'SET_CURRENT_AUCTION_STATUS',
      payload: {
        currentAuctionStatus: newCurrentAuctionStatus,
      },
    });
  }, [sdk]); // eslint-disable-line

  const dispatchCurrentDisplayAuctionStatus = useCallback(async () => {
    let newCurrentAuctionStatus: AuctionStatus[] = currentAuctionStatus.filter(
      (elm: AuctionStatus) => {
        if (displayPeriod === period.all) {
          return true;
        } else if (displayPeriod === period.attend) {
          return elm.isParticipant;
        } else {
          return elm.period === displayPeriod;
        }
      }
    );

    if (displayPeriod !== period.attend) {
      // sort for attending auction
      const attending = newCurrentAuctionStatus
        .filter((elm: AuctionStatus) => elm.isParticipant)
        .sort((a, b) => {
          const aClosing = a.closingTime.getTime();
          const bClosing = b.closingTime.getTime();
          return bClosing - aClosing;
        });
      const noAttending = newCurrentAuctionStatus
        .filter((elm: AuctionStatus) => !elm.isParticipant)
        .sort((a, b) => {
          const aClosing = a.closingTime.getTime();
          const bClosing = b.closingTime.getTime();
          return bClosing - aClosing;
        });
      newCurrentAuctionStatus = attending.concat(noAttending);
    } else {
      newCurrentAuctionStatus.sort((a, b) => {
        const aClosing = a.closingTime.getTime();
        const bClosing = b.closingTime.getTime();
        return bClosing - aClosing;
      });
    }

    const auctionNum = newCurrentAuctionStatus.length;
    const maxPage =
      auctionNum === 0 ? 1 : Math.floor((auctionNum - 1) / auctionMaxRow) + 1; // e.g. 0->1 30->1 31->2
    setAuctionMaxPage(maxPage);
    dispatch({
      type: 'SET_CURRENT_DISPLAY_AUCTION_STATUS',
      payload: {
        currentDisplayAuctionStatus: newCurrentAuctionStatus,
      },
    });
  }, [displayPeriod]); // eslint-disable-line

  const dispatchBondPromise = async (
    auctionGroupMap: Map<string, AuctionGroup>,
    auctionStatus: AuctionStatus,
    currentAuctionGroupMap: Map<string, AuctionGroup>
  ) => {
    const bondID = auctionStatus.bondID;
    const auctionID = auctionStatus.auctionID;
    const { closingTime } = auctionStatus;
    if (!currentAuctionGroupMap.has(auctionID)) {
      let auctionPeriod = auctionStatus.period;
      const [bondRet, participantInfo] = (await asyncAll(
        lien.bondMaker.getBond(bondID),
        auctionParticipantInfo(auctionID)
      )) as [any, any];

      // participantInfo
      let mARDone = false;
      if (![period.bid].includes(auctionPeriod)) {
        mARDone = new BN(participantInfo.auctionLockedIDOLAmountE8).eq(0);
      }

      // result, board
      let result = null;
      let board = null;
      let mEICalled = false;
      const isParticipant = auctionStatus.isParticipant;
      if ([period.winning, period.closed].includes(auctionPeriod)) {
        result = await getAuctionResult(auctionID);

        mEICalled = result?.end.mEICalled || false;
        if (mEICalled) {
          const [tmp, tmp2] = (await asyncAll(
            getSortedBidPrice(auctionID),
            getBoardStatus(auctionID)
          )) as [BN[], BN[]];
          const sorted = tmp.map((priceE8: BN) => new BN(priceE8).div('1e8'));
          const amounts = tmp2.map((amountE8: BN) =>
            new BN(amountE8).div('1e8')
          );
          board = {
            sorted,
            amounts,
          };
          auctionPeriod = getUserPeriod(auctionPeriod, isParticipant, true);
        } else {
          auctionPeriod = getUserPeriod(auctionPeriod, isParticipant, false);
        }
      }

      const userStatus = await getUserStatus(
        auctionID,
        auctionPeriod,
        isParticipant,
        mARDone,
        mEICalled
      );

      // Make AuctionMap (AuctionGroup)
      const auctionGroup = {
        bondID,
        auctionID,
        bondAddress: bondRet.bondTokenAddress,
        maturity: new Date(bondRet.maturity * 1000),
        auctionStatus: {
          closingTime: closingTime,
          isEmergency: auctionStatus.isEmergency,
          auctionAmount: auctionStatus.auctionAmount,
          rewardedAmount: auctionStatus.rewardedAmount,
          totalSBTAmountBid: auctionStatus.totalSBTAmountBid,
          auctionID,
          bondID,
        } as AuctionStatus,
        strikePrice: new BN(bondRet.solidStrikePrice).div(new BN(10000)),
        period: auctionPeriod,
        isParticipant,
        userStatus,
        mARDone,
        result,
        board,
      } as AuctionGroup;

      auctionGroupMap.set(auctionID, auctionGroup);
    }
  };

  const dispatchAuctionList = useCallback(async () => {
    const auctionGroupMap: Map<string, AuctionGroup> = new Map();
    const auctionPromiseArray: any = [];
    if (!currentAddress) {
      setLoading(false);
      return;
    }
    if (!currentAuctionStatus) {
      setLoading(false);
      return;
    }
    if (!sdk) {
      setLoading(false);
      return;
    }
    if (currentAuctionStatus.length === 0) {
      setLoading(false);
      return;
    }

    const auctionStatusList = currentDisplayAuctionStatus.slice(
      (auctionPage - 1) * auctionMaxRow,
      auctionPage * auctionMaxRow
    );

    // forEach AuctionID list
    auctionStatusList.forEach((auctionStatus: any, index: number) => {
      auctionPromiseArray.push(
        dispatchBondPromise(auctionGroupMap, auctionStatus, auctionGroupMap)
      );
    });

    await Promise.all(auctionPromiseArray);
    dispatch({
      type: 'ADD_AUCTION_LIST',
      payload: {
        auctionGroupMap,
      },
    });
  }, [sdk, auctionPage, currentDisplayAuctionStatus]); // eslint-disable-line react-hooks/exhaustive-deps

  const dispatchReloadAuctionList = useCallback(async () => {
    const auctionGroupMap: Map<string, AuctionGroup> = new Map();

    // don't need to set loading false in reloading
    if (!currentAddress) return;
    if (!currentAuctionStatus) return;
    if (!sdk) return;
    if (currentAuctionStatus.length === 0) return;

    let auctionStatusList = currentAuctionStatus;
    auctionStatusList = currentAuctionStatus.filter((elm: AuctionStatus) => {
      if (displayPeriod === period.all) {
        return true;
      } else if (displayPeriod === period.attend) {
        return elm.isParticipant;
      } else {
        return elm.period === displayPeriod;
      }
    });
    auctionStatusList = auctionStatusList.slice(
      (auctionPage - 1) * auctionMaxRow,
      auctionPage * auctionMaxRow
    );

    for (let i = 0; i < auctionStatusList.length; i++) {
      const auctionStatus = auctionStatusList[i];
      if (!auctionStatus) {
        continue;
      }
      const { closingTime, isEmergency } = auctionStatus;
      auctionStatusList[i].period = await getPeriod(closingTime, isEmergency);
    }

    // Each AuctionID list
    await Promise.all(
      auctionStatusList.map((auctionStatus: any, _: number) =>
        dispatchBondPromise(auctionGroupMap, auctionStatus, auctionGroupMap)
      )
    );
    dispatch({
      type: 'ADD_AUCTION_LIST',
      payload: {
        auctionGroupMap,
      },
    });
  }, [minTrigger, txModal]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    dispatchCurrentAuctionStatus();
  }, [sdk]); // eslint-disable-line

  useEffect(() => {
    dispatchCurrentDisplayAuctionStatus();
  }, [dispatchCurrentDisplayAuctionStatus]); // eslint-disable-line

  useEffect(() => {
    dispatchAuctionList();
  }, [dispatchAuctionList]); // eslint-disable-line

  useEffect(() => {
    if (txModal) {
      return;
    }
    dispatchReloadAuctionList();
  }, [dispatchReloadAuctionList]); // eslint-disable-line

  return (
    <AuctionContext.Provider
      value={{
        auctionGroupMap,
      }}
    >
      {children}
    </AuctionContext.Provider>
  );
};
