import React, { useState } from 'react';
import { FormControl } from 'baseui/form-control';
import { IDOLBrand, LienBrand, LiquidBrand } from 'resource/brands';
import {
  PoolSymbol,
  ExchangeData,
  rateString,
  LiquidBond,
  defaultLienExchangeData,
} from 'types';
import SubItem from 'view/components/presentations/SubItem';
import ItemNumber from 'view/components/presentations/ItemNumber';
import { GlobalState } from 'reducer/store';
import {
  SelectablePoolSymbol,
  SymbolListBox,
} from 'view/components/presentations/SymbolListBox';
import CONTRACT_ADDR from 'libs/src/helper/Constant';
import { BigNumber as BN } from 'bignumber.js';
import { useSelector } from 'react-redux';

interface LiquidityUpperProps {
  poolSymbol: PoolSymbol | undefined;
  onSelectPoolSymbol: (poolSymbol: PoolSymbol) => void;
}

export default function (props: LiquidityUpperProps) {
  const state = useSelector((state: GlobalState) => state);
  const lienFairSwap: ExchangeData =
    state.base.exchangeDataMap.get(CONTRACT_ADDR.LIEN_TOKEN) ||
    defaultLienExchangeData();

  const lienSymbol: PoolSymbol = {
    ...lienFairSwap,
    description: 'LIEN', // TODO: add pool info etc.
    balance: state.baseMy.lienBalance,
    buyInfo: {
      fromBrand: IDOLBrand,
      toBrand: LienBrand,
      rate: lienFairSwap.buyPrice,
      pair: `${LienBrand.unit}/${IDOLBrand.unit}`,
    },
    sellInfo: {
      fromBrand: LienBrand,
      toBrand: IDOLBrand,
      rate: lienFairSwap.sellPrice,
      pair: `${LienBrand.unit}/${IDOLBrand.unit}`,
    },
  };

  const bondSymbols: PoolSymbol[] = state.baseMy.liquidBalanceList
    .filter((liquid: { bondId: string; balance: BN }) => {
      const bond = state.base.bondMap.get(liquid.bondId) as LiquidBond;
      const exchangeData = state.base.exchangeDataMap.get(
        bond.bondAddress
      ) as ExchangeData;
      return (
        !liquid.balance.isZero() &&
        bond !== undefined &&
        exchangeData !== undefined
      );
    })
    .map((liquid: { bondId: string; balance: BN }) => {
      // TODO: add balance to PoolSymbol
      const bond = state.base.bondMap.get(liquid.bondId) as LiquidBond;
      const bondAddress = bond.bondAddress;
      const exchangeData = state.base.exchangeDataMap.get(
        bondAddress
      ) as ExchangeData;
      const strMaturity = bond.maturity.toLocaleDateString();
      const tmp = state.baseMy.liquidBalanceList.find(
        (liquid: { bondId: string; balance: BN }) =>
          liquid.bondId === exchangeData.bondId
      );
      const balance = tmp ? tmp.balance : new BN(0);
      return {
        ...exchangeData,
        description: `Strike Price: $${bond.strikePrice} Maturity: ${strMaturity}`,
        balance,
        buyInfo: {
          fromBrand: IDOLBrand,
          toBrand: LiquidBrand,
          rate: exchangeData.buyPrice,
          pair: `${LiquidBrand.unit}/${IDOLBrand.unit}`,
        },
        sellInfo: {
          fromBrand: LiquidBrand,
          toBrand: IDOLBrand,
          rate: exchangeData.sellPrice,
          pair: `${LiquidBrand.unit}/${IDOLBrand.unit}`,
        },
        afterMaturity: bond.maturity.getTime() < Date.now(),
      };
    });

  const poolSymbolMap: Map<string, PoolSymbol> = new Map();
  poolSymbolMap.set(lienSymbol.settlementAddress, lienSymbol);
  bondSymbols.forEach((bond) => {
    poolSymbolMap.set(bond.settlementAddress, bond);
  });

  const selectablePoolSymbols: SelectablePoolSymbol[] = [
    {
      id: lienSymbol.settlementAddress,
      description: lienSymbol.description,
      group: 'ungrouped',
      brand: lienSymbol.buyInfo.toBrand,
    },
    ...bondSymbols.map(
      (bond: PoolSymbol): SelectablePoolSymbol => {
        return {
          id: bond.settlementAddress,
          description: bond.description,
          group: 'LBT',
          brand: bond.buyInfo.toBrand,
        };
      }
    ),
  ];

  const [selectedSymbol, selectSymbol] = useState<string>(
    lienSymbol.settlementAddress
  );

  return (
    <div className="content_box_3">
      <div className="box">
        <SubItem name="Select Pool" />
        <FormControl
          caption={() => {
            if (
              selectedSymbol === CONTRACT_ADDR.LIEN_TOKEN ||
              selectedSymbol === 'ETH'
            )
              return '';
            const exchangeData = state.base.exchangeDataMap.get(
              selectedSymbol
            ) as ExchangeData;
            const bond = state.base.bondMap.get(
              exchangeData.bondId === undefined ? '' : exchangeData.bondId
            ) as LiquidBond;
            return bond.maturity.getTime() < Date.now()
              ? 'expired: if you remove liquidity will get ETH + iDOL'
              : '';
          }}
        >
          <SymbolListBox
            symbols={selectablePoolSymbols}
            onChange={(value: SelectablePoolSymbol[]) => {
              const selected = poolSymbolMap.get(value[0].id) || lienSymbol;
              props.onSelectPoolSymbol(selected);
              selectSymbol(value[0].id);
            }}
          />
        </FormControl>
        <br />
        <SubItem name="Pool info" />
        {props.poolSymbol && (
          <ItemNumber
            name={'Exchange Rate'}
            value={`${props.poolSymbol.buyInfo.pair} = ${rateString(
              props.poolSymbol.buyInfo
            )}`}
          />
        )}
        {props.poolSymbol && (
          <ItemNumber
            name={`${props.poolSymbol?.buyInfo.fromBrand.unit} in FairSwap Pool`}
            value={
              props.poolSymbol.baseTokenPool
                ? props.poolSymbol.baseTokenPool.dp(2).toString()
                : ''
            }
            brand={props.poolSymbol?.buyInfo.fromBrand}
          />
        )}
        {props.poolSymbol && (
          <ItemNumber
            name={`${props.poolSymbol?.buyInfo.toBrand.unit} in FairSwap Pool`}
            value={
              props.poolSymbol.settlementTokenPool
                ? props.poolSymbol.settlementTokenPool.dp(2).toString()
                : ''
            }
            brand={props.poolSymbol?.buyInfo.toBrand}
          />
        )}
      </div>
    </div>
  );
}
