import Contracts from 'libs/src/helper/Contract';
import ERC20helper from 'libs/src/balance/ERC20Helper';
import BigNumber from 'bignumber.js';

export default class StableCoin {
  private contracts: Contracts;
  private erc20Helper: ERC20helper;

  constructor(contracts: Contracts) {
    this.contracts = contracts;
    this.erc20Helper = new ERC20helper(contracts);
  }

  // ===== Methods.sendTx =====

  public async mint(
    bondIDData: string,
    lockAmountData: BigNumber,
    bondAddr: string
  ) {
    const lockAmount = await this.erc20Helper.convertAmount(
      bondAddr,
      lockAmountData
    );
    const funcData = this.contracts.stableCoin.methods.mint(
      bondIDData,
      lockAmount
    );
    return this.contracts.sendTx(funcData);
  }

  public async unlockSBT(
    bondIDData: string,
    burnAmountData: BigNumber,
    bondAddr: string
  ) {
    const burnAmount = await this.erc20Helper.convertAmount(
      bondAddr,
      burnAmountData
    );
    await this.erc20Helper.approve(
      bondAddr,
      this.contracts.stableCoinAddr,
      burnAmountData
    );
    const funcData = this.contracts.stableCoin.methods.unlockSBT(
      bondIDData,
      burnAmount
    );
    return this.contracts.sendTx(funcData);
  }

  public startAuctionOnMaturity(bondIDData: string) {
    const funcData = this.contracts.stableCoin.methods.startAuctionOnMaturity(
      bondIDData
    );
    return this.contracts.sendTx(funcData);
  }

  public startAuctionByMarket(bondIDData: string) {
    const funcData = this.contracts.stableCoin.methods.startAuctionByMarket(
      bondIDData
    );
    return this.contracts.sendTx(funcData);
  }

  public setSettledAverageAuctionPrice(bondIDData: string, price: string) {
    const funcData = this.contracts.stableCoin.methods.setSettledAverageAuctionPrice(
      bondIDData,
      price
    );
    return this.contracts.sendTx(funcData);
  }

  public returnLockedPool(poolIDs: string[]) {
    const funcData = this.contracts.stableCoin.methods.returnLockedPool(
      poolIDs
    );
    return this.contracts.sendTx(funcData);
  }

  // ===== Methods.call =====

  public async calcSBT2IDOL(solidBondAmount: BigNumber) {
    const convertedSolidBondAmount = this.erc20Helper.convertAmount12(
      solidBondAmount
    );
    const funcData = this.contracts.stableCoin.methods.calcSBT2IDOL(
      convertedSolidBondAmount
    );
    const ret = await funcData?.call();
    return await this.erc20Helper.fromAmount8(ret);
  }

  public async isAcceptableSBT(bondID: string) {
    const funcData = this.contracts.stableCoin.methods.isAcceptableSBT(bondID);
    const ret = await funcData.call().catch(() => {
      return false;
    });
    return ret;
  }

  public async isDangerSolidBond(
    rateETH2USD: string,
    solidBondStrikePrice: string,
    volatility: string,
    periodSecBeforeMaturity: string
  ) {
    const funcData = this.contracts.stableCoin.methods.isDangerSolidBond(
      rateETH2USD,
      solidBondStrikePrice,
      volatility,
      periodSecBeforeMaturity
    );
    return await funcData?.call();
  }

  public async generatePoolID(bondID: string, counter: number) {
    const funcData = this.contracts.stableCoin.methods.generatePoolID(
      bondID,
      counter
    );
    return await funcData?.call();
  }

  public async getCurrentPoolID(bondID: string) {
    const funcData = this.contracts.stableCoin.methods.getCurrentPoolID(bondID);
    return await funcData?.call();
  }

  public async getLockedPool(user: string, poolID: string) {
    const funcData = this.contracts.stableCoin.methods.getLockedPool(
      user,
      poolID
    );
    const retData = await funcData?.call();
    const idol = await this.erc20Helper.fromAmount8(retData[0]);
    const sbt = await this.erc20Helper.fromAmount8(retData[1]);
    return [idol, sbt];
  }

  public async getReturnLockedPool(poolID: string) {
    const funcData = this.contracts.stableCoin.methods.returnLockedPool([
      poolID,
    ]);
    try {
      const ret = await funcData?.call();
      const idol = ret
        ? await this.erc20Helper.fromAmount8(ret)
        : new BigNumber(0);
      return idol;
    } catch (e) {
      return new BigNumber(0);
    }
  }
}
