import Contracts from '../helper/Contract';
import ERC20helper from 'libs/src/balance/ERC20Helper';
import { Web3Utils } from '../../utils/web3Utils';
import { WrapperInstance } from 'libs/types/Wrapper';
import BigNumber from 'bignumber.js';

type WrapperMethodParameters<
  T extends keyof WrapperInstance['methods']
> = Parameters<WrapperInstance['methods'][T]>;

export default class Wrapper {
  private contracts: Contracts;
  private web3Utils: Web3Utils;
  private erc20Helper: ERC20helper;

  constructor(contracts: Contracts) {
    this.contracts = contracts;
    this.web3Utils = new Web3Utils();
    this.erc20Helper = new ERC20helper(contracts);
  }

  // ======= Methods.sendTx ========

  public async issueIDOLOnly(
    bondGroupIdData: number,
    ethValueData: string,
    timeout: string,
    isLimit: boolean
  ) {
    const feeIncludedValue = this.web3Utils.toWei(
      this.web3Utils
        .feeIncludedETH(new BigNumber(ethValueData.toString()))
        .toString()
    );
    const funcData = this.contracts.wrapper?.methods.issueIDOLOnly(
      bondGroupIdData,
      timeout,
      isLimit
    );
    return this.contracts.sendTxValue(funcData, feeIncludedValue);
  }

  public async issueLBTOnly(
    bondGroupIdData: number,
    ethValueData: string,
    liquidBondID: string,
    timeout: string,
    isLimit: boolean
  ) {
    const feeIncludedValue = this.web3Utils.toWei(
      this.web3Utils
        .feeIncludedETH(new BigNumber(ethValueData.toString()))
        .toString()
    );
    const funcData = this.contracts.wrapper?.methods.issueLBTOnly(
      bondGroupIdData,
      liquidBondID,
      timeout,
      isLimit
    );
    return this.contracts.sendTxValue(funcData, feeIncludedValue);
  }

  public async issueLBTAndIDOL(bondGroupIdData: number, ethValueData: string) {
    const feeIncludedValue = this.web3Utils.toWei(
      this.web3Utils
        .feeIncludedETH(new BigNumber(ethValueData.toString()))
        .toString()
    );
    const funcData = this.contracts.wrapper?.methods.issueLBTAndIDOL(
      bondGroupIdData
    );
    return this.contracts.sendTxValue(funcData, feeIncludedValue);
  }

  public async returnLockedPoolUsers(
    ...params: WrapperMethodParameters<'returnLockedPoolUsers'>
  ) {
    const funcData = this.contracts.wrapper?.methods.returnLockedPoolUsers(
      ...params
    );
    return this.contracts.sendTx(funcData);
  }

  // ===== Methods.call ======

  public async viewLockedPoolAmountUser(
    ...params: WrapperMethodParameters<'viewLockedPoolAmountUser'>
  ): Promise<BigNumber> {
    const funcData = this.contracts.wrapper?.methods.viewLockedPoolAmountUser(
      ...params
    );
    const ret = await funcData?.call();
    return ret
      ? await this.erc20Helper.fromAmount(this.contracts.stableCoinAddr, ret)
      : new BigNumber(0);
  }

  public async getReturnLockedPoolUsers(
    ...params: WrapperMethodParameters<'returnLockedPoolUsers'>
  ): Promise<BigNumber> {
    try {
      const funcData = this.contracts.wrapper?.methods.returnLockedPoolUsers(
        ...params
      );
      const ret = await funcData?.call();
      // TODO: return values is crazy...
      console.log('returnReturnLockedPoolUsers:', ret, ret.toString());
      const idol = ret
        ? await this.erc20Helper.fromAmount(this.contracts.stableCoinAddr, ret)
        : new BigNumber(0);
      if (idol.isGreaterThan('1e20')) return new BigNumber(0);
      return idol;
    } catch (e) {
      return new BigNumber(0);
    }
  }
}
