import React, { useState, useEffect } from 'react';
import { GlobalState } from 'reducer/store';
import { BigNumber as BN } from 'bignumber.js';
import SubTitle from 'view/components/presentations/SubTitle';
import SubItem from 'view/components/presentations/SubItem';
import ItemNumber from 'view/components/presentations/ItemNumber';
import FormNumber from 'view/components/presentations/FormNumber';
import RadioSolid from 'view/components/presentations/RadioSolid';
import TransactionButton from 'view/components/presentations/TransactionButton';
import { BondGroup, defaultBondGroup } from 'types';
import { viewedDollar } from 'libs/utils/view';
import IssueLBTAndIDOLModal from '../modal/IssueLBTAndIDOLModal';
import IssueLBTAndSBTModal from '../modal/IssueLBTAndSBTModal';
import { Web3Utils } from 'libs/utils/web3Utils';
import { useSelector } from 'react-redux';
import { isValidBondGroup, isValidBalance } from 'libs/utils/validate';
import {
  trancheOutputDescription,
  trancheAmountDescription,
} from 'resource/description/index';

interface TrancheRightContainerProps {
  selectedBondGroupIndex: number;
}

const IDOL_SELECT = 'idol';
const SBT_SELECT = 'sbt';

function TrancheRightContainer(props: TrancheRightContainerProps) {
  const state = useSelector((state: GlobalState) => state);
  const web3Utils = new Web3Utils();
  const bondGroup: BondGroup | undefined = state.base.bondGroupMap.has(
    props.selectedBondGroupIndex
  )
    ? state.base.bondGroupMap.get(props.selectedBondGroupIndex)
    : defaultBondGroup();
  const [ethValue, setEthValue] = useState<BN>(new BN(0));

  /*state.base.bondGroupMap.has(
    props.selectedBondGroupIndex
  )
    ? state.base.bondGroupMap.get(props.selectedBondGroupIndex)
    : defaultBondGroup();*/
  // TODO: add validation for args in Transaction
  const [selected, setSelected] = useState(IDOL_SELECT);
  const [openedIssueLBTAndIDOL, setOpenedIssueLBTAndIDOL] = useState(false);
  const [openedIssueLBTAndSBT, setOpenedIssueLBTAndSBT] = useState(false);

  const txFunc: () => void = () => {
    switch (selected) {
      case IDOL_SELECT:
        setOpenedIssueLBTAndIDOL(true);
        break;
      default:
        setOpenedIssueLBTAndSBT(true);
        break;
    }
  };

  // check txDisabled
  const [txDisabled, setTxDisabled] = useState<boolean>(true);
  useEffect(() => {
    if (
      isValidBondGroup(bondGroup) &&
      isValidBalance(
        ethValue,
        web3Utils.recommendMaxETH(state.baseMy.ethBalance)
      )
    )
      setTxDisabled(false);
    else setTxDisabled(true);
  }, [web3Utils, selected, ethValue, bondGroup, state.baseMy.ethBalance]);

  return (
    <div>
      {props.selectedBondGroupIndex === 0 || bondGroup === undefined ? (
        ''
      ) : (
        <SubTitle title={`BondGroupID ${bondGroup.id}`} />
      )}
      {props.selectedBondGroupIndex !== 0 && (
        <div className="box">
          {/*<SubItem name="Selected LBT" />*/}
          <ItemNumber
            name="Strike Price"
            value={
              bondGroup === undefined
                ? '$' + viewedDollar(0)
                : '$' + viewedDollar(bondGroup.strikePrice.toNumber())
            }
          />
          <ItemNumber
            name="Maturity Date"
            value={
              bondGroup === undefined
                ? new Date(0).toLocaleDateString()
                : bondGroup.maturity.toLocaleDateString()
            }
          />
          <br />
          <SubItem
            name="Amount for Tranche"
            isHelp={true}
            description={trancheAmountDescription}
          />
          <FormNumber
            errorMessage="Insufficient ETH Balance"
            unit="ETH"
            value={ethValue}
            setValue={setEthValue}
            maxValue={web3Utils.recommendMaxETH(state.baseMy.ethBalance)}
          />
          <br />
          <SubItem
            name="Output for Stable Coin"
            isHelp={true}
            description={trancheOutputDescription}
          />
          <RadioSolid
            selected={selected}
            setIDOL={(e) => {
              setSelected(IDOL_SELECT);
            }}
            setSBT={(e) => {
              setSelected(SBT_SELECT);
            }}
          />
        </div>
      )}
      {props.selectedBondGroupIndex !== 0 && (
        <TransactionButton
          name={
            selected === IDOL_SELECT
              ? 'Tranche to LBT and iDOL'
              : 'Tranche to LBT and SBT'
          }
          txFunc={txFunc}
          disabled={txDisabled}
        />
      )}
      {openedIssueLBTAndIDOL && (
        <IssueLBTAndIDOLModal
          isOpen={openedIssueLBTAndIDOL}
          setIsOpen={setOpenedIssueLBTAndIDOL}
          bondGroup={bondGroup === undefined ? defaultBondGroup() : bondGroup}
          inputETH={new BN(ethValue)}
        />
      )}
      {openedIssueLBTAndSBT && (
        <IssueLBTAndSBTModal
          isOpen={openedIssueLBTAndSBT}
          setIsOpen={setOpenedIssueLBTAndSBT}
          bondGroup={bondGroup === undefined ? defaultBondGroup() : bondGroup}
          inputETH={new BN(ethValue)}
        />
      )}
    </div>
  );
}

TrancheRightContainer.defaultProps = {
  selectedBondGroupIndex: 0,
};

export default TrancheRightContainer;
