import { lien } from 'actions/useLien';
import { useDispatch, useSelector } from 'react-redux';
import { GlobalState } from 'reducer/store';
import { BigNumber } from 'bignumber.js';

//TODO: merge BalanceSDK at this moment, but is raised as an issue for StableCoin

const useStableCoin = () => {
  const dispatch = useDispatch();
  const sdk = useSelector((state: GlobalState) => state.sdk);

  const mint = async (
    bondID: string,
    lockAmount: BigNumber,
    bondAddr: string
  ) => {
    try {
      if (lien.address && sdk.lien) {
        const res = await lien.stableCoin.mint(bondID, lockAmount, bondAddr);
        dispatch({
          type: 'MINT_NEW_iDOL',
          payload: res,
        });
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
      return false;
    }
    return true;
  };

  const unlockSBT = async (
    bondID: string,
    burnAmount: BigNumber,
    bondAddr: string
  ) => {
    try {
      if (lien.address && sdk.lien) {
        await lien.stableCoin.unlockSBT(bondID, burnAmount, bondAddr);
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  };

  const returnLockPool = async (bondIDs: any) => {
    try {
      if (lien.address && sdk.lien) {
        await lien.stableCoin.returnLockedPool(bondIDs);
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  };

  return {
    mint,
    unlockSBT,
    returnLockPool,
  };
};

export default useStableCoin;
