import { useDispatch, useSelector } from 'react-redux';
import { lien } from 'actions/useLien';
import { executionBoxPeriod } from 'config';
import BigNumber, { BigNumber as BN } from 'bignumber.js';
import {
  ExchangeData,
  BrandInfo,
  ExchangeInfo,
  defaultWaitingOrders,
  WaitingOrders,
} from 'types';
import CONTRACT_ADDR from 'libs/src/helper/Constant';
import { IDOLBrand, LiquidBrand, LienBrand, ETHBrand } from 'resource/brands';
import { GlobalState } from 'reducer/store';

export const useFairSwap = () => {
  const dispatch = useDispatch();
  const exchangeToLBT = useSelector(
    (state: GlobalState) => state.base.exchangeToLBT
  );
  const exchangeDataMap = useSelector(
    (state: GlobalState) => state.base.exchangeDataMap
  );
  const sdk = useSelector((state: GlobalState) => state.sdk);
  const acceptOrdersListMap = useSelector(
    (state: GlobalState) => state.event.acceptOrdersListMap
  );
  const executionListMap = useSelector(
    (state: GlobalState) => state.event.executionListMap
  );

  // helper -------------
  const exchangeAddressToSettlementAddress = (
    exchangeAddr: string
  ): string | undefined => {
    switch (exchangeAddr) {
      case CONTRACT_ADDR.LIEN_EXCHANGE:
        return CONTRACT_ADDR.LIEN_TOKEN;
      case CONTRACT_ADDR.ETH_EXCHANGE:
        return 'ETH';
      default:
        return exchangeToLBT.get(exchangeAddr);
    }
  };

  const convertExchangeInfoFromOrders = (
    exchangeAddr: string,
    isBuy: boolean
  ): ExchangeInfo => {
    const baseBrand: BrandInfo = IDOLBrand;
    let settlementBrand: BrandInfo = LiquidBrand;
    switch (exchangeAddr) {
      case CONTRACT_ADDR.LIEN_EXCHANGE:
        settlementBrand = LienBrand;
        break;
      case CONTRACT_ADDR.ETH_EXCHANGE:
        settlementBrand = ETHBrand;
    }
    const settlementAddress = exchangeAddressToSettlementAddress(exchangeAddr);
    const exchangeData = exchangeDataMap.get(
      typeof settlementAddress === 'string' ? settlementAddress : ''
    );
    if (isBuy) {
      return {
        fromBrand: baseBrand,
        toBrand: settlementBrand,
        rate: exchangeData ? exchangeData.buyPrice : new BN(0),
        pair: `${settlementBrand.unit}/${baseBrand.unit}`,
      };
    } else {
      return {
        fromBrand: settlementBrand,
        toBrand: baseBrand,
        rate: exchangeData
          ? new BN(1).dividedBy(exchangeData.sellPrice)
          : new BN(0),
        pair: `${settlementBrand.unit}/${baseBrand.unit}`,
      };
    }
  };

  const getPriceImpact = (paymentPool: BN, paymentAmount: BN): BN => {
    return new BN(new BN(1).plus(paymentAmount.div(paymentPool)))
      .pow(2)
      .minus(1)
      .times(100)
      .dp(5);
  };

  // send transaction -----------------------------
  const orderBaseToSettlement = async (
    baseTokenAmountData: BN,
    isLimit: boolean,
    exchangeAddr: string,
    latestBoxNumber: BN
  ) => {
    try {
      if (lien.address && sdk.lien) {
        const timeout = latestBoxNumber.plus(executionBoxPeriod).toString();
        const recipient = '0x0000000000000000000000000000000000000000';
        const res = await lien.fairSwap.orderBaseToSettlement(
          timeout,
          recipient,
          baseTokenAmountData,
          isLimit,
          exchangeAddr
        );

        dispatch({
          type: 'NEW_TRADE_DATA',
          payload: res,
        });
        return true;
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
    return false;
  };

  const orderSettlementToBase = async (
    baseTokenAmountData: BN,
    isLimit: boolean,
    tokenAddr: string,
    exchangeAddr: string,
    latestBoxNumber: BN
  ) => {
    try {
      if (lien.address && sdk.lien) {
        const timeout = latestBoxNumber.plus(executionBoxPeriod).toString();
        const recipient = '0x0000000000000000000000000000000000000000';
        const res = await lien.fairSwap.orderSettlementToBase(
          timeout,
          recipient,
          baseTokenAmountData,
          isLimit,
          tokenAddr,
          exchangeAddr
        );

        dispatch({
          type: 'NEW_TRADE_DATA',
          payload: res,
        });
        return true;
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
    return false;
  };

  const addLiquidity = async (
    baseTokenAmountData: BN,
    baseTokenAddr: string,
    exchangeData: ExchangeData
  ) => {
    try {
      if (lien.address && sdk.lien) {
        const timeout = exchangeData.latestBoxNumber
          .plus(executionBoxPeriod)
          .toString();

        const baseTokenPerShare = exchangeData.baseTokenPerShare;
        const res = await lien.fairSwap.addLiquidity(
          timeout,
          baseTokenAmountData,
          baseTokenPerShare,
          baseTokenAddr,
          exchangeData.exchangeAddress
        );

        dispatch({
          type: 'ADD_LIQUIDITY',
          payload: res,
        });
        return true;
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
    return false;
  };

  const removeLiquidity = async (
    shareTokensAmount: BN,
    exchangeData: ExchangeData
  ) => {
    try {
      if (lien.address && sdk.lien) {
        const timeout = exchangeData.latestBoxNumber
          .plus(executionBoxPeriod)
          .toString();

        const baseTokenPerShare = exchangeData.baseTokenPerShare;
        const settlementTokenPerShare = exchangeData.settlementTokenPerShare;
        const res = await lien.fairSwap.removeLiquidity(
          timeout,
          shareTokensAmount,
          baseTokenPerShare,
          settlementTokenPerShare,
          exchangeData.exchangeAddress
        );

        dispatch({
          type: 'REMOVE_LIQUIDITY',
          payload: res,
        });
        return true;
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
    return false;
  };

  const executeUnexecutedBox = async (
    maxOrderNum: number,
    exchangeAddr: string
  ) => {
    try {
      if (lien.address && sdk.lien) {
        const res = await lien.fairSwap.executeUnexecutedBox(
          maxOrderNum,
          exchangeAddr
        );
        dispatch({
          type: 'EXECUTE_UNEXECUTED_BOX',
          payload: res,
        });
        return true;
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
    return false;
  };

  const removeAfterMaturity = async (
    exchangeAddr: string
  ): Promise<boolean> => {
    try {
      if (lien.address && sdk.lien) {
        const res = await lien.fairSwap.removeAfterMaturity(exchangeAddr);
        dispatch({
          type: 'REMOVE_AFTER_MATURITY',
          payload: res,
        });
        return true;
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
    return false;
  };

  // call -------------------------
  const getWhenToExecute = async (
    boxNumber: number,
    isBuy: boolean,
    isLimit: boolean,
    exchangeAddr: string
  ): Promise<{ isExecuted: boolean; boxCount: number; orderCount: number }> => {
    try {
      const res = await lien.fairSwap.whenToExecute(
        lien.address,
        boxNumber,
        isBuy,
        isLimit,
        exchangeAddr
      );
      return {
        isExecuted: res[0],
        boxCount: res[1],
        orderCount: res[2],
      };
    } catch {
      return {
        isExecuted: false,
        boxCount: -1,
        orderCount: -1,
      };
    }
  };

  const getLatestMyOrders = async (
    exchangeAddr: string
  ): Promise<WaitingOrders> => {
    const acceptOrderList = acceptOrdersListMap.get(exchangeAddr);
    // Get latest accept orders.
    const acceptOrder = acceptOrderList
      ? acceptOrderList.length > 0
        ? acceptOrderList[acceptOrderList.length - 1]
        : undefined
      : undefined;
    const settlementAddress = exchangeAddressToSettlementAddress(exchangeAddr);
    if (acceptOrder && settlementAddress) {
      const result = await getWhenToExecute(
        acceptOrder.returnValues.boxId as number,
        acceptOrder.returnValues.isBuy as boolean,
        acceptOrder.returnValues.isLimit as boolean,
        exchangeAddr
      );
      return {
        ...result,
        isBuy: acceptOrder.returnValues.isBuy,
        isLimit: acceptOrder.returnValues.isLimit,
        boxId: acceptOrder.returnValues.boxId,
        exchangeAddress: exchangeAddr,
        tokenIn: await lien.ERC20.fromAmountBuyOrSell(
          settlementAddress,
          acceptOrder.returnValues.tokenIn,
          acceptOrder.returnValues.isBuy
        ),
      } as WaitingOrders;
    }
    return defaultWaitingOrders();
  };

  const getLatestMyExecution = async (
    exchangeAddr: string
  ): Promise<{
    isBuy: boolean;
    recipient: string;
    orderAmount: BigNumber;
    refundAmount: BigNumber;
    outAmount: BigNumber;
  }> => {
    const executionList = executionListMap.get(exchangeAddr);
    if (!executionList) {
      return {
        isBuy: true,
        recipient: '',
        orderAmount: new BN(-1),
        refundAmount: new BN(-1),
        outAmount: new BN(-1),
      };
    }

    // Get latest accept orders.
    const execution = executionList
      ? executionList[executionList.length - 1]
      : undefined;
    const settlementAddress = exchangeAddressToSettlementAddress(exchangeAddr);
    if (execution && settlementAddress) {
      const isBuy = execution.returnValues.isBuy;
      return {
        isBuy: execution.returnValues.isBuy,
        recipient: execution.returnValues.recipient,
        orderAmount: await lien.ERC20.fromAmountBuyOrSell(
          settlementAddress,
          execution.returnValues.orderAmount,
          isBuy
        ),
        refundAmount: await lien.ERC20.fromAmountBuyOrSell(
          settlementAddress,
          execution.returnValues.refundAmount,
          isBuy
        ),
        outAmount: await lien.ERC20.fromAmountBuyOrSell(
          settlementAddress,
          execution.returnValues.outAmount,
          isBuy,
          true
        ),
      };
    }
    return {
      isBuy: true,
      recipient: '',
      orderAmount: new BN(-1),
      refundAmount: new BN(-1),
      outAmount: new BN(-1),
    };
  };

  return {
    getPriceImpact,
    orderBaseToSettlement,
    orderSettlementToBase,
    addLiquidity,
    removeLiquidity,
    executeUnexecutedBox,
    getWhenToExecute,
    getLatestMyOrders,
    getLatestMyExecution,
    exchangeAddressToSettlementAddress,
    convertExchangeInfoFromOrders,
    removeAfterMaturity,
  };
};
