import React from 'react';
import './ItemPrice.scss';
import './WithIcon.scss';
import { BrandInfo } from 'types';

interface ItemPriceProps {
  name: string;
  value: string;
  feeIncluded?: string;
  fee?: string;
  brand: BrandInfo;
}

export default (props: ItemPriceProps) => {
  return (
    <div className="price">
      <span>{props.name}</span>
      <div className={props.brand.className}>
        {props.feeIncluded && (
          <span>
            {props.brand.view(props.value) +
              '(feeIncluded ' +
              props.brand.view(props.feeIncluded) +
              ')'}
          </span>
        )}
        {props.fee && (
          <span>
            {props.brand.view(props.value) +
              '(+ fee ' +
              props.brand.view(props.fee) +
              ')'}
          </span>
        )}
        {!props.feeIncluded && !props.fee && (
          <span>{props.brand.view(props.value)}</span>
        )}
      </div>
    </div>
  );
};
