export const auctionReducer = (
  state: any,
  action: { type: string; payload: any }
) => {
  switch (action.type) {
    case 'ADD_AUCTION_LIST': {
      return {
        ...state,
        loading: false,
        auctionGroupMap: action.payload.auctionGroupMap,
      };
    }
    case 'SET_AUCTION_LOADING': {
      return {
        ...state,
        loading: action.payload,
      };
    }
    case 'BOARD_STATUS': {
      const { auctionID, boardStatus } = action.payload as {
        auctionID: string;
        boardStatus: number[];
        timestamp: number;
      };
      return {
        ...state,
        boardStatus: (state.boardStatus as Map<string, number[]>).set(
          auctionID,
          boardStatus
        ),
      };
    }
    case 'SORTED_BID_PRICE': {
      const { auctionID, sortedBidPrice } = action.payload as {
        auctionID: string;
        sortedBidPrice: number[];
        timestamp: number;
      };
      return {
        ...state,
        sortedBidPrice: (state.sortedBidPrice as Map<string, number[]>).set(
          auctionID,
          sortedBidPrice
        ),
      };
    }
    case 'ADD_AUCTION_BID':
      return {
        ...state,
        auctionBid: action.payload,
      };
    case 'INC_AUCTION_PAGE':
      return {
        ...state,
        auctionPage: state.auctionPage + 1,
      };
    case 'DEC_AUCTION_PAGE': {
      let newAuctionPage = state.auctionPage - 1;
      if (newAuctionPage < 0) {
        newAuctionPage = 0;
      }
      return {
        ...state,
        auctionPage: newAuctionPage,
      };
    }
    case 'SET_AUCTION_PAGE': {
      const auctionGroupMap = new Map();
      return {
        ...state,
        auctionGroupMap,
        auctionPage: action.payload,
      };
    }
    case 'SET_AUCTION_MAX_PAGE':
      return {
        ...state,
        auctionMaxPage: action.payload,
      };
    case 'SET_CURRENT_AUCTION_STATUS': {
      const loading = action.payload.currentAuctionStatus.size > 0;
      return {
        ...state,
        loading: loading,
        currentAuctionStatus: action.payload.currentAuctionStatus,
        currentDisplayAuctionStatus: action.payload.currentAuctionStatus,
      };
    }
    case 'SET_CURRENT_DISPLAY_AUCTION_STATUS': {
      const loading = action.payload.currentDisplayAuctionStatus.size > 0;
      return {
        ...state,
        loading: loading,
        currentDisplayAuctionStatus: action.payload.currentDisplayAuctionStatus,
      };
    }
    case 'SET_DISPLAY_PERIOD': {
      const newPeriod = action.payload;
      if (state.displayPeriod === newPeriod) {
        return state;
      }
      return {
        ...state,
        loading: true,
        auctionGroupMap: new Map(),
        displayPeriod: action.payload,
      };
    }
    case 'SET_AUCTION_MODAL': {
      return {
        ...state,
        txModal: action.payload,
      };
    }
    case 'SET_MAR_DONE': {
      const auctionID = action.payload;
      const auctionGroupMap = state.auctionGroupMap;
      const auction = auctionGroupMap.get(auctionID);
      if (!auction) {
        return {
          ...state,
        };
      }

      auction.mARDone = true;
      auctionGroupMap.set(auctionID, auction);
      return {
        ...state,
        auctionGroupMap: auctionGroupMap,
      };
    }
    case 'CLEAR_AUCTION':
      return {
        ...state,
        loading: false,
        auctionGroupMap: new Map(),
      };
    default:
      return state;
  }
};
