import React from 'react';
import { Spinner } from 'baseui/icon';
import IconWrapper from 'view/components/presentations/IconWrapper';
import { HelpCircle } from 'react-feather';

interface SubTitleProps {
  title: string;
  spinner?: boolean;
  isHelp?: boolean;
  description?: string[];
}

function SubTitle(props: SubTitleProps) {
  return (
    <div className="title">
      {props.spinner && (
        <span>
          <Spinner size="30px" color="000000" />
        </span>
      )}
      {props.isHelp ? (
        <IconWrapper
          component={<span>{props.title}</span>}
          icon={<HelpCircle size={14} color="#666" />}
          isDescription={props.isHelp}
          description={props.description || []}
          isPadded={false}
        />
      ) : (
        <span>{props.title}</span>
      )}
    </div>
  );
}

export default SubTitle;
