import React from 'react';
import { BarChart, XAxis, YAxis, Bar, Legend, CartesianGrid } from 'recharts';
import { color } from 'config';
import BigNumber from 'bignumber.js';

type AuctionBidBoardProps = {
  data: {
    price: BigNumber;
    win: BigNumber;
    lose: BigNumber;
    user: BigNumber;
  }[];
  height: number;
  width: number;
};

const defaultProps = {
  height: 300,
  width: 300,
};

type GraphProps = {
  price: number;
  win: number;
  lose: number;
  user: number;
}[];

function AuctionBidBoard({ data, height, width }: AuctionBidBoardProps) {
  const barSize = (window.innerWidth * 1440) / 26;

  const graphData: GraphProps = data.map((elm) => {
    return {
      price: elm.price.toNumber(),
      win: elm.win.toNumber(),
      lose: elm.lose.toNumber(),
      user: elm.user.toNumber(),
    };
  });

  return (
    <div className="auction_bid_board">
      <BarChart
        data={graphData}
        width={width}
        height={height}
        margin={{
          right: window.innerWidth / 4,
          left: 0,
        }}
      >
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis
          hide={false}
          dataKey="price"
          label={{
            value: 'iDOL',
            position: 'insideBottomRight',
            offset: 0,
            fontSize: 8,
          }}
          tick={{ fontSize: 8 }}
          ticks={graphData.map((elm) => elm.price)}
          tickCount={10}
          padding={{ left: 0, right: 0 }}
          interval={0.0}
          tickLine={false}
        />
        <YAxis
          label={{
            value: 'SBT',
            angle: -90,
            position: 'insideLeft',
            fontSize: 10,
          }}
          width={36}
          tick={{ fontSize: 10 }}
        />
        <Legend />
        <Bar dataKey="user" stackId="a" barSize={barSize} fill={color.black} />
        <Bar dataKey="lose" stackId="a" barSize={barSize} fill={color.blue} />
        <Bar dataKey="win" stackId="a" barSize={barSize} fill={color.red} />
      </BarChart>
    </div>
  );
}

AuctionBidBoard.defaultProps = defaultProps;

export default AuctionBidBoard;
