import { TxTriggerState } from './store';

export const txTriggerReducer = (
  state: TxTriggerState,
  action: { type: string; payload: any }
) => {
  switch (action.type) {
    case 'ISSUE_NEW_BONDS':
      return {
        ...state,
        issueNewBonds: action.payload,
      };
    case 'LIQUIDATE_BOND':
      return {
        ...state,
        liquidateBond: action.payload,
      };
    case 'ISSUE_IDOL_ONLY':
      return {
        ...state,
        issueIDOLOnly: action.payload,
      };
    case 'ISSUE_LBT_ONLY':
      return {
        ...state,
        issueLBTOnly: action.payload,
      };
    case 'ISSUE_LBT_AND_IDOL':
      return {
        ...state,
        issueLBTAndIDOL: action.payload,
      };
    case 'BURN_ALL':
      return {
        ...state,
        burnAll: action.payload,
      };
    case 'RETURN_LOCKED_POOL':
      return {
        ...state,
        returnLockedPool: action.payload,
      };
    case 'NEW_TRADE_DATA':
      return {
        ...state,
        newTradeData: action.payload,
      };
    case 'MINT_APPROVE_iDOL':
      return {
        ...state,
        mintApproveData: action.payload,
      };
    case 'MINT_NEW_iDOL':
      return {
        ...state,
        mintNewIDOL: action.payload,
      };
    case 'EXECUTE_UNEXECUTED_BOX':
      return {
        ...state,
        executeUnexecutedBox: action.payload,
      };
    case 'APPROVE_ERC20':
      return {
        ...state,
        approveERC20: action.payload,
      };
    case 'ADD_LIQUIDITY':
      return {
        ...state,
        addLiquidity: action.payload,
      };
    case 'REMOVE_LIQUIDITY':
      return {
        ...state,
        removeLiquidity: action.payload,
      };
    case 'REMOVE_AFTER_MATURITY':
      return {
        ...state,
        removeAfterMaturity: action.payload,
      };
    case 'SEND_TX_ERROR':
      return {
        ...state,
        sendTxError: action.payload,
      };
    default:
      return state;
  }
};
