import Contracts from '../helper/Contract';
import { Contract } from 'web3-eth-contract';
import BigNumber from 'bignumber.js';

export default class BondToken {
  private contracts: Contracts;
  private bondTokenInstances: Map<string, Contract>;

  constructor(conracts: Contracts) {
    this.contracts = conracts;
    this.bondTokenInstances = new Map();
  }

  public getBondTokenInstance(tokenAddr: string) {
    if (this.bondTokenInstances.has(tokenAddr)) {
      return this.bondTokenInstances.get(tokenAddr);
    }
    const bondTokenInstance = this.contracts.bondToken.clone();
    bondTokenInstance.options.address = tokenAddr;
    this.bondTokenInstances.set(tokenAddr, bondTokenInstance);
    return bondTokenInstance;
  }

  public async burnAll(tokenAddr: string) {
    const instance = this.getBondTokenInstance(tokenAddr);
    const funcData = instance.methods.burnAll();
    return this.contracts.sendTx(funcData);
  }

  public async getRate(tokenAddr: string) {
    const instance = this.getBondTokenInstance(tokenAddr);
    const retData = await instance.methods.getRate().call();
    return retData[1] === '0'
      ? new BigNumber(0)
      : new BigNumber(retData[0]).dividedBy(new BigNumber(retData[1]));
  }
}
