import React, { createContext, useCallback, useEffect } from 'react';
import { GlobalState } from 'reducer/store';
import { MINUTE } from 'resource/constants';
import { useDispatch, useSelector } from 'react-redux';

export const TimeTriggerContext: any = createContext('');

export const TimeTriggerProvider: React.FC = ({ children }): JSX.Element => {
  const dispatch = useDispatch();
  const minTrigger = useSelector(
    (state: GlobalState) => state.timeTrigger.minTrigger
  );

  const dispatchUpdateLocalTimestamp = useCallback(() => {
    setTimeout(() => {
      dispatch({
        type: 'UPDATE_MIN_TRIGGER',
        payload: new Date(),
      });
    }, MINUTE);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [minTrigger]);

  useEffect(() => {
    dispatchUpdateLocalTimestamp();
  }, [dispatchUpdateLocalTimestamp]);

  return (
    <TimeTriggerContext.Provider value={{ minTrigger }}>
      {children}
    </TimeTriggerContext.Provider>
  );
};
