import React, { useState, useEffect, useCallback } from 'react';
import { useSelector } from 'react-redux';
import AuctionBidBoard from 'view/components/presentations/AuctionBidBoard';
import { BigNumber, BigNumber as BN } from 'bignumber.js';
import { mocked, auctionBidBoardMock } from 'mock';
import { GlobalState } from 'reducer/store';
import { getUserBids } from 'libs/utils/utils';

type AuctionBidBoardContainerProps = {
  auctionID: string;
  strikePrice: string;
};

type AuctionBidBoard = {
  price: BigNumber;
  win: BigNumber;
  lose: BigNumber;
  user: BigNumber;
};

function AuctionBidBoardContainer({
  auctionID,
  strikePrice,
}: AuctionBidBoardContainerProps) {
  const [auctionBidBoard, setAuctionBidBoard] = useState<AuctionBidBoard[]>([]);
  const auctionGroupMap = useSelector(
    (state: GlobalState) => state.auction.auctionGroupMap
  );

  const makeBidBoardData = useCallback(() => {
    const auction = auctionGroupMap.get(auctionID);
    if (!auction) {
      return null;
    }
    const result = auction.result;
    const board = auction.board;
    if (!result || !board) {
      return null;
    }

    const bidList = getUserBids(auctionID);
    const userBids: {
      [price: string]: BigNumber;
    } = {};
    for (let i = 0; i < bidList.length; i++) {
      const { price, amount } = bidList[i];
      if (price in userBids) {
        userBids[price] = userBids[price].plus(new BN(amount));
      } else {
        userBids[price] = new BN(amount);
      }
    }

    const sortBidPrice = board.sorted as BN[];
    const boardStatus = board.amounts as BN[];
    const bidData = sortBidPrice.map((price, priceIndex) => {
      let [win, lose] = [new BN(0), new BN(0)];
      const endPrice = result.end.price;
      const user = userBids[boardStatus[priceIndex].toString()] || new BN(0);
      const amount = new BN(boardStatus[priceIndex]);
      switch (true) {
        case price.gt(endPrice):
          win = amount;
          break;
        case price.eq(endPrice):
          win = result.end.auctionEndPriceWinnerSBTAmount;
          lose = amount.minus(win);
          break;
        default:
          lose = amount.minus(user);
          break;
      }
      return {
        price,
        win,
        lose,
        user,
      };
    });

    const data: AuctionBidBoard[] = [];
    for (let i = 0; i < 10; i++) {
      const price = new BN((parseInt(strikePrice) * (9 + 0.1 * (i + 1))) / 10);
      data.push({
        price,
        win: new BN(0),
        lose: new BN(0),
        user: new BN(0),
      });
    }

    bidData.forEach((bid: AuctionBidBoard) => {
      for (let i = 0; i < 10; i++) {
        if (bid.price > data[i].price) {
          continue;
        }
        const newData = {
          price: data[i].price,
          win: data[i].win.plus(bid.win),
          lose: data[i].lose.plus(bid.lose),
          user: data[i].user.plus(bid.user),
        };
        data[i] = newData;
        break;
      }
    });

    return data;
  }, []); // eslint-disable-line

  useEffect(() => {
    const newState = makeBidBoardData();
    if (!newState) {
      return;
    }
    setAuctionBidBoard(newState);
  }, []); // eslint-disable-line

  const graphRatio = 0.26;
  return (
    <>
      <AuctionBidBoard
        data={mocked ? auctionBidBoardMock : auctionBidBoard}
        height={window.innerHeight * graphRatio}
        width={(window.innerWidth * graphRatio * 16) / 9}
      />
    </>
  );
}

export default AuctionBidBoardContainer;
