import React from 'react';
import { ListItem } from 'baseui/list';
import ItemBalance from 'view/components/presentations/ItemBalance';
import { useSelector } from 'react-redux';
import { Label3 } from 'baseui/typography';
import { ETHBrand, IDOLBrand } from 'resource/brands';
import { GlobalState } from 'reducer/store';
import { PriceInfo } from 'types';
import MarketStatus from '../presentations/MarketStatus';

function InfoContainer() {
  const iDOLBalance = useSelector(
    (state: GlobalState) => state.baseMy.iDOLBalance
  );

  const ethBalance = useSelector(
    (state: GlobalState) => state.baseMy.ethBalance
  );
  const oracleVolatility = useSelector(
    (state: GlobalState) => state.base.oracleVolatility
  );
  const oracleEthPrice = useSelector(
    (state: GlobalState) => state.base.oracleEthPrice
  );
  const idolPrice = {
    brand: IDOLBrand,
    value: iDOLBalance.toString(),
  };
  const ethPrice = {
    brand: ETHBrand,
    value: ethBalance.toString(),
  } as PriceInfo;
  return (
    <div className="box">
      <ListItem sublist>
        <Label3>Wallet Balance</Label3>
      </ListItem>
      <ListItem sublist>
        <ItemBalance price={idolPrice} />
      </ListItem>
      <ListItem sublist>
        <ItemBalance price={ethPrice} />
      </ListItem>
      <br />
      <MarketStatus
        oracleEthPrice={oracleEthPrice}
        oracleVolatility={oracleVolatility}
      />
      <br />
    </div>
  );
}

export default InfoContainer;
