import React from 'react';
import SubTitle from '../SubTitle';
import PriceBox from '../PriceBox';
import SubItem from '../SubItem';
import { PriceInfo, ExchangeRateInfo } from 'types';
import { IDOLBrand, PooledIDOLBrand } from 'resource/brands';
import ItemPrice from '../ItemPrice';
interface MintProps {
  price: PriceInfo;
  getIDOL: string;
  getPooledIDOL: string;
  exchangeRate?: ExchangeRateInfo;
}

export default (props: MintProps) => {
  return (
    <div className="content_box_modal">
      <SubTitle title="Minting stable coin from SBT" />
      <div className="box">
        <SubItem name="You will mint iDOL from" />
        <PriceBox price={props.price} exchangeRate={props.exchangeRate} />
        <ItemPrice
          name="You will immediately receive"
          value={props.getIDOL}
          brand={IDOLBrand}
        />
        <ItemPrice
          name="You will later receive"
          value={props.getPooledIDOL}
          brand={PooledIDOLBrand}
        />
      </div>
    </div>
  );
};
