import React, { createContext, useCallback, useEffect } from 'react';
import { Term, LienTokenDividendState, LienFairSwapState } from 'reducer/store';
import { lien } from 'actions/useLien';
import { BigNumber as BN } from 'bignumber.js';
import { useDispatch } from 'react-redux';

export const LienTokenContext: any = createContext('');

export const LienTokenProvider: React.FC = ({ children }): JSX.Element => {
  const dispatch = useDispatch();
  const currentAddress = lien.address;

  const fetchExchangeData = useCallback(async () => {
    try {
      if (!currentAddress) return;
      const exchangeData = await lien.lienFairSwap.getExchangeData();
      const myShareData: BN = await lien.lienFairSwap.getShare(currentAddress);
      const myShare = await lien.ERC20.fromAmount18(myShareData.toString());
      dispatch({
        type: 'SET_LIEN_FAIRSWAP_STATE',
        payload: {
          ...exchangeData,
          myShare: myShare,
        } as LienFairSwapState,
      });
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  }, [dispatch, currentAddress]); // eslint-disable-line react-hooks/exhaustive-deps

  const dispatchLienToken = useCallback(async () => {
    if (!currentAddress) return;
    const termList: Term[] = [];
    const idolAddress = lien.contracts.stableCoinAddr;

    const interval: BN = await lien.lienToken.interval();
    const initialTime: BN = await lien.lienToken.initialTime();
    const expiration: BN = await lien.lienToken.expiration();
    const lastClosedTermID: BN = (await lien.lienToken.currentTerm()).minus(1);

    for (
      let termID: BN = lastClosedTermID;
      termID.isGreaterThan(0) &&
      lastClosedTermID.minus(termID).isLessThan(expiration);
      termID = termID.minus(1)
    ) {
      const profitOfIDOL: BN = await lien.lienToken.profitAt(
        idolAddress,
        termID
      );
      const profitOfETH: BN = await lien.lienToken.profitOfETHAt(termID);
      const dividendOfIDOL: BN = await lien.lienToken.dividendAt(
        idolAddress,
        currentAddress,
        termID
      );
      const dividendOfETH: BN = await lien.lienToken.dividendOfETHAt(
        currentAddress,
        termID
      );
      const balanceOfLIEN: BN = await lien.lienToken.balanceAt(
        currentAddress,
        termID
      );
      const totalSupplyOfLIEN: BN = await lien.lienToken.totalSupplyAt(termID);
      const share: BN = totalSupplyOfLIEN.isZero()
        ? new BN(0)
        : balanceOfLIEN.div(totalSupplyOfLIEN);
      const snapshotDate = new Date(
        initialTime.plus(interval.times(termID)).times(1000).toNumber()
      );
      const expirationDate = new Date(
        initialTime
          .plus(interval.times(termID.plus(expiration)))
          .times(1000)
          .toNumber()
      );
      const term = {
        id: termID,
        snapshotDate: snapshotDate,
        expirationDate: expirationDate,
        balance: balanceOfLIEN,
        share: share,
        asset: {
          eth: {
            profit: profitOfETH,
            dividend: dividendOfETH,
          },
          idol: {
            profit: profitOfIDOL,
            dividend: dividendOfIDOL,
          },
        },
      } as Term;
      termList.push(term);
    }
    const lastETHReceived = await lien.lienToken.lastETHReceived(
      currentAddress
    );
    const lastIDOLReceived = await lien.lienToken.lastTokenReceived(
      idolAddress,
      currentAddress
    );
    dispatch({
      type: 'SET_LIEN_TOKEN_DIVIDEND',
      payload: {
        termList: termList,
        lastReceivedTerm: {
          eth: lastETHReceived,
          idol: lastIDOLReceived,
        },
      } as LienTokenDividendState,
    });
  }, [dispatch, currentAddress]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    dispatchLienToken();
  }, [dispatchLienToken]);

  useEffect(() => {
    fetchExchangeData();
  }, [fetchExchangeData]);

  return (
    <LienTokenContext.Provider value={{ currentAddress }}>
      {children}
    </LienTokenContext.Provider>
  );
};
