import React, { useState, useEffect } from 'react';
import SubTitle from 'view/components/presentations/SubTitle';
import { IDOLBrand, LiquidBrand, USDBrand, LienBrand } from 'resource/brands';
import { PriceInfo, ExchangeData } from 'types';
import { GlobalState } from 'reducer/store';
import BigNumber from 'bignumber.js';
import LPTable, { LPRowData } from 'view/components/presentations/LPTable';
import PriceBox from 'view/components/presentations/PriceBox';
import { withRouter } from 'react-router-dom';
import { useSelector } from 'react-redux';
import CONTRACT_ADDR from 'libs/src/helper/Constant';

//balanceList.IDOLBalance
function BalanceLPContainer(props: any) {
  const state = useSelector((state: GlobalState) => state);

  const [LPRows, setLPRows] = useState([]);
  useEffect(() => {
    const rowList = Array.from(state.base.exchangeDataMap.values())
      .filter((ex: any) => {
        const exchangeData = ex as ExchangeData;
        return (
          exchangeData.myShare !== undefined &&
          (exchangeData.myShare as BigNumber).toString() !== '0'
        );
      })
      .map((ex: any) => {
        const exchangeData = ex as ExchangeData;
        const myShare = exchangeData.myShare as BigNumber;
        const totalShare = exchangeData.totalShare as BigNumber;
        const occupancy = myShare.dividedBy(totalShare);
        const baseAmount = exchangeData.baseTokenPool.multipliedBy(occupancy);
        const settlementAmount = exchangeData.settlementTokenPool.multipliedBy(
          occupancy
        );
        return {
          id: exchangeData.exchangeAddress,
          data: {
            exchangeAddress: exchangeData.exchangeAddress,
            myShare,
            totalShare,
            occupancy,
            baseAmount: {
              brand: IDOLBrand,
              value: baseAmount.toString(),
            } as PriceInfo,
            settlementAmount: {
              brand:
                exchangeData.exchangeAddress === CONTRACT_ADDR.LIEN_EXCHANGE
                  ? LienBrand
                  : LiquidBrand,
              value: settlementAmount.toString(),
            } as PriceInfo,
            isLBT: true,
            settlementId: exchangeData.bondId,
          } as LPRowData,
        };
      });
    setLPRows(rowList as any);
  }, [state.base.exchangeDataMap]);

  const [total, setTotal] = useState<PriceInfo>({
    brand: USDBrand,
    value: '0',
  } as PriceInfo);
  useEffect(() => {
    setTotal({
      brand: USDBrand,
      value: state.baseMy.totalLPDollar.toString(),
    } as PriceInfo);
  }, [state.baseMy.totalLPDollar]);

  const loading = state.base.exchangeDataMap.size === 0;
  return (
    <div className="content_box_4">
      <SubTitle title="My Liquidity Provided Pool List" />
      <div className="box">
        <PriceBox price={total} title={'Total Liquidity Provided Value'} />
        <LPTable
          rows={LPRows}
          loading={loading}
          onAdd={(row: LPRowData) => {
            if (row.isLBT) {
              // dispatch({
              //   type: 'SET_ADD_LBT',
              //   payload: state.base.bondMap.get(row.settlementId),
              // });
              props.history.push('/trade/liquidity');
            }
          }}
          onRemove={(row: LPRowData) => {
            if (row.isLBT) {
              // dispatch({
              //   type: 'SET_REMOVE_LBT',
              //   payload: state.base.bondMap.get(row.settlementId),
              // });
              props.history.push('/trade/liquidity');
            }
          }}
        />
        <br></br>
      </div>
      <div className="button"></div>
    </div>
  );
}

export default withRouter(BalanceLPContainer);
