import React, { useState, useEffect } from 'react';
import SubTitle from 'view/components/presentations/SubTitle';
import PriceListBox from 'view/components/presentations/PriceListBox';
import {
  ETHBrand,
  IDOLBrand,
  PooledIDOLBrand,
  USDBrand,
  LienBrand,
} from 'resource/brands';
import { PriceInfo, Bond, PooledIDOL, AuctionGroup } from 'types';
import { BaseMyState, GlobalState } from 'reducer/store';
import BigNumber from 'bignumber.js';
import PooledIDOLModal from './PooledIDOLModal';
import PriceBox from 'view/components/presentations/PriceBox';
import { PooledIDOLRowData } from 'view/components/presentations/PooledIDOLTable';
import { withRouter } from 'react-router-dom';
import useBalance from 'actions/useBalance';
import { useSelector } from 'react-redux';

//balanceList.IDOLBalance
function BalanceUpperContainer(props: any) {
  const state = useSelector((state: GlobalState) => state);
  const makePriceInfoList = (
    balanceInfo: BaseMyState,
    latestUnlockedPool: any
  ): PriceInfo[] => {
    const totalPooledIDOL = balanceInfo.pooledIDOLList.reduce(
      (sum, pooled) => sum.plus(pooled.amount),
      new BigNumber(0)
    );
    const totalReturnableIDOL = balanceInfo.pooledIDOLList
      .filter((pooled) => pooled.returnable)
      .reduce(
        (sum, pooled) => sum.plus(pooled.actualRedeemAmount),
        new BigNumber(0)
      );
    const ethPriceInfo = {
      brand: ETHBrand,
      value: balanceInfo.ethBalance.toString(),
      convertable: true,
    };
    const iDOLPriceInfo = {
      brand: IDOLBrand,
      value: balanceInfo.iDOLBalance.toString(),
      tradable: true,
    };
    const pooledIDOLPriceInfo = {
      brand: PooledIDOLBrand,
      value: totalPooledIDOL.toString(),
      returnable: totalReturnableIDOL.toString(),
      latestUnlockedPool,
    };
    const lienPirceInfo = {
      brand: LienBrand,
      value: balanceInfo.lienBalance.toString(),
      lienable: true,
    };
    const priceInfoList: PriceInfo[] = [
      ethPriceInfo,
      iDOLPriceInfo,
      pooledIDOLPriceInfo,
      lienPirceInfo,
    ];
    return priceInfoList;
  };

  const [isOpen, setIsOpen] = React.useState(false);
  const onClick = (index: number) => {
    switch (index) {
      case 0:
        props.history.push('/convert');
        break;
      case 1:
        props.history.push('/trade');
        break;
      case 2:
        // select pooled IDOL
        setIsOpen(true);
        break;
      case 3:
        props.history.push('/lien_token');
    }
  };

  // Pooled iDOL
  const [pooledIDOLRows, setPooledIDOLRows] = useState<PooledIDOLRowData[]>([]);
  const [latestUnlockedPool, setLatestUnlockedPool] = useState();
  const { returnLockedPool } = useBalance();
  useEffect(() => {
    const rowList = state.baseMy.pooledIDOLList.reduce(
      (result: any[], pooledIDOL: PooledIDOL) => {
        const tmp = state.base.bondMap.get(pooledIDOL.bondId);
        if (!tmp) {
          return result;
        }
        const bond: Bond = tmp;
        const auction = Array.from(state.auction.auctionGroupMap.values())
          .filter(
            (value) => (value as AuctionGroup).bondAddress === bond.bondAddress
          )
          .sort(
            (a, b) =>
              (a as AuctionGroup).auctionStatus.closingTime.getTime() -
              (b as AuctionGroup).auctionStatus.closingTime.getTime()
          )[pooledIDOL.count];
        const closingTime = auction
          ? (auction as AuctionGroup).auctionStatus.closingTime
          : bond.maturity;
        const auctionFlag = auction ? (auction as AuctionGroup).period : '';
        result.push({
          id: pooledIDOL.poolId,
          data: {
            poolID: pooledIDOL.poolId,
            strikePrice: bond.strikePrice
              ? (bond.strikePrice as BigNumber)
              : new BigNumber(0),
            maturity: bond.maturity,
            closingTime: closingTime,
            amount: pooledIDOL.amount,
            actualRedeemAmount: pooledIDOL.actualRedeemAmount,
            status: auctionFlag,
            wrapper: !!pooledIDOL.wrapper,
            onReturn: () => {
              returnLockedPool(pooledIDOL.poolId, !!pooledIDOL.wrapper);
            },
          } as PooledIDOLRowData,
        });
        return result;
      },
      []
    );
    const latestUnlockedPoolData = rowList.sort(
      (a: any, b: any) =>
        a.data.closingTime.getTime() - b.data.closingTime.getTime()
    )[0];
    setPooledIDOLRows(rowList);
    setLatestUnlockedPool(
      latestUnlockedPoolData
        ? latestUnlockedPoolData.data.closingTime
        : undefined
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    state.baseMy.pooledIDOLList,
    state.auction.auctionGroupMap,
    state.base.bondMap,
  ]);

  const [priceList, setPriceList] = useState<PriceInfo[]>(
    makePriceInfoList(state.baseMy, latestUnlockedPool)
  );
  useEffect(() => {
    setPriceList(makePriceInfoList(state.baseMy, latestUnlockedPool));
  }, [state.baseMy, latestUnlockedPool]); // eslint-disable-line react-hooks/exhaustive-deps

  const [total, setTotal] = useState<PriceInfo>({
    brand: USDBrand,
    value: '0',
  } as PriceInfo);
  useEffect(() => {
    setTotal({
      brand: USDBrand,
      value: state.baseMy.totalDollar.toString(),
    } as PriceInfo);
  }, [state.baseMy.totalDollar]);

  const loading = state.baseMy.pooledIDOLList.length === 0;
  return (
    <div className="content_box_4">
      <SubTitle title="My Assets in Wallet" />
      <div className="box">
        <PriceBox price={total} title={'Total Value in Lien System'} />
        <PriceListBox priceList={priceList} enableTitle onSelect={onClick} />
      </div>
      <PooledIDOLModal
        rows={pooledIDOLRows}
        isOpen={isOpen}
        setIsOpen={setIsOpen}
        loading={loading}
      />
    </div>
  );
}

export default withRouter(BalanceUpperContainer);
