export const rateString: (exchangeInfo: ExchangeInfo) => string = (
  exchangeInfo
) => {
  // fromAmount * rate = toAmount
  // rateString is settlement/IDOL
  return exchangeInfo.fromBrand.name === IDOLBrand.name
    ? new BigNumber(1).dividedBy(exchangeInfo.rate).dp(5).toString()
    : exchangeInfo.rate.dp(5).toString();
};

