export const isSolid = (bond: Bond): boolean => {
  if (bond.bondType.length !== 2) return false;
  return (
    bond.bondType[0][2] === bond.bondType[0][3] &&
    bond.bondType[1][1] === bond.bondType[1][3]
  );
};

export const isLiquid = (bond: Bond): boolean => {
  return !isSolid(bond);
};

