// TEMP to easy convertor
export const convertLiquidListToLiquidBalanceList = (
  liquidBalanceList: { bondId: string; balance: BN }[],
  bondMap: Map<string, Bond>
): LiquidBalance[] => {
  return liquidBalanceList.map(
    (liquid: { bondId: string; balance: BN }): LiquidBalance => {
      return {
        ...bondMap.get(liquid.bondId),
        balance: liquid.balance,
      } as LiquidBalance;
    }
  );
};

