import React from 'react';
import BigNumber from 'bignumber.js';
import { KIND, Tag, VARIANT } from 'baseui/tag';
import { useStyletron } from 'baseui';
import { Label4 } from 'baseui/typography';

export default (props: { leverage: BigNumber; message?: string }) => {
  const [css] = useStyletron();
  return (
    <div
      className={css({
        alignItems: 'center',
        display: 'flex',
      })}
    >
      <Label4>{props.message}</Label4>
      <Tag closeable={false} kind={KIND.accent} variant={VARIANT.outlined}>
        <Label4>{props.leverage.dp(3).toString()}x</Label4>
      </Tag>
    </div>
  );
};
