import React, { useState, useEffect } from 'react';
import { GlobalState } from 'reducer/store';
import { LiquidBalance, PriceInfo } from 'types';
import { withRouter } from 'react-router-dom';
import SubTitle from 'view/components/presentations/SubTitle';
import PriceBox from 'view/components/presentations/PriceBox';
import { USDBrand } from 'resource/brands';
import useBalance from 'actions/useBalance';
import MyLBTListTable, {
  MyLBTRowData,
} from 'view/components/presentations/MyLBTListTable';
import BigNumber from 'bignumber.js';
import { useSelector, useDispatch } from 'react-redux';

// BondList Table columns
function BalanceLiquidContainer(props: any) {
  const dispatch = useDispatch();
  const state = useSelector((state: GlobalState) => state);
  const { burnAll } = useBalance();
  const [total, setTotal] = useState<PriceInfo>({
    brand: USDBrand,
    value: '0',
  } as PriceInfo);

  const makeMyLBTRows = (
    liquidList: LiquidBalance[]
  ): { id: any; data: MyLBTRowData }[] => {
    return liquidList
      .filter(
        (liquid: { bondId: string; balance: BigNumber }) =>
          liquid.balance.toString() !== '0'
      )
      .filter((liquid: { bondId: string; balance: BigNumber }) =>
        state.base.bondMap.has(liquid.bondId)
      )
      .map((liquid: { bondId: string; balance: BigNumber }) => {
        return {
          ...state.base.bondMap.get(liquid.bondId),
          balance: liquid.balance,
        } as LiquidBalance;
      })
      .map((liquid: LiquidBalance): { id: any; data: MyLBTRowData } => ({
        id: liquid.bondId,
        data: {
          bondId: liquid.bondId,
          maturity: liquid.maturity,
          strikePrice: liquid.strikePrice,
          theoreticalPrice: liquid.theoreticalPrice,
          redeemETH: liquid.redeemETH,
          leverage: liquid.leverage,
          balance: liquid.balance,
          isBurnable: !!liquid.isBurnable,
          onBurn: () => {
            burnAll(liquid.bondAddress);
          },
          onTrade: () => {
            dispatch({
              type: 'SET_SELL_LBT',
              payload: liquid.bondAddress,
            });
            props.history.push('/trade');
          },
        } as MyLBTRowData,
      }));
  };

  const [datum, setDatum] = useState<{ id: any; data: MyLBTRowData }[]>(
    makeMyLBTRows(
      Array.from(state.baseMy.liquidBalanceList.values()) as LiquidBalance[]
    )
  );
  useEffect(() => {
    setDatum(
      makeMyLBTRows(
        Array.from(state.baseMy.liquidBalanceList) as LiquidBalance[]
      )
    );
    setTotal({
      brand: USDBrand,
      value: state.baseMy.totalLiquidDollar.toString(),
    } as PriceInfo);
  }, [state.baseMy.liquidBalanceList, state.baseMy.totalLiquidDollar]); // eslint-disable-line react-hooks/exhaustive-deps

  const loading = state.baseMy.liquidBalanceList.length === 0;
  return (
    <div className="content_box_4">
      <SubTitle title="My LBT List" />
      <div className="box">
        <PriceBox
          price={total}
          title={'Total LBT Value'}
          message=" Actual Leverage Ratio"
          averageLeverage={state.baseMy.averageLeverage}
        />
        <MyLBTListTable rows={datum} loading={loading} />
        <br></br>
      </div>
      <div className="button"></div>
    </div>
  );
}

export default withRouter(BalanceLiquidContainer);
