import React, { useState, useEffect } from 'react';
import { RadioGroup, Radio, ALIGN } from 'baseui/radio';
import TransactionButton from 'view/components/presentations/TransactionButton';
import { IDOLBrand, LienBrand, LiquidBrand } from 'resource/brands';
import { PoolSymbol, ExchangeData, defaultExchangeData } from 'types';
import { GlobalState } from 'reducer/store';
import SubItem from 'view/components/presentations/SubItem';
import ExchangeBox from 'view/components/presentations/ExchangeBox';
import FormNumber from 'view/components/presentations/FormNumber';
import ItemNumber from 'view/components/presentations/ItemNumber';
import CheckBox from 'view/components/presentations/CheckBox';
import { useSelector } from 'react-redux';
import useRecommend from 'actions/useRecommend';
import { useFairSwap } from 'actions/useFairSwap';
import CONTRACT_ADDR from 'libs/src/helper/Constant';
import { BigNumber as BN } from 'bignumber.js';
import {
  SelectablePoolSymbol,
  SymbolListBox,
} from 'view/components/presentations/SymbolListBox';
import IconWrapper from 'view/components/presentations/IconWrapper';
import { HelpCircle } from 'react-feather';
import TradeModal from 'view/components/modal/TradeModal';
import { isValidBalance } from 'libs/utils/validate';
import { strictModeDescription } from 'resource/description/index';
import { LayersManager } from 'baseui/layer';

interface TradeProps {
  onSelectPoolSymbol: (poolSymbol: PoolSymbol) => void;
}

export default function (props: TradeProps) {
  const { getPriceImpact } = useFairSwap();
  const { getMaturitySortedBondGroup } = useRecommend();
  const state = useSelector((state: GlobalState) => state);
  const lienFairSwapData = state.base.exchangeDataMap.get(
    CONTRACT_ADDR.LIEN_TOKEN
  );
  const lienFairSwap: ExchangeData = lienFairSwapData
    ? lienFairSwapData
    : defaultExchangeData();

  const [sideValue, setSideValue] = React.useState('Buy');
  const [paymentAmount, setPaymentAmount] = useState<BN>(new BN(0));
  const [strictMode, setStrictMode] = useState({ change: false });

  const [openedTrade, setOpenedTrade] = useState<boolean>(false);
  const lienBuyPrice = {
    fromBrand: IDOLBrand,
    toBrand: LienBrand,
    rate: lienFairSwap.buyPrice,
    pair: `${LienBrand.unit}/${IDOLBrand.unit}`,
  };
  const lienSellPrice = {
    fromBrand: LienBrand,
    toBrand: IDOLBrand,
    rate: new BN(1).dividedBy(lienFairSwap.sellPrice),
    pair: `${LienBrand.unit}/${IDOLBrand.unit}`,
  };
  const lienSymbol: PoolSymbol = {
    ...lienFairSwap,
    description: 'LIEN', // TODO: add pool info etc.
    balance: state.baseMy.lienBalance,
    buy: () => setOpenedTrade(true),
    sell: () => setOpenedTrade(true),
    buyInfo: lienBuyPrice,
    sellInfo: lienSellPrice,
  };
  const bondSymbols: PoolSymbol[] = getMaturitySortedBondGroup(
    Array.from(state.base.bondGroupMap.values())
  )
    .filter((bondGroup) => {
      const liquidBond = bondGroup.liquidBondList[0];
      const bondAddr = liquidBond ? liquidBond.bondAddress : '';
      const exchangeData = state.base.exchangeDataMap.get(bondAddr);
      return !!exchangeData;
    })
    .map((bond) => {
      const bondAddress = bond.liquidBondList[0].bondAddress;
      const exchangeData = state.base.exchangeDataMap.get(
        bondAddress
      ) as ExchangeData;
      const strMaturity = bond.maturity.toLocaleDateString();
      const bondBuyPrice = {
        fromBrand: IDOLBrand,
        toBrand: LiquidBrand,
        rate: exchangeData.buyPrice,
        pair: `${LiquidBrand.unit}/${IDOLBrand.unit}`,
      };
      const bondSellInfo = {
        fromBrand: LiquidBrand,
        toBrand: IDOLBrand,
        rate: new BN(1).dividedBy(exchangeData.sellPrice),
        pair: `${LiquidBrand.unit}/${IDOLBrand.unit}`,
      };
      const tmp = state.baseMy.liquidBalanceList.find(
        (liquid: { bondId: string; balance: BN }) =>
          liquid.bondId === exchangeData.bondId
      );
      const balance = tmp ? tmp.balance : new BN(0);
      return {
        ...exchangeData,
        description: `Strike Price: $${bond.strikePrice} Maturity: ${strMaturity}`,
        balance,
        buy: () => setOpenedTrade(true),
        sell: () => setOpenedTrade(true),
        buyInfo: bondBuyPrice,
        sellInfo: bondSellInfo,
      };
    });

  const poolSymbolMap: Map<string, PoolSymbol> = new Map();
  poolSymbolMap.set(lienSymbol.settlementAddress, lienSymbol);
  bondSymbols.forEach((bond) => {
    poolSymbolMap.set(bond.settlementAddress, bond);
  });

  const selectablePoolSymbols: SelectablePoolSymbol[] = [
    {
      id: lienSymbol.settlementAddress,
      description: lienSymbol.description,
      group: 'ungrouped',
      brand: lienSymbol.buyInfo.toBrand,
    },
    ...bondSymbols.map(
      (bond: PoolSymbol): SelectablePoolSymbol => {
        return {
          id: bond.settlementAddress,
          description: bond.description,
          group: 'LBT',
          brand: bond.buyInfo.toBrand,
        };
      }
    ),
  ];

  const [selectedSymbolId, selectSymbol] = useState<string>(
    lienSymbol.settlementAddress
  );

  const selectedSymbol = poolSymbolMap.get(selectedSymbolId) || lienSymbol;
  const exchangeInfo =
    sideValue === 'Buy' ? selectedSymbol.buyInfo : selectedSymbol.sellInfo;

  const getPaymentBalance = () => {
    if (exchangeInfo.fromBrand.name === 'iDOL') return state.baseMy.iDOLBalance;
    if (exchangeInfo.fromBrand.name === 'LIEN') return state.baseMy.lienBalance;
    if (exchangeInfo.fromBrand.name === 'LBT') {
      const exchangeData = state.base.exchangeDataMap.get(
        selectedSymbol.settlementAddress
      );
      if (!exchangeData) {
        return new BN(0);
      }
      const targetBond = state.baseMy.liquidBalanceList.find(
        (bond: any) => bond.bondId === exchangeData.bondId
      );
      return targetBond ? targetBond.balance : new BN(0);
    }
    return new BN(0);
  };

  const isBalanceLessThan = (num: BN): boolean => {
    return getPaymentBalance().isLessThan(num);
  };

  const calcReceiveAmount = (): BN => {
    return paymentAmount.multipliedBy(exchangeInfo.rate).dp(8);
  };

  const sendTx = selectedSymbol[sideValue === 'Buy' ? 'buy' : 'sell'];

  const priceImpact =
    sideValue === 'Buy'
      ? getPriceImpact(selectedSymbol.baseTokenPool, paymentAmount)
      : getPriceImpact(selectedSymbol.settlementTokenPool, paymentAmount);

  // check txDisabled
  const [txDisabled, setTxDisabled] = useState<boolean>(true);
  useEffect(() => {
    if (isValidBalance(paymentAmount, getPaymentBalance()))
      setTxDisabled(false);
    else setTxDisabled(true);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [paymentAmount, selectedSymbol]);

  return (
    <div className="content_box_3">
      <div className="box">
        <RadioGroup
          value={sideValue}
          onChange={(e) => setSideValue(e.target.value)}
          name="side"
          align={ALIGN.horizontal}
        >
          <Radio value="Buy">Buy</Radio>
          <Radio value="Sell">Sell</Radio>
        </RadioGroup>
        <br />
        <SubItem name={`Select Asset to ${sideValue}`} />
        <LayersManager zIndex={3}>
          <SymbolListBox
            symbols={selectablePoolSymbols}
            onChange={(value: SelectablePoolSymbol[]) => {
              const selected = poolSymbolMap.get(value[0].id) || lienSymbol;
              props.onSelectPoolSymbol(selected);
              selectSymbol(value[0].id);
              priceImpact.isLessThan(0.1) && setStrictMode({ change: false });
            }}
          />
        </LayersManager>
        <br />
        <SubItem name={`Amount to Pay`} />
        <FormNumber
          errorMessage="Insufficient funds"
          unit={exchangeInfo.fromBrand.unit}
          value={paymentAmount}
          setValue={(value: BN) => {
            setPaymentAmount(value || new BN(0));
            priceImpact.isLessThan(0.1) && setStrictMode({ change: false });
          }}
          maxValue={getPaymentBalance()}
          caption={
            isBalanceLessThan(new BN(paymentAmount))
              ? 'Insufficient funds'
              : null
          }
        />
        <ItemNumber
          name={`Estimated Receive`}
          value={
            paymentAmount.isZero() || exchangeInfo.rate.isZero()
              ? ''
              : exchangeInfo.toBrand.view(calcReceiveAmount().toString())
          }
        />
        {priceImpact.isLessThan(0.1) && (
          <IconWrapper
            component={
              <CheckBox
                id="limit_buy"
                checked={strictMode.change}
                text="strict mode"
                fnChange={(e: React.ChangeEvent<HTMLInputElement>): void =>
                  setStrictMode({ change: e.target.checked })
                }
              />
            }
            icon={<HelpCircle size={16} color="#666" />}
            isDescription={true}
            description={strictModeDescription}
            isPadded={true}
          />
        )}
        <br />
        <SubItem name="Exchange Rate" />
        <ExchangeBox
          exchangeInfo={
            sideValue === 'Buy'
              ? selectedSymbol.buyInfo
              : selectedSymbol.sellInfo
          }
        />
        <ItemNumber
          name={`Price Impact`}
          value={
            paymentAmount.isZero() || exchangeInfo.rate.isZero()
              ? ''
              : sideValue === 'Buy'
              ? priceImpact.isLessThan(0.01)
                ? '< 0.01 %'
                : priceImpact + '%'
              : priceImpact.isLessThan(0.01)
              ? '< 0.01 %'
              : priceImpact + '%'
          }
        />
      </div>
      <TransactionButton
        name="Sign and Send Tx"
        txFunc={sendTx}
        disabled={txDisabled}
      />
      <TradeModal
        isOpen={openedTrade}
        setIsOpen={setOpenedTrade}
        tokenIn={paymentAmount}
        isLimit={strictMode.change}
        tokenAddress={selectedSymbol.settlementAddress}
        exchangeAddress={selectedSymbol.exchangeAddress}
        latestBoxNumber={selectedSymbol.latestBoxNumber}
        exchangeInfo={exchangeInfo}
        totalLiquidity={selectedSymbol.baseTokenPool}
      />
    </div>
  );
}
