import React from 'react';
import BigNumber from 'bignumber.js';
import { KIND, Tag, VARIANT } from 'baseui/tag';
import { useStyletron } from 'baseui';

export default (props: { pool: BigNumber; unit: string }) => {
  const [css] = useStyletron();
  return (
    <div
      className={css({
        alignItems: 'center',
        display: 'flex',
      })}
    >
      <Tag closeable={false} kind={KIND.positive} variant={VARIANT.outlined}>
        {props.pool.toPrecision(3).toString() + props.unit} iDOL
      </Tag>
    </div>
  );
};
