import React, { useState, useEffect } from 'react';
import SubItem from 'view/components/presentations/SubItem';
import ItemNumber from 'view/components/presentations/ItemNumber';
import FormNumber from 'view/components/presentations/FormNumber';
import TransactionButton from 'view/components/presentations/TransactionButton';
import TransactionButtonLoading from 'view/components/presentations/TransactionButtonLoading';
import { AspectRatioBox } from 'baseui/aspect-ratio-box';
import { StateString } from 'types';
import useAuction from 'actions/useAuction';
import { Slider } from 'baseui/slider';
import { useStyletron } from 'baseui';
import { Input, SIZE } from 'baseui/input';
import { BigNumber as BN } from 'bignumber.js';
import useERC20 from 'actions/useERC20';
import {
  getBidStep,
  getUserBids,
  setUserBids as setLocalUserBids,
} from 'libs/utils/utils';
import { lien } from 'actions/useLien';
import { ModalFooter } from 'baseui/modal';
import { useSelector } from 'react-redux';
import { GlobalState } from 'reducer/store';

interface AuctionRightContainerProps {
  todoStrikePrice: string;
  todoSBTMaturityDate: Date;
  todoAuctionStatus: StateString;
  todoAuctionID: string;
  todoAuctionBondAddr: string;
  setCurrent: any;
  amount: string;
  setIsOpen: any;
  setClosable: any;
}

interface Bid {
  price: string;
  amount: string;
  random: number;
  reveal: boolean;
}

function AuctionRightContainer(props: AuctionRightContainerProps) {
  const [isSending, setIsSending] = useState(false);
  const [sbtValue, setSBTValue] = useState(new BN(0));
  const [maxValue, setMaxValue] = useState<BN>(new BN(0));
  const { bid, generateSecret } = useAuction();
  const { convertAmount } = useERC20();
  const [css, theme] = useStyletron();

  const iDOLBalance = useSelector(
    (state: GlobalState) => state.baseMy.iDOLBalance
  );

  const {
    todoStrikePrice,
    todoSBTMaturityDate,
    todoAuctionID,
    todoAuctionBondAddr,
    todoAuctionStatus,
    setIsOpen,
    setClosable,
  } = props;

  const asyncMaxValue = async () => {
    setMaxValue(await lien.balance.erc20Helper.fromAmount8(props.amount));
  };

  useEffect(() => {
    asyncMaxValue();
  }, [props.amount]); // eslint-disable-line react-hooks/exhaustive-deps

  const slider = {
    max: parseInt(todoStrikePrice),
    min: Math.ceil(parseInt(todoStrikePrice) * 0.9),
    step: getBidStep(parseInt(todoStrikePrice)),
  };
  const [value, setValue] = React.useState([slider.min]);
  const [isSucceed, setIsSucceed] = useState(false);
  const [userBids, setUserBids] = useState<Bid[]>();

  const txFunc: () => void = async () => {
    setIsSending(true);
    setClosable(false);
    const price = new BN(value[0]).multipliedBy('1e8').toString();
    const targetSBTAmount = await convertAmount(
      todoAuctionBondAddr,
      new BN(sbtValue)
    );
    const randomArray = window.crypto.getRandomValues(new Uint32Array(1));
    const random = randomArray[0];

    const secret = await generateSecret(
      todoAuctionID,
      price,
      String(targetSBTAmount),
      random
    );
    const success = await bid(
      todoAuctionID,
      secret,
      String(targetSBTAmount),
      todoStrikePrice
    );
    if (success) {
      const bids = getUserBids(todoAuctionID);
      bids.push({
        auctionID: todoAuctionID,
        secret: secret,
        price: new BN(price).div('1e8').toString(),
        amount: new BN(targetSBTAmount || '0').div('1e8').toString(),
        random: random,
        reveal: false,
      });
      setLocalUserBids(todoAuctionID, bids);
    }

    setClosable(true);
    setIsSucceed(success);
  };

  const iDOLPayment = new BN(todoStrikePrice ? todoStrikePrice : 0).times(
    sbtValue
  );

  useEffect(() => {
    setUserBids(getUserBids(props.todoAuctionID));
  }, [isSucceed]); // eslint-disable-line react-hooks/exhaustive-deps

  const iDOLBalanceLack = iDOLBalance.lt(iDOLPayment);
  const iDOLPaymentLT100 = iDOLPayment.lt(new BN(100));
  const txDisabled = iDOLBalanceLack || iDOLPaymentLT100;
  const txDisabledReason = iDOLBalanceLack
    ? "You don't have enough iDOL."
    : iDOLPaymentLT100
    ? 'Payments must be at least $100.'
    : '';
  return (
    <AspectRatioBox width={`${100}%`} height={'auto'}>
      {!isSending ? (
        <>
          <SubItem name="Auction SBT" />
          <ItemNumber name="Strike Price" value={todoStrikePrice} />
          <ItemNumber
            name="Maturity"
            value={todoSBTMaturityDate.toLocaleDateString()}
          />
          <br />
          <SubItem name="Bid Price" />
          <Slider
            value={value}
            onChange={({ value }) => value && setValue(value)}
            step={slider.step}
            min={slider.min}
            max={slider.max}
            overrides={{
              ThumbValue: ({ $value }) => (
                <div
                  className={css({
                    position: 'absolute',
                    top: `-${theme.sizing.scale800}`,
                    ...theme.typography.font200,
                    backgroundColor: 'transparent',
                  })}
                >
                  ${$value}
                </div>
              ),
            }}
          />
          <br />
          <SubItem name="Bid Amount" />
          <FormNumber
            maxValue={maxValue}
            unit="SBT"
            value={sbtValue}
            setValue={setSBTValue}
          />
          <br />
          <SubItem name="Total Deposit" />
          <Input
            value={iDOLPayment.toString()}
            endEnhancer="iDOL"
            placeholder="Total IDOL deposits"
            size={SIZE.compact}
            disabled
          />
          <br />
        </>
      ) : (
        <>
          <SubItem name="Bid Period" />
          <ItemNumber
            name="Price"
            value={
              userBids !== undefined && userBids?.length > 0 && isSucceed
                ? '$' + new BN(userBids[userBids?.length - 1].price).toString()
                : ''
            }
          />
          <ItemNumber
            name="Amount"
            value={
              userBids !== undefined && userBids?.length > 0 && isSucceed
                ? new BN(userBids[userBids?.length - 1].amount).toString() +
                  'SBT'
                : ''
            }
          />
        </>
      )}
      <ModalFooter>
        {isSucceed ? (
          <TransactionButton
            name={`Confirmed`}
            txFunc={() => {
              setIsOpen(false);
            }}
          />
        ) : isSending ? (
          <TransactionButtonLoading />
        ) : (
          <>
            <TransactionButton
              name={todoAuctionStatus}
              disabled={txDisabled}
              txFunc={txFunc}
            />
            {txDisabled ? <span>{txDisabledReason}</span> : <></>}
          </>
        )}
      </ModalFooter>
    </AspectRatioBox>
  );
}

export default AuctionRightContainer;
