import { BigNumber as BN } from 'bignumber.js';
import { period } from 'config';
export const viewedDollar = (a: number | BN, digits?: number): string => {
  return new BN(a.toString()).dp(2).toString();
};
export const viewedStrikePrice = (a: number | BN, digits?: number): string => {
  return new BN(a.toString()).dp(2).toString();
};
export const viewedTheoreticalPrice = (
  a: number | BN,
  _digits?: number
): string => {
  return new BN(a.toString()).dp(2).toString();
};
export const viewedEth = (a: number | BN, _digits?: number): string =>
  new BN(a.toString()).dp(2).toString();
export const viewedLBT = (a: number | BN, _digits?: number): string =>
  new BN(a.toString()).dp(2).toString();
export const viewedSBT = (a: number | BN, _digits?: number): string =>
  new BN(a.toString()).dp(2).toString();

export const statusToTagKind = (status: string) => {
  switch (status) {
    case period.notStarted: {
      return 'neutral';
    }
    case period.bid: {
      return 'accent';
    }
    case period.reveal: {
      return 'primary';
    }
    case period.winning: {
      return 'primary';
    }
    default: {
      return 'neutral';
    }
  }
};
