import React from 'react';
import { useStyletron } from 'baseui';
import { DAY, HOUR, MINUTE } from 'resource/constants';
import { Tag, KIND, VARIANT } from 'baseui/tag';

export default (props: {
  value: Date;
  strictly?: boolean;
  batchDisable?: boolean;
}) => {
  const [css] = useStyletron();
  const afterMilSeconds = props.value.getTime() - new Date().getTime();
  let afterDays = Math.floor(afterMilSeconds / DAY);
  let afterHours = Math.floor(afterMilSeconds / HOUR);
  let afterMinutes = Math.floor(afterMilSeconds / MINUTE);
  const afterKind =
    afterDays > 0
      ? KIND.positive
      : afterDays <= 0 && afterHours > 0
      ? KIND.warning
      : KIND.negative;
  if (afterMilSeconds < 0) {
    afterDays = Math.abs(afterDays);
    afterHours = Math.abs(afterHours);
    afterMinutes = Math.abs(afterMinutes);
  }
  return (
    <div
      className={css({
        alignItems: 'center',
        display: 'flex',
      })}
    >
      {props.strictly && props.value.toLocaleString()}
      {!props.strictly && props.value.toLocaleDateString()}
      {!props.batchDisable && (
        <Tag closeable={false} kind={afterKind} variant={VARIANT.outlined}>
          {afterDays > 0 && `${afterDays} days`}
          {afterDays <= 0 && afterHours > 0 && `${afterHours} hours`}
          {afterHours <= 0 && `${afterMinutes} minutes`}
          {afterMilSeconds < 0 && ' ago'}
        </Tag>
      )}
    </div>
  );
};
