import React from 'react';
import './WithIcon.scss';
import {
  Unstable_StatefulDataTable as StatefulDataTable,
  CustomColumn,
} from 'baseui/data-table';
import BigNumber from 'bignumber.js';
import { LienBrand } from 'resource/brands';
import NoData from './NoData';
import GettingData from './GettingData';
import MaturityCell from './MaturityCell';
import { BrandInfo } from 'types';
import { dateSortFn } from 'libs/utils/utils';

export interface TermTableRowData {
  id: string;
  snapshotDate: Date;
  expirationDate: Date;
  balance: BigNumber;
  profit: BigNumber;
  dividend: BigNumber;
  brand: BrandInfo;
}

const columns = [
  CustomColumn({
    title: 'Snapshot Date',
    mapDataToValue: (data: TermTableRowData) => data.snapshotDate,
    renderCell: function Cell(props: { value: Date }) {
      return <MaturityCell value={props.value} strictly={true} batchDisable />;
    },
    sortFn: dateSortFn,
    sortable: true,
  }),
  CustomColumn({
    title: 'Balance',
    mapDataToValue: (data: TermTableRowData) => data,
    renderCell: function Cell(props: any) {
      return (
        <div className={LienBrand.className}>
          {LienBrand.view(props.value.balance.toString())}
        </div>
      );
    },
  }),
  CustomColumn({
    title: 'Commission',
    mapDataToValue: (data: TermTableRowData) => data,
    renderCell: function Cell(props: any) {
      return (
        <div className={props.value.brand.className}>
          {props.value.brand.view(props.value.dividend.toString())}
        </div>
      );
    },
  }),
  CustomColumn({
    title: 'Expiration Date',
    mapDataToValue: (data: TermTableRowData) => data.expirationDate,
    renderCell: function Cell(props: { value: Date }) {
      return <MaturityCell value={props.value} strictly={true} />;
    },
    sortFn: dateSortFn,
    sortable: true,
  }),
];

export interface MyLBTListTableProps {
  rows: { id: any; data: TermTableRowData }[];
  loading: boolean;
}

export default (props: MyLBTListTableProps) => {
  return (
    <div>
      <div style={{ height: `${4 * 55 + 10}px` }}>
        <StatefulDataTable
          columns={columns}
          rows={props.rows}
          rowHeight={55}
          loading={props.loading}
          loadingMessage={() => <GettingData />}
          emptyMessage={() => <NoData />}
        />
      </div>
    </div>
  );
};
