import Contracts from '../helper/Contract';
import { Web3Utils } from 'libs/utils/web3Utils';
import { BigNumber as BN } from 'bignumber.js';
import { Contract } from 'web3-eth-contract';

export default class ERC20 {
  private contracts: any;
  private web3Utils: any;
  private decimals: Map<string, BN>;
  private erc20Instances: Map<string, Contract>;

  constructor(conracts: Contracts) {
    this.contracts = conracts;
    this.web3Utils = new Web3Utils();
    this.decimals = new Map();
    this.erc20Instances = new Map();
  }

  public async getBalance(tokenAddr: string, ownerAddr: string): Promise<BN> {
    const instance = this.getERC20Instance(tokenAddr);
    const balance = await instance.methods.balanceOf(ownerAddr).call();
    return await this.fromAmount(tokenAddr, balance ? balance.toString() : '0');
  }

  public async getDecimals(tokenAddr: string): Promise<BN> {
    if (this.decimals.has(tokenAddr)) {
      return this.decimals.get(tokenAddr) as BN;
    }
    const instance = this.getERC20Instance(tokenAddr);
    const decimals = new BN(await instance.methods.decimals().call());
    this.decimals.set(tokenAddr, decimals);
    return decimals;
  }

  public async approve(tokenAddr: string, spender: string, amount: BN) {
    const instance = this.getERC20Instance(tokenAddr);
    const ApproveAmt = await this.convertAmount(tokenAddr, amount);
    const funcData = instance.methods.approve(spender, ApproveAmt);
    return this.contracts.sendTx(funcData);
  }

  public async approveAmt(tokenAddr: string, spender: string, amount: string) {
    const instance = this.getERC20Instance(tokenAddr);
    const funcData = instance.methods.approve(spender, amount);
    return this.contracts.sendTx(funcData);
  }

  public async convertAmount(tokenAddr: string, amount: BN): Promise<string> {
    const decimals = await this.getDecimals(tokenAddr);
    return amount.multipliedBy(new BN(10).pow(decimals)).dp(0).toString();
  }

  public convertAmount12(amount: BN): string {
    const decimals = new BN(12);
    return amount.multipliedBy(new BN(10).pow(decimals)).dp(0).toString();
  }

  public async fromAmount(tokenAddr: string, amount: string): Promise<BN> {
    if (tokenAddr === 'ETH') {
      return await this.fromAmount18(amount);
    }
    if (amount === '0') {
      return new BN(0);
    }
    const decimals = await this.getDecimals(tokenAddr);
    return new BN(amount).dividedBy(new BN(10).pow(decimals));
  }

  public async fromAmountBuyOrSell(
    tokenAddr: string,
    amount: string,
    isBuy: boolean,
    reverse?: boolean
  ): Promise<BN> {
    if (!reverse && !isBuy && reverse && isBuy) {
      if (tokenAddr === 'ETH') {
        return await this.fromAmount18(amount);
      }
      if (amount === '0') {
        return new BN(0);
      }
      const decimals = await this.getDecimals(tokenAddr);
      return new BN(amount).dividedBy(new BN(10).pow(decimals));
    } else {
      return await this.fromAmount8(amount);
    }
  }

  public async fromAmount8(amount: string): Promise<BN> {
    if (amount === '0') {
      return new BN(0);
    }
    const decimals = 8;
    return new BN(amount).dividedBy(new BN(10).pow(decimals));
  }

  public async fromAmount18(amount: string): Promise<BN> {
    if (amount === '0') {
      return new BN(0);
    }
    const decimals = 18;
    return new BN(amount).dividedBy(new BN(10).pow(decimals));
  }

  public async fromAmount24(amount: string): Promise<BN> {
    if (amount === '0') {
      return new BN(0);
    }
    const decimals = 24;
    return new BN(amount).dividedBy(new BN(10).pow(decimals));
  }

  public async fromAmount36(amount: string): Promise<BN> {
    if (amount === '0') {
      return new BN(0);
    }
    const decimals = 36;
    return new BN(amount).dividedBy(new BN(10).pow(decimals));
  }

  public getERC20Instance(tokenAddr: string) {
    if (this.erc20Instances.has(tokenAddr)) {
      return this.erc20Instances.get(tokenAddr);
    }
    const erc20Instance = this.contracts.erc20.clone();
    erc20Instance.options.address = tokenAddr;
    this.erc20Instances.set(tokenAddr, erc20Instance);
    return erc20Instance;
  }
}
