import Contracts from 'libs/src/helper/Contract';
import ERC20helper from 'libs/src/balance/ERC20Helper';
import Exchange from 'libs/src/trade/FairSwapHelper';
import { BigNumber as BN } from 'bignumber.js';

export default class FairSwap {
  private contracts: Contracts;
  private erc20Helper: ERC20helper;
  private fairSwapHelper: Exchange;
  private upperLimitMul: number;
  private lowerLimitMul: number;

  constructor(contracts: Contracts) {
    this.contracts = contracts;
    this.erc20Helper = new ERC20helper(contracts);
    this.fairSwapHelper = new Exchange(contracts);
    this.upperLimitMul = 1.1;
    this.lowerLimitMul = 0.95;
  }

  // ===== Methods.sendTx =====

  public async orderBaseToSettlement(
    timeout: string,
    recipient: string,
    baseTokenAmountData: BN,
    isLimit: boolean,
    exchangeAddr: string
  ) {
    const baseTokenAmount = await this.erc20Helper.convertAmount(
      this.contracts.stableCoinAddr,
      baseTokenAmountData
    );
    const exchangeInstance = await this.fairSwapHelper.getExchangeInstance(
      exchangeAddr
    );
    const funcData = exchangeInstance.methods.orderBaseToSettlement(
      timeout,
      recipient,
      baseTokenAmount,
      isLimit
    );
    return this.contracts.sendTx(funcData);
  }

  public async orderSettlementToBase(
    timeout: string,
    recipient: string,
    settlementTokenAmountData: BN,
    isLimit: boolean,
    tokenAddr: string,
    exchangeAddr: string
  ) {
    const settlementTokenAmount = await this.erc20Helper.convertAmount(
      tokenAddr,
      settlementTokenAmountData
    );
    const exchangeInstance = await this.fairSwapHelper.getExchangeInstance(
      exchangeAddr
    );

    const funcData = exchangeInstance.methods.orderSettlementToBase(
      timeout,
      recipient,
      settlementTokenAmount,
      isLimit
    );

    return this.contracts.sendTx(funcData);
  }

  public async addLiquidity(
    timeout: string,
    baseTokenAmountData: BN,
    baseTokenPerShare: BN,
    baseTokenAddr: string,
    exchangeAddr: string
  ) {
    const baseTokenAmount = await this.erc20Helper.convertAmount(
      baseTokenAddr,
      baseTokenAmountData
    );

    // === TODO: delete when DelegateCall is ready ===
    const minShare = new BN(Number(baseTokenAmount))
      .div(baseTokenPerShare)
      .times(this.lowerLimitMul)
      .dp(0)
      .toString();

    const exchangeInstance = await this.fairSwapHelper.getExchangeInstance(
      exchangeAddr
    );

    const funcData = exchangeInstance.methods.addLiquidity(
      timeout,
      baseTokenAmount,
      minShare
    );
    return this.contracts.sendTx(funcData);
  }

  public async removeLiquidity(
    timeout: string,
    shareTokensData: BN,
    baseTokenPerShare: BN,
    settlementTokenPerShare: BN,
    exchangeAddr: string
  ) {
    const exchangeInstance = await this.fairSwapHelper.getExchangeInstance(
      exchangeAddr
    );

    const sharesBurned = await this.erc20Helper.convertAmount(
      exchangeAddr,
      shareTokensData
    );

    const minBaseTokensAmountData = new BN(baseTokenPerShare.toString())
      .times(sharesBurned)
      .times(this.lowerLimitMul)
      .toString();

    const minSettlemenTokensAmountData = new BN(
      settlementTokenPerShare.toString()
    )
      .times(sharesBurned)
      .times(this.lowerLimitMul)
      .toString();

    const minBaseTokensAmount36 = await this.erc20Helper.fromAmount36(
      minBaseTokensAmountData
    );

    const minSettlemenTokensAmount36 = await this.erc20Helper.fromAmount36(
      minSettlemenTokensAmountData
    );

    const minBaseTokensAmount = minBaseTokensAmount36.dp(0).toString();

    const minSettlemenTokensAmount = minSettlemenTokensAmount36
      .dp(0)
      .toString();

    const funcData = exchangeInstance.methods.removeLiquidity(
      timeout,
      minBaseTokensAmount,
      minSettlemenTokensAmount,
      sharesBurned
    );
    return this.contracts.sendTx(funcData);
  }

  public async executeUnexecutedBox(maxOrderNum: number, exchangeAddr: string) {
    const exchangeInstance = await this.fairSwapHelper.getExchangeInstance(
      exchangeAddr
    );
    const funcData = exchangeInstance.methods.executeUnexecutedBox(maxOrderNum);
    return this.contracts.sendTx(funcData);
  }

  public async removeAfterMaturity(exchangeAddr: string) {
    const exchangeInstance = await this.fairSwapHelper.getExchangeInstance(
      exchangeAddr
    );
    const funcData = exchangeInstance.methods.removeAfterMaturity();
    return this.contracts.sendTx(funcData);
  }

  // ===== Methods.call =====

  public async getExchangeData(exchangeAddr: string) {
    const exchangeInstance = await this.fairSwapHelper.getExchangeInstance(
      exchangeAddr
    );
    return exchangeInstance.methods.getExchangeData().call();
  }

  public async getShare(ownerAddr: string, exchangeAddr: string) {
    const exchangeInstance = await this.fairSwapHelper.getExchangeInstance(
      exchangeAddr
    );
    return exchangeInstance.methods.balanceOf(ownerAddr).call();
  }

  public async tokenToExchangeLookup(tokenAddr: string) {
    return this.contracts.exchangeFactory.methods
      .tokenToExchangeLookup(tokenAddr)
      .call();
  }

  public async whenToExecute(
    recipient: string,
    boxNumber: number,
    isBuy: boolean,
    isLimit: boolean,
    exchangeAddr: string
  ) {
    const exchangeInstance = await this.fairSwapHelper.getExchangeInstance(
      exchangeAddr
    );
    return exchangeInstance.methods
      .whenToExecute(recipient, boxNumber, isBuy, isLimit)
      .call();
  }
}
