import { BaseState } from 'reducer/store';
import BigNumber from 'bignumber.js';

export const baseReducer = (state: BaseState, action: any | BaseState) => {
  switch (action.type) {
    case 'ADD_BOND_MAP':
      return {
        ...state,
        bondGroupMap: new Map([
          ...(state.bondGroupMap as any),
          ...action.payload.bondGroupMap,
        ]),
        addedBondGroupMap: action.payload.bondGroupMap,
        bondMap: new Map([
          ...(state.bondMap as any),
          ...action.payload.bondMap,
        ]),
        addedBondMap: action.payload.bondMap,
      };
    case 'UPDATE_BOND_MAP':
      return {
        ...state,
        bondGroupMap: new Map([
          ...(state.bondGroupMap as any),
          ...action.payload.bondGroupMap,
        ]),
        bondMap: new Map([
          ...(state.bondMap as any),
          ...action.payload.bondMap,
        ]),
      };
    case 'ADD_EXCHANGE_MAP':
      return {
        ...state,
        exchangeDataMap: new Map([
          ...(state.exchangeDataMap as any),
          ...action.payload.exchangeDataMap,
        ]),
        addedExchangeDataMap: action.payload.exchangeDataMap,
        exchangeToLBT: new Map([
          ...(state.exchangeToLBT as any),
          ...action.payload.exchangeToLBT,
        ]),
      };
    case 'UPDATE_EXCHANGE_MAP':
      return {
        ...state,
        exchangeDataMap: new Map([
          ...(state.exchangeDataMap as any),
          ...action.payload.exchangeDataMap,
        ]),
      };
    case 'UPDATE_ORACLE':
      return {
        ...state,
        oracleEthPrice: action.payload.oracleEthPrice,
        oracleVolatility: action.payload.oracleVolatility,
      };
    case 'UPDATE_ORDERED_BOND_GROUP_ID_LIST':
      return {
        ...state,
        bondGroupIdList: action.payload,
      };
    case 'ADD_EXCHANGE_ADDRESS_SET':
      return {
        ...state,
        exchangeAddressSet: new Set([
          ...(state.exchangeAddressSet as any),
          ...action.payload,
        ]),
      };
    case 'CLEAR_BASE':
      return {
        bondGroupMap: new Map(),
        bondMap: new Map(),
        exchangeDataMap: new Map(),
        addedBondGroupMap: new Map(),
        addedBondMap: new Map(),
        addedExchangeDataMap: new Map(),
        exchangeToLBT: new Map(),
        oracleEthPrice: new BigNumber(0),
        oracleVolatility: new BigNumber(0),
        exchangeAddressSet: new Set(),
      };

    default:
      return state;
  }
};
