import SubTitle from 'view/components/presentations/SubTitle';
import React, { useState, useEffect } from 'react';
import TransactionButton from '../../presentations/TransactionButton';
import { Term, GlobalState } from 'reducer/store';
import { useLienToken } from 'actions/useLienToken';
import { BrandInfo } from 'types';
import { ETHBrand, IDOLBrand } from 'resource/brands';
import DividendTable, {
  TermTableRowData,
} from 'view/components/presentations/DividendTable';
import { useSelector } from 'react-redux';

const makeTermTableRowDataList = (
  termList: Term[],
  brand: BrandInfo
): { id: any; data: TermTableRowData }[] => {
  return termList
    .sort((a, b) => b.id.toNumber() - a.id.toNumber())
    .map((term: Term): { id: any; data: TermTableRowData } => {
      let asset;
      if (brand === IDOLBrand) {
        asset = term.asset.idol;
      } else {
        asset = term.asset.eth;
      }
      return {
        id: term.id.toString(),
        data: {
          id: term.id.toString(),
          snapshotDate: term.snapshotDate,
          expirationDate: term.expirationDate,
          balance: term.balance,
          profit: asset.profit,
          dividend: asset.dividend,
          brand: brand,
        } as TermTableRowData,
      };
    });
};
function DividendContainer() {
  const state = useSelector((state: GlobalState) => state);
  const displayTermsOfEth: Term[] = state.lienTokenDividend.termList.filter(
    (term: Term) => term.id > state.lienTokenDividend.lastReceivedTerm.eth
  );
  const ethTableColumns: {
    id: any;
    data: TermTableRowData;
  }[] = makeTermTableRowDataList(displayTermsOfEth, ETHBrand);
  const displayTermsOfIDOL: Term[] = state.lienTokenDividend.termList.filter(
    (term: Term) => term.id > state.lienTokenDividend.lastReceivedTerm.idol
  );
  const idolTableColumns: {
    id: any;
    data: TermTableRowData;
  }[] = makeTermTableRowDataList(displayTermsOfIDOL, IDOLBrand);
  const { receiveDividendsInETH, receiveDividendsInIDOL } = useLienToken();

  const [ethLoading, setETHLoading] = useState(true);
  const [idolLoading, setIDOLLoading] = useState(true);
  useEffect(() => {
    ethTableColumns.length > 0 && setETHLoading(false);
    idolTableColumns.length > 0 && setIDOLLoading(false);
  }, [ethTableColumns, idolTableColumns]); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div className="content_box_4">
      <SubTitle title="ETH" />
      <div className="box">
        <DividendTable rows={ethTableColumns} loading={ethLoading} />
        <br></br>
      </div>
      <TransactionButton
        name="Receive Commission"
        txFunc={() => {
          receiveDividendsInETH();
        }}
        disabled // TODO: check the enable receive commissions.
      />
      <SubTitle title="iDOL" />
      <div className="box">
        <DividendTable rows={idolTableColumns} loading={idolLoading} />
        <br></br>
      </div>
      <TransactionButton
        name="Receive Commission"
        txFunc={() => {
          receiveDividendsInIDOL();
        }}
        disabled // TODO: check the enable receive commissions.
      />
    </div>
  );
}

export default DividendContainer;
