import React, { useState, useEffect } from 'react';
import MultiTransactionModal from 'view/components/modal/MultiTransactionModal';
import {
  BondGroup,
  defaultLBT,
  defaultWaitingOrders,
  WaitingOrders,
  defaultExchangeData,
} from 'types';
import BigNumber from 'bignumber.js';
import useWrapper from 'actions/useWrapper';
import CompletedIssueLBTOnly from '../presentations/multitx/CompletedIssueLBTOnly';
import IssueLBTOnly from '../presentations/multitx/IssueLBTOnly';
import { Web3Utils } from 'libs/utils/web3Utils';
import { defaultTimeLimit } from 'config';
import { useFairSwap } from 'actions/useFairSwap';
import WaitingIDOL2LBT from '../presentations/multitx/WaitingIDOL2LBT';
import { GlobalState } from 'reducer/store';
import { useSelector, useDispatch } from 'react-redux';
import { SECOND } from 'resource/constants';

interface IssueLBTOnlyModalProps {
  isOpen: boolean;
  setIsOpen: any;
  bondGroup: BondGroup;
  inputETH: BigNumber;
  estimatedOutLBT: BigNumber;
}

export default (props: IssueLBTOnlyModalProps) => {
  const [isSending, setIsSending] = useState(false);
  const [steps, setSteps] = useState(0);
  const { issueLBTOnly } = useWrapper();
  const state = useSelector((state: GlobalState) => state);
  const dispatch = useDispatch();
  const web3Utils = new Web3Utils();
  const feeETH = web3Utils.feeETH(props.inputETH).toString();
  const { getLatestMyOrders, executeUnexecutedBox } = useFairSwap();
  const [txResponse, setTxResponse] = useState<boolean>(false);

  const exchangeData =
    state.base.exchangeDataMap.get(
      props.bondGroup.liquidBondList[0].bondAddress
    ) || defaultExchangeData();

  const exchangeAddress = exchangeData.exchangeAddress;
  const [yourOrder, setYourOrder] = useState<WaitingOrders>(
    defaultWaitingOrders()
  );
  useEffect(() => {
    if (txResponse) {
      const asyncSetYourOrder = async () => {
        const result = await getLatestMyOrders(exchangeAddress);
        setYourOrder(result);
      };
      setTimeout(() => asyncSetYourOrder(), SECOND);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    exchangeAddress,
    state.event.acceptOrdersListMap,
    state.baseMy.waitingOrdersList,
    txResponse,
  ]);
  // update waiting orders.
  useEffect(() => {
    dispatch({
      type: 'UPDATE_WAITING_ORDERS',
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [steps, props.isOpen]);
  const txFuncList = [
    async () => {
      setIsSending(true);
      const res = await issueLBTOnly(
        props.bondGroup.id,
        props.inputETH.toString(),
        props.bondGroup.liquidBondList[0].bondId,
        defaultTimeLimit.toString(),
        false // TODO: changeable?
      );
      setIsSending(false);
      if (res) {
        // use fairSwap.
        setSteps(1);
        setTxResponse(res);
      }
    },
    async () => {
      if (!yourOrder.isExecuted) {
        // waiting order and can force execute.
        setIsSending(true);
        const res = await executeUnexecutedBox(
          yourOrder.orderCount,
          exchangeAddress
        );
        setIsSending(false);
        if (res) {
          setSteps(2);
        }
      } else if (yourOrder.isExecuted) {
        // executed orders data
        setSteps(2);
      } // nothing to do for other cases
    },
  ];
  const txStatusList: string[] = [
    'Issue LBT',
    'FairSwap Execution',
    'Completed',
  ];
  const getIDOL = web3Utils
    .estimateGetIDOL(props.bondGroup.solidBond, props.inputETH)
    .toString();
  const getPooledIDOL = web3Utils
    .estimateGetPooledIDOL(props.bondGroup.solidBond, props.inputETH)
    .toString();
  const pooledExpired = props.bondGroup.maturity;
  const outETH = props.inputETH.dp(2).toString();
  const getLBT = props.estimatedOutLBT.dp(2).toString();
  const lbt =
    props.bondGroup.liquidBondList.length > 0
      ? props.bondGroup.liquidBondList[0]
      : defaultLBT();
  const tmp =
    state.base.exchangeDataMap.get(lbt.bondAddress) || defaultExchangeData();
  const totalLiquidity = tmp.baseTokenPool;
  const txComponentList = [
    <IssueLBTOnly
      key={0}
      feeETH={feeETH}
      usingEth={outETH}
      getLBT={getLBT}
      getIDOL={getIDOL}
      getPooledIDOL={getPooledIDOL}
      expired={pooledExpired}
      lbt={lbt}
      totalLiquidity={totalLiquidity}
    />,
    <WaitingIDOL2LBT
      key={1}
      isExecuted={yourOrder.isExecuted}
      boxCount={yourOrder.boxCount}
      orderCount={yourOrder.orderCount}
      exchangeAddress={exchangeAddress}
      isBuy={yourOrder.isBuy}
      mintedIDOL={yourOrder.tokenIn}
      trancheLBT={outETH}
      pooledIDOL={getPooledIDOL}
      expired={pooledExpired}
    />,
    <CompletedIssueLBTOnly
      key={2}
      feeETH={feeETH}
      usingEth={outETH}
      getLBT={getLBT}
      getIDOL={getIDOL}
      getPooledIDOL={getPooledIDOL}
      expired={pooledExpired}
      lbt={lbt}
      totalLiquidity={totalLiquidity}
    />,
  ];
  return (
    <MultiTransactionModal
      isOpen={props.isOpen}
      setIsOpen={props.setIsOpen}
      waitingFlag={1}
      waitingAble={!yourOrder.isExecuted}
      txFuncList={txFuncList}
      txStatusList={txStatusList}
      txComponentList={txComponentList}
      steps={steps}
      isSending={isSending}
    />
  );
};
