import React, { useState } from 'react';
import MultiTransactionModal from 'view/components/modal/MultiTransactionModal';
import { ExchangeData, LiquidBond } from 'types';
import BigNumber from 'bignumber.js';
import CompleteRemoveLiquidity from '../presentations/multitx/CompleteRemoveLiquidity';
import RemoveLiquidity from '../presentations/multitx/RemoveLiquidity';
import { GlobalState } from 'reducer/store';
import { useSelector } from 'react-redux';
import { useFairSwap } from 'actions/useFairSwap';
import CONTRACT_ADDR from 'libs/src/helper/Constant';
import { LienBrand, ETHBrand, LiquidBrand, IDOLBrand } from 'resource/brands';

interface RemoveLiquidityModalProps {
  isOpen: boolean;
  setIsOpen: any;
  shareAmount: BigNumber;
  exchangeData: ExchangeData;
  afterMaturity?: boolean;
}

export default (props: RemoveLiquidityModalProps) => {
  const [isSending, setIsSending] = useState(false);
  const [steps, setSteps] = useState(0);
  const { removeLiquidity, removeAfterMaturity } = useFairSwap();
  const bondMap = useSelector((state: GlobalState) => state.base.bondMap);
  const txFuncList = [
    async () => {
      setIsSending(true);
      const res = props.afterMaturity
        ? await removeAfterMaturity(props.exchangeData.exchangeAddress)
        : await removeLiquidity(props.shareAmount, props.exchangeData);
      setIsSending(false);
      if (res) {
        setSteps(1);
      }
    },
  ];
  const txStatusList: string[] = ['Remove Liquidity', 'Completed'];
  const returnBaseToken = props.exchangeData.baseTokenPerShare.multipliedBy(
    props.shareAmount
  );
  const returnSettlementToken = props.exchangeData.settlementTokenPerShare.multipliedBy(
    props.shareAmount
  );
  const lbt = bondMap.get(props.exchangeData.bondId || '');
  const settlementBrand =
    props.exchangeData.exchangeAddress === CONTRACT_ADDR.LIEN_EXCHANGE
      ? LienBrand
      : props.exchangeData.exchangeAddress === CONTRACT_ADDR.ETH_EXCHANGE
      ? ETHBrand
      : LiquidBrand;
  const txComponentList = [
    <RemoveLiquidity
      key={0}
      shareAmount={props.shareAmount}
      returnBaseToken={returnBaseToken}
      returnSettlementToken={returnSettlementToken}
      baseBrand={IDOLBrand}
      settlementBrand={settlementBrand}
      exchangeData={props.exchangeData}
      lbt={lbt as LiquidBond}
    />,
    <CompleteRemoveLiquidity
      key={1}
      shareAmount={props.shareAmount}
      returnBaseToken={returnBaseToken}
      returnSettlementToken={returnSettlementToken}
      baseBrand={IDOLBrand}
      settlementBrand={settlementBrand}
      exchangeData={props.exchangeData}
      lbt={lbt as LiquidBond}
    />,
  ];
  return (
    <MultiTransactionModal
      isOpen={props.isOpen}
      setIsOpen={props.setIsOpen}
      txFuncList={txFuncList}
      txStatusList={txStatusList}
      txComponentList={txComponentList}
      steps={steps}
      isSending={isSending}
    />
  );
};
