import { lien } from 'actions/useLien';
import { useDispatch, useSelector } from 'react-redux';
import { GlobalState } from 'reducer/store';

const useWrapper = () => {
  const dispatch = useDispatch();
  const sdk = useSelector((state: GlobalState) => state.sdk);

  const issueIDOLOnly = async (
    bondGroupId: number,
    inputETH: string,
    timeout: string,
    isLimit: boolean
  ) => {
    try {
      if (lien.address && sdk.lien) {
        const res = await lien.wrapper.issueIDOLOnly(
          bondGroupId,
          inputETH,
          timeout,
          isLimit
        );
        dispatch({
          type: 'ISSUE_IDOL_ONLY',
          payload: res,
        });
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
      return false;
    }
    return true;
  };

  const issueLBTOnly = async (
    bondGroupId: number,
    inputETH: string,
    liquidBondID: string,
    timeout: string,
    isLimit: boolean
  ) => {
    try {
      if (lien.address && sdk.lien) {
        const res = await lien.wrapper.issueLBTOnly(
          bondGroupId,
          inputETH.toString(),
          liquidBondID,
          timeout,
          isLimit
        );
        dispatch({
          type: 'ISSUE_LBT_ONLY',
          payload: res,
        });
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
      return false;
    }
    return true;
  };

  const issueLBTAndIDOL = async (bondGroupId: number, inputETH: string) => {
    try {
      if (lien.address && sdk.lien) {
        const res = await lien.wrapper.issueLBTAndIDOL(bondGroupId, inputETH);
        dispatch({
          type: 'ISSUE_LBT_AND_IDOL',
          payload: res,
        });
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
      return false;
    }
    return true;
  };

  return {
    issueIDOLOnly,
    issueLBTOnly,
    issueLBTAndIDOL,
  };
};

export default useWrapper;
