import React, { createContext, useCallback, useEffect } from 'react';
import { GlobalState } from 'reducer/store';
import useRecommend from 'actions/useRecommend';
import { defaultBondGroup } from 'types';
import { useDispatch, useSelector } from 'react-redux';

export const ConvertRecommendContext: any = createContext('');
export const ConvertRecommendProvider: React.FC = ({
  children,
}): JSX.Element => {
  const dispatch = useDispatch();
  const exchangeDataMap = useSelector(
    (state: GlobalState) => state.base.exchangeDataMap
  );
  const bondGroupMap = useSelector(
    (state: GlobalState) => state.base.bondGroupMap
  );
  const trancheRecommendBondGroupList = useSelector(
    (state: GlobalState) => state.recommend.trancheRecommendBondGroupList
  );

  const { getBestConvertPathBondGroup } = useRecommend();

  const dispatchConvertRecommend = useCallback(async () => {
    const baseBestBondGroup =
      trancheRecommendBondGroupList.length > 0
        ? trancheRecommendBondGroupList[0]
        : defaultBondGroup();
    const isExistExchangeAddress = exchangeDataMap.get(
      baseBestBondGroup.liquidBondList[0]?.bondAddress
    );
    const bestConvertPathBondGroup = getBestConvertPathBondGroup(
      bondGroupMap,
      exchangeDataMap
    );
    dispatch({
      type: 'SET_CONVERT_BOND_GROUP',
      payload: {
        convertLBTBondGroup: isExistExchangeAddress
          ? baseBestBondGroup
          : defaultBondGroup(),
        convertIDOLBondGroup: bestConvertPathBondGroup,
      },
    });
    }, [exchangeDataMap, bondGroupMap, trancheRecommendBondGroupList]); // eslint-disable-line

  useEffect(() => {
    dispatchConvertRecommend();
  }, [dispatchConvertRecommend]);

  return (
    <ConvertRecommendContext.Provider
      value={{ exchangeDataMap, bondGroupMap, trancheRecommendBondGroupList }}
    >
      {children}
    </ConvertRecommendContext.Provider>
  );
};
