import Contracts from '../helper/Contract';
import { Web3Utils } from 'libs/utils/web3Utils';
import { AuctionInstance } from 'libs/types/Auction';
import ERC20helper from 'libs/src/balance/ERC20Helper';
import aes256 from 'aes256';
import { getWeekID } from 'libs/utils/utils';

type AuctionMethodParameters<
  T extends keyof AuctionInstance['methods']
> = Parameters<AuctionInstance['methods'][T]>;

export default class Auction {
  private contracts: Contracts;
  private web3Utils: Web3Utils;
  private erc20Helper: ERC20helper;

  constructor(contracts: Contracts) {
    this.contracts = contracts;
    this.web3Utils = new Web3Utils();
    this.erc20Helper = new ERC20helper(contracts);
  }

  //===== Methods.sendTX =====

  public startAuction(...params: AuctionMethodParameters<'startAuction'>) {
    const funcData = this.contracts.auction?.methods.startAuction(...params);
    return this.contracts.sendTx(funcData);
  }

  public cancelBid(...params: AuctionMethodParameters<'cancelBid'>) {
    const funcData = this.contracts.auction?.methods.cancelBid(...params);
    return this.contracts.sendTx(funcData);
  }

  public makeAuctionResult(
    ...params: AuctionMethodParameters<'makeAuctionResult'>
  ) {
    const funcData = this.contracts.auction?.methods.makeAuctionResult(
      ...params
    );
    return this.contracts.sendTx(funcData);
  }

  public closeAuction(...params: AuctionMethodParameters<'closeAuction'>) {
    const funcData = this.contracts.auction?.methods.closeAuction(...params);
    return this.contracts.sendTx(funcData);
  }

  public receiveUnrevealedBidDistribution(
    ...params: AuctionMethodParameters<'receiveUnrevealedBidDistribution'>
  ) {
    const funcData = this.contracts.auction?.methods.receiveUnrevealedBidDistribution(
      ...params
    );
    return this.contracts.sendTx(funcData);
  }

  // ===== Methods.call ======

  public generateAuctionID(
    ...params: AuctionMethodParameters<'generateAuctionID'>
  ) {
    const funcData = this.contracts.auction?.methods.generateAuctionID(
      ...params
    );
    return funcData?.call() as any;
  }

  public generateSecret(...params: AuctionMethodParameters<'generateSecret'>) {
    const funcData = this.contracts.auction.methods.generateSecret(...params);
    return funcData?.call() as any;
  }

  public getCurrentAuctionID(
    ...params: AuctionMethodParameters<'getCurrentAuctionID'>
  ) {
    const funcData = this.contracts.auction?.methods.getCurrentAuctionID(
      ...params
    );
    return funcData?.call() as any;
  }

  public isAlreadyStarted(bondIDData: string) {
    const funcData = this.contracts.auction?.methods.isAlreadyStarted(
      bondIDData
    );
    return funcData?.call() as any;
  }

  // iterate isEmergency, closingTime, flag, auctionAmount, rewardedAmount and auctionAveragePrice
  public listAuction(...params: AuctionMethodParameters<'listAuction'>) {
    const funcData = this.contracts.auction?.methods.listAuction(...params);
    return funcData?.call();
  }

  public getWeeklyAuctionStatus(weekNumber: number) {
    const funcData = this.contracts.auction.methods.getWeeklyAuctionStatus(
      weekNumber
    );
    return funcData?.call() as any;
  }

  public listBondIDFromAuctionID(
    ...params: AuctionMethodParameters<'listBondIDFromAuctionID'>
  ) {
    const funcData = this.contracts.auction?.methods.listBondIDFromAuctionID(
      ...params
    );
    return funcData?.call() as string[];
  }

  public isInPeriod(...params: AuctionMethodParameters<'isInPeriod'>) {
    const funcData = this.contracts.auction?.methods.isInPeriod(...params);
    return funcData?.call() as any;
  }

  public async sign(message: string): Promise<string> {
    const accounts = await this.contracts.web3.eth.getAccounts();
    const s = await this.contracts.web3.eth.personal.sign(
      message,
      accounts[0],
      'lien'
    );
    return s;
  }

  async getAESKey(): Promise<string> {
    const message = 'lien-bid-auction-' + getWeekID().toString();
    const sign = await this.sign(message);
    return sign;
  }

  public async encryptBidSecret(secret: string): Promise<string> {
    const key = await this.getAESKey();
    const encrypted = aes256.encrypt(key, secret);
    return encrypted;
  }

  public async decryptBidSecret(encrypted: string): Promise<string> {
    const key = await this.getAESKey();
    const secret = aes256.decrypt(key, encrypted);
    return secret;
  }

  public async getTimeControlFlag(auctionID: string) {
    const funcData = await this.contracts.auction?.methods.getTimeControlFlag(
      auctionID
    );
    return funcData?.call() as any;
  }
}
