import React from 'react';
import './HeaderBalancePrice.scss';
import { PriceInfo, ExchangeRateInfo } from 'types';
import { Label3 } from 'baseui/typography';
import { useStyletron } from 'baseui';

interface ItemBalanceProps {
  price: PriceInfo;
  detail: string;
  exchangeRate: ExchangeRateInfo;
  SelectState: boolean;
}

export default (props: ItemBalanceProps) => {
  // eslint-disable-next-line
  const [css, theme] = useStyletron();
  const color = props.SelectState
    ? theme.colors.mono1000
    : theme.colors.primary400;
  return (
    <div className={props.price.brand.className}>
      <Label3 color={color}>
        {props.detail} &nbsp;{' '}
        {`${props.exchangeRate.rate} ${props.price.brand.name}/ETH`}
      </Label3>
    </div>
  );
};
