import React, { useState } from 'react';
import MultiTransactionModal from 'view/components/modal/MultiTransactionModal';
import { BondGroup, defaultLBT, defaultExchangeData } from 'types';
import BigNumber from 'bignumber.js';
import useWrapper from 'actions/useWrapper';
import CompleteIssueLBTAndIDOL from '../presentations/multitx/CompleteIssueLBTAndIDOL';
import IssueLBTAndIDOL from '../presentations/multitx/IssueLBTAndIDOL';
import { Web3Utils } from 'libs/utils/web3Utils';
import { GlobalState } from 'reducer/store';
import { useSelector } from 'react-redux';

interface IssueLBTAndIDOLModalProps {
  isOpen: boolean;
  setIsOpen: any;
  bondGroup: BondGroup;
  inputETH: BigNumber;
}

export default (props: IssueLBTAndIDOLModalProps) => {
  const [isSending, setIsSending] = useState(false);
  const [steps, setSteps] = useState(0);
  const { issueLBTAndIDOL } = useWrapper();
  const state = useSelector((state: GlobalState) => state);
  const web3Utils = new Web3Utils();
  const feeETH = web3Utils.feeETH(props.inputETH).toString();
  const txFuncList = [
    async () => {
      setIsSending(true);
      const res = await issueLBTAndIDOL(
        props.bondGroup.id,
        props.inputETH.toString()
      );
      setIsSending(false);
      if (res) {
        setSteps(1);
      }
    },
  ];
  const txStatusList: string[] = ['Issue LBT and iDOL', 'Completed'];
  const getIDOL = web3Utils.estimateGetIDOL(
    props.bondGroup.solidBond,
    props.inputETH
  );
  const getPooledIDOL = web3Utils.estimateGetPooledIDOL(
    props.bondGroup.solidBond,
    props.inputETH
  );
  const getTotalIDOL = getIDOL.plus(getPooledIDOL);
  const pooledExpired = props.bondGroup.maturity;
  const outETH = props.inputETH.dp(2).toString();
  const getLBT = outETH;
  const lbt =
    props.bondGroup.liquidBondList.length > 0
      ? props.bondGroup.liquidBondList[0]
      : defaultLBT();
  const exchangeData =
    state.base.exchangeDataMap.get(lbt.bondAddress) || defaultExchangeData();
  const totalLiquidity = exchangeData.baseTokenPool;
  const txComponentList = [
    <IssueLBTAndIDOL
      key={0}
      feeETH={feeETH}
      usingEth={outETH}
      getLBT={getLBT}
      getTotalIDOL={getTotalIDOL}
      getIDOL={getIDOL}
      getPooledIDOL={getPooledIDOL}
      expired={pooledExpired}
      lbt={lbt}
      totalLiquidity={totalLiquidity}
    />,
    <CompleteIssueLBTAndIDOL
      key={2}
      feeETH={feeETH}
      usingEth={outETH}
      getLBT={getLBT}
      getTotalIDOL={getTotalIDOL}
      getIDOL={getIDOL}
      getPooledIDOL={getPooledIDOL}
      expired={pooledExpired}
      lbt={lbt}
      totalLiquidity={totalLiquidity}
    />,
  ];
  return (
    <MultiTransactionModal
      isOpen={props.isOpen}
      setIsOpen={props.setIsOpen}
      txFuncList={txFuncList}
      txStatusList={txStatusList}
      txComponentList={txComponentList}
      steps={steps}
      isSending={isSending}
    />
  );
};
