import { BrandInfo, PriceInfo } from 'types';
import ETHIcon from 'resource/images/icon_eth.png';
import LienIcon from 'resource/images/icon_lien.png';
import LBTIcon from 'resource/images/icon_lbt.png';
import iDOLIcon from 'resource/images/icon_idol.png';
import BigNumber from 'bignumber.js';

function weiToEthView(price: string, effectiveDigit?: number): string {
  return new BigNumber(price).dp(4).toString() + ' ETH';
}

function weiToiDOLView(price: string, effectiveDigit?: number): string {
  return new BigNumber(price).dp(3).toString() + ' iDOL';
}

function valueToiDOLView(price: string): string {
  return price + ' iDOL';
}

function weiToUSDView(price: string, effectiveDigit?: number): string {
  return '$' + new BigNumber(price).dp(3).toString();
}

function weiToSolidView(price: string, effectiveDigit?: number): string {
  return price + ' SBT';
}

function weiToLiquidView(price: string, effectiveDigit?: number): string {
  return new BigNumber(price).dp(4).toString() + ' LBT';
}

function weiToLienView(price: string, effectiveDigit?: number): string {
  return new BigNumber(price).dp(4).toString() + ' LIEN';
}

function weiToiDOLPerLBT(price: string, effectiveDigit?: number): string {
  return price + ' iDOL/LBT';
}

function weiToPoolediDOLView(price: string, effectiveDigit?: number): string {
  return price + ' iDOL in Pool';
}

function weiToPoolediDOLViewFromPrice(
  price: PriceInfo,
  effectiveDigit?: number
): string {
  return price.value + ' iDOL(' + price.returnable + ' iDOL returnable)';
}

function weiToOracleETHPriceView(
  price: string,
  effectiveDigit?: number
): string {
  return `ETH/USD $${price}`;
}

function weiToOracleVolatilityView(
  price: string,
  effectiveDigit?: number
): string {
  return `Volatility ${new BigNumber(price).multipliedBy(100).dp(2)}%`;
}

