import React, { useEffect } from 'react';
import SubTitle from '../SubTitle';
import SubItem from '../SubItem';
import { WaitingOrders } from 'types';
import WaitingOrdersProgressBar from '../WaitingOrdersProgressBar';
import ExchangeBox from 'view/components/presentations/ExchangeBox';
import { useFairSwap } from 'actions/useFairSwap';
import ItemPrice from '../ItemPrice';
import { useDispatch } from 'react-redux';

interface ForceExecuteOrdersProps {
  waitingOrders: WaitingOrders;
}

export default (props: ForceExecuteOrdersProps) => {
  const { convertExchangeInfoFromOrders } = useFairSwap();
  const dispatch = useDispatch();
  const exchangeInfo = convertExchangeInfoFromOrders(
    props.waitingOrders.exchangeAddress,
    props.waitingOrders.isBuy
  );
  useEffect(() => {
    dispatch({
      type: 'UPDATE_WAITING_ORDERS',
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.waitingOrders]);
  return (
    <div className="content_box_modal">
      <SubTitle title="FairSwap execution" />
      <div className="box">
        <SubItem name="Exchange Rate" />
        <ExchangeBox exchangeInfo={exchangeInfo} />
        <ItemPrice
          name="From"
          value={props.waitingOrders.tokenIn.toString()}
          brand={exchangeInfo.fromBrand}
        />
        <ItemPrice
          name="To"
          value={exchangeInfo.rate
            .multipliedBy(props.waitingOrders.tokenIn)
            .toString()}
          brand={exchangeInfo.toBrand}
        />
        <hr />
        <WaitingOrdersProgressBar
          isExecuted={props.waitingOrders.isExecuted}
          boxCount={props.waitingOrders.boxCount}
          orderCount={props.waitingOrders.orderCount}
        />
      </div>
    </div>
  );
};
