import React, { useState } from 'react';
import './FormNumber.scss';
import { FormControl } from 'baseui/form-control';
import { Input, SIZE } from 'baseui/input';
import { Button } from 'baseui/button';
import { Block } from 'baseui/block';
import { isValidBalance } from 'libs/utils/validate';
import BigNumber, { BigNumber as BN } from 'bignumber.js';

interface FormNumberProps {
  unit: string;
  disableMaxButton?: boolean;
  disableInput?: boolean;
  maxValue?: BigNumber;
  value: BN;
  setValue: (value: BN) => void;
  caption?: any;
  label?: string;
  errorMessage?: string;
  errorState?: boolean;
  setIsVisited?(IsVisited: boolean): void;
}

function FormNumber(props: FormNumberProps) {
  const isValid = isValidBalance(
    props.value,
    props.maxValue || new BigNumber(1e20)
  );
  const [value, setValue] = useState('');
  if (props.value.toString() !== new BigNumber(value).toString())
    setValue(props.value.toString());
  const shouldShowError = !isValid && props.value.toString() !== '0';
  return (
    <div className="form">
      <div className="form_number">
        <FormControl
          error={shouldShowError ? props.errorMessage : undefined}
          label={props.label}
          caption={() => props.caption}
        >
          <Input
            error={!props.errorState ? false : shouldShowError}
            size={SIZE.compact}
            value={value}
            type="number"
            onChange={(e) => {
              setValue(e.currentTarget.value);
              props.setValue(new BN(e.currentTarget.value));
            }}
            endEnhancer={() => <Block>{props.unit}</Block>}
            disabled={props.disableInput}
            placeholder={'0.00'}
          />
        </FormControl>
        {!props.disableMaxButton && props.maxValue && (
          <Block>
            <Button
              onClick={(e) =>
                props.setValue(props.maxValue || new BigNumber(0))
              }
              size={SIZE.compact}
            >
              MAX
            </Button>
          </Block>
        )}
      </div>
    </div>
  );
}

export default FormNumber;
