import React, { useState } from 'react';
import { GlobalState } from 'reducer/store';
import { WaitingOrders, defaultWaitingOrders } from 'types';
import WaitingOrdersNotification from '../presentations/WaitingOrdersNotification';
import ForceExecuteModal from '../modal/ForceExecuteModal';
import { useSelector } from 'react-redux';

export default () => {
  const waitingOrdersList = useSelector(
    (state: GlobalState) => state.baseMy.waitingOrdersList
  );
  const [isOpen, setIsOpen] = useState(false);
  const [currentWaitingOrders, setCurrentWaitingOrders] = useState(
    defaultWaitingOrders()
  );
  const notifications =
    waitingOrdersList.length > 0
      ? waitingOrdersList.map((waitingOrders: WaitingOrders, index: number) => (
          <WaitingOrdersNotification
            key={index}
            waitingOrders={waitingOrders}
            onClick={() => {
              setIsOpen(true);
              setCurrentWaitingOrders(waitingOrders);
            }}
          />
        ))
      : '';
  return (
    <div id="content_box">
      {notifications}
      {isOpen && (
        <ForceExecuteModal
          isOpen={isOpen}
          setIsOpen={setIsOpen}
          waitingOrders={currentWaitingOrders}
        />
      )}
    </div>
  );
};
