import React from 'react';
import { ProgressBar } from 'baseui/progress-bar';
interface WaitingOrdersProgressBarProps {
  isExecuted: boolean;
  boxCount: number;
  orderCount: number;
}
export default (props: WaitingOrdersProgressBarProps) => {
  const progressValue = props.isExecuted
    ? 100
    : props.boxCount === -1
    ? 0
    : Math.max(100 - Math.sqrt(props.orderCount / 5) * 10, 0);
  const progressMessage = props.isExecuted
    ? 'The order has been completed!'
    : props.boxCount === -1
    ? 'The order has not been completed'
    : `Please wait for ${props.boxCount} box and ${props.orderCount} order to be executed.`;
  return (
    <ProgressBar
      value={progressValue}
      getProgressLabel={(_) => progressMessage}
      showLabel
    />
  );
};
