import React from 'react';
import { Button } from 'baseui/button';

interface TransactionButtonProps {
  name: string;
  disabled?: boolean;
  // TODO txFunc
  txFunc?: () => void;
}

function TransactionButton(props: TransactionButtonProps) {
  return (
    <div className="button">
      <Button
        onClick={(e) => (props.txFunc ? props.txFunc() : undefined)}
        disabled={props.disabled}
        overrides={{
          BaseButton: {
            style: () => {
              return {
                display: 'block',
                textDecoration: 'none',
                border: '1px solid #0A8',
                backgroundColor: '#CED',
                padding: '1px',
                borderRadius: '2px',
                width: '100%',
              };
            },
          },
        }}
      >
        <span aria-disabled={props.disabled}>{props.name}</span>
      </Button>
    </div>
  );
}

export default TransactionButton;
