import { lien } from 'actions/useLien';

const useOracle = () => {
  // ==== oracle Hooks ====

  const latestPrice = async () => {
    if (lien.oracle) {
      return await lien.oracle.latestPrice();
    }
  };

  const getVolatility = async () => {
    if (lien.oracle) {
      return await lien.oracle.getVolatility();
    }
  };

  return { latestPrice, getVolatility };
};

export default useOracle;
