import React, { useRef } from 'react';
import './HeaderHamburger.scss';
import useLien from 'actions/useLien';
import { Link } from 'react-router-dom';
import { SNSLinks } from 'resource/links';

function Hamburger() {
  const { connect } = useLien();
  const snsLinks = SNSLinks.map((link, i) => (
    <a
      href={link.url}
      key={'link' + i}
      target="_blank"
      rel="noopener noreferrer"
    >
      {link.name}
    </a>
  ));
  const menuSwitch = useRef<HTMLInputElement>(null);
  const closeMenu = () => {
    if (menuSwitch && menuSwitch.current) menuSwitch.current.checked = false;
  };
  return (
    <div id="header_box_r_mobile">
      <input
        id="header_box_r_mobile_switch"
        name="header_box_r_mobile_switch"
        type="checkbox"
        ref={menuSwitch}
      />
      <label
        id="header_box_r_mobile_button"
        htmlFor="header_box_r_mobile_switch"
      ></label>
      <div id="header_box_r_mobile_nav">
        <label onClick={closeMenu}>
          <Link to="/">Convert</Link>
          <Link to="/trade">Trade</Link>
          <Link to="/tranche">Tranche</Link>
          <Link to="/auction">Auction</Link>
          <Link to="/balance">Balance</Link>
          <hr />
          <div id="header_box_r_mobile_nav_wallet" onClick={connect}>
            <span>My Wallet</span>
            <span>000000000000000000000000000000000000000000</span>
          </div>
          <hr />
          {snsLinks}
        </label>
      </div>
    </div>
  );
}

export default Hamburger;
