import Web3 from 'web3';
declare global {
  interface Window {
    web3: Web3;
    ethereum: any;
  }
}

export class GetProvider {
  private providerGeted: boolean;

  constructor() {
    this.providerGeted = false;
  }

  // ====== Get Provider Function ======

  public async getBrowserProvider(): Promise<Web3> {
    const ethereum = window.ethereum;
    if (window.ethereum) {
      const web3 = new Web3(ethereum);
      await ethereum.enable();
      this.providerGeted = true;
      return web3;
    } else if (window.web3) {
      const web3 = new Web3(window.web3.currentProvider);
      this.providerGeted = true;
      return web3;
    } else {
      throw new Error(
        'Non-Ethereum browser detected. You should consider trying MetaMask!'
      );
    }
  }

  public async getCurrentAccountAddress() {
    if (this.providerGeted) {
      return window.web3.eth.defaultAccount;
    } else {
      throw new Error('Non-Ethereum Address.');
    }
  }

  public getEthereumProviderApi() {
    const ethereum = window.ethereum;
    return ethereum;
  }
}
