import React from 'react';
import useAuction from 'actions/useAuction';
import { H4 } from 'baseui/typography';
import { Button, SIZE } from 'baseui/button';
import { Input } from 'baseui/input';
function Operation() {
  const {
    startAuction,
    getUnsortedBidPrice,
    sortBidPrice,
    makeEndInfo,
  } = useAuction();
  const [startBondID, setStartBondID] = React.useState('No BondID');
  const [UnsortedBidPrice, setUnsortedBidPrice] = React.useState([]);
  const getSortedBidList = () => {
    const UnsortedBidPriceAsync = async () => {
      setUnsortedBidPrice(await getUnsortedBidPrice(startBondID));
    };
    UnsortedBidPriceAsync();
    UnsortedBidPrice.sort();
    return UnsortedBidPrice;
  };
  return (
    <div id="content_box">
      <H4>Operation Temporary</H4>
      <br />
      <Input
        value={startBondID}
        onChange={(event) => setStartBondID(event.currentTarget.value)}
        placeholder="Auction ID"
        positive
        size={SIZE.compact}
      />
      <Button
        size={SIZE.compact}
        onClick={() => startAuction(startBondID, false)}
      >
        Auction start
      </Button>
      <Button size={SIZE.compact} onClick={() => getSortedBidList()}>
        getSortedBidList
      </Button>
      <Button
        size={SIZE.compact}
        onClick={() => sortBidPrice(startBondID, UnsortedBidPrice)}
      >
        sortBidPrice
      </Button>
      <Button size={SIZE.compact} onClick={() => makeEndInfo(startBondID)}>
        makeEndInfo
      </Button>
      <br></br>
    </div>
  );
}
export default Operation;
