import web3 from 'web3';
import { FlattenLineSegment, SolidBond } from 'types';
import { BigNumber as BN } from 'bignumber.js';
import {
  LOCK_POOL_PERCENT,
  LOCK_POOL_WITHOUT_PERCENT,
  LARGISH_GAS_COST,
  LIEN_FEE_PERCENT,
} from 'resource/constants';

export class Web3Utils {
  private web3: web3;
  constructor() {
    this.web3 = new web3();
  }

  public encodeUint256(value: any) {
    return this.web3.eth.abi.encodeParameter('uint256', value);
  }

  public toHex(value: any) {
    return this.web3.utils.toHex(value);
  }

  public toWei(value: any) {
    return this.web3.utils.toWei(value, 'ether');
  }

  public fromWei(value: any) {
    return this.web3.utils.fromWei(value, 'ether');
  }

  public fromAscii(value: any) {
    return this.web3.utils.fromAscii(value);
  }

  public fromSec(value: number) {
    return value * 1000;
  }

  public fromStrikePrice(solidStrikePrice: number) {
    return new BN(solidStrikePrice).div(new BN(10000));
  }

  public numberToHex(value: any) {
    return this.web3.utils.numberToHex(value);
  }

  public toBN(value: any) {
    return this.web3.utils.toBN(value);
  }

  public encodeBytes32(value: any) {
    return this.web3.eth.abi.encodeParameter('bytes32', value);
  }

  public decodeBytes32(value: any) {
    return this.web3.eth.abi.decodeParameter('bytes32', value);
  }

  public fnMapToLineSegment(fnMap: string, decimal = 4): FlattenLineSegment[] {
    const ten = new BN(10);
    const lineSegments = this.web3.eth.abi.decodeParameter(
      'bytes32[]',
      fnMap
    ) as string[];

    return lineSegments.map((lineSegment) => {
      const x1 = this.web3.eth.abi.decodeParameter(
        'uint64',
        '0x' + lineSegment.slice(-64, -48).padStart(64, '0')
      );
      const y1 = this.web3.eth.abi.decodeParameter(
        'uint64',
        '0x' + lineSegment.slice(-48, -32).padStart(64, '0')
      );
      const x2 = this.web3.eth.abi.decodeParameter(
        'uint64',
        '0x' + lineSegment.slice(-32, -16).padStart(64, '0')
      );
      const y2 = this.web3.eth.abi.decodeParameter(
        'uint64',
        '0x' + lineSegment.slice(-16).padStart(64, '0')
      );

      return [
        ten
          .pow(-decimal)
          .times(x1 as BN)
          .toNumber(),
        ten
          .pow(-decimal)
          .times(y1 as BN)
          .toNumber(),
        ten
          .pow(-decimal)
          .times(x2 as BN)
          .toNumber(),
        ten
          .pow(-decimal)
          .times(y2 as BN)
          .toNumber(),
      ] as FlattenLineSegment;
    });
  }

  // isSolid
  // (0, 0) -> (a, a)
  // (a, a) -> (b, a)
  public isSolid(lines: FlattenLineSegment[]) {
    return (
      lines[0][2] === lines[0][3] &&
      lines[1][1] === lines[1][3] &&
      lines.length === 2
    );
  }

  public estimateGetIDOL(bond: SolidBond, amount: BN): BN {
    return bond.strikePrice
      .multipliedBy(amount)
      .multipliedBy(LOCK_POOL_WITHOUT_PERCENT);
  }

  public estimateGetPooledIDOL(bond: SolidBond, amount: BN): BN {
    return bond.strikePrice
      .multipliedBy(amount)
      .multipliedBy(LOCK_POOL_PERCENT);
  }

  public recommendMaxETH(maxETH: BN): BN {
    const recommended = maxETH
      .minus(LARGISH_GAS_COST)
      .dividedBy(new BN(1).plus(LIEN_FEE_PERCENT))
      .dp(1, BN.ROUND_DOWN);
    return recommended.lt(new BN(0)) ? new BN(0) : recommended;
  }

  public feeIncludedETH(eth: BN): BN {
    return eth.plus(this.feeETH(eth));
  }

  public feeETH(eth: BN): BN {
    return eth.multipliedBy(LIEN_FEE_PERCENT);
  }
}
