import { useDispatch, useSelector } from 'react-redux';
import { lien } from 'actions/useLien';
import { GlobalState } from 'reducer/store';

const useBalance = () => {
  const dispatch = useDispatch();
  const sdk = useSelector((state: GlobalState) => state.sdk);

  const burnAll = async (tokenAddr: string) => {
    try {
      if (lien.address && sdk.lien) {
        const res = await lien.bondToken.burnAll(tokenAddr);
        dispatch({
          type: 'BURN_ALL',
          payload: {
            txHash: res,
          },
        });
      }
      //TODO: add Transaction Manager & Notification
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  };

  const returnLockedPool = async (poolID: string, wrapper?: boolean) => {
    try {
      if (lien.address && sdk.lien) {
        if (wrapper) {
          const res = await lien.wrapper.returnLockedPoolUsers(poolID);
          dispatch({
            type: 'RETURN_LOCKED_POOL',
            payload: {
              txHash: res,
            },
          });
        } else {
          const res = await lien.stableCoin.returnLockedPool([poolID]);
          dispatch({
            type: 'RETURN_LOCKED_POOL',
            payload: {
              txHash: res,
            },
          });
        }
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  };

  return { burnAll, returnLockedPool };
};

export default useBalance;
