import Contracts from 'libs/src/helper/Contract';
import { Web3Utils } from 'libs/utils/web3Utils';
import { BigNumber as BN } from 'bignumber.js';
import ERC20helper from 'libs/src/balance/ERC20Helper';

export default class LienToken {
  private contracts: Contracts;
  private web3Utils: Web3Utils;
  private erc20Helper: ERC20helper;

  constructor(contracts: Contracts) {
    this.contracts = contracts;
    this.web3Utils = new Web3Utils();
    this.erc20Helper = new ERC20helper(contracts);
  }

  // ===== Methods.sendTx =====

  public async settleProfit(tokenAddr: string) {
    const funcData = this.contracts.lienToken.methods.settleProfit(tokenAddr);
    return await this.contracts.sendTx(funcData);
  }

  public async settleProfitOfETH() {
    const funcData = this.contracts.lienToken.methods.settleProfitOfEth();
    return await this.contracts.sendTx(funcData);
  }

  public async receiveDividend(tokenAddr: string, account: string) {
    const funcData = this.contracts.lienToken.methods.receiveDividend(
      tokenAddr,
      account
    );
    return await this.contracts.sendTx(funcData);
  }

  public async receiveDividendOfETH(account: string) {
    const funcData = this.contracts.lienToken.methods.receiveDividendOfEth(
      account
    );
    return await this.contracts.sendTx(funcData);
  }

  // ===== Methods.call =====

  public async currentTerm(): Promise<BN> {
    return new BN(await this.contracts.lienToken.methods.currentTerm().call());
  }

  public async initialTime(): Promise<BN> {
    return new BN(await this.contracts.lienToken.methods.initialTime().call());
  }

  public async interval(): Promise<BN> {
    return new BN(await this.contracts.lienToken.methods.interval().call());
  }

  public async expiration(): Promise<BN> {
    return new BN(await this.contracts.lienToken.methods.expiration().call());
  }

  public async lastTokenReceived(
    tokenAddr: string,
    account: string
  ): Promise<BN> {
    return new BN(
      await this.contracts.lienToken.methods
        .lastTokenReceived(account, tokenAddr)
        .call()
    );
  }

  public async lastETHReceived(account: string): Promise<BN> {
    return new BN(
      await this.contracts.lienToken.methods.lastEthReceived(account).call()
    );
  }

  public async startOfTerm(termID: BN): Promise<BN> {
    return new BN(
      await this.contracts.lienToken.methods
        .startOfTerm(termID.toString())
        .call()
    );
  }

  public async endOfTerm(termID: BN): Promise<BN> {
    return new BN(
      await this.contracts.lienToken.methods.endOfTerm(termID.toString()).call()
    );
  }

  public async balanceAt(account: string, termID: BN): Promise<BN> {
    const funcData = this.contracts.lienToken.methods.balanceOfAtTermEnd(
      account,
      termID.toString()
    );
    const balance = await funcData.call();
    return this.erc20Helper.fromAmount(this.contracts.lienTokenAddr, balance);
  }

  public async totalSupplyAt(termID: BN): Promise<BN> {
    const funcData = this.contracts.lienToken.methods.totalSupplyAtTermEnd(
      termID.toString()
    );
    const balance = await funcData.call();
    return this.erc20Helper.fromAmount(this.contracts.lienTokenAddr, balance);
  }

  public async profitAt(tokenAddr: string, termID: BN): Promise<BN> {
    const funcData = this.contracts.lienToken.methods.profitAt(
      tokenAddr,
      termID.toString()
    );
    const balance = await funcData.call();
    return await this.erc20Helper.fromAmount(
      this.contracts.stableCoinAddr,
      balance
    );
  }

  public async profitOfETHAt(termID: BN): Promise<BN> {
    const funcData = this.contracts.lienToken.methods.profitOfEthAt(
      termID.toString()
    );
    const balance = await funcData.call();
    return new BN(this.web3Utils.fromWei(balance));
  }

  public async dividendAt(
    tokenAddr: string,
    account: string,
    termID: BN
  ): Promise<BN> {
    const funcData = this.contracts.lienToken.methods.dividendAt(
      tokenAddr,
      account,
      termID.toString()
    );
    const balance = await funcData.call();
    return this.erc20Helper.fromAmount(this.contracts.stableCoinAddr, balance);
  }

  public async dividendOfETHAt(account: string, termID: BN): Promise<BN> {
    const funcData = this.contracts.lienToken.methods.dividendOfEthAt(
      account,
      termID.toString()
    );
    const balance = await funcData.call();
    return new BN(this.web3Utils.fromWei(balance));
  }
}
