import Contracts from './helper/Contract';
import BondMaker from './bondMaker/BondMaker';
import StableCoin from './stablecoin/StableCoin';
import Balance from './balance/GetBalance';
import Wrapper from './wrapper/Wrapper';
import ERC20 from 'libs/src/balance/ERC20Helper';
import Auction from 'libs/src/auction/Auction';
import AuctionBoard from 'libs/src/auction/AuctionBoard';
import FairSwap from './trade/FairSwap';
import { GetProvider } from './helper/GetProvider';
import { Web3Utils } from 'libs/utils/web3Utils';
import BondToken from './balance/BondTokenHelper';
import LienToken from './lienToken/LienToken';
import LIENFairSwap from './lienToken/LIENFairSwap';
import Subscribe from './subscribe/eventSubscriber';
import Oracle from './oracle/Oracle';

export class Lien {
  public contracts: Contracts;
  public auction: Auction;
  public auctionBoard: AuctionBoard;
  public bondMaker: BondMaker;
  public stableCoin: StableCoin;
  public wrapper: Wrapper;
  public balance: Balance;
  public fairSwap: FairSwap;
  public currentProvider: any;
  public wsProvider: any;
  public setuped: boolean;
  public getProvider: GetProvider;
  public address: any;
  public utils: Web3Utils;
  public ERC20: ERC20;
  public bondToken: BondToken;
  public lienToken: LienToken;
  public lienFairSwap: LIENFairSwap;
  public subscribe: Subscribe;
  public oracle: Oracle;

  constructor() {
    this.setuped = false;
    this.getProvider = new GetProvider();
    this.contracts = new Contracts();
    this.subscribe = new Subscribe(this.contracts);
    this.auction = new Auction(this.contracts);
    this.auctionBoard = new AuctionBoard(this.contracts);
    this.stableCoin = new StableCoin(this.contracts);
    this.bondMaker = new BondMaker(this.contracts);
    this.fairSwap = new FairSwap(this.contracts);
    this.wrapper = new Wrapper(this.contracts);
    this.utils = new Web3Utils();
    this.balance = new Balance(this.contracts);
    this.ERC20 = new ERC20(this.contracts);
    this.bondToken = new BondToken(this.contracts);
    this.lienToken = new LienToken(this.contracts);
    this.lienFairSwap = new LIENFairSwap(this.contracts);
    this.oracle = new Oracle(this.contracts);
  }

  public async setup() {
    this.setuped = true;
    this.currentProvider = await this.getProvider.getBrowserProvider();
    this.address = await this.getProvider.getCurrentAccountAddress();
    await this.contracts.setWeb3(this.currentProvider, this.address);
    return this.currentProvider;
  }

  public resetAccount() {
    this.address = undefined;
  }
}
