import React from 'react';
import './HeaderBoxRight.scss';
// import Bell from 'view/components/presentations/HeaderBell';
import Balance from 'view/components/presentations/HeaderBalance';
import Wallet from 'view/components/presentations/HeaderWallet';
import Hamburger from 'view/components/presentations/HeaderHamburger';
import useLien, { lien } from 'actions/useLien';
import { GlobalState } from 'reducer/store';
import { useSelector } from 'react-redux';

function BoxRight() {
  const { getAddress, disconnect, connect } = useLien();
  const sdk = useSelector((state: GlobalState) => state.sdk);
  return (
    <div id="header_box_r">
      {/* <Bell /> */}

      <Balance />
      <Wallet
        cureentAddress={
          lien.address === undefined || sdk.web3 === undefined
            ? undefined
            : getAddress()
        }
        label={'Connect Wallet'}
        disconnect={disconnect}
        connect={connect}
        isNetwork={true}
      />
      <Hamburger />
    </div>
  );
}

export default BoxRight;
