import React from 'react';
import './RadioSolid.scss';

interface RadioSolidProps {
  only?: boolean;
  selected: string;
  setIDOL: (e: React.ChangeEvent<HTMLInputElement>) => void;
  setSBT: (e: React.ChangeEvent<HTMLInputElement>) => void;
}

function RadioSolid(props: RadioSolidProps) {
  return (
    <div className="radio">
      {!props.only && (
        <div className="item">
          <input
            type="radio"
            name="radio1"
            id="radio1_1"
            onChange={props.setIDOL}
            checked={props.selected === 'idol'}
          />
          <label htmlFor="radio1_1">iDOL</label>
        </div>
      )}
      <div className="item">
        {!props.only && (
          <input
            type="radio"
            name="radio1"
            id="radio1_2"
            onChange={props.setSBT}
            checked={props.selected === 'sbt'}
          />
        )}
        {!!props.only && (
          <input
            type="radio"
            name="radio1"
            id="radio1_2"
            onChange={props.setSBT}
            checked={props.selected === 'sbt'}
          />
        )}
        <label htmlFor="radio1_2">SBT</label>
      </div>
    </div>
  );
}

export default RadioSolid;
