import Contracts from '../helper/Contract';
import { OracleInterfaceInstance as OracleInterface } from 'libs/types/OracleInterface';
import ERC20helper from 'libs/src/balance/ERC20Helper';

type OracleMethodParameters<
  T extends keyof OracleInterface['methods']
> = Parameters<OracleInterface['methods'][T]>;

export default class Oracle {
  private contracts: Contracts;
  private erc20Helper: ERC20helper;

  constructor(contracts: Contracts) {
    this.contracts = contracts;
    this.erc20Helper = new ERC20helper(contracts);
  }

  public async latestPrice(...params: OracleMethodParameters<'latestPrice'>) {
    const funcData = this.contracts.oracle?.methods.latestPrice(...params);
    return this.erc20Helper.fromAmount8(await funcData?.call());
  }

  public async getVolatility(
    ...params: OracleMethodParameters<'getVolatility'>
  ) {
    const funcData = this.contracts.oracle?.methods.getVolatility(...params);
    return this.erc20Helper.fromAmount8(await funcData.call());
  }
}
