import React from 'react';
import './WithIcon.scss';
import { Button, SIZE, KIND } from 'baseui/button';
import { useStyletron } from 'baseui';
import {
  Unstable_StatefulDataTable as StatefulDataTable,
  NumericalColumn,
  CustomColumn,
  NUMERICAL_FORMATS,
} from 'baseui/data-table';
import BigNumber from 'bignumber.js';
import { LiquidBrand, ETHBrand } from 'resource/brands';
import NoData from './NoData';
import GettingData from './GettingData';
import MaturityCell from './MaturityCell';
import LeverageTag from './LeverageTag';
import { isExpired, dateSortFn } from 'libs/utils/utils';
export interface MyLBTRowData {
  bondId: string;
  maturity: Date;
  strikePrice: BigNumber;
  theoreticalPrice: BigNumber;
  redeemETH: BigNumber;
  leverage?: BigNumber;
  balance: BigNumber;
  isBurnable: boolean;
  onBurn: () => void;
  onTrade: () => void;
}
const columns = [
  CustomColumn({
    title: 'Maturity',
    mapDataToValue: (data: MyLBTRowData) => data.maturity,
    renderCell: function Cell(props: { value: Date }) {
      return <MaturityCell value={props.value} />;
    },
    sortFn: dateSortFn,
    sortable: true,
  }),
  NumericalColumn({
    title: 'Strike Price',
    mapDataToValue: (data: MyLBTRowData) => data.strikePrice,
    format: NUMERICAL_FORMATS.ACCOUNTING,
  }),
  CustomColumn({
    title: 'Theoretical Price',
    precision: 2,
    mapDataToValue: (data: MyLBTRowData) => data,
    renderCell: function Cell(props: any) {
      if (props.value.isBurnable) {
        return (
          <div className={ETHBrand.className}>
            {ETHBrand.view(props.value.redeemETH.toString())}
          </div>
        );
      } else {
        return `$${props.value.theoreticalPrice.dp(3).toString()}`;
      }
    },
  }),
  CustomColumn({
    title: 'Balance',
    precision: 2,
    mapDataToValue: (data: MyLBTRowData) => data.balance.dp(3).toString(),
    renderCell: function Cell(props: any) {
      return <div className={LiquidBrand.className}>{props.value}</div>;
    },
  }),
  CustomColumn({
    title: 'Leverage',
    precision: 2,
    mapDataToValue: (data: MyLBTRowData) => data.leverage,
    renderCell: function Cell(props: any) {
      return <LeverageTag leverage={props.value} />;
    },
  }),
  CustomColumn({
    title: '',
    precision: 2,
    mapDataToValue: (data: MyLBTRowData) => data,
    renderCell: function Cell(props: any) {
      const [css] = useStyletron();
      const buttonComponent = isExpired(props.value.maturity) ? (
        <Button
          kind={KIND.primary}
          size={SIZE.compact}
          onClick={(e) => props.value.onBurn()}
          disabled={!props.value.isBurnable}
        >
          Redeem
        </Button>
      ) : (
        <Button
          kind={KIND.primary}
          size={SIZE.compact}
          onClick={(e) => props.value.onTrade()}
        >
          Trade
        </Button>
      );
      return (
        <div
          className={css({
            alignItems: 'center',
            display: 'flex',
          })}
        >
          {buttonComponent}
        </div>
      );
    },
  }),
];

export interface MyLBTListTableProps {
  rows: { id: any; data: MyLBTRowData }[];
  loading: boolean;
}

export default (props: MyLBTListTableProps) => {
  return (
    <div>
      <div style={{ height: `${props.rows.length * 60 + 170}px` }}>
        <StatefulDataTable
          columns={columns}
          rows={props.rows}
          rowHeight={60}
          loading={props.loading}
          loadingMessage={() => <GettingData />}
          emptyMessage={() => <NoData />}
        />
      </div>
    </div>
  );
};
