import React, { useState, useEffect } from 'react';
import MultiTransactionModal from 'view/components/modal/MultiTransactionModal';
import {
  BondGroup,
  defaultWaitingOrders,
  WaitingOrders,
  defaultExchangeData,
} from 'types';
import BigNumber from 'bignumber.js';
import useWrapper from 'actions/useWrapper';
import CompletedIssueIDOLOnly from '../presentations/multitx/CompletedIssueIDOLOnly';
import IssueIDOLOnly from '../presentations/multitx/IssueIDOLOnly';
import { Web3Utils } from 'libs/utils/web3Utils';
import { defaultTimeLimit } from 'config';
import { ConvertInfoGroupRows } from '../presentations/ConvertInfoUnitTable';
import WaitingLBT2IDOL from '../presentations/multitx/WaitingLBT2IDOL';
import { useFairSwap } from 'actions/useFairSwap';
import { GlobalState } from 'reducer/store';
import { useDispatch, useSelector } from 'react-redux';
import { SECOND } from 'resource/constants';

interface IssueIDOLOnlyModalProps {
  isOpen: boolean;
  setIsOpen: any;
  bondGroup: BondGroup;
  inputETH: BigNumber;
  estimatedOutIDOL: BigNumber;
  recommendedConvertInfo: ConvertInfoGroupRows[];
}

export default (props: IssueIDOLOnlyModalProps) => {
  const [isSending, setIsSending] = useState(false);
  const [steps, setSteps] = useState(0);
  const { issueIDOLOnly } = useWrapper();
  const [txResponse, setTxResponse] = useState<any>(false);
  const exchangeDataMap = useSelector(
    (state: GlobalState) => state.base.exchangeDataMap
  );
  const waitingOrdersList = useSelector(
    (state: GlobalState) => state.baseMy.waitingOrdersList
  );
  const acceptOrdersListMap = useSelector(
    (state: GlobalState) => state.event.acceptOrdersListMap
  );

  const dispatch = useDispatch();
  const { getLatestMyOrders, executeUnexecutedBox } = useFairSwap();

  const web3Utils = new Web3Utils();
  const feeETH = web3Utils.feeETH(props.inputETH).toString();
  const isLimit = false; // TODO: changeable?

  const exchangeData =
    exchangeDataMap.get(props.bondGroup.liquidBondList[0].bondAddress) ||
    defaultExchangeData();
  const exchangeAddress = exchangeData.exchangeAddress;

  const [yourOrder, setYourOrder] = useState<WaitingOrders>(
    defaultWaitingOrders()
  );

  useEffect(() => {
    if (txResponse) {
      const asyncSetYourOrder = async () => {
        const result = await getLatestMyOrders(exchangeAddress);
        setYourOrder(result);
      };
      setTimeout(() => asyncSetYourOrder(), SECOND);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [exchangeAddress, acceptOrdersListMap, waitingOrdersList, txResponse]);

  // update waiting orders.
  useEffect(() => {
    dispatch({
      type: 'UPDATE_WAITING_ORDERS',
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [steps, props.isOpen]);
  const txFuncList = [
    async () => {
      setIsSending(true);
      const res = await issueIDOLOnly(
        props.bondGroup.id,
        props.inputETH.toString(),
        defaultTimeLimit.toString(),
        isLimit
      );
      setIsSending(false);
      if (res) {
        // use fairSwap.
        setSteps(1);
        setTxResponse(res);
      }
    },
    async () => {
      if (/*yourOrder.boxCount === 1 && */ !yourOrder.isExecuted) {
        // waiting order and can force execute.
        setIsSending(true);
        const res = await executeUnexecutedBox(
          yourOrder.orderCount,
          exchangeAddress
        );
        setIsSending(false);
        if (res) {
          setSteps(2);
        }
      } else if (yourOrder.isExecuted) {
        // executed orders data
        setSteps(2);
      } // nothing to do for other cases
    },
  ];

  const txStatusList: string[] = [
    'Issue iDOL',
    'FairSwap Execution',
    'Completed',
  ];
  const getTotalIDOL = props.estimatedOutIDOL.toString();
  const getPooledIDOL = web3Utils
    .estimateGetPooledIDOL(props.bondGroup.solidBond, props.inputETH)
    .toString();
  const pooledExpired = props.bondGroup.maturity;
  const outETH = props.inputETH.dp(2).toString();
  const recommendedUnitConvertInfo =
    props.recommendedConvertInfo.length > 0
      ? props.recommendedConvertInfo[0]
      : undefined;
  const mintedIDOL = recommendedUnitConvertInfo
    ? recommendedUnitConvertInfo.mintedIDOL.toString()
    : '0';
  const rate = recommendedUnitConvertInfo
    ? recommendedUnitConvertInfo.exchangeRate.toString()
    : '0';

  const txComponentList = [
    <IssueIDOLOnly
      key={0}
      feeETH={feeETH}
      usingEth={outETH}
      getTotalIDOL={getTotalIDOL}
      getPooledIDOL={getPooledIDOL}
      expired={pooledExpired}
      recommendedConvertInfo={props.recommendedConvertInfo}
    />,
    <WaitingLBT2IDOL
      key={1}
      isExecuted={yourOrder.isExecuted}
      boxCount={yourOrder.boxCount}
      orderCount={yourOrder.orderCount}
      exchangeAddress={exchangeAddress}
      isBuy={yourOrder.isBuy}
      trancheLBT={yourOrder.tokenIn}
      mintedIDOL={mintedIDOL}
      pooledIDOL={getPooledIDOL}
      expired={pooledExpired}
      rate={rate}
    />,
    <CompletedIssueIDOLOnly
      key={2}
      feeETH={feeETH}
      usingEth={outETH}
      getTotalIDOL={getTotalIDOL}
      getPooledIDOL={getPooledIDOL}
      expired={pooledExpired}
      recommendedConvertInfo={props.recommendedConvertInfo}
    />,
  ];
  return (
    <MultiTransactionModal
      isOpen={props.isOpen}
      setIsOpen={props.setIsOpen}
      waitingFlag={1}
      waitingAble={!yourOrder.isExecuted}
      txFuncList={txFuncList}
      txStatusList={txStatusList}
      txComponentList={txComponentList}
      steps={steps}
      isSending={isSending}
    />
  );
};
