import Contracts from '../helper/Contract';
import { Web3Utils } from '../../utils/web3Utils';
import { BigNumber as BN } from 'bignumber.js';

type SolidityType =
  | 'address'
  | 'address[]'
  | 'bool'
  | 'bytes'
  | 'string'
  | 'uint8'
  | 'uint64'
  | 'uint256'
  | 'uint256[]';

export default class BondMaker {
  private contracts: Contracts;
  private web3Utils: Web3Utils;

  constructor(contracts: Contracts) {
    this.contracts = contracts;
    this.web3Utils = new Web3Utils();
  }

  // ======= Methods.sendTx ========

  public issueNewBonds(bondGroupIdData: number, ethValueData: string | BN) {
    const feeIncludedValue = this.web3Utils.toWei(
      this.web3Utils.feeIncludedETH(new BN(ethValueData.toString())).toString()
    );
    const funcData = this.contracts.bondMaker.methods.issueNewBonds(
      bondGroupIdData
    );
    return this.contracts.sendTxValue(funcData, feeIncludedValue);
  }

  public liquidateBond(bondGroupIdData: number) {
    const funcData = this.contracts.bondMaker.methods.liquidateBond(
      bondGroupIdData
    );
    return this.contracts.sendTx(funcData);
  }

  // ======= Methods.call ========

  public getBond(bondIDData: string) {
    const funcData = this.contracts.bondMaker.methods.getBond(bondIDData);
    return funcData.call();
  }

  public getBondGroup(bondGroupIDData: number) {
    const funcData = this.contracts.bondMaker.methods.getBondGroup(
      bondGroupIDData
    );
    return funcData.call();
  }

  public getNextBondGroupID() {
    const funcData = this.contracts.bondMaker.methods.nextBondGroupID();
    return funcData.call();
  }

  public getFnMap(fnMapIDData: string) {
    const funcData = this.contracts.bondMaker.methods.getFnMap(fnMapIDData);
    return funcData.call();
  }
}
