import React from 'react';
import SubTitle from '../SubTitle';
import SubItem from '../SubItem';
import ExchangeBox from 'view/components/presentations/ExchangeBox';
import ItemPrice from '../ItemPrice';
import { IDOLBrand, PooledIDOLBrand } from 'resource/brands';
import ItemNumber from '../ItemNumber';
import WaitingOrdersProgressBar from '../WaitingOrdersProgressBar';
import { useFairSwap } from 'actions/useFairSwap';
import BigNumber from 'bignumber.js';
interface WaitingLBT2IDOLProps {
  isExecuted: boolean;
  boxCount: number;
  orderCount: number;
  exchangeAddress: string;
  isBuy: boolean;
  trancheLBT: BigNumber;
  mintedIDOL: string;
  pooledIDOL: string;
  expired: Date;
  rate: string;
}

export default (props: WaitingLBT2IDOLProps) => {
  const { convertExchangeInfoFromOrders } = useFairSwap();
  const exchangeInfo = convertExchangeInfoFromOrders(
    props.exchangeAddress,
    props.isBuy
  );
  return (
    <div className="content_box_modal">
      <SubTitle title="FairSwap execution" />
      <div className="box">
        <SubItem name="Your accepted order" />
        <ExchangeBox exchangeInfo={exchangeInfo} />
        <ItemPrice
          name="From"
          value={props.trancheLBT.toString()}
          brand={exchangeInfo.fromBrand}
        />
        <ItemPrice
          name="To"
          value={exchangeInfo.rate.multipliedBy(props.trancheLBT).toString()}
          brand={exchangeInfo.toBrand}
        />
        <WaitingOrdersProgressBar
          isExecuted={props.isExecuted}
          boxCount={props.boxCount}
          orderCount={props.orderCount}
        />

        <ItemPrice
          name="You have already received"
          value={props.mintedIDOL}
          brand={IDOLBrand}
        />
        <ItemPrice
          name="You have already reserved"
          value={props.pooledIDOL}
          brand={PooledIDOLBrand}
        />
        <ItemNumber
          name="Pool Maturity"
          value={props.expired.toLocaleDateString()}
        />
      </div>
    </div>
  );
};
