import React from 'react';
import { ChevronDown, ChevronRight } from 'baseui/icon';
import { Pagination } from 'baseui/pagination';
import {
  Unstable_StatefulDataTable as StatefulDataTable,
  NumericalColumn,
  StringColumn,
  CustomColumn,
  NUMERICAL_FORMATS,
} from 'baseui/data-table';
import { Block } from 'baseui/block';
import { AuctionGroupColumn } from '../pages/AuctionListContainer';
import { BigNumber as BN } from 'bignumber.js';
import { StatefulPopover, PLACEMENT } from 'baseui/popover';
import { Label3, Label4, Paragraph4 } from 'baseui/typography';
import { Button, SHAPE, SIZE, KIND } from 'baseui/button';
import NoData from './NoData';
import GettingData from './GettingData';
import useAuction from 'actions/useAuction';
import { ProgressBar } from 'baseui/progress-bar';
import { period, color } from 'config';
import CopyToClipboard from 'react-copy-to-clipboard';
import { AuctionBid } from 'types';
import { useStyletron } from 'baseui';

//TODO: fix index.d.ts -> import {Unstable_DataTable} from 'baseui/data-table'

interface AuctionListBoxProps {
  rows: { id: any; data: AuctionGroupColumn }[];
  loading?: boolean;
  currentPage: number;
  maxPageNum: number;
  setCurrentPage: (page: number) => void;
  onClickRow: (event: { datum?: {}; index?: number }) => void;
  processNextAuctionGroup: (pageNumber: number) => void;
  setLoading: (loading: boolean) => void;
}

interface Bid {
  price: string;
  amount: string;
  random: number;
  reveal: boolean;
}

export default (props: AuctionListBoxProps) => {
  const { setAuctionPage, getActionLabel } = useAuction();
  const [css] = useStyletron();
  const { onClickRow } = props;
  const columns = [
    CustomColumn({
      title: '#',
      mapDataToValue: (data: AuctionGroupColumn) => data.auctionID,
      renderCell: function Cell(props: any) {
        const auctionIDFull = props.value;
        let auctionID = auctionIDFull;
        if (auctionID.length > 6) {
          auctionID = auctionID.slice(0, 6) + '…';
        }
        return (
          <CopyToClipboard text={auctionIDFull}>
            <StatefulPopover
              content={() => {
                return (
                  <Block padding={'20px'}>
                    copied Auction ID:
                    <p>{`${auctionIDFull}`}</p>
                  </Block>
                );
              }}
              returnFocus
              autoFocus
              animateOutTime={3000}
            >
              <div>{auctionID}</div>
            </StatefulPopover>
          </CopyToClipboard>
        );
      },
      maxWidth: 3,
    }),
    NumericalColumn({
      title: 'Strike Price',
      mapDataToValue: (data: AuctionGroupColumn) => data.strikePrice,
      format: NUMERICAL_FORMATS.ACCOUNTING,
      maxWidth: 40,
    }),
    StringColumn({
      title: 'Maturity',
      mapDataToValue: (data: AuctionGroupColumn) => {
        return data.maturity.toLocaleDateString();
      },
      maxWidth: 40,
    }),
    CustomColumn({
      title: 'Bid / Amount of sale',
      precision: 2,
      maxWidth: 100,
      mapDataToValue: (data: AuctionGroupColumn) => data,
      renderCell: function Cell(props: any) {
        const floorDecimal2 = (n: number) => Math.floor(n * 100) / 100;
        const auction = props.value as AuctionGroupColumn;
        const saleAmount = new BN(auction.amount).div('1e8');
        const bidAmount = new BN(auction.bidAmount).div('1e8');
        const progress = saleAmount.eq(0)
          ? 0
          : floorDecimal2(bidAmount.times(100).div(saleAmount).toNumber());
        return (
          <div>
            {bidAmount.toString() +
              ' / ' +
              saleAmount.toString() +
              ` (${progress}%)`}
          </div>
        );
      },
    }),
    CustomColumn({
      title: 'Global Status',
      mapDataToValue: (data: AuctionGroupColumn) => data,
      maxWidth: 60,
      renderCell: function Cell(props: any) {
        const value = props.value as AuctionGroupColumn;
        const isParticipant = value.isParticipant;
        const status = value.period;
        const progress = value.periodProgress as {
          percent: number;
          label: string;
        };
        return ![period.bid].includes(status) &&
          ([period.closed, period.waiting].includes(status) ||
            !isParticipant) ? (
          <div>{status}</div>
        ) : (
          <StatefulPopover
            content={() => (
              <Block padding={'20px'}>
                <Label3>{progress.label}</Label3>
              </Block>
            )}
            returnFocus
            renderAll
            autoFocus
          >
            <div
              className={css({
                width: '100%',
              })}
            >
              {status}
              <ProgressBar value={progress.percent} successValue={100} />
            </div>
          </StatefulPopover>
        );
      },
    }),
    CustomColumn({
      title: 'Your Status',
      mapDataToValue: (data: AuctionGroupColumn) => data,
      renderCell: function Cell(props: any) {
        const data = props.value as AuctionGroupColumn;
        const bidList = data.bidList || [];
        const bidExists = bidList.length > 0;
        const renderBidList = () => {
          return bidList.map((bid: AuctionBid, index: number) => {
            return (
              <div key={index}>
                <Label4>
                  {`Price: $ ${new BN(bid.price).toString()} `}
                  &emsp;
                  {`Amount: 
                  ${new BN(bid.amount).toString()} SBT`}
                  &emsp;
                  {bid.reveal ? 'Revealed' : ''}
                </Label4>
              </div>
            );
          });
        };
        const { label, kind, disabled } = getActionLabel(
          data.userStatus,
          data.result?.end.mEICalled || false
        );
        return (
          <>
            <div className={css({ paddingRight: '10px' })}>
              <Button
                size={SIZE.mini}
                shape={SHAPE.pill}
                disabled={disabled}
                kind={KIND.tertiary}
                onClick={() => {
                  const row = {
                    ...data,
                    index: data.id,
                    datum: data,
                  };
                  onClickRow(row);
                }}
                endEnhancer={
                  !disabled ? () => <ChevronRight size={16} /> : () => <></>
                }
                overrides={{
                  BaseButton: {
                    style: ({ $theme }) => {
                      return {
                        border: '1px solid',
                        backgroundColor: color[kind],
                      };
                    },
                  },
                }}
              >
                {label}
              </Button>
            </div>
            {bidExists ? (
              <StatefulPopover
                content={() => (
                  <Block padding={'20px'}>
                    <Label3>Bid history</Label3>
                    <Paragraph4>{bidExists ? renderBidList() : ''}</Paragraph4>
                  </Block>
                )}
                placement={PLACEMENT.bottom}
                returnFocus
                renderAll
                autoFocus
              >
                <Button
                  size={SIZE.mini}
                  endEnhancer={() => <ChevronDown size={16} />}
                >
                  {'Bid List'}
                </Button>
              </StatefulPopover>
            ) : (
              <></>
            )}
          </>
        );
      },
    }),
  ];

  return (
    <div>
      <div style={{ height: `${props.rows.length * 60 + 120}px` }}>
        <StatefulDataTable
          columns={columns}
          rows={props.rows}
          rowHeight={60}
          loading={props.loading}
          loadingMessage={() => <GettingData />}
          emptyMessage={() => <NoData />}
          onClick={props.onClickRow}
          filterable={false}
          searchable={false}
        />
      </div>
      <Pagination
        numPages={props.maxPageNum}
        size={SIZE.mini}
        currentPage={props.currentPage}
        onPageChange={({ nextPage }) => {
          props.setLoading(true);
          setAuctionPage(nextPage);
          props.setCurrentPage(Math.min(Math.max(nextPage, 1), 10));
          props.processNextAuctionGroup(nextPage);
        }}
      />
    </div>
  );
};
