import React from 'react';
import SubTitle from '../SubTitle';
import PriceBox from '../PriceBox';
import SubItem from '../SubItem';
import { PriceInfo, ExchangeRateInfo } from 'types';
import ItemPrice from '../ItemPrice';
import { PooledIDOLBrand, IDOLBrand } from 'resource/brands';
interface CompletedMintProps {
  price: PriceInfo;
  getIDOL: string;
  getPooledIDOL: string;
  exchangeRate?: ExchangeRateInfo;
}

export default (props: CompletedMintProps) => {
  return (
    <div className="content_box_modal">
      <SubTitle title="Completed" />
      <div className="box">
        <SubItem name="You minted iDOL from" />
        <PriceBox price={props.price} exchangeRate={props.exchangeRate} />
        <ItemPrice
          name="You have received"
          value={props.getIDOL}
          brand={IDOLBrand}
        />
        <ItemPrice
          name="You have reserved"
          value={props.getPooledIDOL}
          brand={PooledIDOLBrand}
        />
      </div>
    </div>
  );
};
