import React from 'react';
import './SubItem.scss';
import IconWrapper from 'view/components/presentations/IconWrapper';
import { HelpCircle } from 'react-feather';

interface SubItemProps {
  name: string;
  isHelp?: boolean;
  description?: string[];
}

function SubItem(props: SubItemProps) {
  return (
    <div className="sub">
      {props.isHelp ? (
        <IconWrapper
          component={<span>{props.name}</span>}
          icon={<HelpCircle size={14} color="#666" />}
          isDescription={props.isHelp}
          description={props.description || []}
          isPadded={false}
        />
      ) : (
        <span>{props.name}</span>
      )}
    </div>
  );
}

export default SubItem;
