import Contracts from 'libs/src/helper/Contract';
import ERC20helper from 'libs/src/balance/ERC20Helper';
import { BigNumber } from 'bignumber.js';

export interface ExchangeData {
  boxNumber: BigNumber;
  idolTokenPool: BigNumber;
  lienTokenPool: BigNumber;
  totalShare: BigNumber;
  idolTokenPerShare: BigNumber;
  lienTokenPerShare: BigNumber;
  buyPrice: BigNumber; // iDOL/LIEN TODO: need to change LIEN/iDOL?
  sellPrice: BigNumber; // iDOL/LIEN
}

export default class LIENFairSwap {
  private contracts: Contracts;
  private erc20Helper: ERC20helper;
  private upperLimitMul = 1.1;
  private lowerLimitMul = 0.95;
  private idolAddr: string;
  private lienAddr: string;

  constructor(contracts: Contracts) {
    this.contracts = contracts;
    this.erc20Helper = new ERC20helper(contracts);
    this.idolAddr = contracts.stableCoinAddr;
    this.lienAddr = contracts.lienTokenAddr;
  }

  // ===== Methods.sendTx =====

  public async orderIDOLToLIEN(
    timeout: string,
    recipient: string,
    idolAmount: BigNumber,
    isLimit: boolean
  ) {
    const convertedAmount: string = await this.erc20Helper.convertAmount(
      this.idolAddr,
      idolAmount
    );
    await this.erc20Helper.approveAmt(
      this.idolAddr,
      this.contracts.lienFairSwapAddr,
      convertedAmount
    );
    const funcData = this.contracts.lienFairSwap.methods.orderBaseToSettlement(
      timeout,
      recipient,
      convertedAmount,
      isLimit
    );
    return this.contracts.sendTx(funcData);
  }

  public async orderLIENToIDOL(
    timeout: string,
    recipient: string,
    lienAmount: BigNumber,
    isLimit: boolean
  ) {
    const convertedAmount: string = await this.erc20Helper.convertAmount(
      this.lienAddr,
      lienAmount
    );
    await this.erc20Helper.approveAmt(
      this.lienAddr,
      this.contracts.lienFairSwapAddr,
      convertedAmount
    );
    const funcData = this.contracts.lienFairSwap.methods.orderSettlementToBase(
      timeout,
      recipient,
      convertedAmount,
      isLimit
    );
    return this.contracts.sendTx(funcData);
  }
  // ===== Methods.call =====

  //public async getExchangeData(): Promise<ExchangeData> {
  public async getExchangeData() {
    interface RawData {
      boxNumber: string;
      baseTokenPoolAmount: string;
      settlementTokenPoolAmount: string;
      totalShare: string;
      baseTokenPerShare: string;
      settlementTokenPerShare: string;
      buyPriceE18: string;
      sellPriceE18: string;
    }
    const rawData: RawData = await this.contracts.lienFairSwap.methods
      .getExchangeData()
      .call();
    //const decimalsOfLien = await this.erc20Helper.getDecimals(this.lienAddr);
    //const decimalsOfIdol = await this.erc20Helper.getDecimals(this.idolAddr);
    const buyPrice = this.E18toBN(rawData.buyPriceE18); // iDOL/LIEN
    const sellPrice = this.E18toBN(rawData.sellPriceE18); // iDOL/LIEN

    // price = lienPool/idolPool
    // displayPrice = (lienPool/decimalOfLien)/(idolPool/decimalOfIdol)
    // = price * decimalOfIdol / decimalOfLien
    const displayBuyPrice = await this.erc20Helper.fromAmount(
      this.lienAddr,
      await this.erc20Helper.convertAmount(this.idolAddr, buyPrice)
    );
    const displaySellPrice = await this.erc20Helper.fromAmount(
      this.lienAddr,
      await this.erc20Helper.convertAmount(this.idolAddr, sellPrice)
    );

    return {
      boxNumber: new BigNumber(rawData.boxNumber),
      idolTokenPool: await this.erc20Helper.fromAmount(
        this.idolAddr,
        rawData.baseTokenPoolAmount
      ),
      lienTokenPool: await this.erc20Helper.fromAmount(
        this.lienAddr,
        rawData.settlementTokenPoolAmount
      ),
      totalShare: new BigNumber(rawData.totalShare),
      idolTokenPerShare: await this.erc20Helper.fromAmount(
        this.idolAddr,
        rawData.baseTokenPerShare
      ),
      lienTokenPerShare: await this.erc20Helper.fromAmount(
        this.lienAddr,
        rawData.settlementTokenPerShare
      ),
      buyPrice: displayBuyPrice,
      sellPrice: displaySellPrice,
    } as ExchangeData;
  }

  public async getShare(ownerAddr: string): Promise<BigNumber> {
    return new BigNumber(
      await this.contracts.lienFairSwap.methods.balanceOf(ownerAddr).call()
    );
  }

  private addressOfExchange(): string {
    return this.contracts.lienFairSwapAddr;
  }

  private E18toBN(valueE18: string): BigNumber {
    if (valueE18 === '0') {
      return new BigNumber(0);
    }
    return new BigNumber(valueE18).div(
      new BigNumber(10).pow(new BigNumber(18))
    );
  }
}
