import React, { useState, useEffect } from 'react';
import SubTitle from 'view/components/presentations/SubTitle';
import FormNumber from '../presentations/FormNumber';
import ItemSelect from 'view/components/presentations/ItemSelect';
import TransactionButton from 'view/components/presentations/TransactionButton';
import { ETHBrand, IDOLBrand, LiquidBrand } from 'resource/brands';
import { BigNumber as BN } from 'bignumber.js';
import {
  PriceInfo,
  ExchangeRateInfo,
  BondGroup,
  LiquidBond,
  ExchangeData,
  defaultBondGroup,
  defaultExchangeData,
  defaultLBT,
} from 'types';
import { GlobalState } from 'reducer/store';
import BigNumber from 'bignumber.js';
import { Web3Utils } from 'libs/utils/web3Utils';
import BondGroupUnitTable, {
  BondGroupRows,
} from '../presentations/BondGroupUnitTable';
import ConvertInfoUnitTable, {
  ConvertInfoGroupRows,
} from '../presentations/ConvertInfoUnitTable';
import IssueIDOLOnlyModal from '../modal/IssueIDOLOnlyModal';
import IssueLBTOnlyModal from '../modal/IssueLBTOnlyModal';
import { useSelector } from 'react-redux';
import { StatefulButtonGroup, MODE } from 'baseui/button-group';
import { Button, KIND } from 'baseui/button';
import { isValidBondGroup, isValidBalance } from 'libs/utils/validate';
import IconWrapper from 'view/components/presentations/IconWrapper';
import { HelpCircle } from 'react-feather';
import {
  stableCoinDescription,
  leveragedETHDescription,
} from 'resource/description/index';

const IDOL_ONLY_SELECT = 0;
const LBT_ONLY_SELECT = 1;

function ConvertContainer() {
  const recommend = useSelector((state: GlobalState) => state.recommend);
  const exchangeDataMap = useSelector(
    (state: GlobalState) => state.base.exchangeDataMap
  );
  const bondGroupMap = useSelector(
    (state: GlobalState) => state.base.bondGroupMap
  );
  const bondMap = useSelector((state: GlobalState) => state.base.bondMap);
  const ethBalance = useSelector(
    (state: GlobalState) => state.baseMy.ethBalance
  );
  const web3Utils = new Web3Utils();
  const [ethPrice, setEthPrice] = useState<BN>(new BN(0));
  const [iDOLPaymentValue, setIDOLPaymentValue] = useState<BN>(new BN(0));
  const [lbtPaymentValue, setLBTPaymentValue] = useState<BN>(new BN(0));

  const calcBestBondGroup2LBT = (
    bondGroup: BondGroup,
    exchangeDataMap: Map<string, ExchangeData>,
    amount: BigNumber
  ): BigNumber => {
    // no mint idol sbt or no exchange.
    const lbt = bondGroup.liquidBondList[0];
    if (
      !bondGroup.solidBond.isAcceptable ||
      exchangeDataMap.get(lbt.bondAddress) === undefined
    ) {
      return new BigNumber(0);
    }
    // SBT -> iDOL -> LBT, how many number of LBT.
    const estimatedIDOL = web3Utils.estimateGetIDOL(
      bondGroup.solidBond,
      amount
    );
    const numOfLBT = estimatedIDOL.multipliedBy(
      (exchangeDataMap.get(lbt.bondAddress) as ExchangeData).buyPrice
    );
    return amount.plus(numOfLBT);
  };

  const calcBestBondGroup2IDOL = (
    bondGroup: BondGroup,
    exchangeDataMap: Map<string, ExchangeData>
  ): BigNumber => {
    // no exchange or no mint idol sbt.
    if (
      bondGroup.liquidBondList.some(
        (liquid: LiquidBond) =>
          exchangeDataMap.get(liquid.bondAddress) === undefined
      ) ||
      !bondGroup.solidBond.isAcceptable
    ) {
      return new BigNumber(0);
    }
    // strikePrice + sum(liquidExchangePrice)
    return bondGroup.strikePrice.plus(
      bondGroup.liquidBondList.reduce(
        (sum: BigNumber, liquid: LiquidBond) =>
          sum.plus(
            new BigNumber(1).dividedBy(
              (exchangeDataMap.get(liquid.bondAddress) as ExchangeData)
                .sellPrice
            )
          ),
        new BigNumber(0)
      )
    );
  };

  // make output price
  const [outputPriceList, setOutputPriceList] = useState<PriceInfo[]>([
    {
      brand: IDOLBrand,
      value: '0',
    },
    {
      brand: LiquidBrand,
      value: '0',
    },
  ]);
  const [exchangeRateList, setExchangeRateList] = useState<ExchangeRateInfo[]>([
    {
      fromBrand: ETHBrand,
      toBrand: IDOLBrand,
      rate: '0',
    },
    {
      fromBrand: ETHBrand,
      toBrand: LiquidBrand,
      rate: '0',
    },
  ]);
  const [convertLBTBondGroup, setConvertLBTBondGroup] = useState<BondGroup>(
    defaultBondGroup()
  );
  const [convertIDOLBondGroup, setConvertIDOLBondGroup] = useState<BondGroup>(
    defaultBondGroup()
  );
  const [recommendedBondGroupData, setRecommendedBondGroupData] = useState<
    BondGroupRows[]
  >([]);
  const [recommendedConvertInfo, setRecommendedConvertInfo] = useState<
    ConvertInfoGroupRows[]
  >([]);
  useEffect(() => {
    let newIDOLPrice = outputPriceList[0];
    let newLBTPrice = outputPriceList[1];
    let newIDOLRate = exchangeRateList[0];
    let newLBTRate = exchangeRateList[1];
    const web3Utils = new Web3Utils();
    const idolBest = recommend.convertIDOLBondGroup;
    const lbtBest = recommend.convertLBTBondGroup;
    if (idolBest !== undefined && idolBest.id !== -1) {
      setConvertIDOLBondGroup(idolBest);
      let ethValue = new BN(1);
      if (ethPrice !== new BN(0)) ethValue = new BN(ethPrice);
      const lbt =
        idolBest.liquidBondList.length > 0
          ? idolBest.liquidBondList[0]
          : defaultLBT();
      const exchangeData: ExchangeData =
        exchangeDataMap.get(lbt.bondAddress) || defaultExchangeData();
      if (exchangeData.settlementAddress) {
        const mintedIDOL = web3Utils.estimateGetIDOL(
          idolBest.solidBond,
          new BigNumber(ethValue)
        );
        const exchangedIDOL = new BigNumber(ethValue).dividedBy(
          exchangeData.sellPrice
        );
        setRecommendedConvertInfo([
          {
            maturity: idolBest.maturity,
            pooledIDOL: web3Utils.estimateGetPooledIDOL(
              idolBest.solidBond,
              ethValue
            ),
            mintedIDOL: mintedIDOL,
            exchangeRate: exchangeData.sellPrice,
            exchangedIDOL: exchangedIDOL,
            totalLiquidity: exchangeData.baseTokenPool,
            actualIDOL: mintedIDOL.plus(exchangedIDOL),
          },
        ]);
      }

      const bestIDOL = calcBestBondGroup2IDOL(idolBest, exchangeDataMap);
      newIDOLRate = {
        fromBrand: ETHBrand,
        toBrand: IDOLBrand,
        rate: bestIDOL.dp(3).toString(),
      };
      newIDOLPrice = {
        brand: IDOLBrand,
        value: bestIDOL.multipliedBy(new BigNumber(ethValue)).dp(3).toString(),
      };
    }

    if (lbtBest !== undefined && lbtBest.id !== -1) {
      setConvertLBTBondGroup(lbtBest);
      const lbt =
        lbtBest.liquidBondList.length > 0
          ? lbtBest.liquidBondList[0]
          : defaultLBT();
      const exchangeData: ExchangeData =
        exchangeDataMap.get(lbt.bondAddress) || defaultExchangeData();
      setRecommendedBondGroupData([
        {
          maturity: lbtBest.maturity,
          strikePrice: lbtBest.strikePrice.toNumber(),
          sbtPrice: lbtBest.solidBond.theoreticalPrice.dp(2).toNumber(),
          lbtPrice: lbt.theoreticalPrice.dp(2).toNumber(),
          leverage: lbt.leverage.dp(2).toNumber(),
          totalLiquidity: exchangeData.baseTokenPool,
        },
      ]);
      let ethValue = new BN(0);
      if (ethPrice !== new BN(0)) ethValue = new BN(ethPrice);
      const bestLBT = calcBestBondGroup2LBT(
        lbtBest,
        exchangeDataMap,
        ethValue.toString() === '0' ? new BigNumber(1) : ethValue
      );
      newLBTRate = {
        fromBrand: ETHBrand,
        toBrand: LiquidBrand,
        rate: bestLBT
          .dividedBy(ethValue.toString() === '0' ? new BigNumber(1) : ethValue)
          .dp(3)
          .toString(),
      };
      newLBTPrice = {
        brand: LiquidBrand,
        value: ethValue.toString() === '0' ? '0' : bestLBT.dp(3).toString(),
      };
    }

    setExchangeRateList([newIDOLRate, newLBTRate]);
    setOutputPriceList([newIDOLPrice, newLBTPrice]);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    ethPrice,
    exchangeDataMap,
    bondGroupMap,
    bondMap,
    recommend.convertLBTBondGroup,
    recommend.convertIDOLBondGroup,
  ]);

  const lbtPrice = {
    brand: LiquidBrand,
    value: '',
  };
  const idolPrice = {
    brand: IDOLBrand,
    value: '',
  };

  const [selected, setSelected] = useState<number>(0);
  const [openedIssueIDOLOnly, setOpenedIssueIDOLOnly] = useState(false);
  const [openedIssueLBTOnly, setOpenedIssueLBTOnly] = useState(false);
  const txFunc: () => void = () => {
    switch (selected) {
      case IDOL_ONLY_SELECT:
        setOpenedIssueIDOLOnly(true);
        break;
      case LBT_ONLY_SELECT:
        setOpenedIssueLBTOnly(true);
        break;
    }
  };

  // check txDisabled
  const [txDisabled, setTxDisabled] = useState<boolean>(true);
  useEffect(() => {
    switch (selected) {
      case IDOL_ONLY_SELECT:
        if (
          isValidBondGroup(convertIDOLBondGroup) &&
          isValidBalance(ethPrice, web3Utils.recommendMaxETH(ethBalance))
        )
          setTxDisabled(false);
        else setTxDisabled(true);
        break;
      case LBT_ONLY_SELECT:
        if (
          isValidBondGroup(convertLBTBondGroup) &&
          isValidBalance(ethPrice, web3Utils.recommendMaxETH(ethBalance))
        )
          setTxDisabled(false);
        else setTxDisabled(true);
        break;
    }
  }, [
    web3Utils,
    selected,
    ethPrice,
    ethBalance,
    convertIDOLBondGroup,
    convertLBTBondGroup,
  ]);

  return (
    <div className="content_box_3">
      <div className="box">
        <StatefulButtonGroup
          mode={MODE.radio}
          initialState={{ selected: 0 }}
          kind={KIND.secondary}
        >
          <Button
            onClick={() => {
              setSelected(0);
            }}
            startEnhancer={() => (
              <IconWrapper
                component={
                  <ItemSelect
                    price={idolPrice}
                    detail="Stable Coin"
                    exchangeRate={exchangeRateList[0]}
                    SelectState={selected === 0 ? true : false}
                  />
                }
                icon={<HelpCircle size={16} color="#666" />}
                isDescription={true}
                isPadded={false}
                description={leveragedETHDescription}
              />
            )}
          ></Button>
          <Button
            onClick={() => {
              setSelected(1);
            }}
            startEnhancer={() => (
              <IconWrapper
                component={
                  <ItemSelect
                    price={lbtPrice}
                    detail={`2x Levelaged ETH Token`}
                    exchangeRate={exchangeRateList[1]}
                    SelectState={selected === 1 ? true : false}
                  />
                }
                icon={<HelpCircle size={16} color="#666" />}
                isDescription={true}
                isPadded={false}
                description={stableCoinDescription}
              />
            )}
          ></Button>
        </StatefulButtonGroup>
      </div>
      <SubTitle title="Input Asset" />
      <div className="box">
        <FormNumber
          errorMessage="Insufficient ETH Balance"
          unit="ETH"
          value={ethPrice}
          setValue={(value: BN) => {
            setEthPrice(value);
            setLBTPaymentValue(value.times(exchangeRateList[1].rate));
            setIDOLPaymentValue(value.times(exchangeRateList[0].rate));
          }}
          maxValue={web3Utils.recommendMaxETH(ethBalance)}
        />
      </div>
      {selected === 0 && (
        <div>
          <SubTitle title="Output for Stable Coin" />
        </div>
      )}
      {selected === 1 && (
        <div>
          <SubTitle title="Output for 2x Leveraged ETH Token" />
        </div>
      )}
      <div className="box">
        <FormNumber
          errorState={false}
          unit={selected === 1 ? 'LBT' : 'IDOL'}
          value={selected === 1 ? lbtPaymentValue : iDOLPaymentValue}
          setValue={(value: BN) => {
            const nexEth = new BigNumber(value)
              .dividedBy(new BigNumber(exchangeRateList[selected].rate))
              .dp(8);
            setEthPrice(nexEth);
            selected === 1
              ? setLBTPaymentValue(new BigNumber(value))
              : setIDOLPaymentValue(new BigNumber(value));
            selected === 1
              ? setIDOLPaymentValue(nexEth.times(exchangeRateList[0].rate))
              : setLBTPaymentValue(nexEth.times(exchangeRateList[1].rate));
          }}
          maxValue={web3Utils
            .recommendMaxETH(ethBalance)
            .times(new BigNumber(exchangeRateList[selected].rate))}
        />
      </div>
      {selected === 0 && recommendedConvertInfo.length > 0 && (
        <div>
          <SubTitle title="Technical Details" />
        </div>
      )}
      {selected === 1 && recommendedBondGroupData.length > 0 && (
        <div>
          <SubTitle title="Recommended LBT Info" />
        </div>
      )}
      <div className="box">
        {selected === 0 && recommendedConvertInfo.length > 0 && (
          <div>
            <ConvertInfoUnitTable data={recommendedConvertInfo} />
          </div>
        )}
        {selected === 1 && recommendedBondGroupData.length > 0 && (
          <div>
            <BondGroupUnitTable data={recommendedBondGroupData} sbtDisable />
          </div>
        )}
      </div>
      <TransactionButton
        name={'From ETH to ' + (selected === 0 ? 'iDOL' : 'LBT')}
        txFunc={txFunc}
        disabled={txDisabled}
      />
      {openedIssueIDOLOnly && (
        <IssueIDOLOnlyModal
          bondGroup={convertIDOLBondGroup}
          isOpen={openedIssueIDOLOnly}
          setIsOpen={setOpenedIssueIDOLOnly}
          inputETH={new BigNumber(ethPrice)}
          estimatedOutIDOL={new BigNumber(outputPriceList[0].value)}
          recommendedConvertInfo={recommendedConvertInfo}
        />
      )}
      {openedIssueLBTOnly && (
        <IssueLBTOnlyModal
          isOpen={openedIssueLBTOnly}
          setIsOpen={setOpenedIssueLBTOnly}
          bondGroup={convertLBTBondGroup}
          inputETH={new BigNumber(ethPrice)}
          estimatedOutLBT={new BigNumber(outputPriceList[1].value)}
        />
      )}
    </div>
  );
}

export default ConvertContainer;
