import { Lien } from 'libs/index';
import { useDispatch } from 'react-redux';
export let lien = new Lien();
const useLien = () => {
  const dispatch = useDispatch();
  // ==== wallet Hooks ====

  const connect = async () => {
    try {
      const web3 = await lien.setup();
      dispatch({
        type: 'CONNECT',
        payload: web3,
      });
      dispatch({
        type: 'SET_LIEN',
        payload: lien,
      });
      dispatch({
        type: 'UPDATE_MIN_TRIGGER',
        payload: new Date(),
      });
    } catch (error) {
      return true;
    }
  };
  const disconnect = () => {
    if (lien.address) {
      dispatch({
        type: 'CLEAR_RECOMMEND',
      });
      dispatch({
        type: 'CLEAR_LIEN_TOKEN',
      });
      dispatch({
        type: 'CLEAR_AUCTION',
      });
      dispatch({
        type: 'CLEAR_BASE_MY',
      });
      dispatch({
        type: 'CLEAR_BASE',
      });
      dispatch({
        type: 'DISCONNECT',
      });
      lien = new Lien();
    }
  };

  const getAddress = () => {
    return lien.address;
  };

  return { connect, disconnect, getAddress };
};

export default useLien;
