import React from 'react';
import { Button, SIZE } from 'baseui/button';
import { withStyle, useStyletron } from 'baseui';
import { Plus, ChevronDown, ChevronRight } from 'baseui/icon';
import { Pagination } from 'baseui/pagination';
import {
  Unstable_StatefulDataTable as StatefulDataTable,
  NumericalColumn,
  CustomColumn,
  NUMERICAL_FORMATS,
} from 'baseui/data-table';
import { StyledBodyCell } from 'baseui/table-grid';
import NoData from './NoData';
import GettingData from './GettingData';
import MaturityCell from './MaturityCell';
import LeverageTag from './LeverageTag';
import BigNumber from 'bignumber.js';
import LiquidityPoolTag from './LiquidityPoolTag';
import { formatter, dateSortFn, BNSortFN } from 'libs/utils/utils';

//TODO: fix index.d.ts -> import {Unstable_DataTable} from 'baseui/data-table'

export interface TrancheBondGroupRows {
  id: number;
  maturity: Date;
  strikePrice: BigNumber;
  lbtPrice: BigNumber;
  leverage: BigNumber;
  totalLiquidity: BigNumber;
  onClickRow: (index?: number | string) => void;
  selected: number;
}

interface LiquidListBoxProps {
  recommendRows: { id: any; data: TrancheBondGroupRows }[];
  rows: { id: any; data: TrancheBondGroupRows }[];
  loading?: boolean;
  recommendLoading?: boolean;
  currentPage: number;
  maxPageNum: number;
  setCurrentPage: (page: number) => void;
  onClickRow: (index?: number | string) => void;
  processNextBondGroup: (pageNumber: number) => void;
  setLoading: (loading: boolean) => void;
  setRecommendLoading: (loading: boolean) => void;
}

const columns = [
  NumericalColumn({
    title: 'ID',
    maxWidth: 30,
    mapDataToValue: (data: TrancheBondGroupRows) => data.id,
    format: NUMERICAL_FORMATS,
  }),
  CustomColumn({
    title: 'Maturity',
    mapDataToValue: (data: TrancheBondGroupRows) => data.maturity,
    renderCell: function Cell(props: { value: Date }) {
      return <MaturityCell value={props.value} strictly />;
    },
    sortFn: dateSortFn,
    sortable: true,
  }),
  NumericalColumn({
    title: 'Strike Price',
    mapDataToValue: (data: TrancheBondGroupRows) => data.strikePrice,
    format: NUMERICAL_FORMATS.ACCOUNTING,
  }),
  NumericalColumn({
    title: 'LBT Price',
    precision: 2,
    format: NUMERICAL_FORMATS.ACCOUNTING,
    mapDataToValue: (data: TrancheBondGroupRows) => data.lbtPrice,
  }),
  CustomColumn({
    title: 'LBT Leverage',
    precision: 2,
    mapDataToValue: (data: TrancheBondGroupRows) => data.leverage,
    renderCell: function Cell(props: any) {
      return <LeverageTag leverage={new BigNumber(props.value)} />;
    },
    sortFn: BNSortFN,
    sortable: true,
  }),
  CustomColumn({
    title: 'Total Liquidity',
    precision: 2,
    mapDataToValue: (data: TrancheBondGroupRows) => data.totalLiquidity,
    renderCell: function Cell(props: any) {
      return (
        <LiquidityPoolTag
          pool={formatter(new BigNumber(props.value))?.value}
          unit={formatter(new BigNumber(props.value))?.unit}
        />
      );
    },
    sortFn: BNSortFN,
    sortable: true,
  }),
  CustomColumn({
    title: '',
    precision: 2,
    mapDataToValue: (data: TrancheBondGroupRows) => data,
    renderCell: function Cell(props: any) {
      const [css] = useStyletron();
      return (
        <div
          className={css({
            alignItems: 'center',
            display: 'flex',
          })}
        >
          <Button
            onClick={() => props.value.onClickRow(props.value.id)}
            size={SIZE.mini}
            endEnhancer={() => <Plus size={17} />}
          >
            Select
          </Button>
        </div>
      );
    },
  }),
];

const CenteredBodyCell = withStyle(StyledBodyCell, {
  display: 'flex',
  alignItems: 'center',
});

export default (props: LiquidListBoxProps) => {
  const [expanded, setExpanded] = React.useState(false);
  return (
    <div>
      <div style={{ height: `${props.recommendRows.length * 60 + 170}px` }}>
        <StatefulDataTable
          columns={columns}
          rows={props.recommendRows}
          rowHeight={55}
          loading={props.recommendLoading}
          loadingMessage={() => <GettingData />}
          emptyMessage={() => <NoData />}
        />
      </div>
      <div>
        <CenteredBodyCell>
          <Button
            size="compact"
            kind="minimal"
            onClick={() => setExpanded(!expanded)}
            shape="square"
          >
            {expanded ? <ChevronDown size={18} /> : <ChevronRight size={18} />}
          </Button>
          Show All Bond Tokens
        </CenteredBodyCell>
      </div>

      {expanded && props.rows && (
        <div>
          <div style={{ height: `${props.rows.length * 60 + 170}px` }}>
            <StatefulDataTable
              columns={columns}
              rows={props.rows}
              rowHeight={60}
              loading={props.loading}
              loadingMessage={() => <GettingData />}
              emptyMessage={() => <NoData />}
            />
          </div>
          <Pagination
            numPages={props.maxPageNum}
            size={SIZE.mini}
            currentPage={props.currentPage}
            onPageChange={({ nextPage }) => {
              props.setCurrentPage(Math.min(Math.max(nextPage, 1), 20));
              props.setLoading(true);
              props.processNextBondGroup(nextPage);
            }}
          />
        </div>
      )}
    </div>
  );
};
