import React from 'react';
import SubTitle from '../SubTitle';
import SubItem from '../SubItem';
import { LiquidBond } from 'types';
import ItemPrice from '../ItemPrice';
import { IDOLBrand, ETHBrand } from 'resource/brands';
import ItemLiquid from '../ItemLiquid';
import BigNumber from 'bignumber.js';
import ConvertInfoUnitTable, {
  ConvertInfoGroupRows,
} from '../ConvertInfoUnitTable';
interface IssueLBTAndIDOLProps {
  usingEth: string;
  feeIncludedETH?: string;
  feeETH?: string;
  getLBT: string;
  getTotalIDOL: BigNumber;
  getIDOL: BigNumber;
  getPooledIDOL: BigNumber;
  expired: Date;
  lbt: LiquidBond;
  totalLiquidity: BigNumber;
}

export default (props: IssueLBTAndIDOLProps) => {
  const convertInfo = {
    maturity: props.expired,
    pooledIDOL: props.getPooledIDOL,
    mintedIDOL: props.getIDOL,
    exchangeRate: new BigNumber(0),
    exchangedIDOL: new BigNumber(0),
    totalLiquidity: new BigNumber(0),
    actualIDOL: props.getIDOL.plus(props.getPooledIDOL),
  } as ConvertInfoGroupRows;
  return (
    <div className="content_box_modal">
      <SubTitle title="Completed" />
      <div className="box">
        <SubItem name="Input ETH" />
        <ItemPrice
          name="You sent"
          feeIncluded={props.feeIncludedETH}
          fee={props.feeETH}
          value={props.usingEth}
          brand={ETHBrand}
        />
        <hr />
        <SubItem name="Output" />
        <ItemPrice
          name="You have received"
          value={props.getTotalIDOL.toString()}
          brand={IDOLBrand}
        />
        <ConvertInfoUnitTable data={[convertInfo]} exchangeDisable />
        <ItemLiquid
          name="You have received"
          value={props.getLBT}
          lbt={props.lbt}
          totalLiquidity={props.totalLiquidity}
        />
      </div>
    </div>
  );
};
