import React, { useState, useEffect } from 'react';
import { GlobalState } from 'reducer/store';
import SubTitle from 'view/components/presentations/SubTitle';
import TransactionButton from 'view/components/presentations/TransactionButton';
import { PoolSymbol, LiquidBond } from 'types';
import SubItem from 'view/components/presentations/SubItem';
import FormNumber from 'view/components/presentations/FormNumber';
import ItemNumber from 'view/components/presentations/ItemNumber';
import { BigNumber as BN } from 'bignumber.js';
import { useSelector } from 'react-redux';
import AddLiquidityModal from 'view/components/modal/AddLiquidityModal';
import { settlementUpperLimit } from 'config';
import { isValidBalance } from 'libs/utils/validate';

interface LiquidityAddProps {
  poolSymbol: PoolSymbol | undefined;
}

function LiquidityAddContainer(props: LiquidityAddProps) {
  const state = useSelector((state: GlobalState) => state);
  const [baseAmount, setBaseAmount] = useState<BN>(new BN(0));
  const [settlementAmount, setSettlementAmount] = useState<BN>(new BN(0));
  const [openedAddLiquidity, setOpenedAddLiquidity] = useState<boolean>(false);
  const lbt =
    props.poolSymbol &&
    props.poolSymbol.bondId &&
    state.base.bondMap.get(props.poolSymbol.bondId);
  const txFunc: () => void = async () => {
    if (props.poolSymbol) setOpenedAddLiquidity(true);
  };

  const calcDomination = () => {
    if (!props.poolSymbol || !props.poolSymbol.baseTokenPool) return new BN(0);
    return new BN(baseAmount)
      .div(props.poolSymbol.baseTokenPool.plus(baseAmount))
      .multipliedBy(100)
      .dp(3);
  };

  // check txDisabled
  const [txDisabled, setTxDisabled] = useState<boolean>(true);
  useEffect(() => {
    if (
      isValidBalance(baseAmount, state.baseMy.iDOLBalance) &&
      isValidBalance(settlementAmount, props.poolSymbol?.balance || new BN('0'))
    )
      setTxDisabled(false);
    else setTxDisabled(true);
  }, [
    baseAmount,
    settlementAmount,
    state.baseMy.iDOLBalance,
    props.poolSymbol,
  ]);

  return (
    <div className="content_box_3">
      <SubTitle title="Add Liquidity" />
      {props.poolSymbol && (
        <div className="box">
          <SubItem name="Amount to Add Liquidity" />
          <FormNumber
            maxValue={state.baseMy.iDOLBalance}
            errorMessage={`Insufficient ${props.poolSymbol.buyInfo.fromBrand.unit} Balance`}
            unit={props.poolSymbol.buyInfo.fromBrand.unit}
            value={baseAmount}
            setValue={(value: BN) => {
              const newAmount = value;
              setSettlementAmount(
                props.poolSymbol
                  ? newAmount.times(props.poolSymbol.buyInfo.rate)
                  : new BN(0)
              );
              setBaseAmount(newAmount);
            }}
          />
          <FormNumber
            maxValue={props.poolSymbol?.balance}
            errorMessage={`Insufficient ${props.poolSymbol.buyInfo.toBrand.unit} Balance`}
            unit={props.poolSymbol.buyInfo.toBrand.unit}
            value={settlementAmount.times(settlementUpperLimit)}
            setValue={(value: BN) => {
              // TODO: put baseTokenBalance and settlementTokenBalance into PoolSymbol, and delete getPaymentBalance
            }}
            disableMaxButton
            disableInput
          />
          <ItemNumber
            name={'Share of Pool'}
            value={`${
              calcDomination().isLessThan(0.01)
                ? '< 0.01'
                : calcDomination().toString()
            }%`}
          />
        </div>
      )}
      {props.poolSymbol && (
        <TransactionButton
          name="Add Liquidity"
          txFunc={txFunc}
          disabled={txDisabled}
        />
      )}
      {props.poolSymbol && (
        <AddLiquidityModal
          isOpen={openedAddLiquidity}
          setIsOpen={setOpenedAddLiquidity}
          baseAmount={baseAmount}
          settlementAmount={settlementAmount}
          exchangeData={props.poolSymbol}
          lbt={lbt ? (lbt as LiquidBond) : undefined}
        />
      )}
    </div>
  );
}

export default LiquidityAddContainer;
