import { lien } from 'actions/useLien';
import BigNumber, { BigNumber as BN } from 'bignumber.js';
import { useDispatch } from 'react-redux';
import { periodLen, period, networkId, developed, userStatus } from 'config';
import { asyncAll, getUserBids } from 'libs/utils/utils';
import {
  AuctionEnd,
  AuctionResult,
  defaultAuctionResult,
  defaultAuctionEnd,
  StateString,
  AuctionStatus,
  AuctionBid,
  UserStatus,
} from 'types';
import { KIND } from 'baseui/tag';

const useAuction = () => {
  const dispatch = useDispatch();

  const getBlockTime = async (
    blockNumber: string | number
  ): Promise<number> => {
    const block = await lien.contracts.web3.eth.getBlock(blockNumber);
    return block.timestamp as number;
  };

  const getNow = async (): Promise<Date> => {
    const isPrivate = networkId === 'other' || networkId === 'private';
    const now = isPrivate
      ? new Date((await getBlockTime('latest')) * 1000)
      : new Date();
    return now;
  };

  //===== Methods.sendTX =====
  const startAuction = async (bondID: string, isEmergency: boolean) => {
    try {
      if (lien.address) {
        await lien.auction.startAuction(bondID, isEmergency);
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  };

  const bid = async (
    auctionID: string,
    secret: string,
    targetSBTAmount: string | number,
    solidStrikePrice: string
  ): Promise<boolean> => {
    try {
      if (lien.address) {
        const depositAmount = new BN(targetSBTAmount)
          .div(new BN(10).pow(8))
          .times(solidStrikePrice)
          .toString();
        await lien.auctionBoard.bid(
          depositAmount,
          auctionID,
          secret,
          targetSBTAmount
        );
      }
      return true;
    } catch (error) {
      console.error(error);
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
      return false;
    }
  };

  const cancelBid = async (auctionID: string, secret: string) => {
    try {
      if (lien.address) {
        await lien.auction.cancelBid(auctionID, secret);
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  };

  const revealBid = async (
    auctionID: string,
    price: string | number,
    targetSBTAmount: string | number,
    random: string | number
  ): Promise<boolean> => {
    try {
      if (lien.address) {
        await lien.auctionBoard.revealBid(
          auctionID,
          new BN(price).times('1e8').toString(),
          new BN(targetSBTAmount).times('1e8').toString(),
          random
        );
        return true;
      }
      return false;
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
      return false;
    }
  };

  const sortBidPrice = async (
    auctionID: string,
    sortedPrice: React.ReactText[]
  ) => {
    try {
      if (lien.address) {
        await lien.auctionBoard.sortBidPrice(auctionID, sortedPrice);
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  };

  const makeEndInfo = async (auctionID: string) => {
    try {
      if (lien.address) {
        await lien.auctionBoard.makeEndInfo(auctionID);
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  };

  const makeAuctionResult = async (
    auctionID: string,
    myLowestPrice: any,
    winnerBids: any[],
    loserBids: any[]
  ): Promise<boolean> => {
    try {
      if (lien.address) {
        await lien.auction.makeAuctionResult(
          auctionID,
          myLowestPrice,
          winnerBids,
          loserBids
        );
        return true;
      }
      return false;
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
      return false;
    }
  };

  const closeAuction = async (auctionID: string) => {
    try {
      if (lien.address) {
        await lien.auction.closeAuction(auctionID);
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  };

  const receiveUnrevealedBidDistribution = async (
    auctionID: string,
    secret: string
  ) => {
    try {
      if (lien.address) {
        await lien.auction.receiveUnrevealedBidDistribution(auctionID, secret);
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  };

  // ===== Methods.call ======

  const generateAuctionID = async (bondID: string, count: string | number) => {
    if (lien.address) {
      const res = await lien.auction.generateAuctionID(bondID, count);
      return res;
    }
  };

  const generateSecret = async (
    auctionID: string,
    price: string | number,
    targetSBTAmount: string | number,
    random: number
  ) => {
    if (lien.address) {
      //TODO: fix price Decimal
      const res = await lien.auctionBoard.generateSecret(
        auctionID,
        price,
        targetSBTAmount,
        random
      );
      dispatch({
        type: 'ADD_AUCTION_BID',
        payload: {
          auctionID: auctionID,
          price: price,
          amount: targetSBTAmount,
          random: random,
        },
      });
      return res;
    }
  };

  const getAuctionStatus = async (auctionID: string) => {
    if (lien.address) {
      const res = await lien.auctionBoard.getAuctionStatus(auctionID);
      return res;
    }
  };

  const getBidderStatus = async (auctionID: string, bidder: string) => {
    if (lien.address) {
      const res = await lien.auctionBoard.getBidderStatus(auctionID, bidder);
      return res;
    }
  };

  const getBoardStatus = async (auctionID: string) => {
    if (lien.address) {
      const boardStatus = await lien.auctionBoard.getBoardStatus(auctionID);
      const timestamp = new Date().getTime();
      dispatch({
        type: 'BOARD_STATUS',
        payload: {
          auctionID,
          boardStatus: boardStatus.map((v: any) =>
            new BigNumber(v).shiftedBy(-8).toNumber()
          ),
          timestamp,
        },
      });
      return boardStatus ? boardStatus : '';
    }
  };

  const getCurrentAuctionID = async (bondID: string) => {
    if (lien.address) {
      const res = await lien.auction.getCurrentAuctionID(bondID);
      return res;
    }
  };

  const getEndInfo = async (auctionID: string): Promise<AuctionEnd> => {
    if (lien.address) {
      if (auctionID === '') {
        return defaultAuctionEnd();
      }
      const [res, disposalInfo] = (await asyncAll(
        lien.auctionBoard.getEndInfo(auctionID),
        auctionDisposalInfo(auctionID)
      )) as [AuctionEnd, any];
      const mEICalled = disposalInfo.auctionUnrevealedDisposedFlag as boolean;
      res.mEICalled = mEICalled;
      return res;
    }
    return defaultAuctionEnd();
  };

  const getSortedBidPrice = async (auctionID: string) => {
    if (lien.address) {
      const sortedBidPrice = await lien.auctionBoard.getSortedBidPrice(
        auctionID
      );
      const timestamp = new Date().getTime();
      dispatch({
        type: 'SORTED_BID_PRICE',
        payload: {
          auctionID,
          sortedBidPrice: sortedBidPrice.map((v: any) =>
            new BigNumber(v).shiftedBy(-8).toNumber()
          ),
          timestamp,
        },
      });
      return sortedBidPrice;
    }
  };

  const getUnsortedBidPrice = async (auctionID: string) => {
    if (lien.address) {
      const res = await lien.auctionBoard.getUnsortedBidPrice(auctionID);
      return res;
    }
  };

  const getWeeklyAuctionStatus = async (weekNumber: number) => {
    if (lien.address) {
      const res = await lien.auction.getWeeklyAuctionStatus(weekNumber);
      return res;
    }
  };

  const getCurrentAuctionStatus = async (): Promise<AuctionStatus[]> => {
    if (lien.address) {
      const block = await lien.contracts.web3.eth.getBlock('latest');
      const weekNumber = Math.floor((block.timestamp as number) / (86400 * 7));
      const previous = await lien.auction.getWeeklyAuctionStatus(
        weekNumber - 1
      );
      const now = await lien.auction.getWeeklyAuctionStatus(weekNumber);
      const next = await lien.auction.getWeeklyAuctionStatus(weekNumber + 1);
      const auctionStatusArray = previous.concat(now).concat(next) as string[];

      const auctionStatusList = [];
      const size = 6;
      const auctionStatusLength = auctionStatusArray.length / size;
      for (let i = 0; i < auctionStatusLength; i++) {
        const auctionID =
          '0x' +
          new BN(auctionStatusArray[size * i + 5])
            .toString(16)
            .padStart(64, '0');
        const closingTime = new Date(
          new BN(auctionStatusArray[size * i + 0]).times(1000).toNumber()
        );
        const auctionStatusCode = new BN(auctionStatusArray[size * i + 4]);
        const isEmergency = auctionStatusCode.toNumber() % 2 === 1;
        const auctionStatus: AuctionStatus = {
          closingTime,
          auctionAmount: new BN(auctionStatusArray[size * i + 1]),
          rewardedAmount: new BN(auctionStatusArray[size * i + 2]),
          totalSBTAmountBid: new BN(auctionStatusArray[size * i + 3]),
          isEmergency,
          auctionID,
          period: 'None',
          bondID: '',
          isParticipant: false,
        };
        auctionStatusList.push(auctionStatus);
      }
      return auctionStatusList;
    }
    return [];
  };

  const isAlreadyStarted = async (bondIDData: string) => {
    if (lien.address) {
      const res = await lien.auction.isAlreadyStarted(bondIDData);
      return res;
    }
  };

  const listAuction = async () => {
    if (lien.address) {
      const block = await lien.contracts.web3.eth.getBlock('latest');
      const weekNumber = Math.floor((block.timestamp as number) / (86400 * 7));
      const res = await lien.auction.listAuction(weekNumber);
      return res ? res : [];
    }
  };

  const listBondIDFromAuctionID = async (auctionIDs: string[]) => {
    if (lien.address) {
      const res = await lien.auction.listBondIDFromAuctionID(auctionIDs);
      return res;
    }
  };

  const encryptBidSecret = async (message: string) => {
    if (lien.address) {
      const res = await lien.auction.encryptBidSecret(message);
      return res;
    }
  };

  const decryptBidSecret = async (encrypted: string) => {
    if (lien.address) {
      const res = await lien.auction.decryptBidSecret(encrypted);
      return res;
    }
  };

  const calcMyLowestPrice = async (
    auctionID: string,
    winnerAmount: string | number,
    myLoseBids: any[]
  ) => {
    if (lien.address) {
      const res = await lien.auctionBoard.calcMyLowestPrice(
        auctionID,
        winnerAmount,
        myLoseBids
      );
      return res;
    }
  };

  const calcWinnerAmount = async (auctionID: string, myWinBids: any[]) => {
    if (lien.address) {
      const sender = lien.address;
      const res = await lien.auctionBoard.calcWinnerAmount(
        auctionID,
        sender,
        myWinBids
      );
      return res;
    }
  };

  const getAuctionResult = async (
    auctionID: string
  ): Promise<AuctionResult> => {
    try {
      if (lien.address) {
        const bidder = lien.address;
        const end = await getEndInfo(auctionID);
        if (!end.mEICalled) {
          return defaultAuctionResult();
        }
        end.price = new BN(end.price).div('1e8');
        end.loseSBTAmount = new BN(end.loseSBTAmount).div('1e8');
        end.auctionEndPriceWinnerSBTAmount = new BN(
          end.auctionEndPriceWinnerSBTAmount
        ).div('1e8');

        const events = await lien.contracts.auctionBoard.getPastEvents(
          'LogInsertBoard',
          {
            filter: { auctionID, bidder },
            fromBlock: 0,
            toBlock: 'latest',
          }
        );

        const loserBids = [];
        const winnerBids = [];
        for (const { returnValues: bid } of events) {
          const boardIndex = developed
            ? new BN(bid['boardIndex'])
            : new BN(bid['boardIndex']).minus(1); // NOTE: board index from event has one extra.
          const price = new BN(bid.bidPrice).div('1e8');
          if (
            price.gt(end.price) ||
            (price.eq(end.price) && boardIndex.lt(end.boardIndex))
          ) {
            winnerBids.push({
              price,
              boardIndex,
            });
          } else if (
            price.eq(end.price) &&
            boardIndex.eq(end.boardIndex) &&
            end.loseSBTAmount.eq(0)
          ) {
            winnerBids.push({
              price,
              boardIndex,
            });
          } else {
            if (
              price.eq(end.price) &&
              boardIndex.eq(end.boardIndex) &&
              end.loseSBTAmount.isGreaterThan(0)
            ) {
              winnerBids.push({
                price,
                boardIndex,
              });
            }
            loserBids.push({
              price,
              boardIndex,
            });
          }
        }

        const res = {
          end,
          winnerBids,
          loserBids,
        };
        return res;
      }
      return defaultAuctionResult();
    } catch (err) {
      console.error(err);
      return defaultAuctionResult();
    }
  };

  const getMakeAuctionResult = async (auctionID: string) => {
    const contract = lien.contracts.auction;
    const bidder = lien.address;
    const latest = await lien.contracts.web3.eth.getBlockNumber();
    const events = await contract.getPastEvents('LogAuctionResult', {
      filter: { auctionID, bidder },
      fromBlock: latest - 1,
      toBlock: 'latest',
    });
    return events;
  };

  const getPeriod = async (
    closingTime: Date,
    isEmergency: boolean
  ): Promise<StateString> => {
    const closingTimestamp = closingTime.getTime();
    const now = (await getNow()).getTime(); // latency doesn't exist in main or ropsten
    const revealLength = isEmergency
      ? periodLen.emergency.reveal
      : periodLen.main.reveal;
    const winningLength = isEmergency
      ? periodLen.emergency.winning
      : periodLen.main.winning;
    switch (true) {
      case now < closingTimestamp:
        return period.bid;
      case now < closingTimestamp + revealLength:
        return period.reveal;
      case now < closingTimestamp + revealLength + winningLength:
        return period.winning;
      default:
        return period.closed;
    }
  };

  const auctionDisposalInfo = async (auctionID: string) => {
    if (lien.address) {
      const res = await lien.auctionBoard.auctionDisposalInfo(auctionID);
      return res;
    }
  };

  const auctionParticipantInfo = async (auctionID: string) => {
    if (lien.address) {
      const res = await lien.auctionBoard.auctionParticipantInfo(
        auctionID,
        lien.address
      );
      return res;
    }
  };

  const isRevealed = async (
    auctionID: string,
    secret: string
  ): Promise<boolean> => {
    if (lien.address) {
      const res = await lien.auctionBoard.auctionSecret(auctionID, secret);
      if (!res) {
        return false;
      }
      const SBTAmount = new BN(res[1]);
      const IDOLAmount = new BN(res[2]);
      const isRevealed = SBTAmount.plus(IDOLAmount).eq(0);
      return isRevealed;
    }
    return false;
  };

  const setAuctionPage = (page: number) => {
    dispatch({
      type: 'SET_AUCTION_PAGE',
      payload: page,
    });
  };
  const setAuctionMaxPage = (page: number) => {
    dispatch({
      type: 'SET_AUCTION_MAX_PAGE',
      payload: page,
    });
  };
  const setDisplayPeriod = (period: StateString) => {
    dispatch({
      type: 'SET_DISPLAY_PERIOD',
      payload: period,
    });
  };
  const setLoading = (loading: boolean) => {
    dispatch({
      type: 'SET_AUCTION_LOADING',
      payload: loading,
    });
  };
  const setTxModal = (isOpen: boolean) => {
    dispatch({
      type: 'SET_AUCTION_MODAL',
      payload: isOpen,
    });
  };
  const setMARDone = (auctionID: string) => {
    dispatch({
      type: 'SET_MAR_DONE',
      payload: auctionID,
    });
  };

  const getUserUnrevealBids = async (
    auctionID: string
  ): Promise<AuctionBid[]> => {
    try {
      const bids = getUserBids(auctionID);
      const newUnrevealedBids = [];
      for (const bid of bids) {
        const { secret } = bid;
        const reveal = await isRevealed(auctionID, secret);
        if (!reveal) {
          newUnrevealedBids.push(bid);
        }
      }
      return newUnrevealedBids;
    } catch (e) {
      return [];
    }
  };

  const getUserStatus = async (
    auctionID: string,
    status: StateString,
    isParticipant: boolean,
    mARDone: boolean,
    mEICalled: boolean
  ): Promise<UserStatus> => {
    switch (status) {
      case period.bid:
        if (isParticipant) {
          return userStatus.afterBid;
        }
        return userStatus.beforeBid;
      case period.reveal: {
        if (!isParticipant) {
          return userStatus.noParticipation;
        }
        const unrevealBids = await getUserUnrevealBids(auctionID);
        if (unrevealBids.length > 0) {
          return userStatus.beforeReveal;
        }
        return userStatus.afterReveal;
      }
      case period.waiting:
        if (!isParticipant) {
          return userStatus.noParticipation;
        }
        return userStatus.afterReveal;
      case period.winning:
        if (!isParticipant) {
          return userStatus.noParticipation;
        }
        if (mARDone) {
          return userStatus.afterWinning;
        }
        return userStatus.beforeWinning;
      case period.closed:
        if (!mEICalled) {
          return userStatus.afterReveal;
        }
        if (!isParticipant || mARDone) {
          return userStatus.afterWinning;
        }
        return userStatus.beforeWinning;
      default:
        return userStatus.noParticipation;
    }
  };

  const getActionLabel = (
    status: UserStatus,
    mEICalled: boolean
  ): {
    label: string;
    kind:
      | 'positive'
      | 'neutral'
      | 'primary'
      | 'accent'
      | 'warning'
      | 'negative'
      | 'custom';
    disabled: boolean;
  } => {
    switch (status) {
      case userStatus.beforeBid:
      case userStatus.afterBid:
        return {
          label: 'Open',
          kind: KIND.positive,
          disabled: false,
        };
      case userStatus.beforeReveal:
        return {
          label: 'Need to Reveal',
          kind: KIND.accent,
          disabled: false,
        };
      case userStatus.afterReveal:
        return {
          label: 'Waiting for the next step',
          kind: KIND.neutral,
          disabled: false,
        };
      case userStatus.beforeWinning:
        return {
          label: 'Check the result',
          kind: KIND.accent,
          disabled: false,
        };
      case userStatus.afterWinning:
        return {
          label: 'Check the result',
          kind: KIND.primary,
          disabled: false,
        };
      default:
        return {
          label: 'No participation',
          kind: KIND.primary,
          disabled: !mEICalled,
        };
    }
  };

  return {
    getBlockTime,
    getNow,
    startAuction,
    bid,
    cancelBid,
    revealBid,
    sortBidPrice,
    makeEndInfo,
    makeAuctionResult,
    closeAuction,
    receiveUnrevealedBidDistribution,
    generateAuctionID,
    generateSecret,
    getAuctionStatus,
    getBidderStatus,
    getBoardStatus,
    getCurrentAuctionID,
    getEndInfo,
    getSortedBidPrice,
    getUnsortedBidPrice,
    getWeeklyAuctionStatus,
    getCurrentAuctionStatus,
    isAlreadyStarted,
    listAuction,
    listBondIDFromAuctionID,
    encryptBidSecret,
    decryptBidSecret,
    calcWinnerAmount,
    calcMyLowestPrice,
    getAuctionResult,
    getPeriod,
    getMakeAuctionResult,
    auctionDisposalInfo,
    auctionParticipantInfo,
    isRevealed,
    setAuctionPage,
    setAuctionMaxPage,
    setDisplayPeriod,
    setLoading,
    setTxModal,
    getUserUnrevealBids,
    getUserStatus,
    getActionLabel,
    setMARDone,
  };
};

export default useAuction;
