import { UserTriggerState } from './store';

export const userTriggerReducer = (
  state: UserTriggerState,
  action: { type: string; payload: any }
) => {
  switch (action.type) {
    case 'UPDATE_WAITING_ORDERS':
      return {
        ...state,
        updateWaitingOrders: (state.updateWaitingOrders as number) + 1,
      };
    default:
      return state;
  }
};
