import Contracts from '../helper/Contract';
import { AuctionBoardInstance } from 'libs/types/AuctionBoard';
import ERC20helper from 'libs/src/balance/ERC20Helper';
import BigNumber from 'bignumber.js';
import { AuctionEnd } from 'types';

type AuctionBoardMethodParameters<
  T extends keyof AuctionBoardInstance['methods']
> = Parameters<AuctionBoardInstance['methods'][T]>;

export default class AuctionBoard {
  private contracts: Contracts;
  private erc20Helper: ERC20helper;

  constructor(contracts: Contracts) {
    this.contracts = contracts;
    this.erc20Helper = new ERC20helper(contracts);
  }

  //===== Methods.sendTX =====

  public async bid(
    depositAmount: string,
    ...params: AuctionBoardMethodParameters<'bid'>
  ) {
    await this.erc20Helper.approve(
      this.contracts.stableCoinAddr,
      this.contracts.auctionBoardAddr,
      new BigNumber(depositAmount)
    );
    const funcData = this.contracts.auctionBoard?.methods.bid(...params);
    return this.contracts.sendTx(funcData);
  }

  public revealBid(...params: AuctionBoardMethodParameters<'revealBid'>) {
    const funcData = this.contracts.auctionBoard?.methods.revealBid(...params);
    return this.contracts.sendTx(funcData);
  }

  public sortBidPrice(...params: AuctionBoardMethodParameters<'sortBidPrice'>) {
    const funcData = this.contracts.auctionBoard?.methods.sortBidPrice(
      ...params
    );
    return this.contracts.sendTx(funcData);
  }

  public makeEndInfo(...params: AuctionBoardMethodParameters<'makeEndInfo'>) {
    const funcData = this.contracts.auctionBoard?.methods.makeEndInfo(
      ...params
    );
    return this.contracts.sendTx(funcData);
  }

  // ===== Methods.call ======

  public auctionDisposalInfo(
    ...params: AuctionBoardMethodParameters<'auctionDisposalInfo'>
  ) {
    const funcData = this.contracts.auctionBoard?.methods.auctionDisposalInfo(
      ...params
    );
    return funcData?.call() as any;
  }

  public auctionParticipantInfo(
    ...params: AuctionBoardMethodParameters<'auctionParticipantInfo'>
  ) {
    const funcData = this.contracts.auctionBoard?.methods.auctionParticipantInfo(
      ...params
    );
    return funcData?.call() as any;
  }

  public generateSecret(
    ...params: AuctionBoardMethodParameters<'generateSecret'>
  ) {
    const funcData = this.contracts.auctionBoard?.methods.generateSecret(
      ...params
    );
    return funcData?.call() as any;
  }

  public getAuctionStatus(
    ...params: AuctionBoardMethodParameters<'getAuctionStatus'>
  ) {
    const funcData = this.contracts.auctionBoard?.methods.getAuctionStatus(
      ...params
    );
    return funcData?.call() as any;
  }

  public getBidderStatus(
    ...params: AuctionBoardMethodParameters<'getBidderStatus'>
  ) {
    const funcData = this.contracts.auctionBoard?.methods.getBidderStatus(
      ...params
    );
    return funcData?.call() as any;
  }

  public getBoardStatus(
    ...params: AuctionBoardMethodParameters<'getBoardStatus'>
  ) {
    if (this.contracts.auctionBoard === undefined) {
      throw new Error('the auction contract is not set');
    }

    const funcData = (this.contracts
      .auctionBoard as AuctionBoardInstance).methods.getBoardStatus(
      ...params
    ) as any;

    return funcData?.call() as any;
  }

  public getEndInfo(...params: AuctionBoardMethodParameters<'getEndInfo'>) {
    const funcData = this.contracts.auctionBoard?.methods.getEndInfo(...params);
    return funcData?.call() as AuctionEnd;
  }

  public getSortedBidPrice(
    ...params: AuctionBoardMethodParameters<'getSortedBidPrice'>
  ) {
    if (this.contracts.auctionBoard === undefined) {
      throw new Error('the auction contract is not set');
    }

    const funcData = (this.contracts
      .auctionBoard as AuctionBoardInstance).methods.getSortedBidPrice(
      ...params
    ) as any;
    return funcData?.call() as any;
  }

  public getUnsortedBidPrice(
    ...params: AuctionBoardMethodParameters<'getUnsortedBidPrice'>
  ) {
    const funcData = this.contracts.auctionBoard?.methods.getUnsortedBidPrice(
      ...params
    );
    return funcData?.call() as any;
  }

  public getWeeklyAuctionStatus(weekNumber: number) {
    const funcData = this.contracts.auctionBoard.methods.getWeeklyAuctionStatus(
      weekNumber
    );
    return funcData?.call() as any;
  }

  public isAlreadyStarted(bondIDData: string) {
    const funcData = this.contracts.auctionBoard?.methods.isAlreadyStarted(
      bondIDData
    );
    return funcData?.call() as any;
  }

  public discretizeBidPrice(
    ...params: AuctionBoardMethodParameters<'discretizeBidPrice'>
  ) {
    const funcData = this.contracts.auctionBoard?.methods.discretizeBidPrice(
      ...params
    );
    return funcData?.call() as any;
  }

  public calcBillAndCheckLoserBids(
    ...params: AuctionBoardMethodParameters<'calcBillAndCheckLoserBids'>
  ) {
    const funcData = this.contracts.auctionBoard?.methods.calcBillAndCheckLoserBids(
      ...params
    );
    return funcData?.call() as any;
  }

  public calcWinnerAmount(
    ...params: AuctionBoardMethodParameters<'calcWinnerAmount'>
  ) {
    const funcData = this.contracts.auctionBoard?.methods.calcWinnerAmount(
      ...params
    );
    return funcData?.call() as any;
  }

  public calcMyLowestPrice(
    ...params: AuctionBoardMethodParameters<'calcMyLowestPrice'>
  ) {
    const funcData = this.contracts.auctionBoard?.methods.calcMyLowestPrice(
      ...params
    );
    return funcData?.call() as any;
  }

  public auctionSecret(
    ...params: AuctionBoardMethodParameters<'auctionSecret'>
  ) {
    const funcData = this.contracts.auctionBoard?.methods.auctionSecret(
      ...params
    );
    return funcData?.call() as any;
  }
}
