export const cacheReducer = (state: any, action: any) => {
  switch (action.type) {
    case 'SET_BUY_LBT':
      return {
        ...state,
        buyLBT: action.payload,
      };
    case 'SET_SELL_LBT':
      return {
        ...state,
        sellLBT: action.payload,
      };
    case 'SET_ADD_LBT':
      return {
        ...state,
        addLBTPool: action.payload,
      };
    case 'SET_REMOVE_LBT':
      return {
        ...state,
        removeLBTPool: action.payload,
      };
    default:
      return state;
  }
};
