import { Lien } from 'libs';

//import { IState } from '../types';
export const reducer = (state: any, action: any) => {
  switch (action.type) {
    case 'CONNECT':
      return {
        web3: action.payload,
      };
    case 'DISCONNECT':
      return {
        ...state,
        lien: Object.assign({}, new Lien()),
      };
    case 'SET_LIEN':
      return {
        ...state,
        lien: action.payload,
      };
    default:
      return state;
  }
};
