import React from 'react';

import { PriceInfo, ExchangeRateInfo } from 'types';
import { ETHBrand, IDOLBrand } from 'resource/brands';
import { StyledLink } from 'baseui/link';

interface PriceListBoxProps {
  priceList: PriceInfo[];
  exchangeRateList?: ExchangeRateInfo[];
  effective?: number;
  enableTitle?: boolean;
  onSelect?: (index: number) => void;
  selected?: number;
}

function PriceListBox(props: PriceListBoxProps) {
  const priceListLength = props.priceList.length;
  const components = props.priceList.map(
    (price: PriceInfo, index: number): any => {
      const ableExchangeRate: boolean =
        props.exchangeRateList && props.exchangeRateList[index] ? true : false;
      const exchangeRate: ExchangeRateInfo = ableExchangeRate
        ? (props.exchangeRateList as ExchangeRateInfo[])[index]
        : ({
            fromBrand: ETHBrand,
            toBrand: IDOLBrand,
            rate: '0',
          } as ExchangeRateInfo);
      let priceClassName = `item${priceListLength}`;
      if (props.selected !== undefined && props.selected === index) {
        priceClassName += ' selected';
      }
      return (
        <div
          className={priceClassName}
          key={'price' + index}
          onClick={(_) => (props.onSelect ? props.onSelect(index) : '')}
        >
          {props.enableTitle && <span>{price.brand.name + ' Balance'}</span>}
          <span className={price.brand.className}>
            {price.brand.view(price.value, props.effective)}
          </span>
          <br />
          <StyledLink>
            {price.returnable && (
              <span className="expand_frame">
                {'Currently ' + price.returnable + ' iDOL returnable '}
              </span>
            )}
            {price.convertable && (
              <span className="expand_frame">{'Quick convert to iDOL'}</span>
            )}
            {price.tradable && (
              <span className="expand_frame">{' Trade assets for iDOL'}</span>
            )}
            {price.lienable && (
              <span className="expand_frame">{' Redeem commission'}</span>
            )}
          </StyledLink>
          <br />
          <StyledLink>
            {price.latestUnlockedPool && (
              <span>
                {'To be available in ' +
                  price.latestUnlockedPool.toLocaleDateString()}
              </span>
            )}
          </StyledLink>
          {ableExchangeRate && <span>Exchange Rate</span>}
          {ableExchangeRate && (
            <span>
              {'1' +
                exchangeRate.fromBrand.unit +
                '=' +
                exchangeRate.toBrand.view(exchangeRate.rate, props.effective)}
            </span>
          )}
        </div>
      );
    }
  );
  return <div className="price">{components}</div>;
}

export default PriceListBox;
