import React from 'react';
import SubTitle from '../SubTitle';
import SubItem from '../SubItem';
import ExchangeBox from 'view/components/presentations/ExchangeBox';
import ItemPrice from '../ItemPrice';
import { ExchangeInfo, LiquidBond } from 'types';
import BigNumber from 'bignumber.js';
import LiquidListUnitTable from '../LiquidListUnitTable';
interface TradeTxProps {
  exchangeInfo: ExchangeInfo;
  tokenIn: BigNumber;
  isBond?: boolean;
  lbt?: LiquidBond;
  totalLiquidity?: BigNumber;
}

export default (props: TradeTxProps) => {
  const lbtTable =
    props.isBond && props.lbt && props.totalLiquidity ? (
      <LiquidListUnitTable
        data={[
          {
            maturity: props.lbt?.maturity,
            strikePrice: props.lbt?.strikePrice.dp(2).toNumber(),
            lbtPrice: props.lbt?.theoreticalPrice.dp(2).toNumber(),
            leverage: props.lbt?.leverage.dp(2).toNumber(),
            totalLiquidity: props.totalLiquidity,
          },
        ]}
      />
    ) : (
      <div></div>
    );
  return (
    <div className="content_box_modal">
      <SubTitle title="Send order" />
      <div className="box">
        <SubItem name="Exchange Rate" />
        <ExchangeBox exchangeInfo={props.exchangeInfo} />
        <ItemPrice
          name="From"
          value={props.tokenIn.toString()}
          brand={props.exchangeInfo.fromBrand}
        />
        <ItemPrice
          name="To"
          value={props.exchangeInfo.rate.multipliedBy(props.tokenIn).toString()}
          brand={props.exchangeInfo.toBrand}
        />
        {props.isBond && lbtTable}
      </div>
    </div>
  );
};
