import { createStore } from 'redux';
import rootReducer from './rootReducer';
import {
  BondGroup,
  defaultAuctionBid,
  ExchangeData,
  AuctionGroup,
  AuctionBid,
  PooledIDOL,
  defaultBondGroup,
  Bond,
  WaitingOrders,
  AuctionStatus,
  StateString,
} from 'types';
import BigNumber, { BigNumber as BN } from 'bignumber.js';

export interface BaseState {
  bondGroupMap: Map<number, BondGroup>; // bondGroupId => bondGroup
  addedBondGroupMap: Map<number, BondGroup>; // bondGroupId => bondGroup
  bondMap: Map<string, Bond>; // bondId => Bond
  addedBondMap: Map<string, Bond>; // bondId => Bond
  exchangeDataMap: Map<string, ExchangeData>; // bondAddress => ExchangeData
  addedExchangeDataMap: Map<string, ExchangeData>; // bondAddress => ExchangeData
  exchangeToLBT: Map<string, string>; // exchangeAddress => liquidAddress;
  oracleEthPrice: BigNumber;
  oracleVolatility: BigNumber;
  bondGroupIdList: number[];
  exchangeAddressSet: Set<string>;
}

export interface BaseMyState {
  ethBalance: BN;
  iDOLBalance: BN;
  lienBalance: BN;
  solidBalanceList: { bondId: string; balance: BN }[];
  liquidBalanceList: { bondId: string; balance: BN }[];
  pooledIDOLList: PooledIDOL[];

  totalDollar: BN;
  totalSolidDollar: BN;
  totalLiquidDollar: BN;
  totalPooledDollar: BN;
  totalLPDollar: BN;

  averageLeverage: BN;

  waitingOrdersList: WaitingOrders[];
}

export interface AuctionState {
  loading: boolean;
  auctionGroupMap: Map<string, AuctionGroup>; // auctionID => AuctionGroup
  currentAuctionStatus: AuctionStatus[];
  currentDisplayAuctionStatus: AuctionStatus[];
  bondIDList: string[];
  auctionBid: AuctionBid;
  auctionPage: number;
  auctionMaxPage: number;
  displayPeriod: StateString;
  txModal: boolean; // Is txModal open?
}

export interface CacheState {
  buyLBT: string; // bondAddress
  sellLBT: string; // bondAddress
  addLBTPool: string; // bondAddress
  removeLBTPool: string; // bondAddress
}

export interface Term {
  id: BN;
  snapshotDate: Date;
  expirationDate: Date;
  balance: BN;
  share: BN;
  asset: {
    eth: {
      profit: BN;
      dividend: BN;
    };
    idol: {
      profit: BN;
      dividend: BN;
    };
  };
}

export interface LienTokenDividendState {
  termList: Term[];
  lastReceivedTerm: {
    eth: BN;
    idol: BN;
  };
}

export interface LienFairSwapState {
  boxNumber: BN;
  idolTokenPool: BN;
  lienTokenPool: BN;
  totalShare: BN;
  idolTokenPerShare: BN;
  lienTokenPerShare: BN;
  buyPrice: BN;
  sellPrice: BN;
  myShare: BN;
}

export interface RecommendState {
  trancheRecommendBondGroupList: BondGroup[];
  convertLBTBondGroup: BondGroup;
  convertIDOLBondGroup: BondGroup;
}

export interface EventState {
  // FairSwap
  acceptOrdersListMap: Map<string, any[]>;
  executionListMap: Map<string, any[]>;
  moveLiquidityMap: Map<string, any[]>;
  // FairSwap(Only vs LBT)
  removeAfterMaturityMap: Map<string, any[]>;
  // FairSwap(Only vs Lien)
  receiveDividendETH: any[];
  receiveDividendToken: any[];

  // ERC20
  fromTransferListMap: Map<string, any[]>;
  toTransferListMap: Map<string, any[]>;

  // LienToken
  receiveDividend: any[];
  receiveDividendOfEth: any[];

  // BondMaker
  logIssueNewBonds: any[];

  // Wrapper
  logIssueIDOL: any[];
  logIssueLBT: any[];
  logReturnLockedPoolUsers: any[];

  // StableCoin
  logReturnLockedPool: any[];
  logBurnIDOL: any[];
  logMintIDOL: any[];

  // Auction
  logCancelBid: any[];
  logAuctionResult: any[];

  // Auction Board
  logBidMemo: any[];

  pass?: any;
}

export interface TxTriggerState {
  issueNewBonds?: any;
  liquidateBond?: any;
  issueIDOLOnly?: any;
  issueLBTOnly?: any;
  issueLBTAndIDOL?: any;
  burnAll?: any;
  returnLockedPool?: any;
  newTradeData?: any;
  mintApproveData?: any;
  mintNewIDOL?: any;
  executeUnexecutedBox?: any;
  approveERC20?: any;
  addLiquidity?: any;
  removeLiquidity?: any;
  removeAfterMaturity?: any;
  sendTxError?: any;
}

export interface UserTriggerState {
  updateWaitingOrders?: number;
}

export interface TimeTriggerState {
  minTrigger: Date;
}

export interface HistoryState {
  history: any[];
}

export interface GlobalState {
  sdk: any;
  base: BaseState;
  baseMy: BaseMyState;
  auction: AuctionState;
  cache: CacheState;
  lienTokenDividend: LienTokenDividendState;
  lienFairSwap: LienFairSwapState;
  recommend: RecommendState;
  event: EventState;
  txTrigger: TxTriggerState;
  userTrigger: UserTriggerState;
  timeTrigger: TimeTriggerState;
  history: HistoryState;
  error: any;
}

const initialState: GlobalState = {
  sdk: '',
  base: {
    bondGroupMap: new Map(),
    bondMap: new Map(),
    exchangeDataMap: new Map(),
    addedBondGroupMap: new Map(),
    addedBondMap: new Map(),
    addedExchangeDataMap: new Map(),
    exchangeToLBT: new Map(),
    oracleEthPrice: new BigNumber(0),
    oracleVolatility: new BigNumber(0),
    bondGroupIdList: [],
    exchangeAddressSet: new Set(),
  } as BaseState,
  baseMy: {
    ethBalance: new BN(0),
    iDOLBalance: new BN(0),
    lienBalance: new BN(0),
    solidBalanceList: [],
    liquidBalanceList: [],
    pooledIDOLList: [],
    totalDollar: new BN(0),
    totalSolidDollar: new BN(0),
    totalLiquidDollar: new BN(0),
    totalPooledDollar: new BN(0),
    totalLPDollar: new BN(0),
    averageLeverage: new BN(0),
    waitingOrdersList: [],
  } as BaseMyState,
  auction: {
    loading: true,
    auctionGroupMap: new Map(),
    currentAuctionStatus: [],
    currentDisplayAuctionStatus: [],
    bondIDList: [],
    boardStatus: new Map<string, number[]>(),
    sortedBidPrice: new Map<string, number[]>(),
    auctionBid: defaultAuctionBid(),
    auctionPage: 1,
    auctionMaxPage: 1,
    displayPeriod: 'All',
    txModal: false,
  } as AuctionState,
  cache: {} as CacheState,
  event: {
    acceptOrdersListMap: new Map<string, any[]>(),
    executionListMap: new Map<string, any[]>(),
    moveLiquidityMap: new Map<string, any[]>(),
    removeAfterMaturityMap: new Map<string, any[]>(),
    receiveDividendETH: [],
    receiveDividendToken: [],
    fromTransferListMap: new Map<string, any[]>(),
    toTransferListMap: new Map<string, any[]>(),
    receiveDividend: [],
    receiveDividendOfEth: [],
    logIssueNewBonds: [],
    logIssueIDOL: [],
    logIssueLBT: [],
    logReturnLockedPoolUsers: [],
    logReturnLockedPool: [],
    logBurnIDOL: [],
    logMintIDOL: [],
    logCancelBid: [],
    logAuctionResult: [],
    logBidMemo: [],
  } as EventState,
  error: '',
  lienTokenDividend: {
    termList: [],
    lastReceivedTerm: { eth: new BN(0), idol: new BN(0) },
  } as LienTokenDividendState,
  lienFairSwap: {
    boxNumber: new BN(0),
    idolTokenPool: new BN(0),
    lienTokenPool: new BN(0),
    totalShare: new BN(0),
    idolTokenPerShare: new BN(0),
    lienTokenPerShare: new BN(0),
    buyPrice: new BN(0),
    sellPrice: new BN(0),
    myShare: new BN(0),
  } as LienFairSwapState,
  recommend: {
    trancheRecommendBondGroupList: [],
    convertLBTBondGroup: defaultBondGroup(),
    convertIDOLBondGroup: defaultBondGroup(),
  } as RecommendState,
  txTrigger: {} as TxTriggerState,
  userTrigger: {} as UserTriggerState,
  timeTrigger: {
    minTrigger: new Date(),
  } as TimeTriggerState,
  history: {
    history: [],
  },
};

export const Store = createStore(rootReducer, initialState);
