import React from 'react';
import './AuctionState.scss';
import LightWeightChart from 'kaktana-react-lightweight-charts';

interface ChartProps {
  prices: Array<any>;
}

function Chart(props: ChartProps) {
  const options = {
    alignLabels: true,
    timeScale: {
      rightOffset: 8,
      rightBarStaysOnScroll: true,
      borderVisible: false,
      borderColor: '#fff000',
      visible: true,
      timeVisible: true,
      secondsVisible: false,
    },
  };
  const candlestickSeries = [
    {
      data: props.prices,
    },
  ];
  const histogramSeries = [
    {
      options: {
        overlay: true,
        priceFormat: {
          type: 'volume',
        },
        scaleMargins: {
          top: 0.8,
          bottom: 0,
        },
      },
      data: props.prices.map((price) => {
        return {
          time: price.time,
          value: price.volume,
          color:
            price.open < price.close
              ? 'rgba(0, 150, 136, 0.4)'
              : 'rgba(255, 82, 82, 0.4)',
        };
      }),
    },
  ];
  /* TODO: from API
  const areaSeries = [
    {
      options: {
        overlay: true,
        topColor: 'rgba(38, 198, 218, 0.56)',
        bottomColor: 'rgba(38, 198, 218, 0.04)',
        lineColor: 'rgba(38, 198, 218, 1)',
        lineWidth: 2,
      },
      data: props.prices.map((price) => {
        return {
          time: price.time,
          value: price.volume / 1000, // mock
        };
      }),
    },
  ];
  */
  return (
    <LightWeightChart
      options={options}
      candlestickSeries={candlestickSeries}
      histogramSeries={histogramSeries}
      // TODO: from API: areaSeries={areaSeries}
      autoWidth
      height={320}
    />
  );
}

export default Chart;
