import { ExchangeData, BondGroup, LiquidBond, defaultBondGroup } from 'types';
import { bestLev, bestMaturity, maxRecommendSize } from 'config';
import BigNumber from 'bignumber.js';

const useRecommend = () => {
  // ====== Recommend Hooks =====

  const getBaseRecommendBondGroupList = (
    bondGroupMap: Map<number, BondGroup>,
    exchangeDataMap: Map<string, ExchangeData>
  ) => {
    const getBestBondGroup = (
      bondGroupMap: Map<number, BondGroup>,
      exchangeDataMap: Map<string, ExchangeData>
    ): BondGroup[] => {
      const isExpiredMaturity = (bondGroup: BondGroup): boolean => {
        return bondGroup.maturity.getTime() > new Date().getTime();
      };
      const isBestMaturity = (bondGroup: BondGroup): boolean => {
        const expired = bondGroup.maturity.getTime() - new Date().getTime();
        return bestMaturity.since <= expired && expired <= bestMaturity.until;
      };
      const isBestLev = (bondGroup: BondGroup): boolean => {
        if (bondGroup.liquidBondList.length === 0) return false;
        const leverage = bondGroup.liquidBondList[0].leverage.toNumber();
        return bestLev.low <= leverage && leverage <= bestLev.high;
      };
      const getPoolSize = (
        exchangeDataMap: Map<string, ExchangeData>,
        bondGroup: BondGroup
      ): number => {
        if (bondGroup.liquidBondList.length === 0) return 0;
        const exchangeData = exchangeDataMap.get(
          bondGroup.liquidBondList[0].bondAddress
        );
        if (exchangeData === undefined) return 0;
        return exchangeData.baseTokenPool.dp(0).toNumber();
      };

      const getMaturitySortedBondGroup = (
        bondGroupList: BondGroup[]
      ): BondGroup[] => {
        return bondGroupList
          .filter(isBestMaturity)
          .sort(
            (a: BondGroup, b: BondGroup) =>
              b.maturity.getTime() - a.maturity.getTime()
          );
      };
      const getMaturityFilteredBondGroup = (
        bondGroupList: BondGroup[]
      ): BondGroup[] => {
        return bondGroupList.filter(isExpiredMaturity);
      };
      const getLevFilteredBondGroup = (
        bondGroupList: BondGroup[]
      ): BondGroup[] => {
        return bondGroupList.filter(isBestLev);
      };
      const getPooledSortedBondGroup = (
        bondGroupList: BondGroup[]
      ): BondGroup[] => {
        return bondGroupList.sort(
          (a: BondGroup, b: BondGroup) =>
            getPoolSize(exchangeDataMap, b) - getPoolSize(exchangeDataMap, a)
        );
      };

      const bondGroupList = getMaturityFilteredBondGroup(
        Array.from(bondGroupMap.values())
      );
      // 1. sorted by maturity & filtered 6W ~ 12W
      const maturitySortedBondGroup = getMaturitySortedBondGroup(bondGroupList);

      // 2. filtered lev 1.8 ~ 2.2
      const levFilteredBondGroup = getLevFilteredBondGroup(
        maturitySortedBondGroup
      );

      // 3. sorted by pool size.
      const pooledSortedBondGroup = getPooledSortedBondGroup(
        levFilteredBondGroup
      );

      if (pooledSortedBondGroup.length > 0) {
        // 1 & 2 & 3
        return pooledSortedBondGroup.slice(0, maxRecommendSize);
      } else {
        const pooledSortedBondGroup2 = getPooledSortedBondGroup(
          maturitySortedBondGroup
        );
        if (pooledSortedBondGroup2.length > 0) {
          // 1 & 3
          return pooledSortedBondGroup2.slice(0, maxRecommendSize);
        }
        // 1
        return getPooledSortedBondGroup(bondGroupList).slice(
          0,
          maxRecommendSize
        );
      }
    };
    return getBestBondGroup(bondGroupMap, exchangeDataMap);
  };

  const getBestConvertPathBondGroup = (
    bondGroupMap: Map<number, BondGroup>,
    exchangeDataMap: Map<string, ExchangeData>
  ): BondGroup => {
    const best = new BigNumber(0);
    let bestRet: [BondGroup, BigNumber] = [defaultBondGroup(), best];
    for (const bondGroup of Array.from(bondGroupMap.values())) {
      // no exchange or no mint idol sbt.
      if (
        bondGroup.liquidBondList.some(
          (liquid: LiquidBond) =>
            exchangeDataMap.get(liquid.bondAddress) === undefined
        ) ||
        !bondGroup.solidBond.isAcceptable
      ) {
        continue;
      }
      // strikePrice + sum(liquidExchangePrice)
      const estimate = bondGroup.strikePrice.plus(
        bondGroup.liquidBondList.reduce(
          (sum: BigNumber, liquid: LiquidBond) =>
            sum.plus(
              (exchangeDataMap.get(liquid.bondAddress) as ExchangeData)
                .sellPrice
            ),
          new BigNumber(0)
        )
      );
      if (best.lte(estimate)) {
        bestRet = [bondGroup, estimate];
      }
    }
    return bestRet[0];
  };

  const getMaturitySortedBondGroup = (
    bondGroupList: BondGroup[]
  ): BondGroup[] => {
    return bondGroupList.sort(
      (a: BondGroup, b: BondGroup) =>
        b.maturity.getTime() - a.maturity.getTime()
    );
  };

  return {
    getBaseRecommendBondGroupList,
    getBestConvertPathBondGroup,
    getMaturitySortedBondGroup,
  };
};

export default useRecommend;
