import React from 'react';
import './CheckBox.scss';

interface IsLimitProps {
  id: string;
  checked: boolean;
  text: string;
  fnChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
}

export default function (props: IsLimitProps) {
  return (
    <div className="check">
      <div className="item">
        <input
          type="checkbox"
          id={props.id}
          checked={props.checked}
          onChange={(e) => props.fnChange(e)}
        />
        <label htmlFor={props.id}>{props.text}</label>
      </div>
    </div>
  );
}
