import React from 'react';
import './FooterBoxRight.scss';
import { SNSLinks } from 'resource/links';
import Version from 'view/components/presentations/FooterVersion';

function BoxRight() {
  const snsLinks = SNSLinks.map((link, i) => (
    <a href={link.url} key={'link' + i}>
      <img src={link.icon} alt={link.alt} />
    </a>
  ));
  return (
    <div id="header_box_r">
      {/*<span>lien.finace version {version}</span>*/}
      <div id="nav_sns">
        <Version />
        {snsLinks}
      </div>
    </div>
  );
}

export default BoxRight;
