import React, { useState } from 'react';
import { Select, Value } from 'baseui/select';
import { BrandInfo } from 'types';

export interface SelectablePoolSymbol {
  id: string;
  description: string;
  group: string;
  brand: BrandInfo;
}

interface SymbolListBoxProps {
  symbols: SelectablePoolSymbol[];
  onChange: (symbol: any) => void;
}

const getLabel = ({ option }: any) => {
  return (
    <React.Fragment>
      <div className={option.brand.className}>{option.description}</div>
    </React.Fragment>
  );
};

export function SymbolListBox(props: SymbolListBoxProps) {
  const options: any = {
    __ungrouped: props.symbols.filter(
      (symbol: SelectablePoolSymbol) => symbol.group === 'ungrouped'
    ),
  };
  props.symbols.forEach((symbol: SelectablePoolSymbol) => {
    if (symbol.group === 'ungrouped') return;
    if (options[symbol.group]) {
      options[symbol.group].push(symbol);
    } else {
      options[symbol.group] = [symbol];
    }
  });
  const [selectedValue, setValue] = useState<Value>([options.__ungrouped[0]]);
  return (
    <Select
      options={options}
      labelKey="description"
      valueKey="id"
      clearable={false}
      searchable={false}
      onChange={({ value }) => {
        props.onChange(value);
        setValue(value);
      }}
      value={selectedValue}
      getOptionLabel={getLabel}
      getValueLabel={getLabel}
    />
  );
}
