import Contracts from '../helper/Contract';
import { Web3Utils } from 'libs/utils/web3Utils';

export default class Exchange {
  private contracts: Contracts;
  private web3Utils: Web3Utils;

  constructor(conracts: Contracts) {
    this.contracts = conracts;
    this.web3Utils = new Web3Utils();
  }

  public getExchangeInstance(exchangeAddr: string) {
    const exchangeInstance = this.contracts.fairSwap.clone();
    exchangeInstance.options.address = exchangeAddr;
    return exchangeInstance;
  }
}
