import React from 'react';
import { Select, Value, SIZE } from 'baseui/select';
import { period } from 'config';

interface Props {
  current: any;
  handleChange: (period: any) => void;
}

export default (props: Props) => {
  const { handleChange } = props;
  const defaultValue = [{ id: period.all, value: period.all }] as Value;
  const [value, setValue] = React.useState<Value>([defaultValue]);

  return (
    <Select
      size={SIZE.compact}
      options={[
        { id: period.all, value: period.all },
        { id: period.attend, value: period.attend },
        { id: period.bid, value: period.bid },
        { id: period.reveal, value: period.reveal },
        { id: `${period.waiting} & ${period.winning}`, value: period.winning },
        { id: period.closed, value: period.closed },
      ]}
      labelKey="id"
      valueKey="value"
      onChange={({ value }) => {
        if (value.length === 0) {
          value = defaultValue;
        }
        const p = value[0].value;
        setValue(value);
        handleChange(p);
      }}
      value={value}
    />
  );
};
