import React, { useState } from 'react';
import ContentTitle from 'view/components/presentations/ContentTitle';
import { Tabs, TabList, Tab, TabPanel } from 'react-tabs';
import { PoolSymbol } from 'types';
import TradeContainer from 'view/components/pages/trade/TradeContainer';
import ChartContainer from 'view/components/pages/trade/ChartContainer';
import LiquidityUpperContainer from 'view/components/pages/trade/LiquidityUpperContainer';
import LiquidityAddContainer from 'view/components/pages/trade/LiquidityAddContainer';
import LiquidityRemoveContainer from 'view/components/pages/trade/LiquidityRemoveContainer';
import { withRouter } from 'react-router-dom';
import InfoContainer from 'view/components/pages/InfoContainer';
import '../components/presentations/TabHover.scss';

const tabs = ['/trade', '/trade/liquidity'];
function Trade(props: any) {
  const selectedIndex = tabs.indexOf(props.location.pathname);

  const [selectedTradeSymbol, selectTradeSymbol] = useState<PoolSymbol>();
  const [selectedLiquiditySymbol, selectLiquiditySymbol] = useState<
    PoolSymbol
  >();

  return (
    <Tabs
      defaultIndex={selectedIndex !== -1 ? selectedIndex : 0}
      onSelect={(index) => props.history.push(tabs[index])}
    >
      <div id="content_box">
        <ContentTitle title="FairSwap Trade" />
        <ChartContainer poolSymbol={selectedTradeSymbol} />
        <TabList>
          <Tab>
            <div className={selectedIndex === 1 ? `unselect` : ''}>Trade</div>
          </Tab>

          <Tab>
            <div className={selectedIndex === 0 ? `unselect` : ''}>
              Provide Liquidity
            </div>
          </Tab>
        </TabList>

        <TabPanel>
          <TradeContainer onSelectPoolSymbol={selectTradeSymbol} />
          <InfoContainer />
        </TabPanel>
        <TabPanel>
          <LiquidityUpperContainer
            poolSymbol={selectedLiquiditySymbol}
            onSelectPoolSymbol={selectLiquiditySymbol}
          />
          <InfoContainer />
          <br />
          <LiquidityAddContainer poolSymbol={selectedLiquiditySymbol} />
          <LiquidityRemoveContainer poolSymbol={selectedLiquiditySymbol} />
        </TabPanel>
      </div>
    </Tabs>
  );
}

export default withRouter(Trade);
