import React from 'react';
import './Navigation.scss';
import { NavLink } from 'react-router-dom';

function Navigation() {
  const selectedStyle = {
    textShadow: '0px 0px 2.5px #0FF',
    color: '#FFF',
    transition: 'text-shadow, color 0.25s',
  };
  return (
    <div id="nav">
      <NavLink
        to="/convert"
        activeStyle={selectedStyle}
        isActive={(match, location) => {
          return (
            !!match ||
            location.pathname === '/' ||
            location.pathname === `${process.env.PUBLIC_URL}/`
          );
        }}
      >
        Quick Convert
      </NavLink>
      <hr />
      <NavLink to="/trade" activeStyle={selectedStyle}>
        FairSwap Trade
      </NavLink>
      <hr />
      <NavLink to="/tranche" activeStyle={selectedStyle}>
        Manual Tranche
      </NavLink>
      <hr />
      <NavLink to="/auction" activeStyle={selectedStyle}>
        iDOL Auction
      </NavLink>
      <hr />
      <NavLink to="/lien_token" activeStyle={selectedStyle}>
        LienToken
      </NavLink>
      <hr />
    </div>
  );
}

export default Navigation;
