import React from 'react';
import './WithIcon.scss';

import { Tag } from 'baseui/tag';
import { Button, SIZE, KIND } from 'baseui/button';
import {
  Unstable_StatefulDataTable as Table,
  CategoricalColumn,
  CustomColumn,
  NumericalColumn,
  NUMERICAL_FORMATS,
} from 'baseui/data-table';
import BigNumber from 'bignumber.js';
import { StateString } from 'types';
import { statusToTagKind } from 'libs/utils/view';
import { useStyletron } from 'styletron-react';
import { IDOLBrand } from 'resource/brands';
import GettingData from './GettingData';
import NoData from './NoData';
import MaturityCell from './MaturityCell';
import { dateSortFn, BNSortFN } from 'libs/utils/utils';

export interface PooledIDOLRowData {
  poolID: string;
  strikePrice: BigNumber;
  maturity: Date;
  closingTime: Date;
  amount: BigNumber;
  actualRedeemAmount: BigNumber;
  status: StateString;
  wrapper: boolean;
  onReturn: () => void;
}

export const defaultPooledIDOLRowData = (): PooledIDOLRowData => {
  return {
    poolID: '',
    strikePrice: new BigNumber(0),
    maturity: new Date(),
    closingTime: new Date(),
    amount: new BigNumber(0),
    status: '',
    wrapper: false,
    onReturn: () => {}, // eslint-enable-line no-empty-function
  } as PooledIDOLRowData;
};

const columns = [
  CategoricalColumn({
    title: 'PoolID',
    maxWidth: 80,
    mapDataToValue: (data: PooledIDOLRowData) => data.poolID,
  }),
  NumericalColumn({
    title: 'SBT StrikePrice',
    format: NUMERICAL_FORMATS.ACCOUNTING,
    mapDataToValue: (data: PooledIDOLRowData) => data.strikePrice.toString(),
  }),
  CustomColumn({
    title: 'SBT Maturity',
    mapDataToValue: (data: PooledIDOLRowData) => data.maturity,
    renderCell: function Cell(props: { value: Date }) {
      return <MaturityCell value={props.value} />;
    },
    sortFn: dateSortFn,
    sortable: true,
  }),
  CustomColumn({
    title: 'Pool Redeem Time',
    mapDataToValue: (data: PooledIDOLRowData) => data.closingTime,
    renderCell: function Cell(props: { value: Date }) {
      return <MaturityCell value={props.value} />;
    },
    sortFn: dateSortFn,
    sortable: true,
  }),
  CustomColumn({
    title: 'Max Redeemable Amount',
    maxWidth: 185,
    mapDataToValue: (data: PooledIDOLRowData) => data.amount,
    renderCell: function Cell(props: { value: BigNumber }) {
      return (
        <div className={IDOLBrand.className}>
          {IDOLBrand.view(props.value.toString())}
        </div>
      );
    },
    sortFn: BNSortFN,
    sortable: true,
  }),
  CustomColumn({
    title: 'Actual Redeem Amount',
    maxWidth: 180,
    mapDataToValue: (data: PooledIDOLRowData) => data.actualRedeemAmount,
    renderCell: function Cell(props: { value: BigNumber }) {
      return (
        <div className={IDOLBrand.className}>
          {props.value.toString() === '0'
            ? '-'
            : IDOLBrand.view(props.value.toString())}
        </div>
      );
    },
    sortFn: BNSortFN,
    sortable: true,
  }),
  CustomColumn<{ color: string }, {}>({
    title: 'Status',
    mapDataToValue: (data: PooledIDOLRowData) =>
      data.status === 'End' ? 'Redeemable' : 'To be redeemable',
    renderCell: function Cell(props: any) {
      return (
        <Tag closeable={false} kind={statusToTagKind(props.value)}>
          {props.value}
        </Tag>
      );
    },
  }),
  CustomColumn({
    title: '',
    precision: 2,
    mapDataToValue: (data: PooledIDOLRowData) => data,
    renderCell: function Cell(props: any) {
      const [css] = useStyletron();
      const buttonComponent = (
        <Button
          kind={KIND.primary}
          size={SIZE.compact}
          onClick={(e) => {
            props.value.onReturn();
          }}
          disabled={props.value.status !== 'End'}
        >
          Return
        </Button>
      );
      return (
        <div
          className={css({
            alignItems: 'center',
            display: 'flex',
          })}
        >
          {buttonComponent}
        </div>
      );
    },
  }),
];

export interface PooledIDOLTableProps {
  rows: PooledIDOLRowData[];
  loading: boolean;
}

export default (props: PooledIDOLTableProps) => {
  return (
    <div style={{ height: `${props.rows.length * 55 + 170}px` }}>
      <Table
        columns={columns}
        rows={props.rows}
        rowHeight={55}
        loading={props.loading}
        loadingMessage={() => <GettingData />}
        emptyMessage={() => <NoData />}
      />
    </div>
  );
};
