import React from 'react';
import './WithIcon.scss';
import ItemNumber from 'view/components/presentations/ItemNumber';
import { IDOLBrand, PooledIDOLBrand } from 'resource/brands';
import BigNumber from 'bignumber.js';
import { formatter } from 'libs/utils/utils';
import {
  totalLiquidityDescription,
  exchangedIDOLDescription,
  exchangeRateDescription,
  mintedIDOLDescription,
  pooledIDOLDescription,
  poolMaturityDescription,
} from 'resource/description/index';

export interface ConvertInfoGroupRows {
  maturity: Date;
  pooledIDOL: BigNumber;
  mintedIDOL: BigNumber;
  exchangeRate: BigNumber;
  exchangedIDOL: BigNumber;
  totalLiquidity: BigNumber;
  actualIDOL: BigNumber;
}

interface ConvertInfoUnitTableProps {
  data: ConvertInfoGroupRows[];
  short?: boolean;
  exchangeDisable?: boolean;
}

export default (props: ConvertInfoUnitTableProps) => {
  return (
    <div>
      {!props.short && (
        <ItemNumber
          name={'Pool Maturity'}
          value={props.data[0].maturity.toLocaleDateString()}
          isHelp={true}
          description={poolMaturityDescription}
        />
      )}
      {!props.short && (
        <ItemNumber
          name={'Pooled iDOL'}
          value={PooledIDOLBrand.view(
            props.data[0].pooledIDOL.dp(2).toString()
          )}
          isHelp={true}
          description={pooledIDOLDescription}
        />
      )}
      <ItemNumber
        name={'Minted iDOL'}
        value={IDOLBrand.view(props.data[0].mintedIDOL.dp(2).toString())}
        isHelp={true}
        description={mintedIDOLDescription}
      />
      {!props.exchangeDisable && (
        <ItemNumber
          name={'Exchange rate(estimated)'}
          value={`${props.data[0].exchangeRate.dp(2).toString()} iDOL/LBT`}
          isHelp={true}
          description={exchangeRateDescription}
        />
      )}
      {!props.exchangeDisable && (
        <ItemNumber
          name={'Exchanged iDOL(estimated)'}
          value={IDOLBrand.view(props.data[0].exchangedIDOL.dp(2).toString())}
          isHelp={true}
          description={exchangedIDOLDescription}
        />
      )}
      {!props.exchangeDisable && (
        <ItemNumber
          name={'Total Liquidity'}
          value={
            formatter(props.data[0].totalLiquidity).value.toString() +
            formatter(props.data[0].totalLiquidity).unit
          }
          brand={IDOLBrand}
          isHelp={true}
          description={totalLiquidityDescription}
        />
      )}
      <br />
    </div>
  );
};
