import { useDispatch } from 'react-redux';
import { lien } from 'actions/useLien';
import { BigNumber as BN } from 'bignumber.js';

const useBondMaker = () => {
  const dispatch = useDispatch();
  const issueNewBonds = async (bondGroupID: number, inputETH: string | BN) => {
    try {
      if (lien.address) {
        const res = await lien.bondMaker.issueNewBonds(bondGroupID, inputETH);
        dispatch({
          type: 'ISSUE_NEW_BONDS',
          payload: {
            txHash: res,
            bondGroupID: bondGroupID,
          },
        });
      }
      //TODO: add Transaction Manager & Notification
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
      return false;
    }
    return true;
  };
  return { issueNewBonds };
};

export default useBondMaker;
