export const errorReducer = (state: any, action: any) => {
  switch (action.type) {
    case 'SEND_TX_ERROR':
      return {
        ...state,
        errorList: action.payload,
      };
    case 'CALL_ERROR':
      return {
        ...state,
        errorList: action.payload,
      };
    default:
      return state;
  }
};
