import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { RadioGroup, Radio, ALIGN } from 'baseui/radio';
import Chart from 'view/components/presentations/Chart';
import { PoolSymbol } from 'types';
import CONTRACT_ADDR from 'libs/src/helper/Constant';

const CHART_ENDPOINT =
  'https://gist.githubusercontent.com/lien-testnet002/79a83a090175c3b6c7e631edd1c2a995/raw';

interface ChartProps {
  poolSymbol: PoolSymbol | undefined;
}

export default function (props: ChartProps) {
  const [duration, setDuration] = React.useState('3600');
  const [chartData, setChartData] = useState([]);

  useEffect(() => {
    (async () => {
      const response = await axios.get(
        `${CHART_ENDPOINT}/${
          props.poolSymbol && props.poolSymbol.exchangeAddress
            ? props.poolSymbol.exchangeAddress
            : CONTRACT_ADDR.LIEN_EXCHANGE
        }_${duration}.json`
      );
      const entities = response.data;
      let lastPrice: Number | null = null;
      while (entities.length) {
        if (!entities[0].open) {
          entities.shift();
        } else {
          lastPrice = Number(entities[0].close);
          break;
        }
      }
      if (!lastPrice) return;
      setChartData(
        entities.map((entity: any) => {
          if (entity.close) lastPrice = Number(entity.close);
          const time = Date.parse(entity.startAt) / 1000;
          return entity.open
            ? {
                time,
                open: Number(entity.open),
                high: Number(entity.high),
                low: Number(entity.low),
                close: Number(entity.close),
                volume: Number(entity.volume),
              }
            : {
                time,
                open: lastPrice,
                high: lastPrice,
                low: lastPrice,
                close: lastPrice,
                volume: Number(entity.volume),
              };
        })
      );
    })();
  }, [props.poolSymbol, duration]);

  return (
    <div>
      <div className="content_box_4">
        <div className="box">
          <RadioGroup
            value={duration}
            onChange={(e) => setDuration(e.target.value)}
            name="number"
            align={ALIGN.horizontal}
          >
            <Radio value="3600">1H</Radio>
            <Radio value="86400">1D</Radio>
          </RadioGroup>
        </div>
        <Chart prices={chartData} />
      </div>
    </div>
  );
}
