import React from 'react';
import './ItemPrice.scss';
import './WithIcon.scss';
import { SolidBond } from 'types';
import SolidListUnitTable, { SBTGroupRows } from './SolidListUnitTable';
import { SolidBrand } from 'resource/brands';

interface ItemSolidProps {
  name: string;
  value: string;
  sbt: SolidBond;
}

export default (props: ItemSolidProps) => {
  const data: SBTGroupRows[] = [
    {
      maturity: props.sbt.maturity,
      strikePrice: props.sbt.strikePrice.dp(2).toNumber(),
      theoreticalPrice: props.sbt.theoreticalPrice.dp(2).toNumber(),
      isAcceptable: props.sbt.isAcceptable,
    },
  ];
  return (
    <div>
      <div className="price">
        <span>{props.name}</span>
        <div className={SolidBrand.className}>
          <span>{SolidBrand.view(props.value)}</span>
        </div>
      </div>
      <SolidListUnitTable data={data} />
    </div>
  );
};
