import { TimeTriggerState } from './store';
import { SECOND, MINUTE } from 'resource/constants';

export const timeTriggerReducer = (
  state: TimeTriggerState,
  action: { type: string; payload: any }
) => {
  switch (action.type) {
    case 'UPDATE_MIN_TRIGGER':
      // eps 1 seconds. protect from multi-update.
      if (
        state.minTrigger.getTime() <=
        action.payload.getTime() - MINUTE + SECOND
      ) {
        return {
          ...state,
          minTrigger: action.payload,
        };
      }
      return state;
    default:
      return state;
  }
};
