import React from 'react';
import { useStyletron } from 'baseui';
import BigNumber from 'bignumber.js';
import ItemNumber from 'view/components/presentations/ItemNumber';
import { formatter } from 'libs/utils/utils';
import { IDOLBrand } from 'resource/brands';

export interface LBTGroupRows {
  maturity: Date;
  strikePrice: number;
  lbtPrice: number;
  leverage: number;
  totalLiquidity: BigNumber;
}

interface LiquidListUnitTableProps {
  data: LBTGroupRows[];
}

export default (props: LiquidListUnitTableProps) => {
  const [css] = useStyletron();
  return (
    <div className={css({ height: '120px' })}>
      <div>
        <ItemNumber
          name={'Maturity'}
          value={props.data[0].maturity.toLocaleDateString()}
        />

        <ItemNumber
          name={'Strike Price'}
          value={'$' + props.data[0].strikePrice.toString()}
        />

        <ItemNumber
          name={'Price'}
          value={'$' + props.data[0].lbtPrice.toString()}
        />

        <ItemNumber
          name={'LBT Leverage'}
          value={new BigNumber(props.data[0].leverage).dp(3).toString() + 'x'}
        />

        <ItemNumber
          name={'Total Liquidity'}
          value={
            formatter(props.data[0].totalLiquidity).value.toString() +
            formatter(props.data[0].totalLiquidity).unit
          }
          brand={IDOLBrand}
        />
        <br />
      </div>
    </div>
  );
};
