import { lien } from 'actions/useLien';
import { useDispatch } from 'react-redux';

export const useLienToken = () => {
  const dispatch = useDispatch();
  const receiveDividendsInIDOL = async () => {
    try {
      if (lien.address) {
        await lien.lienToken.receiveDividend(
          lien.contracts.stableCoinAddr,
          lien.address
        );
        dispatch({
          type: 'RECEIVE_DIVIDENDS_IN_IDOL',
          payload: {},
        });
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  };
  const receiveDividendsInETH = async () => {
    try {
      if (lien.address) {
        await lien.lienToken.receiveDividendOfETH(lien.address);
        dispatch({
          type: 'RECEIVE_DIVIDENDS_IN_ETH',
          payload: {},
        });
      }
    } catch (error) {
      dispatch({
        type: 'SEND_TX_ERROR',
        payload: error,
      });
    }
  };
  return { receiveDividendsInIDOL, receiveDividendsInETH };
};
