import React, { useState, useEffect, useCallback } from 'react';
import SubTitle from 'view/components/presentations/SubTitle';
import { BigNumber as BN } from 'bignumber.js';
import ListedAuctionTable from 'view/components/presentations/ListedAuctionTable';
import AuctionPeriodSelect from 'view/components/presentations/AuctionPeriodSelect';
import AuctionDetailContainer from 'view/components/pages/AucationDetailContainer';
import { GlobalState } from 'reducer/store';
import {
  AuctionGroup,
  StateString,
  AuctionResult,
  AuctionBid,
  UserStatus,
} from 'types';
import { getPeriodProgress, getUserBids } from 'libs/utils/utils';
import { mocked, auctionMock } from 'mock';
import { useSelector } from 'react-redux';
import useAuction from 'actions/useAuction';
import { userStatus } from 'config';

export interface AuctionGroupColumn {
  id: number;
  bondID: string;
  auctionID: string;
  bondAddress: string;
  strikePrice: BN;
  maturity: Date;
  closingTime: Date;
  amount: BN;
  period: StateString;
  bidList: AuctionBid[];
  progress: number;
  bidAmount: BN;
  isParticipant: boolean;
  userStatus: UserStatus;
  mARDone: boolean;
  result?: AuctionResult;
  board?: {
    sorted: BN[];
    amounts: BN[];
  };
  periodProgress: {
    percent: number;
    label: string;
  };
}

const defaultAuctionGroupColumn: () => AuctionGroupColumn = () =>
  ({
    id: 0,
    bondID: '',
    auctionID: '',
    bondAddress: '',
    strikePrice: new BN(0),
    maturity: new Date(0),
    closingTime: new Date(0),
    amount: new BN(0),
    period: '',
    bidList: [],
    progress: 0,
    bidAmount: new BN(0),
    isParticipant: false,
    userStatus: userStatus.noParticipation,
    mARDone: false,
    periodProgress: {
      percent: 0,
      label: '',
    },
  } as AuctionGroupColumn);

function AuctionListContainer() {
  const auctionGroupMap = useSelector(
    (state: GlobalState) => state.auction.auctionGroupMap
  );
  const loading = useSelector((state: GlobalState) => state.auction.loading);
  const maxPage = useSelector(
    (state: GlobalState) => state.auction.auctionMaxPage
  );
  const current = useSelector(
    (state: GlobalState) => state.auction.displayPeriod
  );
  const txModal = useSelector((state: GlobalState) => state.auction.txModal);
  const [currentProcessGroup, setProcessGroup] = useState(1); // eslint-disable-line
  const [currentPage, setCurrentPage] = React.useState(1);
  const {
    setAuctionPage,
    setDisplayPeriod,
    setLoading,
    setTxModal,
  } = useAuction();

  const makeAuctionGroupColumn = useCallback(
    (auctionList: AuctionGroup[]) => {
      if (mocked) {
        return [{ id: 1, data: auctionMock }];
      }

      auctionList.sort((a, b) => {
        const aClosing = a.auctionStatus.closingTime.getTime();
        const bClosing = b.auctionStatus.closingTime.getTime();
        return bClosing - aClosing;
      });
      const list: {
        id: any;
        data: AuctionGroupColumn;
      }[] = [];
      for (let i = 0; i < auctionList.length; i++) {
        const AuctionGroup = auctionList[i];
        const isParticipant = AuctionGroup.isParticipant;
        const period = AuctionGroup.period;
        const { closingTime, isEmergency } = AuctionGroup.auctionStatus;
        const periodProgress = getPeriodProgress(
          period,
          closingTime,
          isEmergency
        );
        const result = {
          id: i + 1,
          data: {
            id: i + 1,
            bondAddress: AuctionGroup.bondAddress,
            auctionID: AuctionGroup.auctionID,
            strikePrice: AuctionGroup.strikePrice,
            maturity: AuctionGroup.maturity,
            closingTime,
            amount: AuctionGroup.auctionStatus.auctionAmount,
            period,
            bidList: getUserBids(AuctionGroup.auctionID),
            bidAmount: AuctionGroup.auctionStatus.totalSBTAmountBid,
            isParticipant: isParticipant,
            userStatus: AuctionGroup.userStatus,
            mARDone: AuctionGroup.mARDone,
            periodProgress,
          } as AuctionGroupColumn,
        };
        list.push(result);
      }
      return list;
    },
    [] // eslint-disable-line
  ); // eslint-disable-line

  const [selectedDatum, setSelectedDatum] = useState<AuctionGroupColumn>(
    defaultAuctionGroupColumn()
  );

  const onClick = (event: { datum?: {}; index?: number }) => {
    if (event.index === undefined) {
      setSelectedDatum(defaultAuctionGroupColumn());
    } else {
      setTxModal(true);
      setSelectedDatum(event.datum as AuctionGroupColumn);
    }
  };

  const [datum, setDatum] = useState<{ id: any; data: AuctionGroupColumn }[]>(
    []
  );

  // AUCTION STATE
  useEffect(() => {
    if (loading) {
      return;
    }
    setDatum(makeAuctionGroupColumn(Array.from(auctionGroupMap.values())));
  }, [auctionGroupMap, loading]); // eslint-disable-line

  useEffect(() => {
    return () => setAuctionPage(1);
  }, []); // eslint-disable-line

  return (
    <div className="content_box_3">
      <SubTitle title="Available to Auction" />
      <div className="box">
        <AuctionPeriodSelect
          current={current}
          handleChange={(period: any) => {
            setCurrentPage(1);
            setDisplayPeriod(period);
          }}
        />

        <ListedAuctionTable
          loading={loading}
          rows={datum}
          currentPage={currentPage}
          maxPageNum={maxPage}
          setCurrentPage={setCurrentPage}
          onClickRow={onClick}
          processNextAuctionGroup={setProcessGroup}
          setLoading={setLoading}
        />
        <AuctionDetailContainer
          isOpen={txModal}
          setIsOpen={setTxModal}
          auctionSelectDatum={selectedDatum}
        />
      </div>
    </div>
  );
}

export default AuctionListContainer;
