import { BigNumber as BN } from 'bignumber.js';
import { BondGroup } from 'types';

// isValidBalance
// 1. less than valid balance.
// 2. more than and equal to 0.
export const isValidBalance = (balance: BN, maxValue: BN): boolean =>
  balance.isLessThanOrEqualTo(maxValue) && new BN(0).isLessThan(balance);

// isValidBondGroup
// 1. id not equal -1
// 2. liquid BondList length more than 0.
export const isValidBondGroup = (bondGroup?: BondGroup): boolean =>
  !!bondGroup && bondGroup.id !== -1 && bondGroup.liquidBondList.length > 0;
